<template>
  <section  
    v-if="dataListKT.length !== 0"
    class="kelas-terlaris"
  >
    <div class="container">
      <div class="title-kt">
        <h1>Kelas Terlaris</h1>
      </div>
      <carousel
        :autoplay="true"
        :nav="false"
        :dots="true"
        :responsive="{
        0:{
            items:1,
        },
        700:{
            items:2,
        },
        1000:{
            items:3,
					}
				}"
      >
        <div class="child-kelas" v-for="(kelas, idx) in dataListKT" v-bind:key="idx">
          <div class="kelas">
            <div class="thumb">
              <img v-bind:src="kelas.banner" alt />
            </div>
            <div class="kelas-info">
              <h4>{{ kelas.title }}</h4>
              <div class="lingkaran">
                <star-rating :rating="5" :read-only="true" :star-size="20"></star-rating>
              </div>
              <div class="price">
                <p class="del-text">Rp{{ kelas.price }}</p>
                <p class="price-disc">Rp{{ kelas.price_disc }}</p>
              </div>
              <div class="author">By {{ kelas.mentor }}</div>
              <router-link v-bind:to="kelas.url" class="text-white">
                <button class="pilih-kelas btn">Pilih Kelas</button>
              </router-link>
            </div>
          </div>
        </div>
      </carousel>
    </div>
  </section>
</template>

<script>
import $ from "jquery";
import carousel from "vue-owl-carousel";
import axios from 'axios';
import StarRating from "vue-star-rating";

export default {
  name: "KelasTerlaris",
  components: {
    carousel,
    StarRating
  },
  data() {
    return {
      dataKelasTerlaris: [
        {
          id: 1,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        },
        {
          id: 2,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        },
        {
          id: 3,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        },
        {
          id: 4,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        },
        {
          id: 5,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        },
        {
          id: 6,
          title: "Belajar Content Writing",
          mentor: "Andri Darmawan",
          rating: "4.7",
          amount_rating: "500",
          price: "100.000",
					price_disc: "99.000",
					url: '/detail-kelas/belajar-content-writing'
        }
      ],
      dataListKT: [],
    };
  },
  methods: {
    init() {
      // banner: "40069203c1e39d7f85076885798e78ac.jpg"
      // mentor: "MentorBox.id"
      // name: "Ghost Kitchen"
      // price: 1000000
      // promo: 149000
      // reviews: {count: 2, rating: 5}
      // slug: "ghost-kitchen"
      // uuid: "6452df8a-3042-4402-9a7c-8ed23f3e16c8"

      this.getListKelasTerlaris();
    },
    getListKelasTerlaris() {
      // listKelasTerlarisPage
      this.$root.loadingData().show();

      axios.get( this.$root.listKelasTerlarisPage() )
      .then((res) => {
        let data = res.data;
        let result = data.result;
        this.dataListKT = result.map((e) => {
          let obj = {
            title: e.name,
            banner: this.$root.getPhotoUser() + `/${e.banner}`,
            mentor: e.mentor,
            rating: e.reviews.rating,
            amount_rating: e.reviews.count,
            price: e.price,
            price_disc: e.promo,
            url: `/detail-kelas/${e.slug}`
          };
          return obj;
        })
        console.log('res ', result);
        this.$root.loadingData().hide();
      })
      .catch((err) => {
        console.log('err ', err)
        this.$root.loadingData().hide();
      })
    },
    createCarousel() {
      $(".active-testimonial");
    },

  },
  mounted() {
    this.init();
  }
};
</script>

