<template>
  <div class="executor-withdrawal modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Submit Withdrawal</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />
    <div class="content-exe-with">
      <div class="form-input-mbx">
        <label for="bank">Bank</label>
        <input
          type="text"
          class="form-control"
          name="bank"
          placeholder="Bank"
          v-model="dataExecutor.bank"
        />
        <span v-if="!cannotNull.bank" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="rekening">Rekening</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="rekening"
          placeholder="Rekening"
          v-model="dataExecutor.rekening"
        />
        <span v-if="!cannotNull.rekening" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="pemilik">Pemilik</label>
        <input
          type="text"
          class="form-control"
          name="pemilik"
          placeholder="Pemilik"
          v-model="dataExecutor.pemilik"
        />
        <span v-if="!cannotNull.pemilik" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="jumlah">Jumlah</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="jumlah"
          placeholder="Jumlah"
          v-model="dataExecutor.jumlah"
        />
        <span v-if="!cannotNull.jumlah" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="note">Catatan</label>
        <textarea
          type="text"
          class="form-control"
          name="note"
          placeholder="Catatan"
          v-model="dataExecutor.note"
        ></textarea>
        <span v-if="!cannotNull.note" style="color: red;">This field is required.</span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelWithdrawal()" class="btn btn-danger">Cancel</button>
      <button @click="submitWithdrawal()" class="btn btn-success">Submit</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ExecutorWithdrawal",
  components: {},
  data() {
    return {
      dataExecutor: {
        bank: "",
        rekening: "",
        pemilik: "",
        jumlah: "",
        note: ""
      },
      cannotNull: {
        bank: true,
        rekening: true,
        pemilik: true,
        jumlah: true,
        note: true
      }
    };
  },
  methods: {
    init() {
      // Jumlah,
      // Bank,
      // NoRek,
      // Note
    },
    closeModal() {
      this.$modal.hide("executor-withdrawal");
    },
    submitWithdrawal() {
      let body = this.dataExecutor;
      this.postWithdrawal(body);
    },
    postWithdrawal(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listWithdrawalAffiliate(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostWithdrawal().success(
            `Submit Withdrawal Berhasil.`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostWithdrawal().failed(resErr.data.message);
        });
    },
    afterPostWithdrawal() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelWithdrawal() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin Submit Withdrawal ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-withdrawal {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-exe-with {
  }
}
</style>