<template>
  <div class="content-dashboard-admin">
    <div class="content-da">
      <div class="section-left">
        <h5>Dashboard Admin</h5>
        <div class="left-menu">
          <ul class="dashboard-left-menu">
            <template v-for="(menu, idxParent) in menuAdmin">
              <li
                v-bind:key="idxParent"
                v-bind:class="{'menu-has-children': menu.has_children, [ menu.slug ]: menu.slug, 'parent-active': activeMenu.parent == menu.slug}"
              >
                <i
                  v-if="menu.has_children"
                  class="fa fa-chevron-down"
                  v-bind:id="menu.id_menus"
                  v-on:click="showChildrenMenu(menu.id_menus)"
                ></i>
                <!-- 'menu-item-active-child': activeMenu.parent == menu.slug -->
                <a
                  v-bind:class="{'menu-item-active': mainSection == menu.slug}"
                  v-on:click="showMainPage().parent(menu.has_children, menu.slug)"
                >{{ menu.title_menu }}</a>
                <ul v-if="menu.has_children" style="display: none;">
                  <li v-for="(a, idxChild) in menu.menus" v-bind:key="idxChild">
                    <a
                      v-bind:class="{'menu-active': activeMenu.child == a.slug}"
                      @click="showMainPage().child(a.has_children, a.slug, menu.slug)"
                    >{{ a.title_menu }}</a>
                  </li>
                </ul>
              </li>
            </template>
          </ul>
        </div>
        <!-- <p>LogOut</p> -->
      </div>
      <div class="section-right">
        <div class="container">
          <template v-if="mainSection == 'my-profile'">
            <ContentProfileUser />
          </template>
          <template v-if="mainSection == 'main-dashboard'">
            <MainDashboardAdmin />
          </template>

          <template v-if="mainSection == 'list-kelas'">
            <ListKelas />
          </template>
          <template v-if="mainSection == 'list-kategori'">
            <ListKategori />
          </template>
          <template v-if="mainSection == 'flash-sale'">
            <ListFlashSale />
          </template>
          <template v-if="mainSection == 'voucher-sale'">
            <ListVoucherSale />
          </template>
          <template v-if="mainSection == 'jadwal-kelas'">
            <ListJadwal />
          </template>

          <template v-if="mainSection == 'user'">
            <ListUser />
          </template>

          <template v-if="mainSection == 'list-transaksi'">
            <ListTransaksi />
          </template>
          <template v-if="mainSection == 'list-bank'">
            <ListBank />
          </template>
          <template v-if="mainSection == 'list-pengeluaran'">
            <ListPengeluaran />
          </template>

          <template v-if="mainSection == 'list-komisi'">
            <ListKomisi />
          </template>
          <template v-if="mainSection == 'list-withdrawal'">
            <ListWithdrawal />
          </template>
          <template v-if="mainSection == 'saldo-member'">
            <SaldoMember />
          </template>
        </div>
      </div>

      <!-- <h3 class="title-da">Dashboard Admin</h3>
      <div class="parent-tab">
        <ul class="tabs">
          <li
            v-for="tab in dataTabsAdmin"
            v-bind:key="tab.id"
            v-bind:class="{active: tab.sub_id == sectionTab, [ sectionTab ] : tab.active }"
            v-on:click="changeTabH(tab.sub_id)"
          >
            <a>{{ tab.title }}</a>
          </li>
          <hr />
        </ul>
      </div>
      <div class="content-tabs-h">
        <div class="tab-kelas" v-if="sectionTab == 'kelas'">
          <div class="tabs-vertical">
            <ul class="tab-v">
              <li
                v-for="(tab, index) in dataTabsVKelas"
                v-bind:key="index"
                v-bind:active="sectionTabVKelas == tab.sub_id"
                v-bind:class="{active: tab.active}"
              >
                <span v-on:click="changeTabsV(tab.sub_id)">{{ tab.title }}</span>
              </li>
            </ul>
          </div>
          <div class="content-tabs-v">
            <div class="list-kelas" v-if="sectionTabVKelas == 'list-kelas'">
              <ListKelas />
            </div>
            <div class="list-kategori" v-if="sectionTabVKelas == 'list-kategori'">
              <ListKategori />
            </div>
            <div class="list-flash-sale" v-if="sectionTabVKelas == 'flash-sale'">
              <ListFlashSale />
            </div>
            <div class="list-voucher-sale" v-if="sectionTabVKelas == 'voucher-sale'">
              <ListVoucherSale />
            </div>
          </div>
        </div>
        <div class="tab-user" v-else-if="sectionTab == 'user'">
          <ListUser />
        </div>
        <div class="tab-transaksi" v-else-if="sectionTab == 'transaksi'"></div>
        <div class="tab-komisi" v-else-if="sectionTab == 'komisi'"></div>
      </div>-->
    </div>
  </div>
</template>

<script>
import $ from "jquery";

import ContentProfileUser from "../ProfileUser/ContentProfileUser";
import MainDashboardAdmin from "./MainDashboardAdmin";

// for tab kelas
import ListKelas from "./ListKelas/ListKelas";
import ListKategori from "./ListKategori/ListKategori";
import ListFlashSale from "./ListFlashSale/ListFlashSale";
import ListVoucherSale from "./ListVoucherSale/ListVoucherSale";
import ListJadwal from "./ListJadwal/ListJadwal";

// for tab user
import ListUser from "./ListUser/ListUser";

// for tab transaksi
import ListTransaksi from "./Transaksi/ListTransaksi/ListTransaksi";
import ListBank from "./Transaksi/ListBank/ListBank";
import ListPengeluaran from "./Transaksi/ListPengeluaran/ListPengeluaran";

// for tab komisi
import ListKomisi from "./Komisi/ListKomisi";
import ListWithdrawal from "./Komisi/ListWIthdrawal";
import SaldoMember from "./Komisi/SaldoMember";

export default {
  name: "ContentDashboardAdmin",
  components: {
    ContentProfileUser,
    MainDashboardAdmin,

    // for tab kelas
    ListKelas,
    ListKategori,
    ListFlashSale,
    ListVoucherSale,
    ListJadwal,
    // for tab user
    ListUser,
    // for tab transaksi
    ListTransaksi,
    ListBank,
    ListPengeluaran,
    // for tab komisi
    ListKomisi,
    ListWithdrawal,
    SaldoMember
  },
  data() {
    return {
      mainSection: "main-dashboard",
      activeMenu: {
        parent: "main-dashboard",
        child: ""
      },

      sectionTab: "user",
      sectionTabVKelas: "list-kelas",
      dataTabsVKelas: [
        {
          id: 1,
          title: "List Kelas",
          sub_id: "list-kelas",
          active: true
        },
        {
          id: 2,
          title: "List Kategori",
          sub_id: "list-kategori",
          active: false
        },
        {
          id: 3,
          title: "Flash Sale",
          sub_id: "flash-sale",
          active: false
        },
        {
          id: 4,
          title: "Voucher Sale",
          sub_id: "voucher-sale",
          active: false
        }
      ],
      dataTabsAdmin: [
        {
          id: 1,
          title: "Kelas",
          sub_id: "kelas",
          active: true
        },
        {
          id: 2,
          title: "User",
          sub_id: "user",
          active: false
        },
        {
          id: 3,
          title: "Transaksi",
          sub_id: "transaksi",
          active: false
        },
        {
          id: 4,
          title: "Komisi",
          sub_id: "komisi",
          active: false
        }
      ],
      menuAdmin: this.$root.menuAdmin()
    };
  },
  methods: {
    init() {
      this.getMenu();
      this.$root.goToTop();
    },
    getMenu() {
      console.log("menu admin ", this.menuAdmin);
    },
    changeTabsV(id) {
      this.sectionTabVKelas = id;
    },
    changeTabH(subId) {
      let a = this.dataTabsAdmin;

      for (let i = 0; i < a.length; i++) {
        if (a[i].sub_id == subId) {
          a[i].active = true;
          this.sectionTab = subId;
        } else {
          a[i].active = false;
        }
      }
    },
    showChildrenMenu(id) {
      $("#" + id)
        .nextAll("ul")
        .eq(0)
        .slideToggle();
      $("#" + id).toggleClass("fa-chevron-up fa-chevron-down");
    },
    showMainPage() {
      let vm = this;
      return {
        parent(act, id) {
          if (!act) {
            vm.mainSection = id;
            vm.activeMenu.parent = "";
            vm.activeMenu.child = "";
          }
        },
        child(act, id, idParent) {
          if (!act) {
            vm.mainSection = id;
            vm.activeMenu.child = id;
            vm.activeMenu.parent = idParent;
          }
        }
      };
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-dashboard-admin {
  font-family: "Montserrat";
  .content-da {
    display: flex;
    // flex-direction: column;
    // padding: 5px 20px;
    flex-direction: row;
    height: auto;
    min-height: 550px;
    > .section-left {
      max-height: 100%;
      min-height: 550px;
      max-width: 20%;
      width: 20%;
      color: #ffffff;
      background: #004c94;
      > h5 {
        font-size: 1.5em;
        padding: 10px 0;
        border-top: 2px solid #ffffff;
        border-bottom: 2px solid #ffffff;
      }
      > p {
        font-size: 1em;
        padding: 10px 0;
        border-top: 2px solid #ffffff;
        margin: 0.5em 0 0 0;
        position: absolute;
        left: 0;
        width: auto;
      }
      > .left-menu {
        overflow-y: auto;
        height: auto;
        min-height: 90%;
      }
    }
    > .section-right {
      max-width: 80%;
      width: 80%;
      overflow: hidden;
    }

    > .title-da {
      font-size: 1.7em;
      padding: 10px 0;
      margin: 10px 0 0 0;
      border-top: 2px solid #000000;
      border-bottom: 2px solid #000000;
    }
    .parent-tab {
      display: flex;
      width: 100%;
      ul.tabs {
        padding: 0%;
        width: 100%;
        margin: 10px auto;
        li {
          display: inline;
          text-align: center;
          cursor: pointer;
          font-style: normal;
          font-weight: normal;
          font-size: 30px;
          line-height: 25px;
          color: #003461;
          opacity: 0.56;

          a {
            display: inline-block;
            width: 25%;
            padding: 0.75rem 0;
            margin: 0;
            text-decoration: none;
          }
          &.user ~ hr {
            margin-left: 25%;
          }
          &.transaksi ~ hr {
            margin-left: 50%;
          }
          &.komisi ~ hr {
            margin-left: 75%;
          }
          &.active {
            opacity: 1;
          }
        }
        hr {
          height: 0.25rem;
          width: 25%;
          margin: 0;
          background: #c4c4c4;
          border: none;
          transition: 0.5s ease-in-out;
        }
      }
    }
    .content-tabs-h {
      .tabs-vertical {
        width: 15%;
        .tab-v {
          text-align: left;
          list-style: none;
          padding: 0%;
          li {
            color: #ffffff;
            margin: 10px 0;
            cursor: pointer;

            > span {
              background: #004c94;
              border: 1px solid #004c94;
              border-radius: 8px;
              padding: 5px 10px;
              display: inline-block;
              width: 100%;
            }

            &:hover {
              > span {
                background: #ffffff;
                color: #f2994a;
                border: 1px solid #f2994a;
              }
            }

            &[active="true"] {
              border-left: 3px solid #f2994a;
              > span {
                margin: 0 0 0 10px;
                background: #f2994a;
                border: 1px solid #f2994a;
                color: #ffffff;
              }
            }
          }
        }
      }
      .content-tabs-v {
        width: 85%;
        padding: 0 0 0 3%;
      }
      .tab-kelas {
        display: flex;
      }
    }
  }
}
@media (max-width: 767px) {
  .content-dashboard-admin {
    .content-da {
      > .title-da {
        font-size: 1em;
      }
      .tabs-vertical {
        display: none;
      }
      .content-tabs-v {
        width: 100% !important;
        padding: 0% !important;
      }
    }
  }
}
</style>