<template>
  <div class="content-list-withdrawal">
    <h3 class="title-lw">List Withdrawal</h3>
    <div 
      class="top-table-mbx"
      v-if="dataTable.list.length !== 0"
    >
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button
          @click="submitWithdrawal()"
          class="btn btn-primary btn-action-top-tbl-mbx"
        >Submit Withdrawal</button>
      </div>
    </div>
    <div class="content-lw">
      <div v-if="dataTable.list.length == 0">
        <h5>Data List Withdrawal Kosong.</h5>
      </div>
      <div v-else class="list-content-lw">
        <ol>
          <li v-for="(a, idx) in dataTable.list" v-bind:key="idx">
            <div class="ctn-list">
              <p class="number-list">
                <span>{{ idx + 1 }}.</span>
              </p>
              <div class="first" @click="showLW(idx)">
                <p>{{ a.createdAt  == null ? "-" : a.createdAt }}</p>
                <p>{{ a.pemilik == null ? "-" : a.pemilik }}</p>
                <p>Rp.{{ a.jumlah == null ? "-" : a.jumlah }}</p>
                <p>{{ a.status == null ? "-" : a.status }}</p>
                <p>
                  <i v-bind:class="'fa fa-plus icon-dd-lw'+idx"></i>
                </p>
              </div>
            </div>
            <div v-bind:class="'last detail-data-lw'+idx">
              <div class="detail-data-lw">
                <div class="left">
                  <p>
                    Bank :
                    <span>{{a.bank}}</span>
                  </p>
                  <p>
                    Rekening :
                    <span>{{a.rekening}}</span>
                  </p>
                  <p>
                    Pemilik :
                    <span>{{a.pemilik}}</span>
                  </p>
                  <p>
                    Jumlah :
                    <span>Rp.{{a.jumlah}}</span>
                  </p>
                </div>
                <div class="right">
                  <p>
                    Tanggal :
                    <span>{{ a.createdAt }}</span>
                  </p>
                  <p>
                    User :
                    <span>{{a.User.name}}</span>
                  </p>
                  <p>
                    Note :
                    <span>{{a.note}}</span>
                  </p>
                  <p>
                    Status : 
                    <span>{{a.status}}</span>
                  </p>
                </div>
              </div>
            </div>
          </li>
        </ol>
      </div>
    </div>

    <modal name="executor-withdrawal" :width="'70%'" :height="'70%'" :clickToClose="false">
      <ExecutorWithdrawal @init="init()" />
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from 'moment';

import ExecutorWithdrawal from "./ExecutorWithdrawal";

export default {
  name: "ListWithdrawal",
  components: {
    ExecutorWithdrawal
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: ""
      },
      editWithdrawal: {
        section: "detail",
        uuidDetailWithdrawal: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          tanggal: false,
          jumlah: false,
          bank: false,
          noRekening: false,
          note: false,
          status: false
        },
        colSort: {
          tanggal: false,
          jumlah: false,
          bank: false,
          noRekening: false,
          note: false,
          status: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Tanggal,
      // Jumlah,
      // Bank,
      // NoRek,
      // Note,
      // Status

      // {
      //   "id": 1,
      //   "bank": "BNI",
      //   "rekening": "7031234567",
      //   "pemilik": "Nasrullah",
      //   "jumlah": 26800,
      //   "note": "Bismillah",
      //   "status": "Unpaid",
      //   "createdAt": "2020-06-06 11:15:52",
      //   "User": {
      //     "name": "Nasrullah"
      //   }
      // }

      this.getListWithdrawal();
    },
    getListWithdrawal() {
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.withdrawalUser() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map(e => {
            let obj = {
              id: e.id,
              bank: e.bank,
              rekening: e.rekening,
              pemilik: e.pemilik,
              jumlah: e.jumlah,
              note: e.note,
              status: e.status,
              createdAt: moment(e.createdAt).format('DD - MMM - YYYY HH:mm:ss'),
              User: e.User,
            };
            return obj;
          });

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    showLW(idx) {
      $(".detail-data-lw" + idx).slideToggle();
      $(".icon-dd-lw" + idx).toggleClass("fa-plus fa-minus");
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    submitWithdrawal() {
      this.$modal.show("executor-withdrawal");
    }
  },
  mounted() {
    $("#tableLW");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-withdrawal {
  font-family: "Montserrat";
  > .title-lw {
    color: #000000;
    font-size: 1.7em;
    text-align: center;
    margin: 10px 0;
  }
  > .content-lw {
    > .list-content-lw {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      text-align: left;
      color: #ffffff;
      > ol {
        padding: 0%;
        margin: 0%;
        > li {
          display: flex;
          align-items: center;
          flex-direction: column;
          .ctn-list {
            padding: 5px 10px;
            display: flex;
            justify-content: space-between;
            width: 100%;
            cursor: pointer;
            background: #004c94;
            &:hover {
              box-shadow: 0px 4px 8px rgba(190, 188, 188, 0.56);
              border: 1px solid #ffffff;
            }
            > .number-list {
              margin: 0%;
              display: flex;
              justify-content: flex-start;
              width: 3%;
            }
            > .first {
              display: flex;
              justify-content: space-between;
              width: 100%;
              > p {
                margin: 0%;
                width: 20%;
                &:first-child {
                  width: 30%;
                }
                &:last-child {
                  width: 4%;
                  text-align: center;
                }
              }
            }
          }
          > .last {
            display: none;
            width: 100%;
            > .detail-data-lw {
              display: flex;
              padding: 5px 30px;
              background: #0e7fe9;
              color: #ffffff;
              .left,
              .right {
                width: 50%;
              }
              p {
                margin: 0%;
              }
            }
          }
        }
      }
    }
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLW {
    width: 100%;
  }
}
</style>