<template>
  <div class="content-list-user">
    <h3 class="title-lk">List Transaksi</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button 
            class="btn btn-primary"
            @click="searchList()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Transaksi</button> -->
      </div>
    </div>
    <div class="filter-table-mbx">
      <h5>Filter :</h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Status :</span>
            </div>
            <div class="right d-flex">
              <template v-for="(status, index) in dataTable.filter.status">
                <input
                  v-bind:key="index+5"
                  type="radio"
                  name="status"
                  v-bind:id="'f-status-'+ status.value"
                  class="radio-mbx"
                  v-bind:value="status.value"
                  v-bind:checked="dataBody.status == status.value"
                />
                <label
                  v-bind:key="index"
                  v-bind:for="'f-status-'+status.value"
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().status(status.value)"
                >{{ status.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Invoice
                  <span
                    @click="tableMbx().sort('list-invoice')"
                    id="list-invoice"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Tanggal
                  <span
                    @click="tableMbx().sort('list-createdAt')"
                    id="list-createdAt"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Member
                  <span
                    @click="tableMbx().sort('list-member')"
                    id="list-member"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Produk
                  <span
                    @click="tableMbx().sort('list-produk')"
                    id="list-produk"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Sub Total
                  <span
                    @click="tableMbx().sort('list-subtotal')"
                    id="list-subtotal"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Voucher
                  <span
                    @click="tableMbx().sort('list-voucher')"
                    id="list-voucher"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  Total
                  <span
                    @click="tableMbx().sort('list-total')"
                    id="list-total"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  Payment
                  <span
                    @click="tableMbx().sort('list-payment')"
                    id="list-payment"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  Referral
                  <span
                    @click="tableMbx().sort('list-referral')"
                    id="list-referral"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  Status
                  <span
                    @click="tableMbx().sort('list-status')"
                    id="list-status"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 100px;width: 80px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailTransaksi(list)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
									<button
                    class="btn btn-success"
                    v-if="list.status == 'Pending'"
                    @click="acceptTransaksi(list)" 
                  >
                    <i class="fa fa-check"></i>
                  </button>
                  <!-- <button @click="editDataTransaksi(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button> -->
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.invoice }}</td>
              <td>{{ list.createdAt }}</td>
              <td>{{ list.User.name }}</td>
              <td>
                <span v-if="list.produk == '-'"> {{ list.produk }} </span>
                <ul v-if="list.produk !== '-'">
                  <li
                    v-for="(p, idx) in list.produk"
                    v-bind:key="idx"
                  >
                    {{ p.produk }}
                  </li>
                </ul>
              </td>
              <td>Rp.{{ list.subtotal }}</td>
              <td>{{ list.voucher == "" ? "-" : list.voucher }}</td>
              <td>Rp.{{ list.total }}</td>
              <td>{{ list.payment }}</td>
              <td>{{ list.UserReferral == null ? "-" : list.UserReferral.name }}</td>
              <td>{{ list.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Transaksi Kosong, Silahkan Tambah Transaksi.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-transaksi" :clickToClose="false" :width="'70%'" :height="'80%'">
      <ExecutorTransaksi @init="init()" :data="detailData" :user="userData" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from 'moment';

import ExecutorTransaksi from './ExecutorTransaksi';

export default {
  name: "ListTransaksi",
  components: {
    ExecutorTransaksi
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "new",
        search: ""
      },
      dataTable: {
        list: [],
        colSearch: {
          invoice: false,
          createdAt: false,
          member: false,
          produk: false,
          subtotal: false,
          voucher: false,
          total: false,
          payment: false,
          referral: false,
          status: false,
        },
        colSort: {
          invoice: false,
          createdAt: false,
          member: false,
          produk: false,
          subtotal: false,
          voucher: false,
          total: false,
          payment: false,
          referral: false,
          status: false,
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "New",
              value: "new"
            },
            {
              text: "Pending",
              value: "pending"
						},
						{
              text: "Accept",
              value: "accept"
						},
						{
              text: "Reject",
              value: "reject"
            },
          ]
        }
      },
      detailData: {
        section: '',
        invoice: '',
      },
      userData: {}
    };
  },
  methods: {
    init() {
			
			// Invoice, 
			// Tanggal, 
			// Member, 
			// Produk, 
			// SubTotal, 
			// Diskon, 
			// Total, 
			// Payment, 
			// Referral, 
			// Status, 
      // Action (Detail, Accept)
      
      // Transaction_Details: Array(1)
      // User: Object
      // UserReferral: null
      // id: 8
      // invoice: "INV200609LVRW6"
      // kodeUnik: "915"
      // payment: "Transfer Bank"
      // ppn: 0
      // rekNumber: "5675768979"
      // status: "New"
      // subtotal: 7000
      // total: 7915
      // uuid: "3d0c4fd4-21ba-4e69-a0f7-2483bc243651"
      // voucher: ""

      this.getListTransaksi();
    },
    getListTransaksi() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&search=${a.search}`;
      axios
        .get(this.$root.listTransaksiAdmin() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            let obj = {
              id: 3,
              produk: e.Transaction_Details.length == 0 ? "-" : e.Transaction_Details,
              User: e.User,
              UserReferral: e.UserReferral,
              createdAt: moment(e.createdAt).format('DD - MMM - YYYY HH:mm:ss'),
              invoice: e.invoice,
              kodeUnik: e.kodeUnik,
              payment: e.payment,
              ppn: e.ppn,
              rekNumber: e.rekNumber,
              status: e.status,
              subtotal: e.subtotal,
              total: e.total,
              uuid: e.uuid,
              voucher: e.voucher,
            };
            return obj;
          })

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchList() {
      this.init();
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    detailTransaksi(data) {
      this.detailData.section = "detail";
      this.detailData.invoice = data.invoice;
      this.userData = data.User;
      this.$modal.show("executor-transaksi");
    },
    acceptTransaksi(data) {
      this.detailData.section = "accept";
      this.detailData.invoice = data.invoice;
      this.userData = data.User;
      this.$modal.show("executor-transaksi");
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-user {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 150%;
  }
}
</style>