<template>
  <div class="content-list-voucher-sale">
    <h3 class="title-lk">List Voucher Sale</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button 
            class="btn btn-primary"
            @click="searchList()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button class="btn btn-primary btn-action-top-tbl-mbx" @click="addVoucherSale()">Add Voucher Sale</button>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Kode
                  <i @click="tableMbx().search('kode')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.kode" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.search" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Produk
                  <span
                    @click="tableMbx().sort('list-produk')"
                    id="list-produk"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Deskripsi
                  <i @click="tableMbx().search('desk')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.desk" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..." 
                    v-model="dataBody.search" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jenis
                  <i @click="tableMbx().search('jenis')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.jenis" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.search"  
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Diskon
                  <i @click="tableMbx().search('diskon')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.diskon" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..." 
                    v-model="dataBody.search" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Start At
                  <i @click="tableMbx().search('startAt')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.startAt" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..." 
                    v-model="dataBody.search" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Expired At
                  <i @click="tableMbx().search('expiredAt')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.expiredAt" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..." 
                    v-model="dataBody.search" 
                  />
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td>
                <div class="btn-action-tbl-mbx">
                  <!-- <button class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button> -->
                  <button 
                    class="btn btn-warning"
                    @click="editVoucherSale(list.kode)"
                  >
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button> -->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.kode }}</td>
              <td>{{ list.produk.join('\n') }}</td>
              <td>{{ list.desk }}</td>
              <td>{{ list.jenis }}</td>
              <td>{{ list.diskon }}</td>
              <td>{{ list.startAt }}</td>
              <td>{{ list.expiredAt }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Voucher Sale Kosong, Silahkan Tambah Voucher Sale.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-voucher-sale" :clickToClose="false" :width="'50%'" :height="'80%'">
      <ExecutorVoucherSale :data="dataExecutor" @init="init()" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from 'moment';

import ExecutorVoucherSale from './ExecutorVoucherSale';

export default {
  name: "ListVoucherSale",
  components: {
    ExecutorVoucherSale,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: "",
      },
      dataExecutor:  {
        section: '',
        kode: ''
      },
      dataTable: {
        list: [],
        colSearch: {
          kode: false,
          desk: false,
          jenis: false,
          diskon: false,
          produk: false,
          startAt: false,
          expiredAt: false,
        },
        colSort: {
          kode: false,
          desk: false,
          jenis: false,
          diskon: false,
          produk: false,
          startAt: false,
          expiredAt: false,
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
				perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
            {
              text: 'Suspend',
              value: 'suspend'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {
			// id: 1
			// kode: "DIRUMAHAJA"
			// desk: "Lorem ipsum"
			// jenis: "Persen"
			// diskon: "30"
			// produk: ["Belajar Content Writing"]
			// startAt: "2020-05-01 08:00:00"
			// expiredAt: "2020-06-01 07:59:59"

      this.getListVoucherSale();
    },
    getListVoucherSale() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
			let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.listVoucherSaleAdmin() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            let obj = {
              id: e.id,
              desk:e.desk,
              diskon: e.diskon,
              jenis: e.jenis,
              kode: e.kode,
              produk: e.produk,
              startAt: moment(e.startAt).format('DD - MMM - YYYY HH:mm:ss'),
              expiredAt: moment(e.expiredAt).format('DD - MMM - YYYY HH:mm:ss'),
            };
            return obj;
          });

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchList() {
      this.init();
    },
    addVoucherSale() {
      let a = this.dataExecutor;
      a.section = 'add';
      this.$modal.show("executor-voucher-sale");
    },
    editVoucherSale(kode) { 
      let a = this.dataExecutor;
      a.section = 'edit';
      a.kode = kode;
      this.$modal.show("executor-voucher-sale");
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++
          vm.init();
        },
        backTable() {
					a.page.now--;
					b.page--
          vm.init();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
						a.page.now = val;
						b.page = val;
						vm.init();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
					a.perPage = page;
					b.limit = page;
					vm.tableMbx().perPage();
					vm.init();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        }
      };
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-voucher-sale {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 130%;
  }
}
</style>