<template>
  <div class="content-list-tag">
    <h3 class="title-lk">List Tag</h3>
		<div class="top-table-mbx">
			<div class="left">
				<div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="init()"
          />
          <button 
            class="btn btn-primary"
            @click="init()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
			</div>
			<div class="right">
				<button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Tag</button>
			</div>
		</div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th> -->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Judul Tag
                  <span
                    @click="tableMbx().sort('list-name')"
                    id="list-name"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jumlah Artikel
                  <span
                    @click="tableMbx().sort('list-blogs')"
                    id="list-blogs"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jumlah View
                  <span
                    @click="tableMbx().sort('list-views')"
                    id="list-views"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <!-- <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button 
										@click="detailTag(list.uuid)"
										class="btn btn-success"
									>
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button 
										@click="editDataTag(list.uuid)"
										class="btn btn-warning"
									>
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td> -->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.name }}</td>
              <td>{{ list.blogs }}</td>
              <td>{{ list.views }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Tag Kosong, Silahkan Tambah Tag.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ListTag",
  components: {
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
			},
			editTag: {
				section: 'detail',
				uuidDetailTag: 0,
			},
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
				perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
            {
              text: 'Suspend',
              value: 'suspend'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // JudulTag, 
      // JmlArtikel, 
      // JmlView

      // id: 10
      // name: "tetap semangat"
      // blogs: 1
      // views: null

      this.getListTag();
    },
    getListTag() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.listTagCreator() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
			let a = this.dataTable;
			let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++
          vm.init();
        },
        backTable() {
					a.page.now--;
					b.page--
          vm.init();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
						a.page.now = val;
						b.page = val;
						vm.init();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
					a.perPage = page;
					b.limit = page;
					vm.tableMbx().perPage();
					vm.init();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        }
      };
    },
    addLK() {
      this.editTag.section = 'add';
      this.$modal.show("add-tag");
		},
		detailTag(uuid) {
			this.editTag.uuidDetailTag = uuid;
			this.$modal.show("detail-tag");
		},
		editDataTag(uuid) {
			this.editTag.section = 'edit';
			this.editTag.uuidDetailTag = uuid;
			this.$modal.show("add-tag");
		}
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-tag {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>