<template>
  <div class="content-detail-user modal-mbx">
    <div class="top-title-modal">
      <h3  class="title-lk">Detail User</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr class="line-modal" />
    <div class="content-du">
      <div class="top-header-du">
        <div class="left">
          <div class="img-user">
            <img v-bind:src="$root.getPhotoUser() + '/' + dataUser.photo" alt="Photo User" />
          </div>
        </div>
        <div class="right">
          <div class="data-detail-user">
            <label for>
              Name
              <span>:</span>
            </label>
            <span >{{ dataUser.name == null ? '-' : dataUser.name }}</span>
          </div>
          <div class="data-detail-user">
            <label for>
              Job Title
              <span>:</span>
            </label>
            <span>{{ dataUser.jobTitle == null ? '-' : dataUser.jobTitle }}</span>
          </div>
          <div class="data-detail-user">
            <label for>
              About
              <span>:</span>
            </label>
            <span >{{ dataUser.about == null ? '-' : dataUser.about }}</span>
          </div>
        </div>
      </div>
      <div class="data-detail-user">
        <label for>
          Email
          <span>:</span>
        </label>
        <span >{{ dataUser.email == null ? '-' : dataUser.email }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Phone
          <span>:</span>
        </label>
        <span >{{ dataUser.phone == null ? '-' : dataUser.phone }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Address
          <span>:</span>
        </label>
        <span >{{ dataUser.address == null ? '-' : dataUser.address }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Referral
          <span>:</span>
        </label>
        <span >{{ dataUser.referral == null ? '-' : dataUser.referral}}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Status
          <span>:</span>
        </label>
        <span >{{ dataUser.status == null ? '-' : dataUser.status }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Roles
          <span>:</span>
        </label>
        <span >
          <ul>
            <li v-for="(role, index) in dataUser.roles" v-bind:key="index">{{ role }}</li>
          </ul>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "DetailUser",
  components: {},
  props: ["data"],
  data() {
    return {
      uuidUser: 0,
      dataUser: {
      },
    };
  },
  methods: {
    init() {
      this.getDetailUser();
    },
    getDetailUser() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listUserAdmin() + `/${this.uuidUser}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;
          this.dataUser = result;
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.afterPostUser().failed(`${resErr.data.message}`);
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    afterPostUser() {
      return {
        success: msg => {
          this.modal.show("dialog", {
            title: "Success",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  this.closeModal();
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed: msg => {
          this.modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  this.closeModal();
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    closeModal() {
      this.$modal.hide("detail-user");
    },
  },
  mounted() {
    let dataParams = this.$props.data;
    this.uuidUser = dataParams.uuidDetailUser;
    console.log("data params ", dataParams);
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-detail-user {
  padding: 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-du {
    margin: 0 0 0 20px;
    > .top-header-du {
      display: flex;
      align-items: center;
      margin: 0 0 10px 0;
      > .left {
        > .img-user {
          > img {
            height: 110px;
            width: 110px;
            border-radius: 50%;
          }
        }
      }
      > .right {
        padding: 0 0 0 25px;
      }
    }
    .data-detail-user {
      font-size: 1em;
      text-align: left;
      display: flex;
      > label {
        font-weight: 600;
        min-width: 100px;
        max-width: 150px;
        > span {
          float: right;
        }
      }
      > span {
        display: inline-block;
        margin: 0 0 0 15px;
        > input,
        > textarea {
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid #797878;
          padding: 3px 7px;
          width: 150%;
        }
      }
    }
    .inp-roles {
      display: flex;
      padding: 0%;
      margin: 0 auto;
      max-width: 100%;
      flex-wrap: wrap;
      justify-content: center;

      > li {
        list-style: none;
        margin: 5px 15px;
      }
    }
    .list-status-user {
      // position: absolute;
      display: none;
      background: #ffffff;
      padding: 0%;
      width: 50%;
      left: 0;
      margin: 3px 0 0 0;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 0 0 10px 10px;
      > li {
        color: #000000;
        list-style: none;
        padding: 5px 10px;
        cursor: pointer;
        &:nth-child(even) {
          background: #cccccc;
        }
        &:hover {
          background: #999898;
        }
      }
    }
  }
}
</style>