<template>
  <section class="kurikulum-kelas">
    <div class="container">
      <h1 class="title-kl">Kurikulum Kelas</h1>
      <div class="content-kurikulum-kelas">
        <div class="schedule-kl">
          <div class="title-kl-list">
            <p class="kl-topik">Topik</p>
            <p class="kl-materi">Materi</p>
            <!-- <p class="kl-Durasi">Deskripsi</p> -->
          </div>
          <div class="content-list-kl">
            <div class="list-kl" v-for="(kl, idx) in dataListKurikulum" v-bind:key="idx">
              <div class="list-parent-kl">
                <div class="list-topik">{{ kl.topik }}</div>
                <div class="list-materi">{{ kl.materi }}</div>
                <!-- <div class="list-deskripsi">{{ kl.deskripsi }}</div> -->
                <div class="list-btn-plus">
                  <i
                    v-if="kl.have_child"
                    class="fa fa-plus"
                    v-bind:id="kl.unic_id"
                    v-on:click="showCK(kl.unic_id)"
                  ></i>
                </div>
              </div>
              <div
                v-bind:class="('child-id-' + kl.unic_id)"
                v-if="kl.have_child"
                style="display: none;"
              >
                <div class="list-child-kl">
                  <div class="c-topik">
                    <ol>
                      <li
                        v-for="(ckl, index) in kl.child_kurikulum.topik"
                        v-bind:key="index"
                      >{{ ckl }}</li>
                    </ol>
                  </div>
                  <div class="c-materi">
                    <ul>
                      <li
                        v-for="(ckl, index) in kl.child_kurikulum.materi"
                        v-bind:key="index"
                      >{{ ckl }}</li>
                    </ul>
                  </div>
                  <!-- <div class="c-deskripsi">
                    <ul>
                      <li
                        v-for="(ckl, index) in kl.child_kurikulum.deskripsi"
                        v-bind:key="index"
                      >{{ ckl }}</li>
                    </ul>
                  </div> -->
                </div>
              </div>
            </div>
          </div>
          <!-- <span class="amount-deskripsi">Durasi Waktu Kelas Total : 10 Jam, 10 Menit</span> -->
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from "jquery";
export default {
  name: "KurikulumKelas",
  components: {},
  data() {
    return {
      dataKurikulumKelas: [
        {
          id: 1,
          unic_id: this.$root.makeid(7),
          topik: "Perkenalan",
          materi: "1 Materi",
          deskripsi: "13:00",
          have_child: true,
          child_kurikulum: {
            topik: ["Perkenalan", "Content Strategy"],
            materi: ["Video", "Video"],
            deskripsi: ["06:05", "06:05"]
          }
        },
        {
          id: 2,
          unic_id: this.$root.makeid(7),
          topik: "Materi Awal",
          materi: "2 Materi",
          deskripsi: "13:00",
          have_child: true,
          child_kurikulum: {
            topik: ["Perkenalan", "Content Strategy"],
            materi: ["Video", "Video"],
            deskripsi: ["06:05", "06:05"]
          }
        },
        {
          id: 3,
          unic_id: this.$root.makeid(7),
          topik: "Lorem Ipsum",
          materi: "2 Materi",
          deskripsi: "13:00",
          have_child: true,
          child_kurikulum: {
            topik: ["Perkenalan", "Content Strategy"],
            materi: ["Video", "Video"],
            deskripsi: ["06:05", "06:05"]
          }
        },
        {
          id: 4,
          unic_id: this.$root.makeid(7),
          topik: "Lorem Ipsum",
          materi: "2 Materi",
          deskripsi: "13:00",
          have_child: true,
          child_kurikulum: {
            topik: ["Perkenalan", "Content Strategy"],
            materi: ["Video", "Video"],
            deskripsi: ["06:05", "06:05"]
          }
        },
        {
          id: 5,
          unic_id: this.$root.makeid(7),
          topik: "Penutup",
          materi: "3 Materi",
          deskripsi: "13:00",
          have_child: false,
          child_kurikulum: {}
        }
      ],
      dataListKurikulum: [],
    };
  },
  props: ['data'],
  methods: {
    init() {
			// this.handleData( this.$props.data );
		},
		handleData(newData) {
      this.dataListKurikulum = newData.curriculums.map((e) => {
        // id: 5,
        // topics: "gaskeun",
        // materi: "hahaha",
        // description: "hayu wae lah",
        // files: "5_6337001753057165546-2.pdf"

        let obj = {
          id: e.id,
          unic_id: this.$root.makeid(7),
          topik: e.topics,
          materi: e.materi,
          deskripsi: e.description,
          have_child: false,
          child_kurikulum: {
            topik: [],
            materi: [],
            deskripsi: []
          }
        }
        return obj;
      })
		},
    showCK(id) {
      $("#" + id).toggleClass("fa-plus fa-minus");
      $(".child-id-" + id).slideToggle();
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top props ", newData, oldData);
    }
  }
};
</script>

