<template>
  <section class="kebijakan-mentorbox-section">
    <HeaderTop />

    <ContentKebijakanMentorbox />

    <Footer />
  </section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";
import ContentKebijakanMentorbox from "@/components/KebijakanMentorbox/ContentKebijakanMentorbox.vue";

export default {
  name: "KebijakanMentorbox",
  components: {
    HeaderTop,
    Footer,
    ContentKebijakanMentorbox
  },
  methods: {},
  mounted() {
    this.$root.goToTop();
  },
  watch: {
    $route(to, from) {
      let pathTo = to.params.section;
      let pathFrom = from.params.section;
      if (pathTo != pathFrom) {
        this.$root.goToTop();
      }
    }
  }
};
</script>

