<template>
  <div class="content-list-saldo-member">
    <h3 class="title-lk">List Saldo Member</h3>
    <div class="top-table-mbx">
      <div class="left">
        <!-- <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div> -->
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Saldo Member</button> -->
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th> -->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Nama
                  <i @click="tableMbx().search('nama')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.nama" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.nama"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Komisi
                  <i @click="tableMbx().search('komisi')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.komisi" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.komisi"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Withdrawal
                  <i @click="tableMbx().search('withdrawal')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.withdrawal" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.withdrawal"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Saldo
                  <i @click="tableMbx().search('saldo')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.saldo" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.saldo"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr 
              v-for="(list, index) in dataTable.list" 
              v-bind:key="index"
              v-bind:hidden="!(index >= dataTable.minIndex && index <= dataTable.maxIndex)"
            >
              <!-- <td style="max-width: 100px;width: 80px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailSaldoMember(list.uuid)" class="btn btn-success">
                    <i class="fa fa-checklist"></i>
                  </button>
                  <button @click="editDataSaldoMember(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td> -->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.nama }}</td>
              <td>Rp.{{ list.komisi }}</td>
              <td>Rp.{{ list.withdrawal }}</td>
              <td>Rp.{{ list.saldo }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div 
        class="data-list-kosong" 
        v-if="dataTable.list.length == 0"
      >Data List Saldo Member Kosong.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now > 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.total !== dataTable.page.now"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

// import ExecutorSaldoMember from "./ExecutorSaldoMember";

export default {
  name: "SaldoMember",
  components: {
    // ExecutorSaldoMember,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
      },
      editSaldoMember: {
        section: "detail",
        uuidDetailSaldoMember: 0
      },
      dataTable: {
        list: [],
        listTemp: [],
        colSearch: {
          nama: false,
          komisi: false,
          withdrawal: false,
          saldo: false,
        },
        colSearchValue: {
          nama: '',
          komisi: '',
          withdrawal: '',
          saldo: '',
        },
        colSort: {
          nama: false,
          komisi: false,
          withdrawal: false,
          saldo: false,
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
        perPage: 10,
        totalData: 0,
        maxIndex: 0,
        minIndex: 0,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {
			// Nama, 
			// Komisi, 
			// Withdrawal, 
      // Saldo
      
      // commission: 244400
      // name: "Nasrullah"
      // saldo: 214600
      // withdrawal: 29800

      this.getSaldoMember();
    },
    getSaldoMember() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.listSaldoMemberAdmin(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            let obj = {
              nama: e.name,
              komisi: e.commission,
              saldo: e.saldo,
              withdrawal: e.withdrawal,
            }
            return obj;
          });
          this.dataTable.listTemp = result.map((e) => {
            let obj = {
              nama: e.name,
              komisi: e.commission,
              saldo: e.saldo,
              withdrawal: e.withdrawal,
            }
            return obj;
          });

          this.dataTable.totalData = data.count;
          this.tableMbx().counterPageTable(data.count);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          } else {
            this.$root.notification(resErr.data.message);
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
			let a = this.dataTable;
			let b = this.dataBody;
      let vm = this;
      return {
        search: (field) => {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
            this.dataTable.colSearchValue[field] = "";
            let result = this.tableMbx().filterCache();
            this.dataTable.list = result;
            this.tableMbx().counterPageTable(result.length);
          }
        },
        filterSearch: () => {
          let result = this.tableMbx().filterCache();
          console.log('result ', result);
          this.dataTable.list = result;
          this.tableMbx().counterPageTable(result.length);
        },
        filterCache: () => {
          let result = this.dataTable.listTemp;
          let col = [
            'nama',
            'komisi',
            'withdrawal',
            'saldo',
          ];
          for (let j = 0; j < col.length; j++) {
            if (this.dataTable.colSearchValue[col[j]] != '') {
              result = result.filter(search => {
                var name = String(search[col[j]]).toUpperCase();
                var findme = new RegExp(
                  String(this.dataTable.colSearchValue[col[j]]).toUpperCase(),
                  'g',
                );
                return name.match(findme);
              });
            }
          }
          return result;
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++;
          vm.tableMbx().changeShowPage();
        },
        backTable() {
					a.page.now--;
          b.page--;
          vm.tableMbx().changeShowPage();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
            a.page.now = val;
            vm.tableMbx().counterPageTable();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
          a.perPage = page;
          vm.tableMbx().counterPageTable(vm.dataTable.list.length);
          vm.tableMbx().perPage();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        },
        counterPageTable: (length) => {
          let m = Math.ceil(length / a.perPage);
          if (m <= 1) {
            m = 1;
          }
          let lengthData = Array(m)
              .fill(0, 0)
              .map((x, i) => i).length
            a.page.total = lengthData;
            console.log('lengthData ', lengthData, m);
          vm.tableMbx().changeShowPage();
        },
        changeShowPage: () => {
          let pageNow = vm.dataTable.page.now;
          let perPage = vm.dataTable.perPage;
          this.dataTable.minIndex = ((pageNow*perPage)-perPage);
          this.dataTable.maxIndex = ((pageNow*perPage)-1);
        }
      };
    },
    addLK() {
      this.editSaldoMember.section = "add";
      this.$modal.show("add-saldo-member");
    },
    detailSaldoMember(uuid) {
      this.editSaldoMember.uuidDetailSaldoMember = uuid;
      this.$modal.show("detail-saldo-member");
    },
    editDataSaldoMember(uuid) {
      this.editSaldoMember.section = "edit";
      this.editSaldoMember.uuidDetailSaldoMember = uuid;
      this.$modal.show("add-saldo-member");
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-saldo-member {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>