<template>
  <section class="cara-belajar">
    <div class="container">
      <h1 class="title-cb">Cara Belajar</h1>
      <div class="content-cb">
        <p
          class="p-cb"
        >{{ note }}</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "CaraBelajar",
  components: {},
  data() {
    return {
      note: ''
    }
  },
  props: ['data'],
  methods: {
    init() {
			this.note = this.$store.state.dataDetailKelas.note;
			// this.handleData( this.$props.data );
		},
		handleData(newData) {
			this.note = newData.note;
		}
	},
	mounted() {
		this.init();
	},
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top note ", newData, oldData);
    }
  }
};
</script>

<style lang="scss" scoped>
.cara-belajar {
  padding: 20px 0 25px 0;
  font-family: "Montserrat";
  text-align: left;
  .container {
    > .title-cb {
      font-style: normal;
      font-weight: normal;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    > .content-cb {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      > .p-cb {
        color: #ffffff;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .note-kelas {
    .container {
      > .title-cb {
        font-size: 34px;
      }
    }
  }
}
@media (max-width: 481px) {
  .note-kelas {
    .container {
      > .title-cb {
        font-size: 32px;
      }
    }
  }
}
</style>