import Vue from 'vue'
import App from './App.vue'
import router from './router'
import titleMixin from './mixins/titleMixin'
import globalFunctions from './mixins/globalFunctions'
import apiConfig from './mixins/apiConfig'
import store from './store'
import VueCookies from 'vue-cookies'
import Modal from 'vue-js-modal'
import menuAdmin from './mixins/menuDashboard/menuAdmin'
import menuCreator from './mixins/menuDashboard/menuCreator'
import menuOwner from  './mixins/menuDashboard/menuOwner'
import menuMentor from './mixins/menuDashboard/menuMentor'
import menuAffiliate from './mixins/menuDashboard/menuAffiliate'
import VCalendar from 'v-calendar';
// import * as moment from 'moment';
import { Datetime } from 'vue-datetime';
import 'vue-datetime/dist/vue-datetime.css';
import CKEditor from '@ckeditor/ckeditor5-vue';

require('./assets/css/montserrat-fonts.css');
require('./assets/css/bootstrap.css');
require('./assets/css/font-awesome.min.css');
require('./assets/css/linearicons.css');

global.gf = require('./mixins/globalFunctions');
var gf = global.gf;
window.gf = gf;

// global.moment = moment;
// var m = global.moment;
// window.moment = m;

// set default config
Vue.$cookies.config('3h')
Vue.config.productionTip = false;
Vue.use(Modal, {
  componentName: "modal",
  dialog: true 
});
Vue.use(VCalendar, {
  componentPrefix: 'vc',  // Use <vc-calendar /> instead of <v-calendar />
});
Vue.use(Datetime);
Vue.component('datetime', Datetime);
Vue.use( CKEditor );

global.jQuery = require('jquery');
var $ = global.jQuery;
window.$ = $;
global.moment = require('moment');
// require('./assets/css/footable.standalone.css');
// require('./assets/js/footable.js');

new Vue({
  router,
  store,
  VueCookies,
  VCalendar,
  mixins: [
    globalFunctions,
    apiConfig,
    menuAdmin,
    menuCreator,
    menuOwner,
    menuMentor,
    menuAffiliate,
  ],
  render: h => h(App)
}).$mount('#app')

Vue.mixin(titleMixin);