<template>
  <div class="tab-jadwal-kelas">
    <div class="content-tjk">
      <h3 class="title-tjk">Jadwal Kelas</h3>
      <template v-for="(jadwal, index) in dataJadwalKelas">
        <div v-bind:key="index" class="content-form-tjk">
          <div class="left">
            <div class="form-input-tjk inline">
              <label for="name-jadwal">Nama Jadwal</label>
              <input
                type="text"
                class="form-control"
                name="name-jadwal"
                v-bind:id="'nameJadwal' + index"
                placeholder="Nama Jadwal"
                v-model="jadwal.name"
              />
            </div>
            <div class="form-input-tjk inline">
              <label for="slot">Slot</label>
              <input
                onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
                type="text"
                class="form-control"
                name="slot"
                v-bind:id="'slot' + index"
                placeholder="Slot"
                v-model="jadwal.slot"
              />
            </div>
            <div class="form-input-tjk inline-date">
              <label for="jadwal">Jadwal</label>
              <div class="date-form d-flex">
                <datetime 
                  type="datetime" 
                  v-model="jadwal.startDate" 
                  format="yyyy-MM-dd HH:mm:ss"
                  class="field-inp-date"
                  placeholder="Jadwal Mulai"
                ></datetime>
                <datetime 
                  type="datetime" 
                  v-model="jadwal.endDate" 
                  format="yyyy-MM-dd HH:mm:ss"
                  class="field-inp-date"
                  placeholder="Jadwal Selesai"
                ></datetime>
                <!-- <vc-date-picker
                  :value="null"
                  color="red"
                  is-dark
									:locale="{
										id: 'id',
										data: ['YYYY-MM-DD']
									}"
                  v-model="jadwal.startDate"
                  v-bind:id="'jadwalMulai' + index"
                  class="field-inp-date"
                  :placeholder="'Jadwal mulai'"
                />
                <vc-date-picker
                  :value="null"
                  color="red"
                  is-dark
									:locale="{
										id: 'id',
										data: ['YYYY-MM-DD']
									}"
									:data="'YYYY-MM-DD'"
                  v-model="jadwal.endDate"
                  v-bind:id="'jadwalSelesai' + index"
                  class="field-inp-date"
                  :placeholder="'Jadwal Selesai'"
                /> -->
                <!-- <input
                  type="text"
                  class="form-control"
                  name="jadwal-mulai"
                  v-bind:id="'jadwalMulai' + index"
                  placeholder="Jadwal Mulai"
                  v-model="jadwal.startDate"
                />
                <input
                  type="text"
                  class="form-control"
                  name="jadwal-selesai"
                  v-bind:id="'jadwalSelesai' + index"
                  placeholder="Jadwal Selesai"
                  v-model="jadwal.endDate"
                />-->
              </div>
            </div>
          </div>
          <div class="right">
            <div class="icon-plus-minus-jadwal">
              <span
                class="minus-jadwal"
                @click="removeJadwal(index)"
                v-if="dataJadwalKelas.length !== index + 1"
              >
                <i class="fa fa-trash"></i>
                Hapus
              </span>
              <span
                class="plus-jadwal"
                @click="addJadwal(index)"
                v-if="dataJadwalKelas.length == index + 1"
              >
                <i class="fa fa-plus"></i>
                Tambah
              </span>
            </div>
          </div>
        </div>
      </template>
    </div>

    <!-- <vc-date-picker
			:value="null"
			color="red"
			is-dark
    />-->
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "TabJadwalKelas",
  components: {},
  data() {
    return {
      dataJadwalKelas: this.$store.state.dataProductMentor.schedule,
      tryDateTime: ''
    };
  },
  methods: {
    init() {
      this.fixStyleInputVCalendar(0);
    },
    fixStyleInputVCalendar(idx) {
      $(".vdatetime-input").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%",
        "width": "100%",
        "height": "90%"
			});

      $(`#jadwalMulai${idx} > input`).attr("placeholder", "Jadwal Mulai");
      $(`#jadwalSelesai${idx} > input`).attr("placeholder", "Jadwal Selesai");
    },
    addJadwal(idx) {
      let a = this.dataJadwalKelas;
      let list = {
        name: "",
        slot: "",
        startDate: "",
        endDate: ""
      };
      a.push(list);

      setTimeout(() => {
        this.fixStyleInputVCalendar(idx + 1);
      }, 700);
    },
    removeJadwal(idx) {
      let a = this.dataJadwalKelas;
      this.$modal.show("dialog", {
        title: "Warning",
        text: `Apakah kamu yakin ingin menghapus jadwal "${this.dataJadwalKelas[idx].name}" ?`,
        buttons: [
          {
            title: "Cancel"
          },
          {
            title: "Ya",
            handler: () => {
              a.splice(idx, 1);
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.tab-jadwal-kelas {
  .content-tjk {
    > .title-tjk {
      font-style: normal;
      font-weight: 500;
      font-size: 33px;
      line-height: 44px;
    }
    .content-form-tjk {
      display: flex;
      padding: 0 0 10px 0;
      border-bottom: 1px solid #000000;
      > .left {
        width: 80%;
      }
      > .right {
        width: 20%;
        display: flex;
        align-items: center;
        > .icon-plus-minus-jadwal {
          display: flex;
          flex-direction: column;
          margin: auto;
          > span {
            padding: 10px 20px;
            border-radius: 10px;
            cursor: pointer;
            &.minus-jadwal {
              background: #f00707;
              color: #ffffff;
              border: 1px solid #f00707;
              margin: 0 0 5px 0;
              &:hover {
                background: #ffffff;
                color: #f00707;
              }
            }
            &.plus-jadwal {
              background: #003461;
              color: #ffffff;
              border: 1px solid #003461;
              margin: 5px 0 0 0;
              &:hover {
                background: #ffffff;
                color: #003461;
              }
            }
          }
        }
      }
    }
    .form-input-tjk {
      text-align: left;
      margin: 5px 0;
      > label {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 44px;
        color: #000000;
        margin: 0%;
      }
      > input {
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 1px solid #000000;
        border-radius: 0%;
        &[type="file"] {
          display: none;
        }
      }
      > .file-upload-div {
        justify-content: space-between;
        border-bottom: 1px solid #000000;
        padding: 6px 12px;
        align-items: center;
        cursor: pointer;
        > .btn-up {
          background: #003461;
          border-radius: 10px;
          color: #ffffff;
        }
      }
      > .date-form {
        justify-content: space-between;
        > input {
          width: 49%;
          border-top: none;
          border-right: none;
          border-left: none;
          border-bottom: 1px solid #000000;
          border-radius: 0%;
        }
        .field-inp-date {
          width: 49%;
          > input {
            border-top: none;
            border-right: none;
            border-left: none;
            border-bottom: 1px solid #000000;
            border-radius: 0%;
          }
        }
      }

      &.inline {
        display: flex;
        > label {
          width: 25%;
        }
        > input {
          width: 75%;
        }
      }
      &.inline-date {
        display: flex;
        > label {
          width: 25%;
        }
        > .date-form {
          width: 75%;
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .tab-jadwal-kelas {
    .content-tjk {
      > .title-tjk {
        font-size: 25px;
      }
      > .form-input-tjk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .tab-jadwal-kelas {
    .content-tjk {
      > .title-tjk {
        font-size: 25px;
      }
      > .form-input-tjk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>