<template>
  <section class="content-detail-pembayaran">
    <div v-if="paramsRoute.section == 'checkout'">
      <CheckVoucher />
      <MetodePembayaran />
    </div>

    <div v-else-if="paramsRoute.section == 'cara-pembayaran'">
      <ContentCaraPembayaran :section="paramsRoute.bank" />
    </div>

    <div v-else-if="paramsRoute.section == 'finish-payment'">
      <FinishPayment />
    </div>
  </section>
</template>

<script>
import CheckVoucher from "./CheckVoucher";
import MetodePembayaran from "./MetodePembayaran";
import ContentCaraPembayaran from "./CaraPembayaran/ContentCaraPembayaran";
import FinishPayment from "./FinishPayment";

export default {
  name: "ContentDetailPembayaran",
  components: {
    CheckVoucher,
    MetodePembayaran,
    ContentCaraPembayaran,
    FinishPayment
  },
  data() {
    return {
      paramsRoute: {
        section: this.$route.params.section,
        bank: this.$route.params.bank
      }
    };
	},
	methods: {
		init() {
			// let dataKelasCheckout = window.$cookies.get('listKelas');
			// console.log('dataKelasCheckout ', dataKelasCheckout);
		}
	},
  mounted() {
		this.init();
    console.log("section ", this.paramsRoute.section);
  },
  watch: {
    $route(to, from) {
      this.paramsRoute.section = to.params.section;
      this.paramsRoute.bank = to.params.bank;
      from;

      console.log("to ", to, from);
    }
  }
};
</script>

