<template>
  <div class="content-list-user">
    <h3 class="title-lk">List User</h3>
		<div class="top-table-mbx">
			<div class="left">
				<div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button 
            class="btn btn-primary"
            @click="searchList()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
			</div>
			<div class="right">
				<button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add User</button>
			</div>
		</div>
    <div class="filter-table-mbx">
      <h5>Filter : </h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Status : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(status, index) in dataTable.filter.status"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="status" 
                  v-bind:id="'f-status-'+ status.value" 
                  class="radio-mbx"
                  v-bind:value="status.value"
                  v-bind:checked="dataBody.status == status.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-status-'+status.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().status(status.value)"
                >{{ status.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Name
                  <span
                    @click="tableMbx().sort('list-name')"
                    id="list-name"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Email
                  <span
                    @click="tableMbx().sort('list-email')"
                    id="list-email"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Phone
                  <span
                    @click="tableMbx().sort('list-phone')"
                    id="list-phone"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Photo
                  <span
                    @click="tableMbx().sort('list-photo')"
                    id="list-photo"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Status
                  <span
                    @click="tableMbx().sort('list-status')"
                    id="list-status"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Roles
                  <span
                    @click="tableMbx().sort('list-roles')"
                    id="list-roles"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button 
										@click="detailUser(list.uuid)"
										class="btn btn-success"
									>
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button 
										@click="editDataUser(list.uuid)"
										class="btn btn-warning"
									>
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.name }}</td>
              <td>{{ list.email }}</td>
              <td>{{ list.phone }}</td>
              <td>{{ list.photo }}</td>
              <td>{{ list.status }}</td>
              <td>
                <ul>
                  <li v-for="(role, index) in list.roles" v-bind:key="index">{{ role }}</li>
                </ul>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List User Kosong, Silahkan Tambah User.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>

    <modal name="detail-user" :clickToClose="false" :width="'50%'" :height="'80%'">
      <DetailUser :data="editUser" />
    </modal>

    <modal name="add-user" :clickToClose="false" :width="'60%'" :height="'80%'">
      <AddUser @init="init()" :data="editUser" />
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

// import ExecutorListUser from "./ExecutorListUser";
import DetailUser from './DetailUser';
import AddUser from './AddUser';

export default {
  name: "ListUser",
  components: {
		// ExecutorListUser,
    DetailUser,
    AddUser,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
			},
			editUser: {
				section: 'detail',
				uuidDetailUser: 0,
			},
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
				perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
            {
              text: 'Suspend',
              value: 'suspend'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // id: 1
      // name: "Nasrullah"
      // email: "naz.cules@gmail.com"
      // phone: "6282337027239"
      // photo: "avatar.png"
      // roles: (5) ["OWNER", "MENTOR", "ADMIN", "CREATOR", "USER"]
      // status: "Active"
      // uuid: "522947f1-9b8b-4663-bf31-859a058e0e76"

      this.getListUser();
    },
    getListUser() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&roles=${a.roles}&search=${a.search}`;
      axios
        .get(this.$root.listUserAdmin() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchList() {
      this.init();
    },
    tableMbx() {
			let a = this.dataTable;
			let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++
          vm.init();
        },
        backTable() {
					a.page.now--;
					b.page--
          vm.init();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
						a.page.now = val;
						b.page = val;
						vm.init();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
					a.perPage = page;
					b.limit = page;
					vm.tableMbx().perPage();
					vm.init();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        }
      };
    },
    addLK() {
      this.editUser.section = 'add';
      this.$modal.show("add-user");
		},
		detailUser(uuid) {
			this.editUser.uuidDetailUser = uuid;
			this.$modal.show("detail-user");
		},
		editDataUser(uuid) {
			this.editUser.section = 'edit';
			this.editUser.uuidDetailUser = uuid;
			this.$modal.show("add-user");
		}
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-user {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>