<template>
  <section class="blog-page">
		<HeaderTop />

		<ContentBlog />

		<Footer />
	</section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

import ContentBlog from "@/components/Blog/ContentBlog.vue";

export default {
  name: "Blog",
  components: {
		HeaderTop,
		Footer,
		ContentBlog
	},
	methods: {
		goToTop() {
			this.$root.goToTop();
		}
	},
  mounted() {
		this.goToTop()
	},
	watch: {
    $route(to, from) {
      let pathTo = to.params.titleblog;
			let pathFrom = from.params.titleblog;
			if ( pathTo != pathFrom ) {
				this.goToTop()
			}
    }
  }
};
</script>

