<template>
  <section class="ulasan-kelas">
    <div 
      class="container"
      v-if="dataListUlasan.length !== 0"
    >
      <h1 class="title-uk">Ulasan</h1>
      <carousel
        :autoplay="true"
        :nav="false"
        :dots="true"
        :responsive="{
        0:{
            items:1,
        },
        700:{
            items:2,
        },
        1000:{
            items:3,
					}
				}"
				class="div-ulasan-carousel"
      >
        <div class="content-ulasan" v-for="(ulasan, idx) in dataListUlasan" v-bind:key="idx">
          <div>
						<div class="face-user">
							<img v-bind:src="ulasan.image" alt />
							<div class="about-user">
								<p class="name-user">{{ ulasan.name }}</p>
								<p class="position-user">{{ ulasan.position }}</p>
								<star-rating :rating="ulasan.rating" :read-only="true" :star-size="20"></star-rating>
							</div>
						</div>
						<div class="ulasan-user">
							<p class="p-ulasan">{{ ulasan.ulasan }}</p>
						</div>
					</div>
        </div>
      </carousel>
    </div>
    <div 
      class="container"
      v-else
    >
      <h1 class="title-uk">Ulasan</h1>
      <div class="div-ulasan-carousel">
        <h5 class="text-center">Belum ada ulasan.</h5>
      </div>
    </div>
  </section>
</template>

<script>
import carousel from "vue-owl-carousel";
import axios from "axios";
import StarRating from "vue-star-rating";
import $ from 'jquery';

export default {
  name: "UlasanKelas",
  components: {
    carousel,
    StarRating
  },
  data() {
    return {
      slugKelas: "",
      dataUlasan: [
        {
          id: 1,
          image: "/img/foto.jpg",
          name: "Andri Darmawan",
          position: "Social Media Specialist Freelance",
          ulasan:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Utenim ad minim veniam, quis nostrud exercitation."
        },
        {
          id: 2,
          image: "/img/foto.jpg",
          name: "Andri Darmawan",
          position: "Social Media Specialist Freelance",
          ulasan:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Utenim ad minim veniam, quis nostrud exercitation."
        },
        // {
        //   id: 3,
        //   image: "/img/foto.jpg",
        //   name: "Andri Darmawan",
        //   position: "Social Media Specialist Freelance",
        //   ulasan:
        //     "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Utenim ad minim veniam, quis nostrud exercitation."
        // },
        // {
        //   id: 4,
        //   image: "/img/foto.jpg",
        //   name: "Andri Darmawan",
        //   position: "Social Media Specialist Freelance",
        //   ulasan:
        //     "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Utenim ad minim veniam, quis nostrud exercitation."
        // },
        // {
        //   id: 5,
        //   image: "/img/foto.jpg",
        //   name: "Andri Darmawan",
        //   position: "Social Media Specialist Freelance",
        //   ulasan:
        //     "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Utenim ad minim veniam, quis nostrud exercitation."
        // }
      ],
      dataListUlasan: []
    };
  },
  props: ["data"],
  methods: {
    init() {
      // this.getListUlasanKelas();
      console.log("ini from init list ulasan");

      // "id": 1,
      // "rating": 5,
      // "review": "kelas keren",
      // "user": {
      // 		"name": "adrian ariono",
      // 		"jobTitle": null,
      // 		"about": null
      // }
    },
    getListUlasanKelas() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.ulasanKelasPage() + `/${this.slugKelas}`)
        .then(res => {
          let data = res.data;
          let result = data.result;
          this.dataListUlasan = result.map(e => {
            let obj = {
              id: e.id,
              image: this.$root.getPhotoUser() + `/${e.user.photo}`,
              name: e.user.name,
              position: e.user.jobTitle == null ? "-" : e.user.jobTitle,
              ulasan: e.review,
              rating: e.rating
            };
            return obj;
          });

					console.log("res ", result);
					setTimeout(() => {
						$(".vue-star-rating-rating-text").hide();
					}, 1000);
          this.$root.loadingData().hide();
        })
        .catch(err => {
          console.log("err ", err);
          this.$root.loadingData().hide();
        });
    },
    handleData(newData) {
      this.slugKelas = newData.slug;
			console.log("new data ulasan ", newData);
			this.getListUlasanKelas();
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top props ", newData, oldData);
    }
  }
};
</script>

