<template>
  <div class="executor-blog modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Blog</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Blog</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />
    <div class="content-eb">
      <div class="form-input-mbx">
        <label for="title">Title</label>
        <input
          type="text"
          class="form-control"
          name="title"
          placeholder="Title Blog"
          v-model="dataExecutor.title"
        />
        <span v-if="!cannotNull.title" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="description">Deskripsi</label>
        <textarea
          type="text"
          class="form-control"
          name="description"
          placeholder="Deskripsi"
          v-model="dataExecutor.description"
        ></textarea>
        <span v-if="!cannotNull.description" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="content">Content</label>
        <!-- <textarea
          type="text"
          class="form-control"
          name="content"
          placeholder="Content"
          v-model="dataExecutor.content"
        ></textarea>-->
        <ckeditor :editor="editor" v-model="dataExecutor.content" :config="editorConfig"></ckeditor>

        <span v-if="!cannotNull.content" style="color: red;">This field is required.</span>
      </div>
      <!-- <div class="form-input-mbx">
        <label for="status">Status</label>
        <input
          type="text"
          class="form-control"
          name="status"
          placeholder="Status"
          v-model="dataExecutor.status"
          @focus="statusBlog().show()"
          @focusout="statusBlog().show()"
        />
        <ul id="statusBlog" class="list-status-kategori">
          <li
            v-for="(status, index) in dataExecutor.statusList"
            v-bind:key="index"
            @click="statusBlog().choose(status)"
          >{{ status }}</li>
        </ul>
        <span v-if="!cannotNull.status" style="color: red;">This field is required.</span>
      </div>-->
      <div class="form-input-mbx">
        <label for="status">Status</label>
        <div class="d-flex">
          <template v-for="(status, index) in listStatusKelas">
            <input
              v-bind:key="index+5"
              type="radio"
              name="status"
              v-bind:id="'f-status-'+ status.value"
              class="radio-mbx"
              v-bind:value="status.value"
              v-bind:checked="dataExecutor.status == status.value"
            />
            <label
              v-bind:key="index"
              v-bind:for="'f-status-'+status.value"
              style="margin: 3px 5px;"
              @click="statusBlog().choose(status.value)"
            >{{ status.text }}</label>
          </template>
        </div>
        <span v-if="!cannotNull.status" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="tags">Tags</label>
        <p v-if="dataExecutor.tags.length > 0" class="list-sub-kategori">
          <span v-for="(tags, index) in dataExecutor.tags" v-bind:key="index">
            {{ tags }}
            <i @click="tagsList().remove(index)" class="fa fa-times"></i>
          </span>
        </p>
        <input
          type="text"
          class="form-control"
          name="tags"
          placeholder="Tags"
          v-model="dataExecutor.tagsText"
          @keyup.enter="tagsList().add()"
        />
        <span>Note : klik by enter untuk menambahkan list Tags.</span>
        <p v-if="!cannotNull.tags" style="color: red;">This field is required.</p>
      </div>
      <div class="form-input-mbx">
        <label for="banner">Banner</label>
        <input
          type="file"
          class="form-control"
          name="banner"
          id="banner"
          placeholder="Banner"
          v-on:change="handleFile($event)"
        />
        <div class="file-upload-div d-flex" v-on:click="chooseFile('banner')">
          <span>{{ dataExecutor.banner }}</span>
          <span class="btn btn-up">Unggah Banner</span>
        </div>
        <div class="show-image">
          <img v-if="section == 'add'" id="showImage" src alt />
          <img
            v-if="section == 'edit'"
            id="showImage"
            v-bind:src="$root.getPhotoUser() + `/${dataExecutor.banner}`"
            alt
          />
        </div>
      </div>
    </div>

    <div class="btn-add-cancel-blog d-flex">
      <button @click="cancelBlog()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addBlog()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editBlog()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import ClassicEditor from "@ckeditor/ckeditor5-build-classic";

export default {
  name: "AddBlog",
  components: {},
  data() {
    return {
      section: "",
      uuid: "",
      dataExecutor: {
        title: "",
        description: "",
        content: "",
        tagsText: "",
        tags: [],
        status: "",
        banner: "Upload Banner",
        statusList: ["Publish", "Draft"]
      },
      formLogoBlog: "",
      listStatusKelas: [
        {
          text: "Publish",
          value: "Publish"
        },
        {
          text: "Draft",
          value: "Draft"
        }
      ],
      cannotNull: {
        title: true,
        description: true,
        content: true,
        tagsText: true,
        tags: true,
        status: true,
        banner: true
      },
      editor: ClassicEditor,
      editorData: "<p>Content of the editor.</p>",
      editorConfig: {
        // The configuration of the editor.
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      // "title": "",
      // "description": ".",
      // "content": "..",
      // "tags": [
      // 		"MentorBox",
      // 		"RuangMeraihCita"
      // ],
      // "status": "Publish",
      // "banner":"banner-yeah.png"

      let params = this.$props.data;
      this.section = params.section;
      if (params.section == "edit") {
        this.uuid = params.uuid;
        this.getDetailBlog();
      }
      console.log("this params ", params);
    },
    getDetailBlog() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listBlogCreator() + `/${this.uuid}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          a.title = result.title;
          a.description = result.description;
          a.content = result.content;
          a.status = result.status;
          a.tags = result.Tags.map(e => {
            return e.name;
          });
          a.banner = result.banner;

          console.log("dataExecutor ", a);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    chooseFile(id) {
      $("#" + id).click();
    },
    handleFile(evt) {
      let target = evt.target;
      let files = target.files;
      let sizeFiles = target.files[0].size;
      if (sizeFiles > 1000000) {
        this.$root.notification("warning", "Warning", "Max Size file is 1MB.");
      } else {
        let nameFile = target.files[0].name;
        this.dataExecutor.banner = nameFile;
        let bodyFormData = new FormData();
        bodyFormData.append("files", files[0]);
        this.formLogoBlog = bodyFormData;
        console.log("this.formLogoBlog ", this.formLogoBlog);
        this.renderImage(target.files[0]);
      }
      console.log("evt ", evt);
    },
    renderImage(files) {
      console.log("masuk render image ", files);
      var reader = new FileReader();
      reader.onload = function(e) {
        $("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    tagsList() {
      let a = this.dataExecutor;
      return {
        add() {
          let tagsText = a.tagsText;
          a.tags.push(tagsText);
          a.tagsText = "";
        },
        remove(idx) {
          let tagsList = a.tags;
          tagsList.splice(idx, 1);
        }
      };
    },
    statusBlog() {
      let a = this.dataExecutor;
      return {
        show() {
          $("#statusBlog").slideToggle();
        },
        choose(val) {
          a.status = val;
        }
      };
    },
    uploadLogoBlog() {
      let body = this.formLogoBlog;
      let AuthSth = this.$root.authorization();
      this.$root.loadingData().show();
      let vm = this;
      axios
        .post(vm.$root.filesUploadSingle(), body, {
          headers: {
            Authorization: AuthSth,
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let a = this.dataExecutor;
          a.banner = data.files;
          let body = this.convertPayload();
          if (this.section == "add") {
            this.postBlog(body);
          } else {
            this.putBlog(body);
          }
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostProfile().failed(`${resErr.data.message}`);
        });
    },
    convertPayload() {
      let a = this.dataExecutor;
      let body = {
        title: a.title,
        description: a.description,
        content: a.content,
        tags: a.tags,
        status: a.status,
        banner: a.banner
      };
      return body;
    },
    addBlog() {
      let body = this.convertPayload();
      console.log("body blog ", body);
      // this.postBlog(body);
      this.uploadLogoBlog();
    },
    postBlog(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listBlogCreator(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostBlog().success(
            `Blog ${body.title} Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostBlog().failed(`${resErr.data.message}`);
        });
    },
    afterPostBlog() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelBlog() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit data Blog ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    editBlog() {
      let a = this.dataExecutor;
      let formLogoBlog = this.formLogoBlog;
      console.log("data executor ", a, formLogoBlog);
      if (formLogoBlog !== "") {
        this.uploadLogoBlog();
      } else {
        let body = this.converPayloadBody();
        this.putBlog(body);
      }
    },
    putBlog(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;

      axios
        .put(vm.$root.listBlogCreator() + `/${this.uuid}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostBlog().success(`Blog ${body.title} Berhasil di Perbarui`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostBlog().failed(`${resErr.data.message}`);
        });
    },
    closeModal() {
      // this.$modal.hide("executor-blog");

      this.$parent.showMainPage().parent(false, "list-blog");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-blog {
  padding: 15px 15px 30px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-eb {
    .list-sub-kategori {
      margin: 0 0 5px 0;
      height: auto;
      display: flex;
      flex-wrap: wrap;
      > span {
        background: #cccccc;
        padding: 5px;
        border-radius: 5px;
        margin: 3px;
        > i {
          color: #000000;
          cursor: pointer;
          &:hover {
            color: #ee0f0f;
          }
        }
      }
    }
    .list-status-kategori {
      // position: absolute;
      background: #ffffff;
      padding: 0%;
      display: none;
      width: 50%;
      left: 0;
      margin: 3px 0 0 0;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 0 0 10px 10px;
      > li {
        color: #000000;
        list-style: none;
        padding: 5px 10px;
        cursor: pointer;
        &:nth-tags(even) {
          background: #cccccc;
        }
        &:hover {
          background: #999898;
        }
      }
    }
  }
}
.btn-add-cancel-blog {
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  width: 100%;
  padding: 10px;
  > button {
    width: 49%;
  }
}
.show-image {
  display: flex;
  justify-content: center;
  margin: 15px 0;
  > img {
    width: 80%;
    height: 100%;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  }
}
</style>