<template>
  <div class="check-voucher">
    <div class="container">
      <div class="content-check-voucher">
        <h1 class="title-cv">Detail Pembayaran</h1>
        <div class="add-kelas">
          <div class="name-kelas">
            <ul class="list-kelas-checkout">
              <li v-if="dataListDetailKelas.length == 0">
                <input
                  type="checkbox"
                  name="check-voucher"
                  id="checkV"
                  class="checkbox-mbx"
                  checked
                  disabled="disabled"
                />
                <label for="checkV" class="checklist">List Kelas Kosong, silahkan tambah Kelas</label>
              </li>
              <li v-for="(list, idx) in dataListDetailKelas" v-bind:key="idx">
                <span 
                  class="delete-kelas"
                  @click="deleteKelas(idx)"
                >
                  <i class="fa fa-trash"></i>
                </span>
                <input
                  type="checkbox"
                  name="check-voucher"
                  id="checkV"
                  class="checkbox-mbx"
                  checked
                  disabled="disabled"
                />
                <label for="checkV" class="checklist">“ {{ list.name }} “ <span>Telah ditambahkan</span></label>
              </li>
            </ul>
          </div>
          <router-link v-bind:to="'/mentoring'" class="btn-ak">
            <button class="btn">Tambah Kelas</button>
          </router-link>
        </div>

        <div class="voucher-kelas">
          <div class="vk-top">
            <div class="cvk-kelas">
              <!-- <input type="checkbox" name="check-voucher" id="checkV" class="checkbox-custom" checked> -->
              <label for="checkV" class="checklist">Check Voucher Kelas</label>
              <label 
                for="voucherValidated" 
                class="voucher-validated"
                v-if="dataListVoucher.length !== 0"
              > 
                Kode Voucher 
                <span>{{ textVoucherValidated }} </span>
              </label>
              <ol 
                v-if="dataListVoucher.length !== 0"
                class="list-voucher-validated"
              >
                <li 
                  v-for="(data, idx) in dataListVoucher"
                  v-bind:key="idx"
                >
                  Kelas <b>{{ data.name }}</b> mendapatkan diskon <b>{{ data.diskon }}.</b>
                </li>
              </ol>
            </div>
            <div class="btn-plus" id="btnCV" v-on:click="showCV()">
              <i class="fa fa-plus"></i>
            </div>
          </div>
          <div id="divCheckVoucher">
            <div class="vk-bottom">
              <input 
                type="text" 
                id="inpCheckVoucher" 
                class="form-control" 
                v-model="textVoucher" 
                @keyup.enter="checkVoucher()"
              />
              <button class="btn btn-vk" @click="checkVoucher()">Gunakan Voucher</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from 'axios';

export default {
  name: "CheckVoucher",
  components: {},
  data() {
    return {
      dataProdukUser: [],
      dataListDetailKelas: [],
      textVoucher: "",
      textVoucherValidated: '',
      idxDataProdukUser: '',
      dataListVoucher: []
    };
  },
  methods: {
    init() {
      this.handleData();
    },
    updateDataStore(data) {
      this.$store.state.dataCheckoutUser = data;
    },
    handleData() {
      let getDataKelas = window.$cookies.get("listKelas");
      if ( getDataKelas !== null ) {
        let dataUser = window.$cookies.get("loginData");
        let uuidUser = dataUser.result.uuid;
        this.dataProdukUser = getDataKelas.data
          .filter((e, idx) => {
            if (e.uuid == uuidUser) {
              idx;
              this.idxDataProdukUser = idx;
              return e;
            }
          })
          .map(j => {
            return j;
          });

        if ( this.dataProdukUser.length == 0 ) {
          this.updateDataStore([]);
        } else {
          this.dataListDetailKelas = this.dataProdukUser[0].kelas.map(e => {
            let data = window.$cookies.get(e);
            // console.log('data ', data)
            if ( data !== null ) {
              return data;
            } else {
              return undefined;
            }
          }).map(j => {
            return j;
          });
          this.updateDataStore( this.dataProdukUser );
        }
  
        console.log("data list kelas ", this.dataListDetailKelas, this.idxDataProdukUser);

      }
    },
    showCV() {
      $("#btnCV i").toggleClass("fa-plus fa-minus");
      $("#divCheckVoucher").slideToggle();
    },
    executeDelete(idx) {
      this.$root.loadingData().show();
      let dataProdukUser = this.dataProdukUser;
      let dataListDetailKelas = this.dataListDetailKelas;

      dataListDetailKelas.splice(idx, 1);
      dataProdukUser[0].kelas.splice(idx, 1);

      // for remove data kelas in cookies by per user login.
      // let dataForRemove = dataProdukUser[0].kelas[idx];
      // window.$cookies.remove(dataForRemove);
      
      console.log('data after remove ', dataProdukUser);

      setTimeout(() => {
        this.$root.loadingData().hide();
        this.updateDataStore( dataProdukUser );
        this.fixingDeleteDataCookies(dataProdukUser);
      }, 2000);

    },
    fixingDeleteDataCookies(newData) {
      let getDataKelas = window.$cookies.get("listKelas");
      let idxData = this.idxDataProdukUser;

      // set temp data from cookies and replace after delete kelas by user.
      getDataKelas.data[idxData] = newData[0];

      // set cookies again after delete data kelas.
      window.$cookies.set('listKelas', getDataKelas);

      console.log('semua data ', window.$cookies.get("listKelas"));

      window.location.reload();
    },
    deleteKelas(idx) {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> Ingin menghapus Kelas ?",
        buttons: [
					{
            title: "No",
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.$modal.hide("dialog");
              this.executeDelete(idx);
            }
          }
        ]
      })
    },
    checkVoucher() {
      let body = {
        code: this.textVoucher,
        uuid: this.dataListDetailKelas.map((e) => {
          return e.uuid;
        })
      };

      if ( this.textVoucher == "" ) {
        this.$root.notification("warning", "Warning", "Please Input kode Voucher, kode Voucher is Required!")
      } else {
        this.postVoucher(body);
        console.log('body check voucher ', body);
      }
    },
    postVoucher(body) {
      this.$root.loadingData().show();
      axios.post( this.$root.checkVoucherPage(), body )
      .then((res) => {
        let data = res.data;
        let result = data.detail;
        let tempNotif = [];
        tempNotif = result.filter((e) => {
          if ( e.diskon !== 0 ) {
            return e;
          }
        }).map((j) => {
          return `Voucher untuk Kelas ${j.name} adalah ${j.diskon}`
        })
        this.$root.notification("success", "Success", tempNotif.join('<br>'));

        // for handle data show list voucher
        this.textVoucherValidated = data.code;
        this.dataListVoucher = result.filter((e) => {
          if ( e.diskon !== 0 ) {
            return e;
          }
        }).map((j) => {
          return j;
        })
        this.showCV();

        let dataForCheckout = this.$store.state.dataForCheckout;
        dataForCheckout.voucher = data.code;
        console.log('dataForCheckout ', dataForCheckout);

        // for save data voucher;
        this.$store.state.dataVoucherForCheckout = result;


        console.log('res voucher ', res);
        this.$root.loadingData().hide();
      })
      .catch((err) => {
        let resErr = err.response;
        if ( resErr.status == 400 ) {
          this.$root.notification("danger", "Warning", "Voucher Not Found or Voucher is Expired.");
        }
        console.log('err voucher ', err);
        this.$root.loadingData().hide();
      })
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/style-global.scss";

.check-voucher {
  font-family: "Montserrat";
  padding: 20px 0;
  .content-check-voucher {
    color: #ffffff;
    text-align: left;
    .title-cv {
      font-style: normal;
      font-weight: normal;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    .add-kelas {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      display: flex;
      padding: 2% 3%;
      border-radius: 10px;
      justify-content: space-between;
      margin: 10px 0;
      > .name-kelas {
        > .list-kelas-checkout {
          padding: 0%;
          margin: 0%;
          > li {
            list-style: none;
            display: flex;
            align-items: center;
            > .delete-kelas {
              padding: 0 10px 0 0;
              cursor: pointer;
              > i {
                font-size: 1.5em;
              }
              &:hover {
                color: #f80808;
              }
            }
          }
        }
        label {
          &::before {
            background: #fff;
          }
        }
      }
      > .btn-ak {
        display: flex;
        margin: auto 0;
        &:hover {
          text-decoration: none;
        }
        > button {
          background: #f2994a;
          color: #ffffff;
          border: 1px solid #f2994a;
          &:hover {
            background: #003461;
            border: 1px solid #ffffff;
          }
        }
      }
    }
    .voucher-kelas {
      > .vk-top {
        background: #003461;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
        padding: 2% 3%;
        border-radius: 10px;
        margin: 10px 0;
        display: flex;
        justify-content: space-between;
        .cvk-kelas {
          display: flex;
          flex-direction: column;
          > label {
            margin: 0;
            &::before {
              background: #fff;
            }
          }
          .voucher-validated {
            margin: 15px 0 5px 0;
            > span {
              padding: 10px;
              background: #0ae24b;
              border-radius: 5px;
            }
          }
          .list-voucher-validated {
            padding: 0%;
            margin: 10px 0;
            > li {
              list-style: inside;
            }
          }
        }
        .btn-plus {
          cursor: pointer;
        }
      }
      > #divCheckVoucher {
        width: 100%;
        display: none;
        > .vk-bottom {
          padding: 30px 20px 20px 20px;
          border: 1px solid #003461;
          margin: -20px 0 0 0;
          display: flex;
          border-radius: 0 0 10px 10px;
          > .btn-vk {
            background: #f2994a;
            color: #ffffff;
            margin: 0 0 0 20px;
          }
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .check-voucher {
    .content-check-voucher {
      .title-cv {
        font-size: 30px;
      }
      .add-kelas {
        flex-direction: column;
        > .name-kelas {
          > label {
            text-align: center;
          }
        }
        > .btn-ak {
          margin: auto;
        }
      }
      .voucher-kelas {
        > #divCheckVoucher {
          > .vk-bottom {
            flex-direction: column;
            > .btn-vk {
              margin: 10px auto 0 auto;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .check-voucher {
    .content-check-voucher {
      .title-cv {
        font-size: 25px;
      }
      .add-kelas {
        flex-direction: column;
        > .name-kelas {
          > label {
            text-align: center;
          }
        }
        > .btn-ak {
          margin: auto;
        }
      }
      .voucher-kelas {
        > #divCheckVoucher {
          > .vk-bottom {
            flex-direction: column;
            > .btn-vk {
              margin: 10px auto 0 auto;
            }
          }
        }
      }
    }
  }
}
</style>