<template>
  <div 
    class="menu-kelas"
    v-if="dataKategoriList.length !== 0"
  >
    <div class="container justify-content-center">
      <ul 
        class="menu-kelas-parent"
        v-if="dataKategoriList.length > 0"
      >
        <li 
          class="menus-parent" 
          v-for="(menu, idx) in dataKategoriList" 
          v-bind:key="idx" 
          v-bind:class="{'has-child' : (menu.menus).length > 0} "
          v-bind:id="'showChild'+idx"
          @click="showChild(idx)"
        >
          <a> 
            {{ menu.title_menu }} 
            <span v-if="menu.use_icon">
              <i v-bind:class="menu.icon_up + ' up'" aria-hidden="true"></i>
              <i v-bind:class="menu.icon_down + ' down'" aria-hidden="true"></i>
            </span>
            <ul 
              v-if="(menu.menus).length > 0" 
              class="child-menu"
              v-bind:id="'childMenu'+idx"
            >
              <li 
                class="menus-child"
                v-for="(menus, idxj) in menu.menus" 
                v-bind:key="idxj" 
              >
                <router-link v-bind:to="menus.url" > {{ menus.sub_title }} </router-link>
              </li>
            </ul>
          </a>
        </li>
      </ul>
      <ul
        class="menu-kelas-parent"
        v-else
      >
        <li class="menus-parent">
          <a href="#">
            Belum ada Data Kategori
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
  name: "LandingMenu",
  data () {
    return {
      landingMenu: [
        {
          id: 1,
          title_menu: "Teknologi",
          use_icon: true,
          icon_up: "fa fa-angle-up",
          icon_down: "fa fa-angle-down",
          url : "#",
          menus : [
            {
              id: 1,
              sub_title: "StartUp",
              url: "startup"
            },
            {
              id: 2,
              sub_title: "Fintech",
              url: "fintech"
            },
            {
              id: 3,
              sub_title: "IOT",
              url: "iot"
            },
            {
              id: 4,
              sub_title: "Programming",
              url: "programming"
            },
          ]
        },
        {
          id: 2,
          title_menu: "Bisnis dan Keuangan",
          use_icon: false,
          icon_up: "fa fa-angle-up",
          icon_down: "fa fa-angle-down",
          url : "#",
          menus : []
        },
        {
          id: 3,
          title_menu: "Pengembangan Diri",
          use_icon: false,
          icon_up: "fa fa-angle-up",
          icon_down: "fa fa-angle-down",
          url : "#",
          menus : []
        },
        {
          id: 4,
          title_menu: "Agriculture",
          use_icon: false,
          icon_up: "fa fa-angle-up",
          icon_down: "fa fa-angle-down",
          url : "#",
          menus : []
        },
        {
          id: 5,
          title_menu: "Lainnya...",
          use_icon: false,
          icon_up: "fa fa-angle-up",
          icon_down: "fa fa-angle-down",
          url : "#",
          menus : []
        }
      ],
      dataKategoriList: []
    }
  },
  components: {},
  methods: {
    init() {
      this.getListKategoriPage();
    },
    getListKategoriPage() {
      axios.get( this.$root.listKategoriPage() )
      .then((res) => {
        console.log('res ', res)
        let data = res.data;
        let result = data.result;
        this.dataKategoriList = result.map((e) => {
          let obj = {
            id: e.id,
            title_menu: e.title,
            slug: e.slug,
            use_icon: e.child.length == 0 ? false : true,
            icon_up: "fa fa-angle-up",
            icon_down: "fa fa-angle-down",
            url : "#",
            menus : e.child.length == 0 ? [] : e.child.map((j) => {
              let objC = {
                id: j.id,
                sub_title: j.title,
                slug: j.slug,
                url: `/mentoring?kategori=${e.slug}&subkategori=${j.slug}`
              };
              return objC;
            })
          };
          return obj;
        });

        console.log('dataKategoriList ', this.dataKategoriList);
      })
      .catch((err) => {
        console.log('err ', err);
      })
    },
    showChild(idx) {
      $(`#showChild${idx}`).toggleClass('active');
      $(`#childMenu${idx}`).slideToggle();
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.menu-kelas {
  margin: 23px 0px;
  > .container {
    background: linear-gradient(
      339.56deg,
      #004c94 4.18%,
      #0094ff 100.63%,
      #878045 100.63%
    );
    border-radius: 16px;
    padding: 18px 12px;
    text-align: center;
    height: auto;
    > .menu-kelas-parent {
			padding: 0;
			margin: 0;
      z-index: 10;
      display: flex;
      align-items: flex-start;
      justify-content: center;
			> .menus-parent {
				display: inline-block;
				background-color: white;
				padding: 8px 0px;
				border-radius: 15px;
				margin: 0px 10px;
				width: 200px;
				font-size: 16px;
        color: #ffffff;
        &.active {
          a {
            > span {
              margin: 0 0 0 10px;
              .up {
                display: initial;
              }
              .down {
                display: none;
              }
            }
          }
        }
        a {
          color: #000;
          cursor: pointer;
          &:hover {
            text-decoration: none;
            color: #0094ff;
          }
          > span {
            margin: 0 0 0 10px;
            .up {
              display: none;
            }
            .down {
              display: initial;
            }
          }
          ul.child-menu {
            display: none;
            text-align: left;
            padding: 10px;
            width: 200px;
            position: absolute;
            z-index: 99;
            background: #ffffff;
            border-radius: 15px;
            margin: 10px 0 0 0;
            > li {
              list-style: none;
              padding: 5px 10px;
            }
          }
        }
			}
		}
  }
}
@media (min-width: 1024px) and (max-width: 1280px) {
  .menu-kelas {
    > .container {
      > .menu-kelas-parent {
        > .menus-parent {
          margin: 0 5px;
        }
      }
    }
  }  
}
@media (min-width: 768px) and (max-width: 1023px) {
  .menu-kelas {
    > .container {
      > .menu-kelas-parent {
        > .menus-parent {
          margin: 0 5px;
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .menu-kelas {
    display: none;
  }
}
</style>