<template>
  <section class="tentang-mentor-kelas">
    <div class="container">
      <h1 class="title-dk">Tentang Mentor</h1>
      <div class="desc-mentor">
        <div class="face-mentor">
          <img
            v-bind:src="$root.getPhotoUser() + `/${mentor.photo}`"
            v-bind:alt="mentor.name"
          />
          <div class="about-mentor">
            <p class="name-mentor">{{ mentor.name }}</p>
            <p class="position-mr">{{ mentor.jobTitle }}</p>
          </div>
        </div>
        <div class="btn-spand-dm" v-on:click="showDescMentor()">
          <i class="fa fa-plus"></i>
          <!-- <i class="fa fa-minus"></i> -->
        </div>
      </div>
      <div id="contentDm" class="content-dm">
        <p
          class="p-dm"
        >{{ mentor.about }}</p>
      </div>
    </div>
  </section>
</template>

<script>
import $ from "jquery";

export default {
  name: "TentangMentor",
  components: {},
  data() {
    return {
      mentor: {
        about: "",
        jobTitle: "",
        name: "",
        photo: "",
      }
    }
  },
  props: ['data'],
  methods: {
    init() {
			this.mentor = this.$store.state.dataDetailKelas.mentor;
			// this.handleData( this.$props.data );
		},
    showDescMentor() {
      $("#contentDm").slideToggle();
      $(".btn-spand-dm i").toggleClass("fa-plus fa-minus");
    },
		handleData(newData) {
			this.mentor = newData.mentor;
		}
  },
	mounted() {
		this.init();
	},
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top mentor ", newData, oldData);
    }
  }
};
</script>

<style lang="scss" scoped>
.tentang-mentor-kelas {
  padding: 20px 0 25px 0;
  font-family: "Montserrat";
  text-align: left;
  .container {
    > .title-dk {
      font-style: normal;
      font-weight: normal;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    > .desc-mentor {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 15px 25px;
      display: flex;
      justify-content: space-between;
      > .face-mentor {
        display: flex;
        > img {
          width: 75px;
          border-radius: 50%;
        }
        > .about-mentor {
          color: #ffffff;
          padding: 0 15px;
          display: flex;
          flex-direction: column;
          margin: auto 0;
          > p {
            margin: 5px 0;
            &.name-mentor {
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
              line-height: 20px;
            }
            &.position-mr {
              font-style: normal;
              font-weight: normal;
              font-size: 12px;
              line-height: 15px;
            }
          }
        }
      }
      > .btn-spand-dm {
        color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        cursor: pointer;
        > i {
          font-size: 22px;
        }
      }
    }
    > .content-dm {
      padding: 35px 25px 15px 25px;
      border-right: 1px solid #000000;
      border-bottom: 1px solid #000000;
      border-left: 1px solid #000000;
      margin: -10px 0 0 0;
      display: none;
      > .p-dm {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .tentang-mentor-kelas {
    .container {
      > .title-dk {
        font-size: 34px;
      }
    }
  }
}
@media (max-width: 481px) {
  .tentang-mentor-kelas {
    .container {
      > .title-dk {
        font-size: 32px;
      }
    }
  }
}
</style>