<template>
  <section class="visi-misi-us">
    <div class="container">
      <div class="parent-tab">
        <ul class="tabs">
          <li
            v-for="tab in dataVisiMisi"
            v-bind:key="tab.id"
            v-bind:class="{active: tab.show, [ tab.sub_id ] : tab.show }"
            v-on:click="changeTab($event, tab.sub_id)"
          >
            <a>{{ tab.title_tab }}</a>
          </li>
          <hr />
        </ul>
      </div>
      <div class="content-visi-misi" v-if="dataVisiMisi[0].show">
        <p class="message text-center"> Mentoring Online Nomor 1 di Indonesia </p>
      </div>
      <div class="content-visi-misi" v-if="dataVisiMisi[1].show">
        <p class="message"> 1. Memperluas akses orang-orang untuk menemukan mentor terbaik dibidangnya. </p>
        <p class="message"> 2. Menyediakan layanan mentoring dengan mentor dan materi terbaik untuk bisa diaplikasikan langsung. </p>
        <p class="message"> 3. Menyediakan teknologi untuk memudahkan semua klien mendapatkan mentoring berkualitas. </p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "VisiMisi",
  components: {},
  data() {
    return {
      dataVisiMisi: [
        {
          id: 1,
          sub_id: "visi",
          title_tab: "Visi",
          show: true,
          message:
            "Mentoring Online Nomor 1 di Indonesia"
        },
        {
          id: 2,
          sub_id: "misi",
          title_tab: "Misi",
          show: false,
          message: `1. Memperluas akses orang-orang untuk menemukan mentor terbaik dibidangnya. \n
          2. Menyediakan layanan mentoring dengan mentor dan materi terbaik untuk bisa diaplikasikan langsung. \n
          3. Menyediakan teknologi untuk memudahkan semua klien mendapatkan mentoring berkualitas.`
        }
      ]
    };
  },
  methods: {
    changeTab(evt, id) {
      console.log("change Tab ", id);

      let dataTab = this.dataVisiMisi;
      for (let i = 0; i < dataTab.length; i++) {
        if (dataTab[i].sub_id == id) {
          dataTab[i].show = true;
        } else {
          dataTab[i].show = false;
        }
      }
    }
  }
};
</script>

