import $ from 'jquery';
import store from '../store';
import router from '../router';

export default {
    methods: {
        api() {
            let api = 'https://mentorbox.herokuapp.com/'
            let version = 'v1'
            return api + version;
        },
        makeid(length) {
            var result = '';
            var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
            var charactersLength = characters.length;
            for (var i = 0; i < length; i++) {
                result += characters.charAt(Math.floor(Math.random() * charactersLength));
            }
            return result;
        },
        makeidNumber(length) {
            var result = '';
            var characters = '0123456789';
            var charactersLength = characters.length;
            for (var i = 0; i < length; i++) {
                result += characters.charAt(Math.floor(Math.random() * charactersLength));
            }
            return result;
        },
        getParameterByName(name) {
            let url = window.location.href;
            name = name.replace(/[\\[\]]/g, '\\$&');
            var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
                results = regex.exec(url);
            if (!results) return null;
            if (!results[2]) return '';
            return decodeURIComponent(results[2].replace(/\+/g, ' '));
        },
        goToTop() {
            $("html, body")
                .stop()
                .animate({
                    scrollTop: 0
                }, 700, "swing");
        },
        loadingMentorbox: function () {
            return {
                show() {
                    $('body').css({
                        'overflow': 'hidden'
                    })
                    $('.overlay-load-mentorbox').show()
                    $('#alm').show();
                },
                hide() {
                    $('body').css({
                        'overflow': 'auto'
                    })
                    $('.overlay-load-mentorbox').hide()
                    $('#alm').hide();
                }
            }
        },
        loadingData: function () {
            return {
                show() {
                    $('body').css({
                        'overflow': 'hidden'
                    })
                    $('.overlay-load-mentorbox-transparent').show()
                    $('.loading-center').show()
                },
                hide() {
                    $('body').css({
                        'overflow': 'auto'
                    })
                    $('.overlay-load-mentorbox-transparent').hide()
                    $('.loading-center').hide()
                }
            }
        },
        alertCtrl: function (data) {
            data;
        },
        showPassword: function (id, inp) {
            $(`#${id} > i`).toggleClass("fa-eye fa-eye-slash");
            let input = $(`#${inp}`);
            if (input.attr("type") == "password") {
                input.attr("type", "text");
            } else {
                input.attr("type", "password");
            }
        },
        checkValidateEmail(email) {
            var re = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
            return re.test(email);
        },
        notification: function (j, h, m) {
            let idContainer = 'notification-container';
            let idNotif = 'idNotif' + this.makeid(7);

            let notif = `<div class="notification notification-${j}" id="${idNotif}" style="display: none;">
                <div class="header-message">
                <strong>${h} : </strong> <span class="close-${idNotif}"><i class="fa fa-times"></i></span>
                </div>
                <p class="message">${m}</p>
            </div>`

            $('#' + idContainer).append(notif)
            $('#' + idNotif).slideToggle();
            $('.close-' + idNotif).on('click', function () {
                $('#' + idNotif).slideToggle();
                setTimeout(() => {
                    $('#' + idNotif).remove();
                }, 500)
            })

            setTimeout(() => {
                $('#' + idNotif).remove();
            }, 7000)
        },
        authentication() {
            store.commit('setCookies');
            let loginData = window.$cookies.get('loginData');

            // console.log('loginData ', loginData);
            if (loginData !== null) {
                return loginData.auth;
            } else {
                return false;
            }
        },
        authorization() {
            let loginData = window.$cookies.get('loginData');
            // console.log('loginData ', loginData);
            if (loginData !== null) {
                return loginData.token;
            } else {
                return false;
            }
        },
        checkRole(role) {
            let loginData = window.$cookies.get("loginData");
            if (loginData == null) {
                return false;
            } else {
                let result = loginData.result;
                let roles = result.roles;

                let temp = roles
                    .filter(e => {
                        if (e == role) {
                            return true;
                        }
                    })
                    .map(e => {
                        return e;
                    });

                if (temp[0] == role) {
                    return true
                } else {
                    return false
                }
            }
        },
        backPage() {
            router.go(-1);
        },
        logoutUser() {
            this.loadingMentorbox().show();
            window.$cookies.remove("loginData");

            setTimeout(() => {
                store.state.loginData = {};
                let path = window.location.pathname;
                if (path == "/") {
                    router.go("/");
                } else {
                    router.push("/");
                }
            }, 3000);
        },
        executeCheckout(data) {
            // start set data referal for checkout;
            store.state.dataForCheckout.kodeReferral = this.getParameterByName('ref');
            window.$cookies.set('kodeReferral', this.getParameterByName('ref'));

            // start for set data kelas user in cookies;
            let getDataKelas = window.$cookies.get('listKelas');
            console.log('list kelas cookies ', getDataKelas);
            let dataUser = window.$cookies.get('loginData');
            let uuidUser = dataUser.result.uuid;
            console.log('data user ', uuidUser);
            let listKelas = {
                data: []
            };
            let obj = {
                uuid: uuidUser,
                name: dataUser.result.name,
                email: dataUser.result.email,
                kelas: []
            };
            let newID = `mbxuuid-${uuidUser}-${this.makeid(10)}`;
            let tempListKelas = [];
            if (getDataKelas == null) {
                obj.kelas.push(newID);
                listKelas.data.push(obj);
                window.$cookies.set('listKelas', listKelas);
                window.$cookies.set( newID, data );
                router.push('/detail-pembayaran/' + 'checkout');
            } else {
                
                let idxDataUser = '';
                tempListKelas = getDataKelas.data.filter((e, idx) => {
                    if ( e.uuid == uuidUser ) {
                        idxDataUser = idx;
                        return e;
                    }
                }).map((j) => {
                    return j; 
                })

                console.log('tempListKelas ', tempListKelas);
                if ( tempListKelas.length == 0 ) {
                    obj.kelas.push(newID);
                    getDataKelas.data.push(obj);
                    window.$cookies.set('listKelas', getDataKelas);
                    window.$cookies.set( newID, data );

                    router.push('/detail-pembayaran/' + 'checkout');
                } else {
                    tempListKelas[0].kelas.push(newID);
                    getDataKelas.data[idxDataUser] = tempListKelas[0];
                    window.$cookies.set('listKelas', getDataKelas);
                    window.$cookies.set(newID, data);
                    
                    router.push('/detail-pembayaran/' + 'checkout');
                }
                
            }
        },
        executeSuccessCheckout() {
            let getDataKelas = window.$cookies.get("listKelas");
            let dataUser = window.$cookies.get("loginData");
            let uuidUser = dataUser.result.uuid;
            let idxDataProdukUser = '';
            let dataProdukUser = getDataKelas.data
            .filter((e, idx) => {
                if (e.uuid == uuidUser) {
                idx;
                idxDataProdukUser = idx;
                return e;
                }
            })
            .map(j => {
                return j;
            });
            dataProdukUser;
            getDataKelas.data.splice(idxDataProdukUser, 1);
            window.$cookies.set("listKelas", getDataKelas);
        },
        sessionTimeUp() {
            this.notification(
                "danger",
                "Warning",
                `Session Time Up, Please Login Again!`
            );
            this.logoutUser();
        },
        hideMenuToHeader() {
            $("#listDetailLogin").css({
                display: 'none'
            });
            $("#listDMP").css({
                display: 'none'
            });
            $("#idMoreDashboard").css({
                display: 'none'
            });
            $("#notifList").css({
                display: 'none'
            });
        },
        sortBy(data, colIndex, srt) {
            // cool sort
            function normalize(value) {
                var padding = '000000000000000';
                value = value.replace(/(\d+)((\.\d+)+)?/g, function ($0, integer, decimal, $3) {
                    if (decimal !== $3) {
                        return padding.slice(integer.length) + integer + decimal;
                    }
                    decimal = decimal || '.0';
                    return padding.slice(integer.length) + integer + decimal + padding.slice(decimal.length);
                });
                return value;
            }
            const mount = (a, b) => {
                var aMixed = normalize(a);
                var bMixed = normalize(b);
                return aMixed < bMixed ? -1 : 1;
            };
            data.sort((a, b) => mount(String(a[colIndex]).toLowerCase(), String(b[colIndex]).toLowerCase()));
            let temp = [
                []
            ];
            let j = 0;
            data.map((e, i) => {
                temp[j].push(e);
                if (i < data.length - 1 && e[colIndex]) {
                    if (String(e[colIndex][0]).toLowerCase() != String(data[i + 1][colIndex][0]).toLowerCase()) {
                        j++;
                        temp[j] = [];
                    }
                }
            });
            let change = [];
            temp.map((e, i) => {
                i;
                change.push(...e.sort((a, b) => mount(String(a[colIndex]), String(b[colIndex]))));
            });
            return srt == true ?
                change.every((val, i, arr) => val[colIndex] === arr[0][colIndex]) ?
                change.reverse() :
                change :
                change.reverse();
        }

    }
}