<template>
  <section class="deskripsi-kelas">
    <div class="container">
      <h1 class="title-dk">Deskripsi Kelas</h1>
      <div class="content-dk">
        <p
          class="p-dk"
        >{{ about }}</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "DeskripsiKelas",
	components: {},
	data() {
		return {
			about: ''
		}
	},
	props: ['data'],
  methods: {
    init() {
			this.about = this.$store.state.dataDetailKelas.about;
			// this.handleData( this.$props.data );
		},
		handleData(newData) {
			this.about = newData.about;
		}
	},
	mounted() {
		this.init();
	},
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top deskripsi ", newData, oldData);
    }
  }
};
</script>

<style lang="scss" scoped>
.deskripsi-kelas {
  padding: 20px 0 25px 0;
  font-family: "Montserrat";
  text-align: left;
  .container {
    > .title-dk {
      font-style: normal;
      font-weight: normal;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    > .content-dk {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      > .p-dk {
        color: #ffffff;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .deskripsi-kelas {
    .container {
      > .title-dk {
        font-size: 34px;
      }
    }
  }
}
@media (max-width: 481px) {
  .deskripsi-kelas {
    .container {
      > .title-dk {
        font-size: 32px;
      }
    }
  }
}
</style>