<template>
  <div class="content-list-flash-sale">
    <h3 class="title-lk">List Flash Sale</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button 
            class="btn btn-primary"
            @click="searchList()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button class="btn btn-primary btn-action-top-tbl-mbx" @click="addFS()">Add Flash Sale</button>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Name Kelas
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Harga Flash
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Harga Jual
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Harga Coret
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Start At
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Expired At
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 100px;width: 80px;">
                <div class="btn-action-tbl-mbx">
                  <!-- <button class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button> -->
                  <button
                    class="btn btn-warning"
                    @click="editFS(list.id)"
                  >
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button> -->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.namaKelas }}</td>
              <td>{{ list.hargaFlash }}</td>
              <td>{{ list.hargaJual }}</td>
              <td>{{ list.hargaCoret }}</td>
              <td>
                <p style="margin: 0;">{{ list.startAt.split(' ')[0] }}</p>
                <p style="margin: 0;">{{ list.startAt.split(' ')[1] }}</p>
              </td>
              <td>
                <p style="margin: 0;">{{ list.expiredAt.split(' ')[0] }}</p>
                <p style="margin: 0;">{{ list.expiredAt.split(' ')[1] }}</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Flash Sale Kosong, Silahkan Tambah Flash Sale.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="perPage()">
            10
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li>5</li>
            <li>10</li>
            <li>20</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <p>
            Page
            <span>1</span>
          </p>
          <p>
            Of
            <span>1</span>
          </p>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" name id />
          <p>Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-fs" :clickToClose="false" :width="'80%'" :height="'80%'">
      <ExecutorFlashSale @init="init()" :data="dataExecutor" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import ExecutorFlashSale from './ExecutorFlashSale';

export default {
  name: "ListFlashSale",
  components: {
    ExecutorFlashSale,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: "",
      },
      dataExecutor: {
        section: '',
        id: ''
      },
      dataTable: {
        list: [],
        colSearch: {
          namaKelas: false,
          hargaFlash: false,
          hargaJual: false,
          hargaCoret: false,
          startAt: false,
          expiredAt: false
        },
        colSort: {
          namaKelas: false,
          hargaFlash: false,
          hargaJual: false,
          hargaCoret: false,
          startAt: false,
          expiredAt: false
        }
      }
    };
  },
  methods: {
    init() {
      // id: 1
      // namaKelas: "Belajar Content Writing"
      // hargaFlash: 79000
      // hargaJual: 99000
      // hargaCoret: 150000
      // startAt: "2020-05-01 08:00:00"
      // expiredAt: "2020-06-01 07:59:59"

      this.getListFlashSale();
    },
    getListFlashSale() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.listFlashSaleAdmin() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          this.dataTable.list = [];
          let result = res.data.result;
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchList() {
      this.init();
    },
    addFS() {
      let a = this.dataExecutor;
      a.section = 'add';
      this.$modal.show("executor-fs");
    },
    editFS(id) {
      let a = this.dataExecutor;
      a.section = 'edit';
      a.id = id;
      this.$modal.show("executor-fs");
    },
    perPage() {
      $(".list-pp").slideToggle();
    },
    tableMbx() {
      let a = this.dataTable;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        }
      };
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-flash-sale {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    // width: 150%;
  }
}
</style>