<template>
  <div class="edit-jadwal-kelas modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Kategori</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Jadwal</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-ej">
      <div class="form-input-mbx">
        <label for="kelas">Kelas</label>
        <input
          type="text"
          class="form-control"
          name="kelas"
          placeholder="Kelas"
					disabled
          v-model="dataExecutor.kelas"
        />
        <span v-if="!cannotNull.kelas" style="color: red;">This field is required.</span>
      </div>
			<div class="form-input-mbx">
        <label for="batch">Batch</label>
        <input
          type="text"
          class="form-control"
          name="batch"
          placeholder="Batch"
          v-model="dataExecutor.jadwal"
        />
        <span v-if="!cannotNull.jadwal" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="slot">Slot</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="slot"
          placeholder="slot"
          v-model="dataExecutor.slot"
        />
        <span v-if="!cannotNull.slot" style="color: red;">This field is required.</span>
      </div>
			<div class="form-input-mbx">
        <label for="linkKelas">Link Kelas</label>
        <input
          type="text"
          class="form-control"
          name="linkKelas"
          placeholder="Link Kelas"
          v-model="dataExecutor.linkKelas"
        />
        <span v-if="!cannotNull.linkKelas" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="startDate">Start Date</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.startDate"
          id="startDate-ej"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.startDate" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="endDate">End Date</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.endDate"
          id="endDate-ej"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.endDate" style="color: red;">This field is required.</span>
      </div>
    </div>

		<div class="btn-add-cancel d-flex">
      <button @click="cancelJadwal()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addKategori()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editJadwal()" class="btn btn-success">Update</button>
    </div>

  </div>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
  name: "EditJadwal",
  components: {},
  data() {
    return {
      section: "",
      id: "",
      dataExecutor: {
        kelas: "",
				jadwal: "",
				slot: "",
        startDate: "",
        endDate: "",
        linkKelas: ""
			},
			cannotNull: {
        kelas: true,
				jadwal: true,
				slot: true,
        startDate: true,
        endDate: true,
        linkKelas: true
      }
    };
	},
	props: ['data'],
  methods: {
    init() {
      let params = this.$props.data;
      this.section = params.section;
      // let a = this.dataExecutor;
      if (params.section == "edit") {
        this.id = params.id;
        this.getDetailJadwal();
			}
			this.fixStyleInputVCalendar();
		},
		fixStyleInputVCalendar() {
      $(".vc-text-base").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%"
      });

      $(`#startDate-ej > input`).attr("placeholder", "Start Date");
      $(`#endDate-ej > input`).attr("placeholder", "End Date");
		},
		getDetailJadwal() {
			this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listJadwalAdmin() + `/${this.id}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          a.kelas = result.kelas;
          a.jadwal = result.jadwal;
          a.slot = result.slot;
          a.linkKelas = result.linkKelas == null ? "-" : result.linkKelas;
          a.startDate = new Date(result.startDate);
          a.endDate = new Date(result.endDate);

          console.log("dataExecutor ", a);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
		},
		closeModal() {
      this.$modal.hide("edit-jadwal");
		},
		cancelJadwal() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin Edit Jadwal ?",
        buttons: [
					{
            title: "No",
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
		convertPayloadEdit() {
			let a = this.dataExecutor;
			let body = {
				name: a.jadwal,
				slot: a.slot,
				startDate: a.startDate,
				endDate: a.endDate,
				linkKelas: a.linkKelas
			};
			return body;
		},
		editJadwal() {
			let body = this.convertPayloadEdit();
			console.log('body ', body);
			this.putJadwal(body);
    },
    putJadwal(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;

      axios
        .put(vm.$root.listJadwalAdmin() + `/${this.id}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostJadwal().success(
            `Kategori ${body.title} Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostJadwal().failed(`${resErr.data.message}`);
        });
		},
		afterPostJadwal() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.edit-jadwal-kelas {
	padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
</style>