<script>
import * as moment from 'moment';

export default {
  name: "FinishPayment",
  components: {},
  data() {
    return {
      bank: this.$route.params.bank,
      dataCP: window.$cookies.get('successCheckoutData'),
      dataBank: window.$cookies.get('chooseBankUser'),
      dateExpired: ''
    };
  },
  mounted() {
    this.dateExpired = moment( this.dataCP.expiredAt ).format('DD - MMM - YYYY HH:mm:ss')
    this.$root.goToTop();
  },
  watch: {
    $route(to, from) {
      this.bank = to.params.bank;
      from;
    }
  }
};
</script>

