<template>
  <div class="konfirmasi-pembayaran-user">
    <HeaderTop />

    <div class="content-kpu container">
      <h3 class="title-kpu">Konfirmasi Pembayaran</h3>

      <div class="content-form-kpu">
        <div class="form-input-kpu">
          <label for="invoice">Invoice</label>
          <input
            type="text"
            class="form-control"
            name="invoice"
            placeholder="Invoice"
            disabled
            v-model="dataBody.invoice"
          />
        </div>
        <div class="form-input-kpu">
          <label for="total-tagihan">Total Tagihan</label>
          <input
            onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
            type="text"
            class="form-control"
            name="total-tagihan"
            placeholder="Total Tagihan"
            v-model="dataBody.totalTagihan"
          />
        </div>
        <div class="form-input-kpu">
          <label for="bank-kelas">Rekening Tujuan</label>
          <input
            type="text"
            class="form-control"
            name="bank-kelas"
            placeholder="Rekening Tujuan"
            v-model="chooseBank"
            @focus="rekeningTujuan().show()"
            @focusout="rekeningTujuan().show()"
          />
          <ul id="rekeningTujuan" class="list-data-field">
            <li
              v-for="(bank, index) in dataListBank"
              v-bind:key="index"
              v-bind:class="{'active': bank.rekNumber == dataBody.rekTujuan}"
              @click="rekeningTujuan().choose(bank)"
            >
              <span>{{ bank.bankName }}</span> -
              <span>{{ bank.accountName }}</span> -
              <span>{{ bank.code }}</span> -
              <span>{{ bank.rekNumber }}</span>
            </li>
          </ul>
        </div>
        <div class="form-input-kpu">
          <label for="tanggal-transfer">Tanggal Transfer</label>
          <!-- <input
            type="text"
            class="form-control"
            name="tanggal-transfer"
            placeholder="Tanggal Transfer"
            v-model="dataBody.tglTransfer"
          />-->
          <vc-date-picker
            :value="null"
            color="red"
            is-dark
            :locale="{
							id: 'id',
							data: ['YYYY-MM-DD']
						}"
            :data="'YYYY-MM-DD'"
            v-model="dataBody.tglTransfer"
            id="tglTransfer"
            class="field-inp-date"
            :placeholder="'Tanggal Transfer'"
          />
        </div>
        <div class="form-input-kpu">
          <label for="jumlah-transfer">Jumlah Transfer</label>
          <input
            onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
            type="text"
            class="form-control"
            name="jumlah-transfer"
            placeholder="Jumlah Transfer"
            v-model="dataBody.jmlTransfer"
          />
        </div>
        <div class="form-input-kpu">
          <label for="rekening-pengirim">Rekening Pengirim</label>
          <input
            onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
            type="text"
            class="form-control"
            name="rekening-pengirim"
            placeholder="Rekening Pengirim"
            v-model="dataBody.rekPengirim"
          />
        </div>
        <div class="form-input-kpu">
          <label for="nama-pengirim">Nama Pengirim</label>
          <input
            type="text"
            class="form-control"
            name="nama-pengirim"
            placeholder="Nama Pengirim"
            v-model="dataBody.namaPengirim"
          />
        </div>
        <div class="form-input-kpu">
          <label for="buktiTransfer">Bukti Transfer</label>
          <input
            type="file"
            class="form-control"
            name="buktiTransfer"
            accept="image/png, image/jpeg"
            id="buktiTransfer"
            placeholder="Banner"
            v-on:change="handleFile($event)"
          />
          <div class="file-upload-div d-flex" v-on:click="chooseFile('buktiTransfer')">
            <span>{{ buktiTransferText }}</span>
            <span class="btn btn-up">Unggah Dokumen</span>
          </div>
          <span>Note : 1MB Max Size file dokumen.</span>
        </div>
        <div class="show-image">
          <img id="showImage" src alt />
        </div>
        <div class="btn-kp">
          <button class="btn btn-blue-mbx" @click="submitKP()">Submit</button>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "KonfirmasiPembayaran",
  components: {
    HeaderTop,
    Footer
  },
  data() {
    return {
      buktiTransferText: "Unggah Dokumen",
      dataBody: {
        invoice: "",
        totalTagihan: "",
        rekTujuan: "",
        tglTransfer: "",
        jmlTransfer: "",
        rekPengirim: "",
        namaPengirim: "",
        buktiTransfer: new FormData()
      },
      dataForPost: new FormData(),
      dataListBank: [],
      chooseBank: "",
      uuidtrx: this.$route.params.uuidtrx,
      uuidusr: this.$route.params.uuidusr,
      invoicetrx: this.$route.params.invoice,
    };
  },
  methods: {
    init() {
      this.$root.loadingMentorbox().show();
      setTimeout(() => {
        this.fixStyleInputVCalendar();
        this.$root.loadingMentorbox().hide();
      }, 3000);
      this.$root.goToTop();

      console.log('this.invoicetrx ', this.invoicetrx)

      if ( this.invoicetrx !== undefined ) {
        // for handle if user logged
        this.handleData();
        this.getListBank();
      } else {
        // for handle if user not logged
        this.getDataKP();
      }
    },
    getDataKP() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.getDataKPPage() + `/${this.uuidtrx}/${this.uuidusr}` )
        .then(res => {
          let data = res.data;
          let result = data.result;
          let a = this.dataBody;
          a.invoice = result.invoice;
          a.totalTagihan = result.total;
          a.rekTujuan = result.rekNumber;

          console.log("data list bank ", result);
          this.getListBank();
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getListBank() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.listBankPage())
        .then(res => {
          let data = res.data;
          let result = data.result;
          let rekTujuan = this.dataBody.rekTujuan;
          this.dataListBank = result.map(e => {
            if ( e.rekNumber == rekTujuan ) {
              this.chooseBank = `${e.bankName} - ${e.accountName} - ${e.code} - ${e.rekNumber}`
            }

            let obj = {
              accountName: e.accountName,
              bankLogo: e.bankLogo,
              bankName: e.bankName,
              code: e.code,
              rekNumber: e.rekNumber,
              unic_id: this.$root.makeid(7)
            };
            return obj;
          });
          console.log("data list bank ", this.dataListBank);
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    handleData() {
      let data = window.$cookies.get("konfirmasiPembayaran");
      this.dataBody.totalTagihan = data.total;
      this.dataBody.invoice = data.invoice;
    },
    fixStyleInputVCalendar() {
      $(".vc-text-base").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%"
      });

      $(`#tglTransfer > input`).attr("placeholder", "Tanggal Transfer");
    },
    chooseFile(id) {
      $("#" + id).click();
    },
    rekeningTujuan() {
      return {
        show() {
          $("#rekeningTujuan").slideToggle();
        },
        choose: data => {
          this.chooseBank = `${data.bankName} - ${data.accountName} - ${data.code} - ${data.rekNumber}`;
          this.dataBody.rekTujuan = data.rekNumber;
        }
      };
    },
    handleFile(evt) {
      let target = evt.target;
      let files = target.files;
      let sizeFiles = target.files[0].size;
      if ( sizeFiles > 1000000 ) {
        this.$root.notification("warning", "Warning", "Max Size file is 1MB.")
      } else {
        let nameFile = target.files[0].name;
        this.buktiTransferText = nameFile;
        this.dataForPost.append("buktiTransfer", files[0]);
        this.dataBody.buktiTransfer.append("buktiTransfer", files[0]);
        this.renderImage(target.files[0]);
      }

      console.log("evt ", evt);
    },
    renderImage(files) {
      var reader = new FileReader();
      reader.onload = function(e) {
        $("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    validateKP() {
      let a = this.dataBody;
      if (
        a.rekTujuan == "" ||
        a.totalTagihan == "" ||
        a.tglTransfer == "" ||
        a.jmlTransfer == "" ||
        a.rekPengirim == "" ||
        a.namaPengirim == "" ||
        this.buktiTransferText == "Unggah Dokumen"
      ) {
        this.$root.notification("warning", "Warning", "All field cannot be empty!");
        return false;
      }

      if ( 
        a.rekTujuan !== "" &&
        a.totalTagihan !== "" &&
        a.tglTransfer !== "" &&
        a.jmlTransfer !== "" &&
        a.rekPengirim !== "" &&
        a.namaPengirim !== "" &&
        this.buktiTransferText !== "Unggah Dokumen"
      ) {
        return true;
      }
    },
    submitKP() {
      let validateKP = this.validateKP();
      if ( validateKP ) {
        let dataBody = this.dataBody;
        this.dataForPost.set("invoice", dataBody.invoice);
        this.dataForPost.set("totalTagihan", dataBody.totalTagihan);
        this.dataForPost.set("rekTujuan", dataBody.rekTujuan);
        this.dataForPost.set("tglTransfer", dataBody.tglTransfer);
        this.dataForPost.set("jmlTransfer", dataBody.jmlTransfer);
        this.dataForPost.set("rekPengirim", dataBody.rekPengirim);
        this.dataForPost.set("namaPengirim", dataBody.namaPengirim);
        // dataBody.buktiTransfer = this.dataForPost;
  
        console.log("data body ", dataBody, this.dataForPost);
        this.postKP();
      }
    },
    postKP() {
      this.$root.loadingData().show();
      // let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.konfirmasiPembayaranPage(), this.dataForPost, {
          headers: {
            // Authorization: AuthSth,
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostKP().success(`Submit Konfirmasi Pembayaran Berhasil.`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostKP().failed(`${resErr.data.message}`);
        });
    },
    afterPostKP() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  if ( vm.invoicetrx !== undefined ) {
                    vm.$root.backPage();
                  } else {
                    vm.$router.push('/');
                  }
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.konfirmasi-pembayaran-user {
  .content-kpu {
    font-family: "Montserrat";
    padding: 15px 0 30px 0;
    > .title-kpu {
      font-style: normal;
      font-weight: 600;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    > .content-form-kpu {
      > .form-input-kpu {
        text-align: left;
        margin: 5px 0;
        > label {
          font-style: normal;
          font-weight: 500;
          font-size: 25px;
          line-height: 44px;
          color: #000000;
          margin: 0%;
        }
        > input {
          border-top: none;
          border-right: none;
          border-left: none;
          border-bottom: 1px solid #000000;
          border-radius: 0%;
          &[type="file"] {
            display: none;
          }
        }
        > .file-upload-div {
          justify-content: space-between;
          border-bottom: 1px solid #000000;
          padding: 6px 12px;
          align-items: center;
          cursor: pointer;
          > .btn-up {
            background: #003461;
            border-radius: 10px;
            color: #ffffff;
          }
        }
        > .date-form {
          justify-content: space-between;
          > input {
            width: 49%;
            border-top: none;
            border-right: none;
            border-left: none;
            border-bottom: 1px solid #000000;
            border-radius: 0%;
          }
        }
      }
      > .btn-kp {
        margin: 30px 0 10px 0;
        .btn-blue-mbx {
          background: #003461;
          border-radius: 10px;
          padding: 5px 7%;
          color: #ffffff;
          border: 1px solid #003461;
          &:hover {
            background: #ffffff;
            color: #003461;
          }
        }
      }
    }
  }
}
#rekeningTujuan {
  display: none;
}
.list-data-field {
  position: absolute;
  transform: translate(120px, 0px);
  background: #ffffff;
  padding: 0%;
  width: 50%;
  left: 0;
  margin: 3px 0 0 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  border-radius: 0 0 10px 10px;
  max-height: 200px;
  overflow-y: auto;
  > li {
    color: #000000;
    list-style: none;
    padding: 5px 10px;
    cursor: pointer;
    &:nth-child(even) {
      background: #cccccc;
    }
    &:hover {
      background: #999898;
    }
    &.active {
      background: #003461;
      color: #ffffff;
    }
  }
}
.show-image {
	display: flex;
  justify-content: center;
  margin: 15px 0;
  widows: 100%;
  > img {
    width: 80%;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .tab-detail-kelas {
    .content-tdk {
      > .title-tdk {
        font-size: 25px;
      }
      > .form-input-kpu {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .tab-detail-kelas {
    .content-tdk {
      > .title-tdk {
        font-size: 25px;
      }
      > .form-input-kpu {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>