<template>
  <div class="content-sign-in">
    <div class="row">
      <div class="col-sm-6">
        <div class="img-login-left">
          <!-- <img src="../../assets/img/side-mentor.png" alt /> -->
          <div class="msg-side-mentor">
            <h2>Welcome Back MentorBox!</h2>
          </div>
        </div>
      </div>
      <div class="col-sm-6">
        <div class="container">
          <div class="parent-tab">
            <ul class="tabs">
              <li
                v-for="tab in dataTabs"
                v-bind:key="tab.id"
                v-bind:class="{active: tab.sub_id == section, [ section ] : tab.show }"
                v-on:click="changeTab($event, tab.sub_id)"
              >
                <a>{{ tab.title_tab }}</a>
              </li>
              <hr />
            </ul>
          </div>

          <div class="form-content-sign">
            <LoginMentorBox />
            <!-- <div class="not-have-account">
              <p>Belum memiliki Akun MentorBox?</p>
              <p>
                Silahkan
                <span v-on:click="changeTab($event, 'register')">Daftar</span>
              </p>
            </div> -->
          </div>
          <!-- <div class="form-content-sign" v-if="section == 'register'">
            <RegisterMentorBox />
            <div class="not-have-account">
              <p>Sudah memiliki Akun MentorBox?</p>
              <p>
                Silahkan
                <span v-on:click="changeTab($event, 'login')">Masuk</span>
              </p>
            </div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoginMentorBox from "./LoginMentorBox.vue";
// import RegisterMentorBox from "./RegisterMentorBox.vue";

export default {
  name: "ContentSignInMentorBox",
  components: {
    LoginMentorBox,
    // RegisterMentorBox
  },
  data() {
    return {
      section: 'login',
      dataTabs: [
        {
          id: 1,
          sub_id: "login",
          title_tab: "Masuk",
          show: true
        },
        // {
        //   id: 2,
        //   sub_id: "register",
        //   title_tab: "Daftar",
        //   show: false
        // }
      ]
    };
  },
  methods: {
    changeTab(evt, id) {
      let dataTab = this.dataTabs;
      for (let i = 0; i < dataTab.length; i++) {
        if (dataTab[i].sub_id == id) {
          dataTab[i].show = true;
          this.section = id;
        } else {
          dataTab[i].show = false;
          this.section = id;
        }
      }
    }
  },
  mounted() {
    console.log("section ", this.section);
  }
};
</script>

<style lang="scss" scoped>
