  font-family: "Montserrat";
  overflow: hidden;
  .img-login-left {
    overflow: hidden;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-image: url('../../assets/img/side-mentor.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    > .msg-side-mentor {
      width: 70%;
      margin: auto;
      text-align: left;
      > h2 {
        color: #ffffff;
        font-size: 50px;
        font-weight: bold;
      }
      > p {
        color: #ffffff;

      }
    }
  }
  .parent-tab {
    display: flex;
    width: 100%;
    ul.tabs {
      padding: 0%;
      width: 80%;
      margin: 10px auto;
      li {
        display: inline;
        text-align: center;
        cursor: pointer;
        font-style: normal;
        font-weight: normal;
        font-size: 48px;
        line-height: 59px;
        color: #003461;
        opacity: 0.56;

        a {
          display: inline-block;
          width: 100%;
          padding: 0.75rem 0;
          margin: 0;
          text-decoration: none;
        }
        &.register ~ hr {
          margin-left: 50%;
        }
        &.active {
          opacity: 1;
        }
      }
      hr {
        height: 0.25rem;
        width: 100%;
        margin: 0;
        background: #c4c4c4;
        border: none;
        transition: 0.5s ease-in-out;
      }
    }
  }
  .form-content-sign {
    width: 80%;
    justify-content: center;
    margin: 0 auto;
    padding: 0 0 30px 0;
    > .not-have-account {
      display: flex;
      justify-content: space-between;
      margin: 30px 0 0 0;
      > p {
        > span {
          cursor: pointer;
          text-decoration: underline;
          color: #000;
          &:hover {
            color: #0d1df6;
          }
        }
      }
    }
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .content-sign-in {
    .img-login-left {
      > .msg-side-mentor {
        > h2 {
          font-size: 40px;
        }
      }
    }
		.parent-tab {
			ul.tabs {
				width: 95%;
				li {
					font-size: 42px;
				}
			}
		}
		.form-content-sign {
			width: 95%;
		}
	}
}
@media (min-width: 577px) and (max-width: 767px) {
