<template>
  <div class="dashboard-siswa">
    <HeaderTop />

    <ContentDashboardSiswa :data-siswa="dataDashboard" />

    <Footer />
  </div>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

import ContentDashboardSiswa from "@/components/DashboardSiswa/ContentDashboardSiswa.vue";

export default {
  name: "DashboardSiswa",
  components: {
    HeaderTop,
    Footer,
    ContentDashboardSiswa,
  },
  data() {
    return {
      dataDashboard: {
        username: this.$route.params.username,
        section: this.$route.params.section,
        tab: this.$route.params.tab
      }
    };
  },
  methods: {
    init() {
      setTimeout(() => {
        this.$root.loadingMentorbox().hide();
      }, 2000);
    }
  },
  mounted() {
		this.init();
    this.$root.goToTop();
  },
  watch: {
    $route(to, from) {
      console.log('to ', to);
      console.log('from ', from);

      let data = this.dataDashboard;
      data.username = to.params.username;
      data.section = to.params.section;
      data.tab = to.params.tab;

      this.init();
    }
  }
};
</script>

