          </router-link>
          <div class="subscribe-footer">
            <input
              type="email"
              id="inp-subscribe"
              placeholder="Email"
              class="input-form inp-email"
              v-model="emailSubscribe"
              @keyup.enter="subscribeMbx()"
            />
            <div class="btn-subscribe">
              <button class="btn" @click="subscribeMbx()">Subscribe</button>
            </div>
          </div>
        </div>
        <div class="menu-url">
          <div class="left">
            <div class="url-web-mentorboxid">
              <h1 class="title-footer">{{ dataUrl[0].title }}</h1>
              <div class="sub-title-footer">
                <div class="menus" v-for="menu in dataUrl[0].menus " v-bind:key="menu.id">
                  <router-link
                    v-on:click.native="goToPage(menu.slug)"
                    v-bind:to="menu.url"
                  >{{ menu.sub_title }}</router-link>
                </div>
              </div>
            </div>
            <div class="url-web-mentorboxid">
              <h1 class="title-footer">{{ dataUrl[1].title }}</h1>
              <div class="sub-title-footer">
                <div class="menus" v-for="menu in dataUrl[1].menus " v-bind:key="menu.id">
                  <router-link v-bind:to="menu.url">{{ menu.sub_title }}</router-link>
                </div>
              </div>
