                    href="#"
                    class
                  >
                    GENG WORK <br />
                    Jl. Tamalanrea Raya, <br />
                    Bumi Tamalanrea Permai (BTP) Blok C No. 15-16, Makassar, Sulawesi Selatan 90245
                  </a>
