<script>
import axios from "axios";

export default {
  name: "Footer",
  data() {
    return {
      emailSubscribe: "",
      dataUrl: [
        {
          id: 1,
          title: "Mentorbox.id",
          menus: [
            {
              id: 1,
              sub_title: "Tentang Kami",
              url: "/tentang-kami",
              slug: "tentang-kami"
            },
            {
              id: 2,
              sub_title: "Mentoring",
              url: "/mentoring",
              slug: "mentoring"
            },
            {
              id: 3,
              sub_title: "FAQ",
              url: "/faq",
              slug: ""
            },
            {
              id: 4,
              sub_title: "Tips & Artikel",
              url: "#",
              slug: ""
            }
          ]
        },
        {
          id: 2,
          title: "Kebijakan",
          menus: [
            {
              id: 1,
              sub_title: "Kebijakan Privasi",
              url: "/kebijakan-mentorbox/kebijakan-privasi",
              slug: ""
            },
            {
              id: 2,
              sub_title: "Syarat & Ketentuan",
              url: "/kebijakan-mentorbox/kebijakan-sk",
              slug: ""
            },
            {
              id: 3,
              sub_title: "Kebijakan Cookies",
              url: "/kebijakan-mentorbox/kebijakan-cookies",
              slug: ""
            }
          ]
        },
        {
          id: 3,
          title: "Kontak Kami",
          menus: [
            {
              id: 1,
              sub_title:
                "GENG WORK \n Jl. Tamalanrea Raya, Bumi Tamalanrea Permai (BTP) Blok C No. 15-16, Makassar, Sulawesi Selatan 90245",
              url: "#",
              slug: ""
            },
            {
              id: 2,
              sub_title: "+62 817-5113-000",
              url: "#",
              slug: ""
            },
            {
              id: 3,
              sub_title: "support@mentorbox.id",
              url: "#",
              slug: ""
            }
          ]
        },
        {
          id: 4,
          title: "Ikuti Kami",
          menus: [
            {
              sub_title: "Intagram",
              url: "https://www.instagram.com/mentorbox.id/",
              slug: "",
              faicon: "fa fa-instagram"
            },
            {
              sub_title: "Facebook",
              url: "https://web.facebook.com/mentorbox.id/",
              slug: "",
              faicon: "fa fa-facebook-f"
            },
            {
              sub_title: "Twitter",
              url: "#",
              slug: "",
              faicon: "fa fa-twitter"
            },
            {
              sub_title: "Youtube",
              url:
                "https://www.youtube.com/channel/UChGhHyrZ0FYT-q-MG47vYDw/featured?view_as=subscriber",
              slug: "",
              faicon: "fa fa-youtube"
            }
          ]
        }
      ]
    };
  },
  components: {},
  methods: {
    goToPage(id) {
      this.$store.state.headerTop.activeMenu = id;
    },
    subscribeMbx() {
      let email = this.emailSubscribe;
      if (email == "") {
        this.$root.notification(
          "warning",
          "Warning",
          "Field Email Cannot be empty!."
        );
      } else {
        let body = {
          email: email
        };
        this.postSubscribe(body);
      }
    },
    postSubscribe(body) {
      this.$root.loadingData().show();
      // let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.subscribePage(), body)
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterpostSubscribe().success(`Subscribe Berhasil.`);
        })
        .catch(err => {
          let resErr = err.response;

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterpostSubscribe().failed(`${resErr.data.message}`);
        });
    },
    afterpostSubscribe() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$root.goToTop();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    }
  }
};
</script>

