              </div>
              <div class="message-ta">
                <div class="title-author">
                  <img 
                    class="img-author" 
                    v-bind:src="$root.getPhotoUser() + `/${data.img_author}`" 
                    v-bind:alt="data.author" 
                  />
                  <p class="by-author">
                    <span>Oleh</span>
                    {{data.author}}
                  </p>
                </div>
                <h1 
                  class="title-article" 
                  v-bind:title="data.title"
                >{{ data.title.slice(0, 30) }}...</h1>
                <div class="row date-comment-article">
                  <div class="col-sm-6 left-date-comment">
                    <img src="/icon/calendar.svg" />
                    <p class="date-article">{{ data.date_article }}</p>
                  </div>
                  <div class="col-sm-6 right-date-comment">
                    <img src="/icon/comment.svg" />
                    <p class="comment-article">{{ data.amount_comment }} Comment</p>
                  </div>
                </div>
                <div class="description-article">
                  <p>{{ data.description }}...</p>
                </div>
                <router-link v-bind:to="data.url" class="btn-tips-article">
                  <button class="btn">Selengkapnya</button>
                </router-link>
              </div>
            </div>
