<script>
import axios from 'axios';
import * as moment from 'moment';

export default {
  name: "TipsArticleMenu",
  components: {},
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 3,
      },
      tipsArticleData: [
        {
          id: 1,
          img_article: "/img/tips-article-1.png",
          author: "Andri Darmawan",
          img_author: "/img/foto.jpg",
          title: "Heboh Tiktok kini menghiasi Kurikulum 2020",
          date_article: "25 April 2020",
          amount_comment: "0",
          description:
