<template>
  <div class="content-list-member">
    <h3 class="title-lk">List Member</h3>
    <div 
      class="top-table-mbx"
      v-if="dataTable.list.length !== 0"
    >
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
      </div>
    </div>
    <div class="content-lm">
      <div v-if="dataTable.list.length == 0">
        <h5>Data List Member Kosong.</h5>
      </div>
      <div v-else class="list-content-lm">
        <ol>
          <li v-for="(a, idx) in dataTable.list" v-bind:key="idx">
            <div class="ctn-list">
              <p class="number-list">
                <span>{{ idx + 1 }}.</span>
              </p>
              <div class="first" @click="showLM(idx)">
                <p>{{ a.invoice == null ? "-" : a.invoice }}</p>
                <p>{{ a.tanggal  == null ? "-" : a.tanggal }}</p>
                <p>{{ a.member == null ? "-" : a.member }}</p>
                <p>{{ a.status == null ? "-" : a.status }}</p>
                <p>
                  <i v-bind:class="'fa fa-plus icon-dd-lm'+idx"></i>
                </p>
              </div>
            </div>
            <div v-bind:class="'last detail-data-lm'+idx">
              <div class="detail-data-lm">
                <div class="left">
                  <p>
                    Invoice :
                    <span>{{a.invoice}}</span>
                  </p>
                  <p>
                    Tanggal :
                    <span>{{a.tanggal}}</span>
                  </p>
                  <p>
                    Member :
                    <span>{{a.member}}</span>
                  </p>
                  <p>
                    Follow Up :
                    <span
                      class="btn-follow-up"
                      @click="followUpMember(a.invoice)"
                    >
                      <i class="fa fa-external-link"></i>
                    </span>
                  </p>
                </div>
                <div class="right">
                  <p>
                    Total :
                    <span>Rp.{{ a.total }}</span>
                  </p>
                  <p>
                    Status :
                    <span>{{a.status}}</span>
                  </p>
                  <p class="d-flex">
                    Produk :
                    <span>
                      <ul class="list-produk-lm">
                        <li v-for="(p, idx) in a.produk" v-bind:key="idx">
                          <p>Harga : <span>{{ p.harga }}</span></p>
                          <p>Kelas : <span>{{ p.kelas }}</span></p>
                        </li>
                      </ul>
                    </span>
                  </p>
                </div>
              </div>
            </div>
          </li>
        </ol>
      </div>
    </div>

    <modal name="follow-up-member" :width="'85%'" :height="'70%'" :clickToClose="false">
      <FollowUpMember :data="invoiceMember" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from 'moment';

import FollowUpMember from './FollowUpMember';

export default {
  name: "ListMember",
  components: {
    FollowUpMember,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
      },
      invoiceMember: '',
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Filter: Pilih kelas
      // Table:
      //   NamaKelas,
      //   JadwalKelas,
      //   NamaMember,
      //   EmailMember,
      //   PhoneMember,
      //   TglBergabung

      // email: "naz.cules@gmail.com"
      // id: 1
      // nama: "Nasrullah"
      // phone: "6282337027239"
      // produk: "Belajar Content Writing"
      // tanggal: "2020-06-06 11:10:17"

      this.getListMember();
    },
    getListMember() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&roles=${a.roles}&search=${a.search}`;
      // tambah params query uuid dari kelas belum di tambahkan
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.affiliateUser(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            let obj = {
              invoice: e.invoice,
              member: e.member,
              produk: e.produk,
              status: e.status,
              tanggal: moment( e.tanggal ).format('DD - MMM - YYYY HH:mm:ss'),
              total: e.total,
            };
            return obj;
          });

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    showLM(idx) {
      $(".detail-data-lm" + idx).slideToggle();
      $(".icon-dd-lm" + idx).toggleClass("fa-plus fa-minus");
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    followUpMember(uuid) {
			this.invoiceMember = uuid;
			this.$modal.show("follow-up-member");
		},
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-member {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: center;
    margin: 10px 0;
  }
  > .content-lm {
    > .list-content-lm {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      text-align: left;
      color: #ffffff;
      > ol {
        padding: 0%;
        margin: 0%;
        > li {
          display: flex;
          align-items: center;
          flex-direction: column;
          .ctn-list {
            padding: 5px 10px;
            display: flex;
            justify-content: space-between;
            width: 100%;
            cursor: pointer;
            background: #004c94;
            &:hover {
              box-shadow: 0px 4px 8px rgba(190, 188, 188, 0.56);
              border: 1px solid #ffffff;
            }
            > .number-list {
              margin: 0%;
              display: flex;
              justify-content: flex-start;
              width: 3%;
            }
            > .first {
              display: flex;
              justify-content: space-between;
              width: 100%;
              > p {
                margin: 0%;
                width: 20%;
                &:nth-child(2) {
                  width: 25%;
                }
                &:last-child {
                  width: 4%;
                  text-align: center;
                }
              }
            }
          }
          > .last {
            display: none;
            width: 100%;
            > .detail-data-lm {
              display: flex;
              padding: 5px 30px;
              background: #0e7fe9;
              color: #ffffff;
              .left,
              .right {
                width: 50%;
              }
              p {
                margin: 0%;
              }
              .list-produk-lm {
                > li {
                  border-bottom: 1px solid #ffffff;
                }
              }
            }
          }
        }
      }
    }
    .btn-follow-up {
      > i {
        font-size: 20px;
        margin: 3px 10px 10px 10px;
        transform: translate(-5px, 5px);
        cursor: pointer;
      }
    }
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
  .detail-list-mbr {
    text-align: left;
    > p {
      margin: 0%;
      > span {
        font-weight: 600;
      }
    }
  }
}
</style>