<template>
  <div class="content-follow-up-member modal-mbx">
		<div class="top-title-modal">
      <h3 class="title-lk">Detail Follow Up {{ invoiceMember }}</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

		<div class="content-fum">
			<div class="d-flex justify-content-sm-end">
				<h5>Jumlah : {{ count }}</h5>
			</div>
			<div class="content-table-mbx">
				<div class="table-list-mbx">
					<table id="tableLK" class="table-mbx">
						<thead>
							<tr>
								<th>
									<div class="th-tbl-mbx">
										Title
									</div>
								</th>
								<th>
									<div class="th-tbl-mbx">
										Message
									</div>
								</th>
								<th>
									<div class="th-tbl-mbx">
										Status
									</div>
								</th>
								<th>
									<div class="th-tbl-mbx">
										Created At
									</div>
								</th>
							</tr>
						</thead>
						<tbody>
							<tr 
								v-for="(list, index) in dataTable.list" 
								v-bind:key="index"
							>
								<td>{{ list.title }}</td>
								<td 
									style="width: 50%"
									class="text-left"
								>{{ list.message }}</td>
								<td>{{ list.status }}</td>
								<td>{{ list.createdAt }}</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
import axios from 'axios';
import * as moment from 'moment';

export default {
  name: "FollowUpMember",
  components: {},
  data() {
    return {
			invoiceMember: '',
			count: 0,
			dataTable: {
        list: []
      }
		};
  },
  props: ["data"],
  methods: {
    init() {
			this.invoiceMember = this.$props.data;
			console.log('invoiceMember ', this.invoiceMember);

			// "title": "Order Detail",
			// "message": "Halo kak *User*\nSilahkan menyelesaikan pembayaran untuk mentoring *Membuka Usaha Ghost Kitchen* dengan nomor invoice *INV2006196KXNP*.\n\nBiaya Mentoring: *Rp. 249000*\nPotongan Kode Unik : *Rp. 608*\n\n*Total Transfer : Rp. 248392*\n\nSilahkan melakukan transfer pembayaran segera ke nomor rekening\n\n*Bank BNI Syariah*\n*1117575889*\na.n *IIN KUSUMA WARDANI*",
			// "status": "sent",
			// "createdAt": "2020-06-19 14:51:51"
			this.getListFollowUp();
		},
		getListFollowUp() {
			this.$root.loadingData().show();
			let AuthSth = this.$root.authorization();

			axios.get(this.$root.listFollowUpAffiliate() + `/${this.invoiceMember}`, {
				headers: { Authorization: AuthSth }
			})
			.then((res) => {
				let data = res.data;
				let result = data.data;

				this.count = data.count;
				this.dataTable.list = [];
				this.dataTable.list  = result.map((e) => {
					let obj = {
						title: e.title,
						message: e.message,
						status: e.status,
						createdAt: moment( e.createdAt ).format('DD - MMM - YYYY HH:mm:ss'),
					};
					return obj;
				})

				console.log('res list follow up ', res);

				this.$root.loadingData().hide();
			})
			.catch((err) => {
				let resErr = err;

				console.log('err ', resErr.response);
				this.$root.loadingData().hide();
			})
		},
		closeModal() {
			this.$modal.hide('follow-up-member')
		}
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-follow-up-member {
	padding: 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
</style>