<template>
  <div class="content-list-user">
    <h3 class="title-lk">List Blog</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="init()"
          />
          <button 
            class="btn btn-primary"
            @click="init()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button @click="addBlog()" class="btn btn-primary btn-action-top-tbl-mbx">Add Blog</button>
      </div>
    </div>
    <div class="filter-table-mbx">
      <h5>Filter : </h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Status : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(status, index) in dataTable.filter.status"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="status" 
                  v-bind:id="'f-status-'+ status.value" 
                  class="radio-mbx"
                  v-bind:value="status.value"
                  v-bind:checked="dataBody.status == status.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-status-'+status.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().status(status.value)"
                >{{ status.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Banner
                  <span
                    @click="tableMbx().sort('list-banner')"
                    id="list-banner"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Judul
                  <span
                    @click="tableMbx().sort('list-title')"
                    id="list-title"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Deskripsi
                  <span
                    @click="tableMbx().sort('list-description')"
                    id="list-description"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Tag
                  <span
                    @click="tableMbx().sort('list-tag')"
                    id="list-tag"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  Status
                  <span
                    @click="tableMbx().sort('list-status')"
                    id="list-status"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
							<th>
                <div class="th-tbl-mbx">
                  View
                  <span
                    @click="tableMbx().sort('list-views')"
                    id="list-views"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 100px;width: 80px;">
                <div class="btn-action-tbl-mbx">
                  <!-- <button @click="detailBlog(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button> -->
                  <button @click="editDataBlog(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>
                {{ list.banner }}
                <div class="show-image">
                  <img
                    id="showImageBlog"
                    v-bind:src="$root.getPhotoUser() + `/${list.banner}`"
                    alt
                  />
                </div>
              </td>
              <td style="max-width: 250px; width: 230px;">{{ list.title }}</td>
              <td  
                style="max-width: 350px; width: 330px;"
                class="text-left"
              >{{ list.description }}</td>
              <td>
                <ul>
                  <li v-for="(tag, idx) in list.Tags" v-bind:key="idx">
                    {{ tag.name }}
                  </li>
                </ul>
              </td>
              <td>{{ list.status }}</td>
              <td>{{ list.views == null ? "-" : list.views }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Blog Kosong, Silahkan Tambah Blog.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-blog" :clickToClose="false" :width="'60%'" :height="'80%'">
      <ExecutorBlog @init="init()" :data="dataExecutor" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import ExecutorBlog from './ExecutorBlog';

export default {
  name: "ListBlog",
  components: {
    ExecutorBlog,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "",
        search: ""
      },
      dataExecutor: {
        section: "detail",
        uuid: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          banner: false,
          title: false,
          description: false,
          tag: false,
          views: false,
          status: false,
        },
        colSort: {
          banner: false,
          title: false,
          description: false,
          tag: false,
          views: false,
          status: false,
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Publish',
              value: 'Publish'
            },
            {
              text: 'Draft',
              value: 'Draft'
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
			
			// Banner, 
			// Judul, 
			// Deskripsi, 
			// Tag, 
			// Status, 
			// View, 
      // Action (Edit)
      
      // banner: "banner.png"
      // title: "Belajar Content Writing"
      // description: ""
      // Tags: Array(3)
      // author: Object
      // views: null

      // id: 3
      // slug: "belajar-content-writing-2"
      // uuid: "9db5b1c2-ae0c-4dc6-9da8-2ba2d6ee75c5"

      this.getListBlog();
    },
    getListBlog() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&search=${a.search}`;
      axios
        .get(this.$root.listBlogCreator() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    addBlog() {
      this.dataExecutor.section = "add";
      // this.$modal.show("executor-blog");

      this.$parent.dataExecutor.section = "add";
      this.$parent.showMainPage().parent(false, 'executor-blog');
    },
    editDataBlog(uuid) {
      // this.dataExecutor.section = "edit";
      // this.dataExecutor.uuid = uuid;
      // this.$modal.show("executor-blog");

      this.$parent.dataExecutor.uuid = uuid;
      this.$parent.dataExecutor.section = "edit";
      this.$parent.showMainPage().parent(false, 'executor-blog');
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-user {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 120%;
  }
}
.show-image {
  display: flex;
  justify-content: center;
  > img {
    width: 300px;
    height: 190px;
    border-radius: 10px;
  }
}
</style>