<template>
  <div class="executor-list-kategori modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Kategori</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Kategori</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-executor-lk">
      <div class="form-input-mbx">
        <label for="title">Title Kategori</label>
        <input
          type="text"
          class="form-control"
          name="title"
          placeholder="Title Kategori"
          v-model="dataExecutor.title"
        />
        <span v-if="!cannotNull.title" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="status">Status</label>
        <input
          type="text"
          class="form-control"
          name="status"
          placeholder="Status"
          v-model="dataExecutor.status"
          @focus="statusKategori().show()"
          @focusout="statusKategori().show()"
        />
        <ul id="statusKategori" class="list-status-kategori">
          <li
            v-for="(status, index) in dataExecutor.statusList"
            v-bind:key="index"
            @click="statusKategori().choose(status)"
          >{{ status }}</li>
        </ul>
        <span v-if="!cannotNull.status" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="sub-kategori">Sub Kategori</label>
        <p v-if="dataExecutor.child.length > 0" class="list-sub-kategori">
          <span v-for="(child, index) in dataExecutor.child" v-bind:key="index">
            {{ child }}
            <i @click="subKategori().remove(index)" class="fa fa-times"></i>
          </span>
        </p>
        <input
          type="text"
          class="form-control"
          name="sub-kategori"
          placeholder="Sub Kategori"
          v-model="dataExecutor.childText"
          @keyup.enter="subKategori().add()"
        />
        <span>Note : klik by enter untuk menambahkan sub kategori</span>
        <p v-if="!cannotNull.child" style="color: red;">This field is required.</p>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelKategori()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addKategori()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editKategori()" class="btn btn-success">Update</button>
    </div>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ExecutorListKategori",
  components: {},
  data() {
    return {
      section: "",
      dataExecutor: {
        id: "",
        title: "",
        status: "",
        statusList: ["Publish", "Reject", "Draft"],
        child: [],
        childText: ""
      },
      cannotNull: {
        title: true,
        status: true,
        child: true
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      let params = this.$props.data;
      this.section = params.section;
      let a = this.dataExecutor;
      if (params.section == "edit") {
        let dataParams = params.data;
        a.id = dataParams.id;
        a.title = dataParams.title;
        a.status = dataParams.status;

        let childParams = dataParams.child;
        for (let i = 0; i < childParams.length; i++) {
          a.child.push(childParams[i].title);
        }
      }
      console.log("this params ", params);
    },
    subKategori() {
      let a = this.dataExecutor;
      return {
        add() {
          let childText = a.childText;
          a.child.push(childText);
          a.childText = "";
        },
        remove(idx) {
          let child = a.child;
          child.splice(idx, 1);
        }
      };
    },
    statusKategori() {
      let a = this.dataExecutor;
      return {
        show() {
          $("#statusKategori").slideToggle();
        },
        choose(val) {
          a.status = val;
        }
      };
    },
    validatePostKategori() {
      let a = this.dataExecutor;
      let b = this.cannotNull;

      a.title == "" ? (b.title = false) : (b.title = true);
      a.status == "" ? (b.status = false) : (b.status = true);
      a.child.length == 0 ? (b.child = false) : (b.child = true);

      if (a.title !== "" && a.status !== "" && a.child.length !== 0) {
        return true;
      } else {
        return false;
      }
    },
    addKategori() {
      let a = this.dataExecutor;
      let validate = this.validatePostKategori();
      if (validate) {
        let body = {
          title: a.title,
          status: a.status,
          child: a.child
        };
        this.postKategori(body);
      }
    },
    postKategori(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.addKategoriAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostKategori().success(
            `Kategori ${body.title} Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostKategori().failed(`${resErr.data.message}`);
        });
    },
    afterPostKategori() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("executor-list-kategori");
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("executor-list-kategori");
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelKategori() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit kategori ?",
        buttons: [
					{
            title: "No",
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.$modal.hide("executor-list-kategori");
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    editKategori() {
      let a = this.dataExecutor;
      let validate = this.validatePostKategori();
      if (validate) {
        let body = {
          title: a.title,
          status: a.status,
          child: a.child
        };
        this.putKategori(a.id, body);
      }
    },
    putKategori(id, body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;

      axios
        .put(vm.$root.addKategoriAdmin() + `/${id}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostKategori().success(
            `Kategori ${body.title} Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostKategori().failed(`${resErr.data.message}`);
        });
    },
    closeModal() {
      this.$modal.hide("executor-list-kategori");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-list-kategori {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  > .title-lk {
    text-align: left;
    font-size: 20px;
    font-weight: 600;
  }
  .list-sub-kategori {
    margin: 0 0 5px 0;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    > span {
      background: #cccccc;
      padding: 5px;
      border-radius: 5px;
      margin: 3px;
      > i {
        color: #000000;
        cursor: pointer;
        &:hover {
          color: #ee0f0f;
        }
      }
    }
  }
  .list-status-kategori {
    // position: absolute;
    background: #ffffff;
    padding: 0%;
    display: none;
    width: 50%;
    left: 0;
    margin: 3px 0 0 0;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
    border-radius: 0 0 10px 10px;
    > li {
      color: #000000;
      list-style: none;
      padding: 5px 10px;
      cursor: pointer;
      &:nth-child(even) {
        background: #cccccc;
      }
      &:hover {
        background: #999898;
      }
    }
  }
  .btn-add-cancel {
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    bottom: 0;

    background: #ffffff;
    width: 100%;
    padding: 10px;
    > button {
      width: 49%;
    }
  }
}
</style>