<template>
  <section class="mentoring-page">
    <HeaderTop />

    <div class="content-give-rating container">
      <h3>Give Rating Kelas</h3>
      <div class="content-gr">
        <div class="left">
          <div class="banner-kelas">
            <img
              v-bind:src="$root.getPhotoUser() + `/${kelasData.banner}`"
              v-bind:alt="kelasData.kelas"
            />
          </div>
        </div>
        <div class="right">
          <div class="top-for-review">
            <h3 style="font-weight: 700;">{{ kelasData.kelas }}</h3>
            <p>Oleh <span style="font-weight: 700;">{{ kelasData.mentor }}</span></p>
          </div>
          <div class="give-review">
            <star-rating :rating="dataBody.rating" :star-size="30"></star-rating>
            <div class="form-input-mbx">
              <label for="ulasa">Ulasan</label>
              <textarea
                type="text"
                class="form-control"
                name="Ulasa"
                placeholder="Ulasan"
                v-model="dataBody.review"
              ></textarea>
            </div>
          </div>
          <div class="btn-kp">
            <button class="btn btn-blue-mbx" @click="submitRating()">Submit</button>
          </div>
        </div>
      </div>
    </div>

    <Footer />
  </section>
</template>

<script>
import axios from "axios";
import StarRating from "vue-star-rating";
import $ from 'jquery';

import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "Rating",
  components: {
    HeaderTop,
    Footer,
    StarRating
  },
  data() {
    return {
      dataBody: {
        courses: "",
        user: "",
        rating: 5,
        review: ""
      },
      kelasData: {
        banner: "",
        kelas: "",
        mentor: ""
      },
      uuidcrs: this.$route.params.uuidcrs,
      uuidusr: this.$route.params.uuidusr
    };
  },
  methods: {
    init() {
      this.getDataRatingKelas();
    },
    getDataRatingKelas() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.getDataRating() + `/${this.uuidcrs}/${this.uuidusr}`)
        .then(res => {
          let data = res.data;
          let result = data.result;
          let a = this.kelasData;
          a.banner = result.banner;
          a.kelas = result.kelas;
					a.mentor = result.mentor;
					let b = this.dataBody;
					b.courses = this.uuidcrs;
					b.user = this.uuidusr;

					console.log("data rating kelas ", result);
					this.hideRatingText();
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
		},
		hideRatingText() {
      setTimeout(() => {
        $(".vue-star-rating-rating-text").hide();
      }, 1500);
    },
		validateRating() {
			let body = this.dataBody;
			if ( body.review == "" ) {
				return false;
			} else {
				return true;
			}
		},
		submitRating() {
			if ( this.validateRating ) {
				this.postRating()
			}
		},
		postRating() {
      this.$root.loadingData().show();
      // let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.getDataRating(), this.dataBody)
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterpostRating().success(`Submit Rating Berhasil.`);
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterpostRating().failed(`${resErr.data.message}`);
        });
    },
    afterpostRating() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
									vm.$router.push('/');
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    }
  },
  mounted() {
    this.$root.goToTop();
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-give-rating {
  font-family: "Montserrat";
  padding: 15px;
  > h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
  }
  > .content-gr {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    > .left {
			width: 40%;
			> .banner-kelas {
				width: 100%;
				> img {
					width: 90%;
				}
			}
    }
    > .right {
      width: 59%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      > .give-review {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        > .form-input-mbx {
          width: 100%;
        }
      }
      > .btn-kp {
				margin: 30px 0 10px 0;
				width: 30%;
        .btn-blue-mbx {
					width: 100%;
          background: #003461;
          border-radius: 10px;
          padding: 5px 7%;
          color: #ffffff;
          border: 1px solid #003461;
          &:hover {
            background: #ffffff;
            color: #003461;
          }
        }
      }
    }
  }
}
</style>