<template>
  <div class="content-list-komisi">
    <h3 class="title-lk">List Komisi</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Komisi</button> -->
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th> -->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Tanggal
                  <span
                    @click="tableMbx().sort('list-tanggal')"
                    id="list-tanggal"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jumlah Transaksi
                  <span
                    @click="tableMbx().sort('list-jumlahTransaksi')"
                    id="list-jumlahTransaksi"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Total Komisi
                  <span
                    @click="tableMbx().sort('list-totalKomisi')"
                    id="list-totalKomisi"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Note
                  <span
                    @click="tableMbx().sort('list-note')"
                    id="list-note"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <!-- <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailKomisi(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button @click="editDataKomisi(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td> -->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.tanggal }}</td>
              <td>{{ list.jumlahTransaksi }}</td>
              <td>{{ list.totalKomisi }}</td>
              <td>{{ list.note }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Komisi Kosong.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

// import ExecutorListKomisi from "./ExecutorListKomisi";

export default {
  name: "ListKomisi",
  components: {
    // ExecutorListKomisi,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
      },
      editKomisi: {
        section: "detail",
        uuidDetailKomisi: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          tanggal: false,
          jumlahTransaksi: false,
          totalKomisi: false,
          note: false,
        },
        colSort: {
          tanggal: false,
          jumlahTransaksi: false,
          totalKomisi: false,
          note: false,
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {}
      }
    };
  },
  methods: {
    init() {
			// Tanggal, 
			// Jumlah Transaksi, 
			// Total Komisi, 
			// Note

      this.getListKomisi();
    },
    getListKomisi() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listKomisiOwner(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    addLK() {
      this.editKomisi.section = "add";
      this.$modal.show("add-komisi");
    },
    detailKomisi(uuid) {
      this.editKomisi.uuidDetailKomisi = uuid;
      this.$modal.show("detail-komisi");
    },
    editDataKomisi(uuid) {
      this.editKomisi.section = "edit";
      this.editKomisi.uuidDetailKomisi = uuid;
      this.$modal.show("add-komisi");
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-komisi {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>