<template>
  <div class="content-dashboard-creator">
    <div class="content-da">
      <div class="section-left">
        <h5>Dashboard Creator</h5>
        <div class="left-menu">
          <ul class="dashboard-left-menu">
            <template v-for="(menu, idxParent) in menuCreator">
              <li
                v-bind:key="idxParent"
                v-bind:class="{'menu-has-children': menu.has_children, [ menu.slug ]: menu.slug, 'parent-active': activeMenu.parent == menu.slug}"
              >
                <i
                  v-if="menu.has_children"
                  class="fa fa-chevron-down"
                  v-bind:id="menu.id_menus"
                  v-on:click="showChildrenMenu(menu.id_menus)"
                ></i>
                <!-- 'menu-item-active-child': activeMenu.parent == menu.slug -->
                <a
                  v-bind:class="{'menu-item-active': mainSection == menu.slug}"
                  v-on:click="showMainPage().parent(menu.has_children, menu.slug)"
                >{{ menu.title_menu }}</a>
                <ul v-if="menu.has_children" style="display: none;">
                  <li v-for="(a, idxChild) in menu.menus" v-bind:key="idxChild">
                    <a
                      v-bind:class="{'menu-active': activeMenu.child == a.slug}"
                      @click="showMainPage().child(a.has_children, a.slug, menu.slug)"
                    >{{ a.title_menu }}</a>
                  </li>
                </ul>
              </li>
            </template>
          </ul>
        </div>
        <!-- <p>LogOut</p> -->
      </div>
      <div class="section-right">

        <div class="container">
          <template v-if="mainSection == 'my-profile'">
            <ContentProfileUser />
          </template>
          <template v-if="mainSection == 'list-blog'">
            <ListBlog />
          </template>
          <template v-if="mainSection == 'list-tag'">
            <ListTag />
          </template>

          <template v-if="mainSection == 'executor-blog'">
            <AddBlog :data="dataExecutor" />
          </template>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

import ContentProfileUser from '../ProfileUser/ContentProfileUser';
import ListBlog from './ListBlog/ListBlog';

import ListTag from './ListTag/ListTag';

import AddBlog from './ListBlog/AddBlog';

export default {
  name: "ContentDashboardCreator",
  components: {
    ContentProfileUser,
    ListBlog,
    ListTag,
    AddBlog,
	},
	data() {
    return {
      mainSection: 'my-profile',
      activeMenu: {
        parent: '',
        child: ''
      },
      menuCreator: this.$root.menuCreator(),
      dataExecutor: {
        section : "",
        uuid: 0,
      }
    };
  },
  methods: {
    init() {
      this.getMenu();
      this.$root.goToTop();
    },
    getMenu() {
      console.log("menu creator ", this.menuCreator);
    },
    showChildrenMenu(id) {
      $("#" + id)
        .nextAll("ul")
        .eq(0)
        .slideToggle();
      $("#" + id).toggleClass("fa-chevron-up fa-chevron-down");
    },
    showMainPage() {
      let vm = this;
      return {
        parent(act, id) {
          if (!act) {
            vm.mainSection = id;
            vm.activeMenu.parent = '';
            vm.activeMenu.child = '';
          }
        },
        child(act, id, idParent) {
          if (!act) {
            vm.mainSection = id;
            vm.activeMenu.child = id;
            vm.activeMenu.parent = idParent;
          }
        }
      }
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-dashboard-creator  {
  font-family: "Montserrat";
  .content-da {
    display: flex;
    // flex-direction: column;
    // padding: 5px 20px;
    flex-direction: row;
    height: auto;
    min-height: 550px;
    > .section-left {
      max-height: 100%;
      min-height: 550px;
      max-width: 20%;
      width: 20%;
      color: #ffffff;
      background: #004c94;
      > h5 {
        font-size: 1.5em;
        padding: 10px 0;
        border-top: 2px solid #ffffff;
        border-bottom: 2px solid #ffffff;
      }
      > p {
        font-size: 1em;
        padding: 10px 0;
        border-top: 2px solid #ffffff;
        margin: 0.5em 0 0 0;
        position: absolute;
        left: 0;
        width: auto;
      }
      > .left-menu {
        overflow-y: auto;
        height: auto;
        min-height: 90%;
      }
    }
    > .section-right {
      max-width: 80%;
      width: 80%;
      overflow: hidden;
    }

    > .title-da {
      font-size: 1.7em;
      padding: 10px 0;
      margin: 10px 0 0 0;
      border-top: 2px solid #000000;
      border-bottom: 2px solid #000000;
    }
    .parent-tab {
      display: flex;
      width: 100%;
      ul.tabs {
        padding: 0%;
        width: 100%;
        margin: 10px auto;
        li {
          display: inline;
          text-align: center;
          cursor: pointer;
          font-style: normal;
          font-weight: normal;
          font-size: 30px;
          line-height: 25px;
          color: #003461;
          opacity: 0.56;

          a {
            display: inline-block;
            width: 25%;
            padding: 0.75rem 0;
            margin: 0;
            text-decoration: none;
          }
          &.user ~ hr {
            margin-left: 25%;
          }
          &.transaksi ~ hr {
            margin-left: 50%;
          }
          &.komisi ~ hr {
            margin-left: 75%;
          }
          &.active {
            opacity: 1;
          }
        }
        hr {
          height: 0.25rem;
          width: 25%;
          margin: 0;
          background: #c4c4c4;
          border: none;
          transition: 0.5s ease-in-out;
        }
      }
    }
    .content-tabs-h {
      .tabs-vertical {
        width: 15%;
        .tab-v {
          text-align: left;
          list-style: none;
          padding: 0%;
          li {
            color: #ffffff;
            margin: 10px 0;
            cursor: pointer;

            > span {
              background: #004c94;
              border: 1px solid #004c94;
              border-radius: 8px;
              padding: 5px 10px;
              display: inline-block;
              width: 100%;
            }

            &:hover {
              > span {
                background: #ffffff;
                color: #f2994a;
                border: 1px solid #f2994a;
              }
            }

            &[active="true"] {
              border-left: 3px solid #f2994a;
              > span {
                margin: 0 0 0 10px;
                background: #f2994a;
                border: 1px solid #f2994a;
                color: #ffffff;
              }
            }
          }
        }
      }
      .content-tabs-v {
        width: 85%;
        padding: 0 0 0 3%;
      }
      .tab-kelas {
        display: flex;
      }
    }
  }
}
@media (max-width: 767px) {
  .content-dashboard-creator  {
    .content-da {
      > .title-da {
        font-size: 1em;
      }
      .tabs-vertical {
        display: none;
      }
      .content-tabs-v {
        width: 100% !important;
        padding: 0% !important;
      }
    }
  }
}
</style>