<template>
  <div class="content-list-marketing-kit">
    <h3 class="title-mk">List MarketingKit</h3>
    <div 
      class="top-table-mbx"
      v-if="dataTable.list.length !== 0"
    >
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input
            type="text"
            class
            placeholder="Search here..."
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button class="btn btn-primary" @click="searchList()">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <h5>Total List = {{ dataTable.page.total }}</h5>
        <!-- <button @click="addMK()" class="btn btn-primary btn-action-top-tbl-mbx">Add MarketingKit</button> -->
      </div>
    </div>
    <div class="content-mk">
      <div v-if="dataTable.list.length == 0">
        <h5>Data List MarketingKit Kosong.</h5>
      </div>
      <div v-else class="list-content-mk">
        <ol>
          <li v-for="(a, idx) in dataTable.list" v-bind:key="idx">
            <div class="ctn-list">
              <p class="number-list">
                <span>{{ idx + 1 }}.</span>
              </p>
              <div class="first" @click="showMK(idx)">
                <p>{{ a.produk }}</p>
                <p>{{ a.link }}</p>
                <p>
                  <i v-bind:class="'fa fa-plus icon-dd-mk'+idx"></i>
                </p>
              </div>
            </div>
            <div v-bind:class="'last detail-data-mk'+idx">
              <div class="detail-data-mk">
                <div class="left">
                  <p>
                    Produk :
                    <span>{{a.produk}}</span>
                  </p>
                  <p>
                    Link :
                    <span>
                      <input type="text" v-model="a.link" v-bind:id="'copyLinkInput'+idx" />
                    </span>
                  </p>
                  <p>
                    Copy Link :
                    <span class="link-alternative">
                      <i class="fa fa-clone" @click="copyLink(idx)"></i>
                      <i class="fa fa-external-link" @click="openNewTab(a.link)"></i>
                    </span>
                  </p>
                </div>
                <div class="right">
                  <p>
                    Harga Coret :
                    <span>Rp.{{a.hargaCoret}}</span>
                  </p>
                  <p>
                    Harga Jual :
                    <span>Rp.{{a.hargaJual}}</span>
                  </p>
                  <p>
                    Embed :
                    <span class="link-alternative">
                      <i class="fa fa-clone" @click="copyEmbed(idx)"></i>
                      <input type="text" v-model="a.embed" v-bind:id="'copyEmbed'+idx" />
                    </span>
                  </p>
                </div>
              </div>
            </div>
          </li>
        </ol>
        <div class="btn-show-more">
          <button
            class="btn btn-primary"
            @click="showMoreListMK()"
            v-if="dataTable.list.length !== dataTable.page.total"
          >Show More</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "MarketingKit",
  components: {},
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: ""
      },
      editMarketingKit: {
        section: "detail",
        uuidDetailMarketingKit: 0
      },
      chooseSales: "",
      dataSales: [],
      dataTable: {
        list: [],
        colSearch: {
          sales: false,
          produk: false,
          hargaCoret: false,
          hargaJual: false,
          link: false
        },
        colSort: {
          sales: false,
          produk: false,
          hargaCoret: false,
          hargaJual: false,
          link: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // hargaCoret: 500000
      // hargaJual: 249000
      // link: "/detail-kelas/copywriting-dahsyat-tes?ref=nazcules"
      // produk: "Copywriting Dahsyat Tes"

      this.getListMarketingKit();
    },
    getListMarketingKit() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.marketingKitUser() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.count;

          let result = res.data.data;
          this.dataTable.list = [];
          let origin = window.location.origin;
          this.dataTable.list = result.map(e => {
            let obj = {
              hargaCoret: e.hargaCoret,
              hargaJual: e.hargaJual,
              link: origin + e.link,
              produk: e.produk,
              embed: e.embed
            };
            return obj;
          });

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    showMK(idx) {
      $(".detail-data-mk" + idx).slideToggle();
      $(".icon-dd-mk" + idx).toggleClass("fa-plus fa-minus");
    },
    searchList() {
      this.init();
    },
    showMoreListMK() {
      let a = this.dataBody;
      a.limit += 10;
      this.init();
    },
    copyLink(idx) {
      var copyText = document.getElementById("copyLinkInput" + idx);
      console.log("copy text ", copyText);
      copyText.select();
      copyText.setSelectionRange(0, 99999);
      document.execCommand("copy");
      this.$root.notification(
        "success",
        "Success",
        "Link Copied to Clipboard."
      );
    },
    copyEmbed(idx) {
      var copyText = document.getElementById("copyEmbed" + idx);
      console.log("copy text ", copyText);
      copyText.select();
      copyText.setSelectionRange(0, 99999);
      document.execCommand("copy");
      this.$root.notification(
        "success",
        "Success",
        "Embed Copied to Clipboard."
      );
    },
    openNewTab(url) {
      window.open(url);
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    addMK() {
      this.editMarketingKit.section = "add";
      this.$modal.show("add-marketing-kit");
    },
    detailMarketingKit(uuid) {
      this.editMarketingKit.uuidDetailMarketingKit = uuid;
      this.$modal.show("detail-marketing-kit");
    },
    editDataMarketingKit(uuid) {
      this.editMarketingKit.section = "edit";
      this.editMarketingKit.uuidDetailMarketingKit = uuid;
      this.$modal.show("add-marketing-kit");
    },
    openFilterSales() {
      $("#filterSales").toggleClass("active");
      $("#activeDateKelas").toggleClass("lnr-chevron-down lnr-chevron-up");
      $(".list-date-kelas").slideToggle();
    },
    chooseDK(data, cond) {
      if (!cond) {
        this.dataBody.sales = data.name;
        this.chooseSales = data.name;
        this.getListSales();
        this.openFilterSales();
      }
    }
  },
  mounted() {
    $("#tableMK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
