            display: flex;
            justify-content: space-between;
            width: 100%;
            cursor: pointer;
            background: #004c94;
            &:hover {
              box-shadow: 0px 4px 8px rgba(190, 188, 188, 0.56);
              border: 1px solid #ffffff;
            }
            > .number-list {
              margin: 0%;
              display: flex;
              justify-content: flex-start;
              width: 3%;
            }
            > .first {
              display: flex;
              justify-content: space-between;
              width: 100%;
              > p {
                margin: 0%;
                width: 50%;
                &:first-child {
                  width: 30%;
                }
                &:last-child {
                  width: 4%;
                  text-align: center;
                }
              }
            }
          }
          > .last {
            display: none;
            width: 100%;
            > .detail-data-mk {
              display: flex;
              padding: 5px 30px;
              background: #0e7fe9;
              color: #ffffff;
              .left,
              .right {
                width: 50%;
              }
              p {
                margin: 0%;
              }
              .link-alternative {
                > i {
                  cursor: pointer;
                  margin: 5px;
                  font-size: 20px;
                }
              }
            }
          }
        }
      }
      > .btn-show-more {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px 0 0 0;
      }
    }
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableMK {
    width: 120%;
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .content-list-marketing-kit {
    > .title-mk {
      font-size: 1.5em;
    }
    > .content-mk {
