<template>
  <div class="executor-pengeluaran modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Pengeluaran</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Pengeluaran</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-eb">
      <div class="form-input-mbx">
        <label for="title">Title</label>
        <input
          type="text"
          class="form-control"
          name="title"
          placeholder="Title"
          v-model="dataExecutor.title"
        />
        <span v-if="!cannotNull.title" style="color: red;">This field is required.</span>
      </div>

      <div class="form-input-mbx">
        <label for="tanggal">Tanggal</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.tanggal"
          id="tanggal"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.tanggal" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="jumlah">Jumlah</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="jumlah"
          placeholder="Jumlah"
          v-model="dataExecutor.jumlah"
        />
        <span v-if="!cannotNull.jumlah" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="note">Note</label>
        <input
          type="text"
          class="form-control"
          name="note"
          placeholder="Note"
          v-model="dataExecutor.note"
        />
        <span v-if="!cannotNull.note" style="color: red;">This field is required.</span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelLP()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addLP()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editLP()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ExecutorPengeluaran",
  components: {},
  data() {
    return {
      section: "",
      id: "",
      dataExecutor: {
        tanggal: "",
        title: "",
        jumlah: "",
        note: ""
      },
      cannotNull: {
        tanggal: true,
        title: true,
        jumlah: true,
        note: true
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      // tanggal
      // title
      // jumlah
      // note

      let params = this.$props.data;
      this.section = params.section;
      if (params.section == "edit") {
        this.id = params.id;
        this.getDetailLP();
      }
      console.log("this params ", params);
      this.fixStyleInputVCalendar();
    },
    fixStyleInputVCalendar() {
      $(".vc-text-base").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%"
      });

      $(`#tanggal > input`).attr("placeholder", "Tanggal");
    },
    getDetailLP() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listPengeluaranAdmin() + `/${this.id}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;

          a.tanggal = new Date(result.tanggal);
          a.title = result.title;
          a.jumlah = result.jumlah;
          a.note = result.note;

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    converPayloadBody() {
      let a = this.dataExecutor;
      let body = {
        tanggal: a.tanggal,
        title: a.title,
        jumlah: a.jumlah,
        note: a.note
      };
      return body;
    },
    addLP() {
      let a = this.dataExecutor;
      console.log("data executor ", a);
      let body = this.converPayloadBody();
      this.postLP(body);
    },
    editLP() {
      let a = this.dataExecutor;
      console.log("data executor ", a);
      let body = this.converPayloadBody();
      this.putLP(body);
    },
    postLP(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listPengeluaranAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostLP().success(`Data ${body.title} Berhasil di Tambahkan`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostLP().failed(`${resErr.data.message}`);
        });
    },
    putLP(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .put(vm.$root.listPengeluaranAdmin() + `/${this.id}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostLP().success(`Data ${body.title} Berhasil di Perbarui`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostLP().failed(`${resErr.data.message}`);
        });
    },
    afterPostLP() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    closeModal() {
      this.$modal.hide("executor-pengeluaran");
    },
    cancelLP() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit Pengeluaran ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-pengeluaran {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-eb {
    .list-status-kategori {
      // position: absolute;
      background: #ffffff;
      padding: 0%;
      display: none;
      width: 50%;
      left: 0;
      margin: 3px 0 0 0;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 0 0 10px 10px;
      > li {
        color: #000000;
        list-style: none;
        padding: 5px 10px;
        cursor: pointer;
        &:nth-child(even) {
          background: #cccccc;
        }
        &:hover {
          background: #999898;
        }
      }
    }
  }
}
</style>