<template>
  <div class="content-list-marketing-kit">
    <h3 class="title-lk">List MarketingKit</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="init()"
          />
          <button 
            class="btn btn-primary"
            @click="init()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add MarketingKit</button> -->
      </div>
    </div>
    <div class="filter-table-mbx">
      <h5>Filter :</h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Sales :</span>
            </div>
            <div class="right d-flex">
              <div class="schedule-kelas">
                <!-- <h5 class="t-schedule">Jadwal Kelas</h5> -->
                <div class>
                  <div id="filterSales" class="date-kelas" v-on:click="openFilterSales()">
                    <div class="choose-date">
                      <!-- <img src="/icon/calendar.svg" /> -->
                      <p class="text-date">{{ chooseSales }}</p>
                    </div>
                    <i class="lnr lnr-chevron-down" id="activeDateKelas"></i>
                  </div>
                  <ul class="list-date-kelas">
                    <li
                      class="date-kl"
                      v-for="(list, idx) in dataSales"
                      v-bind:key="idx"
                      v-on:click="chooseDK(list, list.name == chooseSales)"
                    >
                      <input
                        class="radio-kl radio-mbx"
                        type="radio"
                        name="sales-list"
                        v-bind:id="'radio-kl-' + idx"
                        v-bind:checked="list.name == chooseSales"
                      />
                      <label v-bind:for="'radio-kl-' + idx" class="text-date">{{ list.name }}</label>
                      <!-- <p class="status-kl-available" v-if="list.available">Available</p>
                      <p class="status-kl-unavailable" v-else>Unvailable</p>-->
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Sales
                  <span
                    @click="tableMbx().sort('list-sales')"
                    id="list-sales"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Produk
                  <span
                    @click="tableMbx().sort('list-produk')"
                    id="list-produk"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Harga Coret
                  <span
                    @click="tableMbx().sort('list-hargaCoret')"
                    id="list-hargaCoret"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Harga Jual
                  <span
                    @click="tableMbx().sort('list-hargaJual')"
                    id="list-hargaJual"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">Link</div>
              </th>
              <th>
                <div class="th-tbl-mbx">Embed</div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, idx) in dataTable.list" v-bind:key="idx">
              <td style="max-width: 100px;width: 80px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailMarketingKit(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <!-- <button @click="editDataMarketingKit(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>-->
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.sales }}</td>
              <td>{{ list.produk }}</td>
              <td>{{ list.hargaCoret }}</td>
              <td>{{ list.hargaJual }}</td>
              <td>
                <div class="link-mk-aff">
                  <i 
                    class="fa fa-clone"
                    @click="copyLink(idx)"
                  ></i>
                  <input 
                    type="text" 
                    name="linkMK"
                    readonly="readonly"
                    v-bind:id="'linkMK' + idx"
                    v-model="list.link" 
                  >
                </div>
              </td>
              <td>
                <div class="link-mk-aff">
                  <i 
                    class="fa fa-clone"
                    @click="copyEmbed(idx)"
                  ></i>
                  <input 
                    type="text" 
                    name="embedMK"
                    readonly="readonly"
                    v-bind:id="'embedMK' + idx"
                    v-model="list.embed" 
                  >
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List MarketingKit Kosong, Silahkan Tambah MarketingKit.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "MarketingKit",
  components: {},
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: "",
        sales: ""
      },
      editMarketingKit: {
        section: "detail",
        uuidDetailMarketingKit: 0
      },
      chooseSales: "",
      dataSales: [],
      dataTable: {
        list: [],
        colSearch: {
          sales: false,
          produk: false,
          hargaCoret: false,
          hargaJual: false,
          link: false
        },
        colSort: {
          sales: false,
          produk: false,
          hargaCoret: false,
          hargaJual: false,
          link: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Flter: Pilih Sales
      // Table:
      // 	Sales,
      // 	Produk,
      // 	Harga Coret,
      // 	Harga Jual,
      // 	Link LP,
      // 	Action (Open link new tab)

      // "sales": "Sales Nasrullah",
      // "produk": "Copywriting Dahsyat",
      // "hargaCoret": 249000,
      // "hargaJual": 500000,
      // "link": "/detail-kelas/copywriting-dahsyat?ref=Wozl41Yc"

      this.getListSales();
    },
    getListSales() {
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&sales=${a.sales}&search=${a.search}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listSalesAffiliate() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;

          if (result.length !== 0) {
            this.dataSales = result;
            this.dataBody.sales = this.dataBody.sales == "" ? result[0].name : this.dataBody.sales;
            this.chooseSales = this.chooseSales == "" ? result[0].name : this.chooseSales;
            this.getListMarketingKit();
          } else {
            this.dataTable.list = [];
            this.$root.loadingData().hide();
          }

          this.$root.goToTop();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getListMarketingKit() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&sales=${a.sales}&search=${a.search}`;
      axios
        .get(this.$root.listMarketingKitAffiliate() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.data;
          this.dataTable.list = [];
          let origin = window.location.origin;
          this.dataTable.list = result.map((e) => {
            let obj = {
              hargaCoret: e.hargaCoret,
              hargaJual: e.hargaJual,
              link: origin + e.link,
              produk: e.produk,
              sales: e.sales,
              embed: e.embed,
            };
            return obj;
          })

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    addLK() {
      this.editMarketingKit.section = "add";
      this.$modal.show("add-marketing-kit");
    },
    detailMarketingKit(uuid) {
      this.editMarketingKit.uuidDetailMarketingKit = uuid;
      this.$modal.show("detail-marketing-kit");
    },
    editDataMarketingKit(uuid) {
      this.editMarketingKit.section = "edit";
      this.editMarketingKit.uuidDetailMarketingKit = uuid;
      this.$modal.show("add-marketing-kit");
    },
    openFilterSales() {
      $("#filterSales").toggleClass("active");
      $("#activeDateKelas").toggleClass("lnr-chevron-down lnr-chevron-up");
      $(".list-date-kelas").slideToggle();
    },
    chooseDK(data, cond) {
			if ( !cond )  {
				this.dataBody.sales = data.name;
				this.chooseSales = data.name;
				this.getListSales();
				this.openFilterSales();
			}
    },
    copyLink(idx) {
      var copyText = document.getElementById('linkMK'+idx);
      copyText.removeAttribute('readonly');
      console.log('copy text ', copyText);
      copyText.select();
      copyText.setSelectionRange(0, 99999);
      document.execCommand("copy");
      this.$root.notification("success", "Success", "Link Copied to Clipboard.");
      copyText.setAttribute('readonly', 'readonly');
    },
    copyEmbed(idx) {
      var copyText = document.getElementById('embedMK'+idx);
      copyText.removeAttribute('readonly');
      console.log('copy text ', copyText);
      copyText.select();
      copyText.setSelectionRange(0, 99999);
      document.execCommand("copy");
      this.$root.notification("success", "Success", "Embed Copied to Clipboard.");
      copyText.setAttribute('readonly', 'readonly');
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-marketing-kit {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 150%;
  }
  .link-mk-aff {
    display: flex;
    > i {
      margin: 3px 7px;
      font-size: 23px;
      cursor: pointer;
    }
  }
}
.schedule-kelas {
  display: flex;
  > .t-schedule {
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 34px;
    color: #000000;
    margin: 0%;
  }
  .date-kelas {
    display: flex;
    margin: 0 20px;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 7px;
    cursor: pointer;
    width: 300px;
    &.active {
      border-radius: 7px 7px 0 0;
    }
    > .choose-date {
      display: flex;
      align-items: center;
      > p {
        margin: 3px;
      }
    }
    > i {
      margin: auto 0 auto auto;
      padding: 0 0 0 10px;
    }
  }
  .list-date-kelas {
    padding: 5px 10px;
    background: #ccc;
    color: #ffffff;
    margin: 0 20px;
    border-radius: 0 0 7px 7px;
    display: none;
    position: absolute;
    width: 300px;
    > .date-kl {
      list-style: none;
      display: flex;
      align-items: center;
      padding: 3px 0;
      &[readonly="readonly"] {
        opacity: 0.5;
        cursor: not-allowed;
        > label {
          cursor: not-allowed;
        }
      }
      > .text-date {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 15px;
        width: 100%;
        color: #ffffff;
        margin: 7px 0;
        padding: 0px 0 0 39px;
      }
      > p {
        color: #ffffff;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        width: 95px;
        padding: 3px;
        text-align: center;
        margin: 0px 0 0 auto;
        border-radius: 20px;
        &.status-kl-available {
          background: #219653;
        }
        &.status-kl-unavailable {
          background: #e40000;
        }
      }
    }
  }
}
</style>