<template>
  <section class="dashboard-admin">
    <HeaderTop />

    <ContentDashboardAdmin />

    <Footer />
  </section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

import ContentDashboardAdmin from "@/components/DashboardAdmin/ContentDashboardAdmin.vue";

export default {
  name: "DashboardAdmin",
  components: {
    HeaderTop,
    Footer,
    ContentDashboardAdmin,
  },
  data() {
    return  {
    }
  },
  methods: {
    init() {
      setTimeout(() => {
        this.$root.loadingMentorbox().hide();
      }, 2000);
    }
  },
  mounted() {
    this.init();
    this.$root.goToTop();
  },
  watch: {
    $route(to, from) {
      to; 
      from;
      this.init();
    }
  }
};
</script>

