<template>
  <div class="bayar-sekarang">
    <div class="content-buy-now">
      <div class="bagikan-sekarang">
        <i class="fa fa-share-alt"></i>
        <p class="p-share-now">Bagikan Sekarang</p>
      </div>
      <div class="buy-now">
        <div class="price-buy-now">
          <div class="price-top">
            <p class="price-through">Rp.{{ price }}</p>
            <p class="price-hemat">Hemat Rp.{{ hemat }}</p>
          </div>
          <p class="price-real">Rp.{{ promo }}</p>
        </div>
        <a class="btn-buy-now">
          <button 
						class="btn"
						@click="checkout()"
					>Beli Sekarang</button>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BayarSekarang",
	components: {},
	data() {
		return {
      promo: '',
      price: '',
      hemat: '',
			forCheckout: {
        uuid: "",
        name: "",
        price: "",
        promo: "",
        diskon: "",
        total: "",
        scheduleId: ""
      }
		}
	},
	props: ['data'],
	methods: {
		init() {

		},
		handleData(newData) {
      this.promo = newData.promo;
      this.price = newData.price;
      this.hemat = (newData.price - newData.promo);

      let forCheckout = this.forCheckout;
      forCheckout.uuid = newData.uuid;
      forCheckout.name = newData.name;
      forCheckout.price = newData.price;
      forCheckout.promo = newData.promo;
      forCheckout.total = newData.promo;
      forCheckout.scheduleId = window.scheduleId;
    },
		checkout() {
      let url = "/detail-pembayaran/" + "checkout";
      url;
      let auth = this.$root.authentication();
      let roleUser = this.$root.checkRole("USER");
      let forCheckout = this.forCheckout;
      forCheckout.scheduleId = window.scheduleId;
      console.log("role user ", roleUser, auth, this.forCheckout);
      if (!auth) {
        this.$router.push("/signin/user/login");
        this.$root.notification(
          "warning",
          "Warning",
          "Anda belum Login, silahkan Login terlebih dahulu."
        );
      } else if (!roleUser) {
        this.$root.notification(
          "warning",
          "Warning",
          "Anda bukan seorang User, silahkan Daftar sebagai User."
        );
      } else if ( window.scheduleId == 0 ) {
        this.$root.notification(
          "warning",
          "Warning",
          "Anda belum memilih Jadwal, Silahkan pilih Jadwal terlebih dahulu."
        );
      } else {
        let data = this.forCheckout;
        this.$root.executeCheckout(data);
      }
    }
	},
	mounted() {
    this.init();
  },
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top props ", newData, oldData);
    }
  }
};
</script>

