<template>
  <div class="kelas-saya">
		<div class="filter-table-mbx">
      <h5>Filter : </h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Kelas : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(kelas, index) in dataFilter.filter.kelas"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="kelas" 
                  v-bind:id="'f-kelas-'+ kelas.value" 
                  class="radio-mbx"
                  v-bind:value="kelas.value"
                  v-bind:checked="dataBody.filter == kelas.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-kelas-'+kelas.value" 
                  style="margin: 3px 5px;"
                  @click="filterListKelas(kelas.value)"
                >{{ kelas.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <h3 
      class="title-ks"
      v-if="dataBody.filter == 'Semua Kelas'"
    >Semua Kelas</h3>
    <h3 
      class="title-ks"
      v-if="dataBody.filter == 'Dalam Progress'"
    >Dalam Progress</h3>
    <h3 
      class="title-ks"
      v-if="dataBody.filter == 'Materi Terselesaikan'"
    >Materi Terselesaikan</h3>
    <div v-if="(dataListKelas).length > 0" class="content-ks">
      <ul>
        <li class="list-ks d-flex" v-for="(data, index) in dataListKelas" v-bind:key="index">
          <div class="left-ks">
            <div class="img-ks">
              <img v-bind:src="data.banner" v-bind:alt="data.name" />
            </div>
          </div>
          <div class="right-ks d-flex">
            <div class="title-list-ks">
              <h5 class="title-kelas">{{ data.name }}</h5>
              <p class="mentor-kelas">
								<span>Oleh : </span> 
								{{ data.mentor }}
							</p>
							<p class="lk-kelas-wa">{{ data.jenis }}</p>
							<p 
                class="lk-kelas"
                @click="detailKelas(data)"
              >Klik untuk detail kelas. <i class="fa fa-external-link"></i></p>
							<p 
                class="lk-kelas"
                @click="reviewKelas(data)"
              >Review kelas ini. <i class="fa fa-external-link"></i></p>
            </div>

            <div class="btn-kelas d-flex">
              <button class="btn btn-success">Open WhatsApp</button>
              <button class="btn btn-primary">Open Zoom</button>
            </div>
          </div>
        </li>
      </ul>
      <div class="btn-etc-ks">
        <!-- <button class="btn">Lainnya</button> -->
      </div>
    </div>
    <div v-else class="empty-kelas">
      <img src="/icon/empty-folder.svg" alt />
      <p>Oops, Kamu Belum Memulai Kelas.</p>
      <router-link v-bind:to="'/mentoring'" v-on:click.native="goToPage('mentoring')">
        <button class="btn btn-choose-kelas">Pilih Kelas</button>
      </router-link>
    </div>

    <modal name="detail-kelas" :clickToClose="false" :width="'80%'" :height="'80%'">
      <DetailKelasSaya :data="dataDetailKelas" />
    </modal>

    <modal name="review-kelas" :clickToClose="false" :width="'80%'" :height="'80%'">
      <ReviewKelas :data="dataReviewKelas" />
    </modal>

  </div>
</template>

<script>
import axios from "axios";

import DetailKelasSaya from './DetailKelasSaya';
import ReviewKelas from './ReviewKelas';

export default {
  name: "KelasSaya",
  components: {
    DetailKelasSaya,
    ReviewKelas,
  },
  data() {
    return {
			dataListKelas: [],
			dataBody: {
        page: 1,
        limit: 10,
        filter: "Semua Kelas",
			},
			dataFilter: {
        filter: {
          kelas: [
            {
              text: 'Semua Kelas',
              value: 'Semua Kelas'
            },
            {
              text: 'Dalam Progress',
              value: 'Dalam Progress'
						},
						{
              text: 'Materi Terselesaikan',
              value: 'Materi Terselesaikan'
            },
          ]
        }
      },
      dataDetailKelas: {},
      dataReviewKelas: {},
    };
  },
  methods: {
    init() {
      this.getListKelas();
    },
    getListKelas() {
      let a = this.dataBody;
      let sQuery = `?filter=${a.filter}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listKelasUser() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res kelas user ", res);
          let data = res.data;
          let result = data.result;
          this.dataListKelas = result.map(e => {
            let obj = {
              id: e.id,
              uuid: e.uuid,
							name: e.name,
							banner: this.$root.getPhotoUser() + `/${e.banner}`,
              jenis: e.jenis,
              mentor: e.mentor,
              schedule: e.schedule == undefined ? "-" : e.schedule,
              curriculum: e.curriculum.map((j) => {
								let objC = {
                  topics: j.topics,
                  materi: j.materi,
                  files: j.files
								}
								return objC
							}),
						};
						return obj
					});
					
					console.log('list data kelas ', this.dataListKelas)

          this.$root.loadingData().hide();
        })
        .catch(err => {
					let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
						this.$root.sessionTimeUp();
					}
					
					console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    filterListKelas(val) {
      this.dataBody.filter = val;
      this.init();
    },
    goToPage(id) {
      this.$store.state.headerTop.activeMenu = id;
    },
    detailKelas(data) {
      this.dataDetailKelas = data;
      this.$modal.show("detail-kelas");
    },
    reviewKelas(data) {
      this.dataReviewKelas = data;
      this.$modal.show("review-kelas");
    }
  },
  mounted() {
    this.init();
    // console.log('yeah terpanggil ')
  }
};
</script>

<style lang="scss" scoped>
.kelas-saya {
