          padding: 0 0 0 15px;
          flex-direction: column;
					> .title-list-ks {
						> .title-kelas {
							font-size: 1.7em;
							font-weight: bold;
						}
						> p {
							margin: 0%;
						}
						> .mentor-kelas {
							font-size: 1em;
							font-weight: 700;
						}
						> .lk-kelas-wa {
							font-size: 0.9em;
						}
						> .lk-kelas {
							font-size: 0.9em;
							cursor: pointer;
						}
					}
          > .btn-kelas {
            margin: 10px 0 0 0;
            // justify-content: space-between;
            > button {
              padding: 5px 20px;
							margin: 0 5px;
            }
