<template>
  <div class="title-blog">
    <div class="container">
      <div class="content-title-tb d-flex">
        <div class="col-sm-6 left-tb d-flex">
          <div class="bg-blue-blog"></div>
          <img 
						class="image-tb" 
						v-bind:src="$root.getPhotoUser() + `/${detailDataBlog.banner}`" 
						alt="blog image title" 
					/>
        </div>
        <div class="col-sm-6 right-tb d-flex">
          <div class="content-tb">
            <h3 class="title-tb">{{ detailDataBlog.title }}</h3>
            <div class="author-tb">
              <img 
								class="img-author" 
								v-bind:src="$root.getPhotoUser() + `/${detailDataBlog.author.photo}`" 
								alt="name author" 
							/>
              <p class="by-author">
                <span>Oleh</span>
                {{ detailDataBlog.author.name }}
              </p>
            </div>
            <div class="share-tb">
              <p class="title-share">Share this blog</p>
              <div class="icon-share-tb d-flex">
                <i class="fa fa-facebook-f"></i>
                <i class="fa fa-instagram"></i>
                <i class="fa fa-twitter"></i>
              </div>
            </div>
            <div class="highlight-message-tb">
              <p>{{ detailDataBlog.description }}</p>
            </div>
          </div>
        </div>
      </div>
      <div id="contentMessageTB" class="content-message-tb">
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import $ from 'jquery';

export default {
  name: "TitleBlog",
  components: {},
  data() {
    return {
			slugBlog: this.$route.params.slug,
			detailDataBlog: {
				author: {
					name: "", 
					photo: ""
				},
				banner: "",
				content: "",
				description: "",
				slug: "",
				tags: [],
				title: "",
				uuid: "",
			}
		};
	},
	methods: {
		init() {
			this.getDetailBlog();
		},
		getDetailBlog() {
      this.$root.loadingData().show();

      axios.get( this.$root.detailBlogPage() + `/${this.slugBlog}` )
      .then((res) => {
        let data = res.data;
				let result = data.result;

        this.detailDataBlog = result;
        $('#contentMessageTB').append(result.content)
				
				// author: {name: "Change Profile", photo: "avatar.png"}
				// banner: "afae78fb38b50134ac443e37df7ddec2.jpg"
				// content: "<p>hadapi semua rintangan yang ada</p><p><strong>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</strong></p>"
				// description: "Mari kita maju bersama. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolo"
				// slug: "bebas-rasisme"
				// tags: (6) ["kuy", "gaskeun", "tetap semangat", "Mari kita terus berjuang", "visioner", "wisequotes"]
				// title: "Bebas Rasisme"
				// uuid: "87cd6dea-b9a2-4bb2-b14c-b1b9e12bd314"

        console.log('res ', result);
        this.$root.loadingData().hide();
      })
      .catch((err) => {
        console.log('err ', err.response)
        this.$root.loadingData().hide();
      })
    },
	},
	mounted() {
		this.init();
	}
};
</script>

