<template>
  <div class="cara-pembayaran">
    <div class="container">
      <div class="content-cara-pembayaran">
        <h1 class="title-cp">Detail Pembayaran</h1>
        <div class="row">
          <div class="col-sm-6">
            <div class="user-cp">
              <h1 class="title-mbx text-left">Mentorbox.id</h1>
              <hr />
              <div class="dt-pdc name-produk d-flex justify-content-between">
                <p>Produk</p>
                <span>
                  <ol class="list-produk-dp">
                    <li v-for="(data, idx) in dataCP.item" v-bind:key="idx">{{ data.name }}</li>
                  </ol>
                </span>
              </div>
              <div class="dt-pdc order-id d-flex justify-content-between">
                <p>Order ID</p>
                <p>{{ dataCP.invoice }}</p>
              </div>
              <div class="dt-pdc voucher-produk d-flex justify-content-between">
                <p>Voucher</p>
                <p>{{ dataCP.voucher == null ? "-" : dataCP.voucher }}</p>
              </div>
              <div class="dt-pdc amount-produk d-flex justify-content-between">
                <p>Jumlah</p>
                <p class="price-amount">Rp.{{ dataCP.total }}</p>
              </div>
              <hr />
              <div class="data-user">
                <div class="name-user d-flex justify-content-between">
                  <p>Name</p>
                  <p>{{ dataCP.user.name }}</p>
                </div>
                <div class="email-user d-flex justify-content-between">
                  <p>Email</p>
                  <p>{{ dataCP.user.email }}</p>
                </div>
              </div>
              <hr />
              <div class="data-bank">
                <div class="name-bank d-flex justify-content-between">
                  <p>Bank</p>
                  <p>{{ dataBank.bankName }}</p>
                </div>
                <div class="nomor-va d-flex justify-content-between">
                  <p>Nomor Rekening</p>
                  <p>{{ dataBank.rekNumber }}</p>
                </div>
              </div>
              <hr />
            </div>
          </div>
          <div class="col-sm-6">
            <div class="bank-cp">
              <div class="btn-back">
                <span class="lnr lnr-chevron-left"></span>
                <span>Kembali</span>
              </div>
              <hr />

              <!-- cara pembayaran bank nanti disini -->

              <div class="content-bayar-mandiri">
                <div class="title-content d-flex justify-content-between align-items-center">
                  <h5>Cara Pembayaran</h5>
                  <img 
                    v-bind:src="dataBank.urlIcon"
                    alt 
                  />
                </div>
                <hr />
                <ul class="tabs-list-bank">
                  <li
                    v-for="tab in tabBank"
                    v-bind:key="tab.id"
                    v-bind:class="{active: tab.show, [ tab.sub_id ] : tab.sub_id}"
                    v-bind:active="tab.show"
                    v-on:click="changeTab($event, tab.sub_id)"
                  >
                    <a>{{ tab.field }}</a>
                  </li>
                  <hr />
                </ul>

                <div v-if="sectionTab == 'atm'">
                  <div class="atm">
                    <ol>
                      <li
                        v-for="(atm, idx) in dataBank.BankDetails.atm"
                        v-bind:key="idx"
                      >{{ atm }}</li>
                    </ol>
                  </div>
                </div>
                <div v-if="sectionTab == 'ib'">
                  <div class="ib">
                    <ol>
                      <li
                        v-for="(ib, idx) in dataBank.BankDetails.internet"
                        v-bind:key="idx"
                      >{{ ib }}</li>
                    </ol>
                  </div>
                </div>
                <div v-if="sectionTab == 'mb'">
                  <div class="mb">
                    <ol>
                      <li
                        v-for="(mb, idx) in dataBank.BankDetails.mobile"
                        v-bind:key="idx"
                      >{{ mb }}</li>
                    </ol>
                  </div>
                </div>
              </div>
              <div class="btn-check-rek">
                <button class="btn" v-on:click="finishPayment()">Check Rekening</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import ContentBayarBNI from "./bni/ContentBayarBNI";
// import ContentBayarMandiri from "./mandiri/ContentBayarMandiri";

export default {
  name: "ContentCaraPembayaran",
  components: {
    // ContentBayarBNI,
    // ContentBayarMandiri
  },
  data() {
    return {
      // dataCP: this.$store.state.successCheckoutData,
      dataCP: window.$cookies.get("successCheckoutData"),
      dataBank: window.$cookies.get("chooseBankUser"),
      sectionTab: "atm",
      tabBank: [
        {
          id: 1,
          field: "ATM",
          sub_id: "atm",
          show: true
        },
        {
          id: 2,
          field: "Internet Banking",
          sub_id: "ib",
          show: false
        },
        {
          id: 3,
          field: "Mobile Banking",
          sub_id: "mb",
          show: false
        }
      ]
    };
  },
  props: ["section"],
  methods: {
    init() {
      console.log("dataCP ", this.dataCP, this.dataBank);
      setTimeout(() => {
        this.$root.loadingMentorbox().hide();
      }, 2000);
    },
    finishPayment() {
      let urlCheckout = "/detail-pembayaran";
      let params = {
        section: "/finish-payment",
        bank: "/" + this.$props.section
      };
      this.$router.push({ path: urlCheckout + params.section + params.bank });
    },
    changeTab(evt, id) {
      let dataTab = this.tabBank;
      for (let i = 0; i < dataTab.length; i++) {
        if (dataTab[i].sub_id == id) {
          dataTab[i].show = true;
          this.sectionTab = dataTab[i].sub_id;
        } else {
          dataTab[i].show = false;
        }
      }
    }
  },
  mounted() {
    this.$root.goToTop();
    this.init();
  }
};
</script>

