<template>
  <div class="content-kebijakan-mentorbox">
    <div class="container">
      <div class="parent-tab">
        <ul class="tabs">
          <li
            v-for="(tab, idx) in dataTabs"
            v-bind:key="idx"
            v-bind:class="{active: tab.sub_id == section, [ section ] : tab.show }"
            v-on:click="changeTab($event, tab.sub_id)"
          >
            <a>{{ tab.title_tab }}</a>
          </li>
          <hr />
        </ul>
      </div>
      <div class="child-tab">
        <div class="content-child-tab" v-if="section == 'hukum'">
          <KebijakanHukum />
        </div>
        <div class="content-child-tab" v-if="section == 'kebijakan-privasi'">
          <KebijakanPrivasi />
        </div>
        <div class="content-child-tab" v-if="section == 'kebijakan-cookies'">
          <KebijakanCookies />
        </div>
        <div class="content-child-tab" v-if="section == 'kebijakan-sk'">
          <KebijakanSK />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import KebijakanHukum from "./KebijakanHukum";
import KebijakanPrivasi from "./KebijakanPrivasi";
import KebijakanCookies from "./KebijakanCookies";
import KebijakanSK from "./KebijakanSK";

export default {
  name: "ContentKebijakanMentorbox",
  components: {
    KebijakanHukum,
    KebijakanPrivasi,
    KebijakanCookies,
    KebijakanSK
  },
  data() {
    return {
      section: this.$route.params.section,
      dataTabs: [
        // {
        //   id: 1,
        //   sub_id: "hukum",
        //   title_tab: "Hukum",
        //   show: false
        // },
        {
          id: 2,
          sub_id: "kebijakan-privasi",
          title_tab: "Kebijakan Privasi",
          show: true
		},
		{
          id: 3,
          sub_id: "kebijakan-sk",
          title_tab: "Syarat & Ketentuan",
          show: false
        },
        {
          id: 4,
          sub_id: "kebijakan-cookies",
          title_tab: "Kebijakan Cookies",
          show: false
        },
      ]
    };
  },
  methods: {
    changeTab(evt, id) {
      let dataTab = this.dataTabs;
      for (let i = 0; i < dataTab.length; i++) {
        if (dataTab[i].sub_id == id) {
          dataTab[i].show = true;
          this.section = id;
        } else {
          dataTab[i].show = false;
          this.section = id;
        }
      }
    }
  },
  watch: {
    $route(to, from) {
      let pathTo = to.params.section;
      let pathFrom = from.params.section;
      this.section = pathTo;
      pathFrom;
    }
  }
};
</script>

<style lang="scss" scoped>
.content-kebijakan-mentorbox {
  font-family: "Montserrat";
  .container {
    .parent-tab {
      display: flex;
      width: 100%;
      ul.tabs {
        padding: 0%;
        width: 85%;
        margin: 10px 0;
        li {
          display: inline;
          text-align: center;
          cursor: pointer;
          font-style: normal;
          font-weight: normal;
          font-size: 20px;
          line-height: 20px;
          color: #003461;
          opacity: 0.56;

          a {
            display: inline-block;
            width: 33.3333333%;
            padding: 0.75rem 0;
            margin: 0;
            text-decoration: none;
          }
        //   &.kebijakan-privasi ~ hr {
        //     margin-left: 25%;
        //   }
          &.kebijakan-sk ~ hr {
            margin-left: 33.3333333%;
          }
          &.kebijakan-cookies ~ hr {
            margin-left: 66.6666666%;
          }
          &.active {
            opacity: 1;
          }
        }
        hr {
          height: 0.25rem;
          width: 33.33333%;
          margin: 0;
          background: #c4c4c4;
          border: none;
          transition: 0.5s ease-in-out;
        }
      }
    }
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .content-kebijakan-mentorbox {
    .container {
      .parent-tab {
        ul.tabs {
          width: 100%;
        }
      }
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .content-kebijakan-mentorbox {
    .container {
      .parent-tab {
        display: none;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .content-kebijakan-mentorbox {
    .container {
      .parent-tab {
        display: none;
      }
    }
  }
}
@media (max-width: 481px) {
  .content-kebijakan-mentorbox {
    .container {
      .parent-tab {
        display: none;
      }
    }
  }
}
</style>