<template>
  <div class="kelas-menu">
    <div class="container">
      <div class="kelas-title">
        <div class="row justify-content-center text-center">
          <div class="parent-tab">
            <ul class="tabs">
              <li v-for="tab in dataTabKelas" v-bind:key="tab.id" 
                v-bind:class="{active: tab.show, [ tab.sub_id ] : tab.show }" 
                v-on:click="changeTab($event, tab.sub_id)">
                <a>{{ tab.title_tab }}</a>
              </li>
              <hr />
            </ul>
          </div>
        </div>
      </div>
      <div class="content-kelas-menu" v-for="tab in dataTabKelas" v-bind:key="tab.sub_id">
        <div class="row justify-content-center" v-if="tab.show">
          <h5 
            v-if="tab.data.length == 0"
          >
            Data List Kelas Kosong
          </h5>

          <div class="col-sm-4 child-kelas" v-for="kelas in tab.data" v-bind:key="kelas.id">
            <div class="kelas">
              <div class="thumb">
                <img
                  v-bind:src="$root.getPhotoUser() + `/${kelas.banner}`"
                  v-bind:alt="kelas.name"
                />
              </div>
              <div class="kelas-info">
                <h4 class="title-km">{{ kelas.name }}</h4>
                <div class="lingkaran">
                  <star-rating :rating="kelas.rating == null ? 0 : parseInt(kelas.rating)" :read-only="true" :star-size="20"></star-rating>

                  <!-- <img src="../../assets/icon/group_3.svg" alt /> -->
                </div>
                <div class="price">
                  <p class="del-text">Rp.{{ kelas.price }}</p>
                  <p class="price-disc">Rp.{{ kelas.promo }}</p>
                </div>
                <div class="author">By {{ kelas.mentor }}</div>
                <router-link v-bind:to="'/detail-kelas/' + kelas.slug" class="text-white">
                  <button class="pilih-kelas btn">
                    Pilih Kelas
                  </button>
                </router-link>
              </div>
            </div>
          </div>
          <a
            class="link-lainnya" 
            v-bind:href="'/mentoring'"
            v-if="$route.path == '/'" 
          >
            <button 
              class="btn btn-lainnya"
              v-bind:disabled="tab.data.length == 0"
            >
              Lainnya
            </button>
          </a>
          <a
            class="link-lainnya" 
            v-else
            v-bind:hidden="hideBtnLainnya"
            @click="dataBody.limit+=5; init();"
          >
            <button 
              class="btn btn-lainnya"
              v-bind:disabled="tab.data.length == 0"
            >
              Lainnya
            </button>
          </a>
        </div>
			</div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import StarRating from "vue-star-rating";
import $ from 'jquery';

export default {
  name: "KelasMenu",
  components: {
    StarRating,
  },
  data() {
    return {
      hideBtnLainnya: false,
      dataTabKelas: [
        {
          id: 1,
          sub_id: "kelas-whatsapp",
          title_tab: "Kelas WhatsApp",
          show: true,
          data: [
            {
              id: 1,
              name: "Belajar Content Writing",
              mentor: "Andri Darmawan",
              rating: "4.7",
              amount_rating: "500",
              price: "100.000",
              promo: "99.000",
              url: '/detail-kelas/belajar-content-writing'
            },
          ]
        },
        {
          id: 2,
          sub_id: "kelas-webinar",
          title_tab: "Kelas Webinar",
          show: false,
          data: [
            {
              id: 1,
              name: "Belajar Programming",
              mentor: "Andri Darmawan",
              rating: "4.7",
              amount_rating: "500",
              price: "100.000",
              promo: "99.000",
              url: '/detail-kelas/belajar-content-writing'
            },
          ]
        }
      ],
      dataBody: {
        page: 1,
        limit: 5,
        jenis: 'whatsapp',
        kategori: this.$root.getParameterByName('kategori') == false ? "" : this.$root.getParameterByName('kategori'),
        sub: this.$root.getParameterByName('subkategori') == false ? "" : this.$root.getParameterByName('subkategori'),
        search: this.$root.getParameterByName('search') == false ? "" : this.$root.getParameterByName('search'),
      }
    };
  },
  methods: {
    init() {

      // id: 2,
      // name: "Belajar Programming",
      // mentor: "Andri Darmawan",
      // rating: "4.7",
      // amount_rating: "500",
      // price: "100.000",
      // promo: "99.000",
      // url: '/detail-kelas/belajar-content-writing'

      // banner: "banner.png"
      // mentor: "Nasrullah"
      // name: "Belajar Content Writing"
      // price: 150000
      // promo: 99000
      // rating: 5
      // slug: "belajar-content-writing"
      // uuid: "be537873-05cd-4672-b70e-7bb1b12880ad"
      
      this.getListKelas();
    },
    hideRatingText() {
      setTimeout(() => {
        $(".vue-star-rating-rating-text").hide();
      }, 1500);
    },
    getListKelas() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&kategori=${a.kategori}&sub=${a.sub}&search=${a.search}`;
      console.log('squery ', sQuery)

      axios.get( this.$root.listKelasPage() + sQuery )
      .then((res) => {
        let data = res.data;
        let result = data.result;
        if ( this.dataBody.jenis == 'whatsapp' ) {
          this.dataTabKelas[0].data = result;
        } else {
          this.dataTabKelas[1].data = result;
        }

        this.dataBody.limit >= data.count ? this.hideBtnLainnya = true : this.hideBtnLainnya = false; 

        console.log('res ', res);
        this.hideRatingText();
        this.$root.loadingData().hide();
      })
      .catch((err) => {
        console.log('err ', err)
        this.$root.loadingData().hide();
      })
    },
		changeTab(evt, id) {
			let dataTab = this.dataTabKelas;
			for (let i = 0; i < dataTab.length; i++) {
				if (dataTab[i].sub_id == id) {
					dataTab[i].show = true
				} else {
					dataTab[i].show = false
        }
        
        if ( id == 'kelas-whatsapp' ) {
          this.dataBody.jenis = 'whatsapp';
          this.getListKelas();
        } else {
          this.dataBody.jenis = 'webinar';
          this.getListKelas();
        }
			}
		}
  },
  mounted() {
    this.init();
  },
  computed: {

  },
  watch: {
    $route(to, from) {
      let a = this.dataBody;
      a.kategori = this.$root.getParameterByName('kategori') == false ? "" : this.$root.getParameterByName('kategori'),
      a.sub = this.$root.getParameterByName('subkategori') == false ? "" : this.$root.getParameterByName('subkategori')
      this.getListKelas();
      to; from;
    }
  }
};
</script>

<style lang="scss" scoped>
.kelas-menu {
  padding: 36px 0px;
  .kelas-title {
    .parent-tab {
      display: flex;
      width: 100%;
      ul.tabs {
        width: 100%;
        padding: 0 15px;
        margin: 0 auto 30px auto;
        li {
          display: inline;
          text-align: center;
          cursor: pointer;
          font-style: normal;
          font-weight: normal;
          font-size: 48px;
          line-height: 59px;
          color: #003461;
          opacity: 0.56;
          a {
            display: inline-block;
            width: 50%;
            padding: .75rem 0;
            margin: 0;
            text-decoration: none;
          }
          &.kelas-webinar ~ hr {
            margin-left: 50%;
          }
          &.active {
            opacity: 1;
          }
        }
        hr {
          height: .25rem;
          width: 50%;
          margin: 0;
          background: #c4c4c4;
          border: none;
          transition: .5s ease-in-out;
        }
      } 
    }
  }
  .content-kelas-menu {
    .kelas {
      padding: 0 0 30px 0;
      .thumb {
        text-align: center;
        margin-bottom: 14px;
        img {
          width: 100%;
        }
      }
      .kelas-info {
        width: 100%;
        margin: auto;
        text-align: left;
        padding: 0 30px;
        > .title-km {
          font-weight: 700;
        }
        .price {
          line-height: 20px;
          padding-bottom: 12.34px;
          display: flex;
          padding: 0%;
          > .del-text {
            font-size: 15px;
            font-family: "Montserrat";
            text-decoration: line-through;
            font-weight: normal;
            margin: 0%;
          }
          > .price-disc {
            font-size: 18px;
            margin: 0 0 0 30px;
            font-weight: bold;
          }
        }
        .author {
          font-weight: 300;
          font-size: 13px;
          line-height: 15px;
          opacity: 0.6;
          margin: 15px 0;
        }
        .pilih-kelas {
          padding: 0px;
          background-color: #003461;
          width: 100%;
          height: 40px;
          color: #ffffff;
          font-weight: bold;
          &:hover {
            opacity: 0.7;
          }
        }
        h4 {
          font-size: 16px;
          line-height: 20px;
        }
        .lingkaran img {
          width: 100px;
        }
      }
    }
    .link-lainnya {
      width: 100%;
      padding: 0 15px;
      display: block;
      &:hover {
        opacity: 0.7;
      }
      > .btn-lainnya {
        width: 100%;
        height: 100%;
        background-color: #003461;
        color: #ffffff;
        font-size: 16px;
        font-weight: bold;
        line-height: 40px;
      }
    }
  }
}
@media (min-width: 1024px) and (max-width: 1280px) {

}
@media (min-width: 768px) and (max-width: 1023px) {
  .kelas-menu {
    .kelas-title {
      .parent-tab {
        ul.tabs {
          li {
            font-size: 35px;
          }
        } 
      }
    }
    .content-kelas-menu {
      .child-kelas {
        .kelas {
          .kelas-info {
            padding: 0 20px;
          }
        }
      }
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .kelas-menu {
    .kelas-title {
      .parent-tab {
        ul.tabs {
          li {
            font-size: 25px;
          }
        } 
      }
    }
    .content-kelas-menu {
      .child-kelas {
        width: 50%;
        max-width: 50%;
        flex: 0 0 50%;
        &:nth-child(5), &:nth-child(6) {
          display: none;
        }
        .kelas {
          .kelas-info {
            padding: 0 20px;
          }
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .kelas-menu {
    padding: 0 0 20px 0;
    .kelas-title {
      .parent-tab {
        ul.tabs {
          li {
            font-size: 25px;
          }
        } 
      }
    }
    .content-kelas-menu {
      .child-kelas {
        width: 80%;
        max-width: 80%;
        flex: 0 0 80%;
        &:nth-child(4), &:nth-child(5), &:nth-child(6) {
          display: none;
        }
        .kelas {
          .kelas-info {
            padding: 0 20px;
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .kelas-menu {
    padding: 0 0 20px 0;
    .kelas-title {
      .parent-tab {
        ul.tabs {
          li {
            font-size: 18px;
          }
        } 
      }
    }
    .content-kelas-menu {
      .child-kelas {
        width: 80%;
        max-width: 80%;
        flex: 0 0 80%;
        &:nth-child(4), &:nth-child(5), &:nth-child(6) {
          display: none;
        }
        .kelas {
          .kelas-info {
            padding: 0 20px;
          }
        }
      }
    }
  }
}
</style>