<template>
  <div class="welcome-profile-user container">
    <div class="title-ws d-flex">
      <div class="left d-flex">
        <h3 class="title-ds">My Profile</h3>
        <hr />
        <span class="date-now">{{ dateNow }}</span>
      </div>
      <div class="right d-flex">
        <i @click="showSetting()" class="fa fa-cog"></i>
        <ul id="showSetting" class="show-setting-profile">
          <li @click="editProfile();">Edit Profile</li>
          <li @click="changePassword();">Change Password</li>
        </ul>
        <!-- <i id="showWA" class="fa fa-chevron-down show" v-on:click="toggleWA()"></i> -->
      </div>
    </div>
    <div id="contentWA">
      <h3 class="name-admin">Welcome Back {{ welcomeBack }}!</h3>
      <div class="content-ws">
        <div class="left-ws">
          <p class="desc-profile name">{{ profileUser.name == null ? "-" : profileUser.name.toUpperCase() }}</p>
          <p class="desc-profile job-title">{{ profileUser.jobTitle == null ? "-" : profileUser.jobTitle }}</p>
          <div class="about-profile">
            <p>About :</p>
            <p class="a-profile">{{ profileUser.about == null ? "-" : profileUser.about }}</p>
          </div>
          <div class="bottom-profile">
            <div class="left">
              <div class="contact-profile">
                <p>Contact :</p>
                <p class="c-profile">
                  <i class="fa fa-envelope"></i> 
                  {{ profileUser.email == null ? "-" : profileUser.email }}
                </p>
                <p class="c-profile">
                  <i class="fa fa-phone"></i>
                  {{ profileUser.phone == null ? "-" : profileUser.phone }}
                </p>
                <p class="c-profile">
                  <i class="fa fa-map-marker"></i>
                  {{ profileUser.alamat == null ? "-" : profileUser.alamat }}
                </p>
              </div>
            </div>
            <div class="right">
              <div class="saldo-user">
                <p>Saldo :</p>
                <p class="s-profile">Rp. {{ saldoUser }}</p>
              </div>
              <div class="saldo-user">
                <p>Kode Referral :</p>
                <p class="s-profile">{{ profileUser.referral == null ? "-" : profileUser.referral }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="right-ws">
          <div class="img-ws">
            <img 
              v-bind:src="$root.getPhotoUser() + `/${profileUser.photo}`" 
              v-bind:alt="profileUser.name" 
              v-bind:title="profileUser.name" 
            />
          </div>
          <span 
            class="lnr lnr-camera"
            @click="showEditPP()"
          ></span>
        </div>
      </div>
    </div>

    <modal name="executor-profile" :width="'70%'" :height="'70%'" :clickToClose="false">
      <ExecutorProfile :data="dataExecutorProfile" @init="init()" />
    </modal>

    <modal name="edit-pp" :width="'80%'" :height="'80%'" :clickToClose="false">
      <EditPP :data="dataExecutorProfile" @init="init()" />
    </modal>
  </div>
</template>

<script>
import * as moment from "moment";
import $ from "jquery";
import axios from "axios";

import ExecutorProfile from './ExecutorProfile';
import EditPP from './EditPP';

export default {
  name: "ContentProfileUser",
  components: {
    ExecutorProfile,
    EditPP,
  },
  data() {
    return {
      welcomeBack: '',
      profileUser: {},
      dateNow: moment().format("DD - MMM - YYYY"),
      saldoUser: 0,
      dataExecutorProfile: {
        section: '',
      }
    };
  },
  methods: {
    init() {
      // name: "admin"
      // jobTitle: null
      // about: "admin pokonya bebas"
      // email: "admin@admin.com"
      // phone: "08928378123"
      // address: null

      // photo: "avatar.png"
      // roles: Array(1)
      // leaderId: null
      // referral: null
      // uuid: "e5345c88-af88-4fbc-8a6b-733c10199e60"
      // id: 6

      // console.log("login data from state ", this.$store.state.loginData);
      // console.log("login data from cookies ", window.$cookies.get("loginData"));

      this.getDetailProfile();
    },
    getDetailProfile() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&status=${a.status}&mentor=${a.mentor}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.getMyProfile(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          this.profileUser = result;
          this.welcomeBack = result.name.split(' ')[0];
          this.$store.state.USP = result.photo;
          this.getSaldoUser();
          // this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getSaldoUser() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&status=${a.status}&mentor=${a.mentor}`;
      // this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.getSaldoUser(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          this.saldoUser = result.saldo;

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    toggleWA() {
      $("#showWA").toggleClass("show");
      $("#contentWA").slideToggle();
    },
    showSetting() {
      $('#showSetting').slideToggle();
    },
    editProfile() {
      let a = this.dataExecutorProfile;
      a.section = 'edit-profile';
      this.$modal.show("executor-profile") ;     
      this.showSetting();
    },
    changePassword() {
      let a = this.dataExecutorProfile;
      a.section = 'change-password';
      this.$modal.show("executor-profile") ;  
      this.showSetting();
    },
    showEditPP() {
      this.$modal.show('edit-pp');
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.welcome-profile-user {
  font-family: "Montserrat";
  padding: 20px 0;
  > .title-ws {
    align-items: center;
    margin: 0 0 10px 0;
    justify-content: space-around;
    color: #000;
    > .left {
      align-items: center;
