        margin: 0%;
      }
      > .date-now {
        margin: 0%;
      }
      > hr {
        width: 10%;
        border-top: 1px solid #000;
      }
    }
    > .right {
      margin: auto 10px auto auto;
      > i {
        font-size: 20px;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.7s ease;
        margin: 0 10px;
        &:hover {
          color: #004c94;
        }
        &:first-child {
          margin: 0 10px 0 0;
        }
        &:last-child {
          margin: 0 0 0 10px;
        }
        &.fa-chevron-down {
          transform: rotate(90deg);
        }
        &.show {
          transform: rotate(0deg);
        }
