    justify-content: center;
    flex-direction: column-reverse;
    > .left-ws {
      text-align: left;
      width: 45%;
      // padding: 0 10px 0 0;
      margin: auto;
      > .desc-profile {
        text-align: center;
        &.name {
          font-size: 2rem;
          font-weight: 700;
          margin: 5px 0 0 0;
        }
        &.job-title {
          margin: 0;
          font-size: 1.0625rem;
        }
      }
      > .about-profile {
        margin: 15px 0;
        >  p {
          margin: 0;
          &:first-child {
            font-weight: 700;
          }
        }
      }
      > .bottom-profile {
        display: flex;
        justify-content: space-between;
        > .left {
          width: 45%;
          > .contact-profile {
            margin: 15px 0;
            >  p {
              margin: 0;
              &:first-child {
                font-weight: 700;
              }
              > i {
                margin: 3px 5px 3px 0;
                font-size: 1.0625rem;
                &.fa-phone {
                  margin: 3px 5px 3px 2px;
                }
                &.fa-map-marker {
                  margin: 3px 7px 3px 3px;
                }
              }
            }
          }
        }
        > .right {
          width: 45%;
          > .saldo-user {
            margin: 15px 0;
            >  p {
              margin: 0;
              &:first-child {
                font-weight: 700;
              }
            }
          }
        }
      }
    }
    > .right-ws {
      width: 35%;
      height: auto;
      overflow: hidden;
      margin: auto;
      > .img-ws {
        margin: auto;
        position: relative;
        width: 200px;
        height: 200px;
        > img {
          border-radius: 50%;
          width: 200px;
          height: 200px;
          border: 3px solid #ffffff;
        }
      }
    }
  }
}
.show-setting-profile {
  position: absolute;
  padding: 5px 0;
  margin: 5px 0;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  transform: translate3d(-165px, 25px, 0px);
  width: 15%;
  display: none;
  z-index: 10;
  > li {
    text-align: left;
    list-style: none;
    padding: 5px 20px;
    color: #004c94;
    cursor: pointer;
    &:hover {
      background: #004c94;
      color: #ffffff;
    }
  }
}
.lnr-camera {
  display: inline-block;
  font-size: 2em;
  position: absolute;
  transform: translate(70px, -30px);
  cursor: pointer;
}
@media (min-width: 1024px) and (max-width: 1199px) {
  .welcome-profile-user {
    > .title-ws {
      > .left {
        width: 60%;
      }
    }
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
