}
@media (min-width: 577px) and (max-width: 767px) {
  .welcome-profile-user {
    > .title-ws {
      > .left {
        width: 90%;
      }
      > .right {
        margin: auto 5px auto 15px;
      }
    }
    .content-ws {
      flex-direction: column-reverse;
      > .left-ws {
        padding: 15px 0 0 0;
        width: 100%;
      }
      > .right-ws {
        margin: auto;
        width: 80%;
        // display: none;
        > .img-ws {
          margin: auto;
        }
      }
    }
  }
  .show-setting-profile {
    width: 29%;
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .welcome-profile-user {
    padding: 20px 15px;
