        align-items: flex-start;
        > .title-ds {
          font-size: 25px;
        }
        > .date-now {
          font-size: 16px;
        }
        > hr {
          width: 100%;
          margin: 5px 0;
        }
      }
      > .right {
        margin: auto 5px auto 15px;
      }
    }
    .content-ws {
      flex-direction: column-reverse;
      > .left-ws {
        padding: 15px 0 0 0;
        width: 100%;
        > .name-admin {
          font-size: 25px;
        }
        > .desc-profile {
          font-size: 16px;
        }
      }
      > .right-ws {
        margin: auto;
        width: 80%;
        // display: none;
        > .img-ws {
          margin: auto;
        }
      }
    }
  }
  .show-setting-profile {
    width: 39%;
  }
}
@media (max-width: 481px) {
  .welcome-profile-user {
    padding: 20px 15px;
    > .title-ws {
      > .left {
        width: 90%;
        flex-direction: column;
        align-items: flex-start;
        > .title-ds {
          font-size: 25px;
        }
        > .date-now {
          font-size: 16px;
        }
        > hr {
          width: 100%;
          margin: 5px 0;
        }
      }
      > .right {
        margin: auto 5px auto 15px;
      }
    }
    .content-ws {
      flex-direction: column-reverse;
      > .left-ws {
        padding: 15px 0 0 0;
        width: 100%;
        > .name-admin {
          font-size: 25px;
        }
        > .desc-profile {
          font-size: 16px;
        }
      }
      > .right-ws {
        margin: auto;
