<template>
  <div class="content-daftar-affiliate modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Daftar Affiliate</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-da">
      <h5>Untuk Daftar menjadi Affiliate, silahkan masukan kode sesuai yang Anda inginkan.</h5>
      <div class="form-input-mbx">
        <label for="kode" class="with-counting">Kode <span>{{ dataBody.kode.length }} / 10</span></label>
        <input
          type="text"
          class="form-control"
          name="kode"
          placeholder="Kode"
          maxlength="10"
          v-model="dataBody.kode"
          @keyup.enter="submitDA()"
        />
        <span>*Note: Kode minimal 4 chcracter, and maximal 10 characters.</span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelDA()" class="btn btn-danger">Cancel</button>
      <button @click="submitDA()" class="btn btn-success">Daftar</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "DaftarAffiliate",
  components: {},
  data() {
    return {
      dataBody: {
        kode: ""
      }
    };
  },
  methods: {
    init() {},
    closeModal() {
      this.$modal.hide("daftar-affiliate");
    },
    submitDA() {
      let body = this.dataBody;
      if ( body.kode.length < 4 ) {
        this.$root.notification("warning", "Warning", "Kode Affiliate minimal 4 characters");
      } else {
        this.postDA(body);
      }
    },
    postDA(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.affiliateUser(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostDA().success(
            `Daftar Affiliate Berhasil. <br> Dan silahkan Login kembali.`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          if ( resErr.status == 400 ) {
            vm.afterPostDA().failed(resErr.data.message);
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
        });
    },
    afterPostDA() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelDA() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin Daftar Affiliate ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-daftar-affiliate {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-da {
    .with-counting {
      display: flex;
      justify-content: space-between;
      > span {
        font-weight: normal;
        font-size: 15px;

      }
    }
  }
}
</style>