<template>
  <header class>
    <div id="headerMentorbox" class="header content-fixed bg-blue">
      <div class="container">
        <div class="row">
          <div class="col-sm-12">
            <div class="nav-header-top">
              <div id="nav-icon" v-on:click="openMenu()">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </div>
              <div class="nav-logo">
                <router-link v-bind:to="'/'" class="img-logo" v-on:click.native="goToHome()">
                  <img class="mbx-logo" src="../assets/img/mentorboxlogo-parent.png" alt />
                </router-link>
                <nav class="nav">
                  <router-link
                    v-for="nav in dataNav"
                    v-bind:key="nav.id"
                    v-bind:to="nav.url"
                    class="nav-link"
                  >{{ nav.title_nav }}</router-link>
                </nav>
              </div>
              <div class="user-menu" v-if="!auth">
                <router-link v-bind:to="'/signin/user/' + 'login'" class="text-white">Masuk</router-link>
                <router-link v-bind:to="'/signin/user/' + 'register'" class="tbl-biru">Daftar</router-link>
              </div>
              <div class="user-menu logged" v-if="auth">
                <span 
                  class="for-checkout"
                  v-if="$root.checkRole('USER')"
                  @click="goToCheckout()"
                >
                  <span class="lnr lnr-cart"></span>
                </span>
                <span @click="showNotifLogin()" id="bellLogin" class="bell-login">
                  <i class="fa fa-bell"></i>
                  <span class="bullet-red-notif"></span>
                  <ul id="notifList" class="notif-list-login">
                    <span>Pemberitahuan</span>
                    <li>-</li>
                  </ul>
                </span>
                <span v-if="(detailLogin.roles).length == 1">
                  <router-link
                    v-if="detailLogin.roles[0] == 'USER'"
                    v-bind:to="'/dashboard-siswa/' + detailLogin.uuid"
                  >Dashboard Siswa</router-link>
                  <router-link
                    v-if="detailLogin.roles[0] == 'MENTOR'"
                    v-bind:to="'/dashboard-mentor/' + detailLogin.uuid"
                  >Dashboard Mentor</router-link>
                  <router-link
                    v-if="detailLogin.roles[0] == 'ADMIN'"
                    v-bind:to="'/dashboard-admin/' + detailLogin.uuid"
                  >Dashboard Admin</router-link>
                  <router-link
                    v-if="detailLogin.roles[0] == 'CREATOR'"
                    v-bind:to="'/dashboard-creator/' + detailLogin.uuid"
                  >Dashboard Creator</router-link>
                  <router-link
                    v-if="detailLogin.roles[0] == 'OWNER'"
                    v-bind:to="'/dashboard-owner/' + detailLogin.uuid"
                  >Dashboard OWNER</router-link>
                  <router-link
                    v-if="detailLogin.roles[0] == 'AFFILIATE'"
                    v-bind:to="'/dashboard-affiliate/' + detailLogin.uuid"
                  >Dashboard AFFILIATE</router-link>
                </span>
                <span
                  class="more-dashboard"
                  v-if="(detailLogin.roles).length > 1"
                  @click="showMD();"
                >
                  <p>List Dashboard</p>
                </span>
                <ul id="idMoreDashboard" class="list-more-dashboard">
                  <li v-for="(role, index) in dataDashboard" v-bind:key="index">
                    <a
                      v-on:click="goToDashboard(role.url, detailLogin.uuid);"
                    >{{ role.title }}</a>
                  </li>
                </ul>
                <div id="logoUserLogin" v-on:click="showDU()" class="logo-user-login">
                  <img
                    v-bind:src="$root.getPhotoUser() + `/${detailLogin.photo}`"
                    v-bind:alt="detailLogin.name"
                  />
                </div>
                <ul id="listDetailLogin" class="list-detail-login">
                  <li
                    @click="goToProfile(detailLogin.uuid);"
                  >My Profile</li>
                  <li @click="$root.logoutUser();">Logout</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="content-non-fixed"></div>
    <div id="left-menu">
      <div v-if="$root.authentication()" id="leftDetailUser" class="left-detail-user">
        <div class="up-logged-user">
          <div class="logo-user">
            <img
              v-if="detailLogin.photo == 'avatar.png'"
              src="../assets/icon/avatar-user.svg"
              v-bind:alt="detailLogin.name"
            />
            <img
              v-else
              v-bind:src="$root.getPhotoUser() + `/${detailLogin.photo}`"
              v-bind:alt="detailLogin.name"
            />
          </div>
          <div class="title-user">
            <p class="name-user">{{ detailLogin.name }}</p>
            <p
              class="job-title-user"
            >{{ detailLogin.jobTitle == null ? "-" : detailLogin.jobTitle }}</p>
          </div>
        </div>
        <ul class="content-left-menu">
          <li class="menu-has-children kelas-saya">
            <i @click="showDMP()" id="iconShowDMP" class="lnr lnr-chevron-down"></i>
            <a class>Profile</a>
            <ul id="listDMP" style="display: none;">
              <template v-for="(role, index) in dataDashboard">
                <li v-bind:key="index">
                  <a
                    class
                    @click="goToDashboard(role.url, detailLogin.uuid);"
                  >{{ role.title }}</a>
                </li>
              </template>
              <li>
                <a
                  class
                  @click="goToProfile(detailLogin.uuid);"
                >My Profile</a>
              </li>
              <li>
                <a class @click="detailNotif();">Pemberitahuan</a>
              </li>
              <li>
                <a class @click="$root.logoutUser()">LogOut</a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <ul class="content-left-menu">
        <template v-for="(menu, idx) in dataTabSiswa">
          <li
            v-if="menu.show"
            v-bind:key="idx"
            v-bind:class="{'menu-has-children': menu.has_children, [ menu.slug ]: menu.slug}"
          >
            <i
              v-if="menu.has_children"
              class="lnr lnr-chevron-down"
              v-bind:id="menu.id_menus"
              v-on:click="activeChildrenMenu(menu.id_menus)"
            ></i>
            <a
              v-on:click="activeTabPageDashboard(menu.slug)"
            >{{ menu.title_menu }}</a>
            <ul v-if="menu.has_children" style="display: none;">
              <li v-for="a in menu.menus" v-bind:key="a.id">
                <a
                  v-on:click="activeChildMenu(a.url, a.slug, menu.slug)"
                >{{ a.title_menu }}</a>
              </li>
            </ul>
          </li>
        </template>
      </ul>
      <ul class="content-left-menu">
        <template v-for="(menu) in leftMenu">
          <li
            v-if="menu.show"
            v-bind:key="menu.id"
            v-bind:class="{'menu-has-children': menu.has_children, [ menu.slug ]: menu.slug}"
          >
            <i
              v-if="menu.has_children"
              class="lnr lnr-chevron-down"
              v-bind:id="menu.id_menus"
              v-on:click="activeChildrenMenu(menu.id_menus)"
            ></i>
            <a
              v-on:click="activeMenu(menu.url, menu.slug)"
            >{{ menu.title_menu }}</a>
            <ul v-if="menu.has_children" style="display: none;">
              <li v-for="a in menu.menus" v-bind:key="a.id">
                <a
                  v-on:click="activeChildMenu(a.url, a.slug, menu.slug)"
                >{{ a.title_menu }}</a>
              </li>
            </ul>
          </li>
        </template>
      </ul>
    </div>
    <div id="mobile-body-overly" style="display: none;" v-on:click="closeMenu()">
      <div class="close-logo container">
        <div id="nav-icon" class="open">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import $ from "jquery";

export default {
  name: "HeaderTop",
  data() {
    return {
      auth: "",
      detailLogin: {
        id: "",
        name: "",
        photo: "",
        roles: [],
        uuid: "",
        jobTitle: ""
      },
      dataNav: [
        {
          id: 1,
          title_nav: "Tentang Kami",
          url: "/tentang-kami"
        },
        {
          id: 2,
          title_nav: "Mentoring",
          url: "/mentoring"
        }
      ],
      window: {
        width: 0,
        height: 0
      },
      leftMenuLogin: [],
      leftMenu: [
        {
          id: 0,
          title_menu: "Home",
          url: "/",
          slug: "home",
          active: true,
          has_children: false,
          show: true
        },
        {
          id: 1,
          title_menu: "Tentang Kami",
          url: "/tentang-kami",
          slug: "tentang-kami",
          active: false,
          has_children: false,
          show: true
        },
        {
          id: 2,
          title_menu: "Mentoring",
          url: "/mentoring",
          slug: "mentoring",
          active: false,
          has_children: false,
          show: true
        },
        {
          id: 3,
          title_menu: "Tips & Artikel",
          url: "#",
          slug: "tips-article",
          active: false,
          has_children: false,
          show: true
        },
        {
          id: 4,
          title_menu: "Masuk",
          url: "/signin/user/login",
          slug: "login",
          active: false,
          has_children: false,
          show: !this.$root.authentication()
        },
        {
          id: 5,
          title_menu: "Daftar",
          url: "/signin/user/register",
          slug: "register",
          active: false,
          has_children: false,
          show: !this.$root.authentication()
        },
        // {
        //   id: 6,
        //   title_menu: "Kelas Saya",
        //   url: "#",
        //   slug: "kelas-saya",
        //   active: false,
        //   has_children: true,
        //   id_menus: this.$root.makeid(10),
        //   show: true,
        //   menus: [
        //     {
        //       id: 1,
        //       title_menu: "Semua Kelas",
        //       url: "/dashboard-siswa/" + "",
        //       slug: "semua-kelas",
        //       active: false,
        //       has_children: false
        //     },
        //   ]
        // },
        // {
        //   id: 8,
        //   title_menu: "Jadi Mentor",
        //   url: "/dashboard-siswa/" + "",
        //   slug: "jadi-mentor",
        //   active: false,
        //   has_children: false,
        //   id_menus: this.$root.makeid(10)
        // }
      ],
      dataDashboard: [
        {
          id: 1,
          title: "Dashboard Siswa",
          roles: "USER",
          url: "/dashboard-siswa/"
        },
        {
          id: 2,
          title: "Dashboard Mentor",
          roles: "MENTOR",
          url: "/dashboard-mentor/"
        },
        {
          id: 3,
          title: "Dashboard Admin",
          roles: "ADMIN",
          url: "/dashboard-admin/"
        },
        {
          id: 4,
          title: "Dashboard Creator",
          roles: "CREATOR",
          url: "/dashboard-creator/"
        },
        {
          id: 5,
          title: "Dashboard Owner",
          roles: "OWNER",
          url: "/dashboard-owner/"
        },
        {
          id: 6,
          title: "Dashboard Affiliate",
          roles: "AFFILIATE",
          url: "/dashboard-affiliate/"
        }
      ],
      dataTabSiswa: [
        {
          title_menu: "Kelas Saya",
          slug: "kelas-saya",
          has_children: false,
          show: this.$root.checkRole('USER')
        },
        {
          title_menu: "History",
          slug: "history",
          has_children: false,
          show: this.$root.checkRole('USER')
        },
        {
          title_menu: "Affiliate",
          slug: "affiliate",
          has_children: false,
          show: this.$root.checkRole('USER')
        },
        {
          title_menu: "Jadi Mentor",
          slug: "jadi-mentor",
          has_children: false,
          show: this.$root.checkRole('USER')
        },
      ]
    };
  },
  components: {},
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    init() {
      this.$root.hideMenuToHeader();
      let loginData = window.$cookies.get("loginData");
      // console.log('cek cookies loginData headertop ', loginData);
      if (loginData !== null) {
        let result = loginData.result;
        if (loginData.auth) {
          this.auth = loginData.auth;
          let a = this.detailLogin;

          a.id = result.id;
          a.name = result.name;
          a.photo = result.photo;
          a.roles = result.roles.map(e => {
            return e;
          });
          a.uuid = result.uuid;
          a.jobTitle = result.jobTitle;

          let tempDashboard = this.dataDashboard;
          let temp = [];

          for (let i = 0; i < tempDashboard.length; i++) {
            // console.log('tempDashboard[i] ', tempDashboard[i])
            for (let j = 0; j < a.roles.length; j++) {
              // console.log('a.roles[j] ', a.roles[j])
              if (tempDashboard[i].roles == a.roles[j]) {
                temp.push(tempDashboard[i]);
              }
            }
            if (i + 1 == tempDashboard.length) {
              this.dataDashboard = temp;
              // console.log('temp ', temp)
            }
          }
          // console.log("this.dataDashboard ", this.dataDashboard);
        } else {
          this.auth = false;
        }
        // console.log(
        //   "mounted headertop ",
        //   loginData,
        //   this.detailLogin,
        //   loginData.result
        // );
      }
    },
    goToDashboard(url, uuid) {
      this.$router.push(url + uuid);
      this.showMD();
      this.giveMenuDashboard();

    },
    giveMenuDashboard() {
      let getUrl = window.location.pathname.split('/')[1];
      if ( getUrl == 'dashboard-siswa' ) {
        this.leftMenuLogin = this.dataTabSiswa ;
        console.log('masuk kuy ', this.leftMenuLogin);
      }

      console.log('get url ', getUrl);
    },
    showDMP() {
      $("#iconShowDMP").toggleClass("lnr-chevron-up lnr-chevron-down");
      $("#listDMP").slideToggle();
    },
    goToProfile(uuid) {
      this.$router.push("/profile/" + uuid);
      this.showDU();
    },
    goToCheckout() {
      this.$router.push("/detail-pembayaran/checkout");
    },
    detailNotif() {
      alert("Pemberitahuan masih on going");
    },
    showDU() {
      $("#listDetailLogin").slideToggle();
    },
    showMD() {
      $("#idMoreDashboard").slideToggle();
    },
    showNotifLogin() {
      $("#notifList").slideToggle();
    },
    hideListSlide(id) {
      $("#" + id).hide();
    },
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
    },
    openMenu() {
      $("#nav-icon").toggleClass("open");
      $("#left-menu").toggleClass("open");
      $("#mobile-body-overly").css({ display: "block" });
    },
    closeMenu() {
      $("#nav-icon").toggleClass("open");
      $("#left-menu").toggleClass("open");
      $("#mobile-body-overly").css({ display: "none" });
    },
    scrollFunction() {
      $(window).scroll(function() {
        if ($(this).scrollTop() > 100) {
          $("#headerMentorbox").addClass("header-scrolled");
        } else {
          $("#headerMentorbox").removeClass("header-scrolled");
        }
      });
    },
    activeMenu(url, slug) {
      this.$store.commit("activeMenu", slug);
      if (slug == "jadi-mentor") {
        this.$store.commit("activeTabParent", slug);
      }
      this.closeMenu();
      setTimeout(() => {
        if (url != this.$route.path) {
          this.$router.push(url);
        }
      }, 300);
    },
    activeTabPageDashboard(slug) {
      if ( this.$root.checkRole('USER') ) {
        this.$store.commit('activeTabPageDashboard', slug);
      }
      let loginData = window.$cookies.get('loginData');
      let uuid = loginData.result.uuid;
      let urlDashboardSiswa = `/dashboard-siswa/${uuid}`;
      if ( this.$route.path !== urlDashboardSiswa ) {
        this.$router.push(urlDashboardSiswa);
      }
      this.closeMenu();
    },
    activeChildMenu(url) {
      this.closeMenu();
      setTimeout(() => {
        if (url != this.$route.path) {
          this.$router.push(url);
        }
      }, 300);
    },
    goToHome() {
      this.$store.commit("activeMenu", "home");
    },
    activeChildrenMenu(id) {
      $("#" + id)
        .next()
        .toggleClass("menu-item-active");
      $("#" + id)
        .nextAll("ul")
        .eq(0)
        .slideToggle();
      $("#" + id).toggleClass("lnr-chevron-up lnr-chevron-down");
    },
  },
  mounted() {
    this.init();
    this.scrollFunction();

    if ($(".menu-has-children ul>li a").hasClass("menu-active")) {
      $(".menu-active")
        .closest("ul")
        .parentsUntil("a")
        .addClass("parent-active");
    }
  },
  computed: {
    updatePhoto() {
      let photo = this.$store.state.USP;
      return photo;
    }
  },
  watch: {
    $route(to, from) {
      to;
      from;
    },
    updatePhoto(newP, oldP) {
      console.log('update photo ', newP, oldP);
      this.detailLogin.photo = newP;
    }
  }
};
</script>

<style lang="scss" scoped>
.content-non-fixed {
  height: 100px;
}
.header {
  height: 100px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 991;
  font-family: "Montserrat";
  &.header-scrolled {
    transition: all 0.5s;
    box-shadow: -21.213px 21.213px 30px 0px rgba(66, 66, 66, 0.3);
  }
  &.bg-blue {
    background-color: #004c94;
  }
  .nav-header-top {
    padding: 18px 0px;
    display: flex;
    justify-content: space-between;
    .nav-logo {
      display: flex;
      > .img-logo {
        cursor: pointer;
        > .mbx-logo {
          width: 200px;
        }
      }
      nav {
        padding-top: 13px;
        margin: 0 20px;
        a {
          color: white;
          opacity: 0.64;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
  .user-menu {
    text-align: right;
    color: white;
    margin: auto 0;
    a {
      display: inline-block;
      font-size: 16px;
      font-weight: normal;
      text-decoration: none;
      padding: 8px 20px;
      text-align: center;
      color: #ffffff;
      color: white;
      opacity: 0.64;
      &:hover {
        opacity: 1;
      }
      &.tbl-biru {
        color: #004c94;
        font-weight: bold;
        background-color: white;
        border: 1px solid #ffffff;
        opacity: 1;
        &:hover {
          background-color: #004c94;
          color: #ffffff;
        }
      }
    }

    &.logged {
      align-items: center;
      display: flex;
      > .for-checkout {
        margin: 0 10px 0 0;
        cursor: pointer;
        > span {
          font-size: 2em;
        }
      }
      > .bell-login {
        cursor: pointer;
        > i.fa-bell {
          font-size: 30px;
        }
        > .bullet-red-notif {
          background: red;
          width: 13px;
          height: 13px;
          border-radius: 50%;
          position: absolute;
          transform: translate3d(-12px, 2px, 0px);
          border: 2px solid #004c94;
          display: none;
        }
        > .notif-list-login {
          position: absolute;
          padding: 5px 0;
          margin: 5px 0;
          background: #ffffff;
          border-radius: 10px;
          box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
          transform: translate3d(-95px, 5px, 0px);
          width: 20%;
          display: none;
          > span {
            display: flex;
            color: #f2994a;
            align-items: center;
            justify-content: center;
            margin: 5px 0;
          }
          > li {
            text-align: left;
            list-style: none;
            padding: 5px 20px;
            color: #004c94;
            cursor: pointer;
            &:hover {
              background: #004c94;
              color: #ffffff;
            }
          }
        }
      }
      > .more-dashboard {
        margin: 0 25px;
        cursor: pointer;
        > p {
          margin: 0%;
        }
      }
      > ul.list-more-dashboard {
        position: absolute;
        padding: 5px 0;
        margin: 5px 0;
        background: #ffffff;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
        transform: translate3d(20px, 0px, 0px);
        top: 65px;
        width: 17%;
        display: none;
        > li {
          text-align: left;
          list-style: none;
          cursor: pointer;
          > a {
            padding: 5px 20px;
            color: #004c94;
          }
          &:hover {
            background: #004c94;
            > a {
              color: #ffffff;
            }
          }
        }
      }
      > .logo-user-login {
        border: 3px solid #ffffff;
        border-radius: 50%;
        cursor: pointer;
        > img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
        }
      }
      > .list-detail-login {
        position: absolute;
        padding: 5px 0;
        margin: 5px 0;
        background: #ffffff;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
        transform: translate3d(125px, 70px, 0px);
        width: 13%;
        display: none;
        > li {
          text-align: left;
          list-style: none;
          padding: 5px 20px;
          color: #004c94;
          cursor: pointer;
          &:hover {
            background: #004c94;
            color: #ffffff;
          }
        }
      }
    }
  }
}
#nav-icon {
  width: 45px;
  height: 45px;
  position: relative;
  margin: 50px auto;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 5px;
    width: 100%;
    background: #ffffff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.25s ease-in-out;
    -moz-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
    &:nth-child(1) {
      top: 0px;
    }
    &:nth-child(2) {
      top: 12px;
    }
    &:nth-child(3) {
      top: 12px;
    }
    &:nth-child(4) {
      top: 24px;
    }
  }
  &.open {
    span {
      &:nth-child(1) {
        top: 18px;
        width: 0%;
        left: 50%;
      }
      &:nth-child(2) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
      }
      &:nth-child(3) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
      &:nth-child(4) {
        top: 18px;
        width: 0%;
        left: 50%;
      }
    }
  }
}

