<template>
  <div class="content-list-member">
    <h3 class="title-lk">List Member</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Member</button> -->
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th> -->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Nama Kelas
                  <span
                    @click="tableMbx().sort('list-namaKelas')"
                    id="list-namaKelas"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jadwal Kelas
                  <span
                    @click="tableMbx().sort('list-jadwalKelas')"
                    id="list-jadwalKelas"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Detail Member
                  <span
                    @click="tableMbx().sort('list-namaMember')"
                    id="list-namaMember"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Tanggal Bergabung
                  <span
                    @click="tableMbx().sort('list-tabggalBergabung')"
                    id="list-tanggalBergabung"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr 
              v-for="(list, index) in dataTable.list" 
              v-bind:key="index"
              v-bind:hidden="!(index >= dataTable.minIndex && index <= dataTable.maxIndex)"
            >
              <!-- <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailMember(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button @click="editDataMember(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td> -->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.produk }}</td>
              <td>
                <div class="detail-list-mbr">
                  <p>Name : <span>{{ list.jadwal == null ? "-" : list.jadwal.name }}</span></p>
                  <p>Jadwal Mulai : <span>{{ list.jadwal == null ? "-" : list.jadwal.startDate }}</span></p>
                  <p>Jadwal Selesai : <span>{{ list.jadwal == null ? "-" : list.jadwal.endDate }}</span></p>
                </div>
              </td>
              <td>
                <div class="detail-list-mbr">
                  <p>Nama : <span>{{ list.member == null ? "-" : list.member.name }}</span></p>
                  <p>Email : <span>{{ list.member == null ? "-" : list.member.email }}</span></p>
                  <p>Phone : <span>{{ list.member == null ? "-" : list.member.phone }}</span></p>
                </div>
              </td>
              <td>{{ list.tanggal }}</td>
              <!-- <td>
                <ul>
                  <li v-for="(role, index) in list.roles" v-bind:key="index">{{ role }}</li>
                </ul>
              </td> -->
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Member Kosong, Silahkan Tambah Member.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now > 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.total !== dataTable.page.now"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

// import ExecutorListMember from "./ExecutorListMember";

export default {
  name: "ListMember",
  components: {
    // ExecutorListMember,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
      },
      editMember: {
        section: "detail",
        uuidDetailMember: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
        perPage: 10,
        totalData: 0,
        maxIndex: 0,
        minIndex: 0,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
          ]
        }
      }
    };
  },
  methods: {
    init() {

      // Filter: Pilih kelas
      // Table: 
      //   NamaKelas, 
      //   JadwalKelas, 
      //   NamaMember, 
      //   EmailMember, 
      //   PhoneMember, 
      //   TglBergabung

      // email: "naz.cules@gmail.com"
      // id: 1
      // nama: "Nasrullah"
      // phone: "6282337027239"
      // produk: "Belajar Content Writing"
      // tanggal: "2020-06-06 11:10:17"

      this.getListMember();
    },
    getListMember() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&roles=${a.roles}&search=${a.search}`;
      // tambah params query uuid dari kelas belum di tambahkan
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listMemberMentor(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            return e;
          });

          this.dataTable.totalData = data.count;
          this.tableMbx().counterPageTable();

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          } else {
            this.$root.notification(resErr.data.message);
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
			let a = this.dataTable;
			let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++;
          vm.tableMbx().changeShowPage();
        },
        backTable() {
					a.page.now--;
          b.page--;
          vm.tableMbx().changeShowPage();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
            a.page.now = val;
            vm.tableMbx().counterPageTable();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
          a.perPage = page;
          vm.tableMbx().counterPageTable();
          vm.tableMbx().perPage();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        },
        counterPageTable: () => {
          let totalData  = vm.dataTable.totalData;
          let m = Math.ceil(totalData / a.perPage);
          if (m <= 1) {
            m = 1;
          }
          let lengthData = Array(m)
              .fill(0, 0)
              .map((x, i) => i).length
            a.page.total = lengthData;
            console.log('lengthData ', lengthData, m);
          vm.tableMbx().changeShowPage();
        },
        changeShowPage: () => {
          let pageNow = vm.dataTable.page.now;
          let perPage = vm.dataTable.perPage;
          this.dataTable.minIndex = ((pageNow*perPage)-perPage);
          this.dataTable.maxIndex = ((pageNow*perPage)-1);
        }
      };
    },
    addLK() {
      this.editMember.section = "add";
      this.$modal.show("add-member");
    },
    detailMember(uuid) {
      this.editMember.uuidDetailMember = uuid;
      this.$modal.show("detail-member");
    },
    editDataMember(uuid) {
      this.editMember.section = "edit";
      this.editMember.uuidDetailMember = uuid;
      this.$modal.show("add-member");
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-member {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
  .detail-list-mbr {
    text-align: left;
    > p {
      margin: 0%;
      > span {
        font-weight: 600;
      }
    }
  }
}
</style>