<template>
  <div class="content-list-komisi">
    <h3 class="title-lk">List Komisi</h3>
    <div 
      class="top-table-mbx"
      v-if="dataTable.list.length !== 0"
    >
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <!-- <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Komisi</button> -->
      </div>
    </div>
    <div class="content-lk">
      <div v-if="dataTable.list.length == 0">
        <h5>Data List Komisi Kosong.</h5>
      </div>
      <div v-else class="list-content-lk">
        <ol>
          <li v-for="(a, idx) in dataTable.list" v-bind:key="idx">
            <div class="ctn-list">
              <p class="number-list">
                <span>{{ idx + 1 }}.</span>
              </p>
              <div class="first" @click="showLK(idx)">
                <p>{{ a.invoice }}</p>
                <p>{{ a.tanggal }}</p>
                <p>{{ a.member }}</p>
                <p>Rp.{{ a.jumlah }}</p>
                <p>
                  <i v-bind:class="'fa fa-plus icon-dd-lk'+idx"></i>
                </p>
              </div>
            </div>
            <div v-bind:class="'last detail-data-lk'+idx">
              <div class="detail-data-lk">
                <div class="left">
                  <p>
                    Invoice :
                    <span>{{a.invoice}}</span>
                  </p>
                  <p>
                    Tanggal :
                    <span>{{a.tanggal}}</span>
                  </p>
                  <p>
                    Member :
                    <span>{{a.member}}</span>
                  </p>
                </div>
                <div class="right">
                  <p>
                    Jumlah :
                    <span>Rp.{{a.jumlah}}</span>
                  </p>
                  <p>
                    Jenis :
                    <span>{{a.jenis}}</span>
                  </p>
                  <p class="d-flex">
                    Produk :
                    <span>
                      <ul>
                        <li v-for="(p, idx) in a.produk" v-bind:key="idx">{{ p }}</li>
                      </ul>
                    </span>
                  </p>
                </div>
              </div>
            </div>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import * as moment from "moment";

// import ExecutorListKomisi from "./ExecutorListKomisi";

export default {
  name: "ListKomisi",
  components: {
    // ExecutorListKomisi,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "active",
        roles: "",
        search: ""
      },
      editKomisi: {
        section: "detail",
        uuidDetailKomisi: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Tanggal,
      // Produk,
      // Jumlah

      // invoice: "INV2006063U03X"
      // jenis: "Bagi Hasil"
      // jumlah: 16200
      // member: "Nasrullah"
      // produk: ["Kelas Coding Seru"]
      // tanggal: "2020-06-06"

      this.getListKomisi();
    },
    getListKomisi() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&roles=${a.roles}&search=${a.search}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.komisiUser(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          // let data = res.data;
          // let dataTable = this.dataTable;
          // dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map(e => {
            let obj = {
              invoice: e.invoice,
              jenis: e.jenis,
              jumlah: e.jumlah,
              member: e.member,
              produk: e.produk,
              tanggal: moment(e.tanggal).format("DD - MMM - YYYY")
            };
            return obj;
          });

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    showLK(idx) {
      $(".detail-data-lk" + idx).slideToggle();
      $(".icon-dd-lk" + idx).toggleClass("fa-plus fa-minus");
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    addLK() {
      this.editKomisi.section = "add";
      this.$modal.show("add-komisi");
    },
    detailKomisi(uuid) {
      this.editKomisi.uuidDetailKomisi = uuid;
      this.$modal.show("detail-komisi");
    },
    editDataKomisi(uuid) {
      this.editKomisi.section = "edit";
      this.editKomisi.uuidDetailKomisi = uuid;
      this.$modal.show("add-komisi");
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-komisi {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: center;
    margin: 10px 0;
  }
  > .content-lk {
    > .list-content-lk {
      background: #003461;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      text-align: left;
      color: #ffffff;
      > ol {
        padding: 0%;
        margin: 0%;
        > li {
          display: flex;
          align-items: center;
          flex-direction: column;
          .ctn-list {
            padding: 5px 10px;
            display: flex;
            justify-content: space-between;
            width: 100%;
            cursor: pointer;
            background: #004c94;
            &:hover {
              box-shadow: 0px 4px 8px rgba(190, 188, 188, 0.56);
              border: 1px solid #ffffff;
            }
            > .number-list {
              margin: 0%;
              display: flex;
              justify-content: flex-start;
              width: 3%;
            }
            > .first {
              display: flex;
              justify-content: space-between;
              width: 100%;
              > p {
                margin: 0%;
              }
            }
          }
          > .last {
            display: none;
            width: 100%;
            > .detail-data-lk {
              display: flex;
              padding: 5px 30px;
              background: #0e7fe9;
              color: #ffffff;
              .left,
              .right {
                width: 50%;
              }
              p {
                margin: 0%;
              }
            }
          }
        }
      }
    }
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>