<template>
  <div class="executor-bank modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Bank</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Bank</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-eb">
      <div class="show-logo-bank">
        <div class="left form-input-mbx">
          <label for="logoBank">Logo Bank</label>
          <input
            type="file"
            class="form-control"
            name="logoBank"
            id="logoBank"
            placeholder="Logo Bank"
            v-on:change="handleFile($event)"
          />
          <div class="file-upload-div d-flex" v-on:click="chooseFile('logoBank')">
            <span>{{ logoBankText }}</span>
            <span class="btn btn-up">Upload Logo Bank</span>
          </div>
        </div>
        <div class="right">
          <div class="show-image">
            <img 
              v-if="section == 'add'"
              id="showImage" 
              src 
              alt 
            />
            <img 
              v-if="section == 'edit'"
              id="showImage" 
              v-bind:src="$root.getPhotoUser() + `/${dataExecutor.logoBank}`" 
              alt 
            />
          </div>
        </div>
      </div>
      <div class="kode-name-bank">
        <div class="left form-input-mbx">
          <label for="kodeBank">Kode Bank</label>
          <input
            onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
            type="text"
            class="form-control"
            name="kodeBank"
            placeholder="Kode Bank"
            v-model="dataExecutor.kodeBank"
          />
          <span v-if="!cannotNull.kodeBank" style="color: red;">This field is required.</span>
        </div>
        <div class="right form-input-mbx">
          <label for="namaBank">Nama Bank</label>
          <input
            type="text"
            class="form-control"
            name="namaBank"
            placeholder="Nama Bank"
            v-model="dataExecutor.namaBank"
          />
          <span v-if="!cannotNull.namaBank" style="color: red;">This field is required.</span>
        </div>
      </div>
      <div class="pemilik-norek-bank">
        <div class="left form-input-mbx">
          <label for="pemilikRek">Pemilik Rekening</label>
          <input
            type="text"
            class="form-control"
            name="pemilikRek"
            placeholder="Pemilik Rekening"
            v-model="dataExecutor.pemilikRek"
          />
          <span v-if="!cannotNull.pemilikRek" style="color: red;">This field is required.</span>
        </div>
        <div class="right form-input-mbx">
          <label for="nomorRek">Nomor Rekening</label>
          <input
            onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
            type="text"
            class="form-control"
            name="nomorRek"
            placeholder="Nomor Rekening"
            v-model="dataExecutor.nomorRek"
          />
          <span v-if="!cannotNull.nomorRek" style="color: red;">This field is required.</span>
        </div>
      </div>
      <div class="form-input-mbx">
        <label for="status">Status</label>
        <div class="d-flex">
          <template v-for="(status, index) in listStatusKelas">
            <input
              v-bind:key="index+5"
              type="radio"
              name="status"
              v-bind:id="'f-status-'+ status.value"
              class="radio-mbx"
              v-bind:value="status.value"
              v-bind:checked="dataExecutor.status == status.value"
            />
            <label
              v-bind:key="index"
              v-bind:for="'f-status-'+status.value"
              style="margin: 3px 5px;"
              @click="statusBank().choose(status.value)"
            >{{ status.text }}</label>
          </template>
        </div>
        <span v-if="!cannotNull.status" style="color: red;">This field is required.</span>
      </div>

      <div class="content-cara-pembayaran">
        <h5>Cara Pembayaran Bank</h5>
        <div class="form-input-mbx">
          <label for="nomorRek">ATM</label>
          <div
            class="list-cp-bank"
            v-for="(atm, idx) in dataExecutor.BankDetails.atm"
            v-bind:key="idx"
          >
            <div class="left">
              <span>{{ idx+1 }}.</span>
              <input
                type="text"
                class="form-control"
                name="nomorRek"
                placeholder="ATM"
                v-model="dataExecutor.BankDetails.atm[idx]"
              />
            </div>
            <div class="right">
              <div class="icon-plus-minus">
                <span
                  class="minus-jadwal"
                  @click="removeCP('atm', idx)"
                  v-if="dataExecutor.BankDetails.atm.length !== idx + 1"
                >
                  <i class="fa fa-trash"></i>
                </span>
                <span
                  class="plus-jadwal"
                  @click="addCP('atm', idx)"
                  v-if="dataExecutor.BankDetails.atm.length == idx + 1"
                >
                  <i class="fa fa-plus"></i>
                </span>
              </div>
            </div>
          </div>
          <span v-if="!cannotNull.nomorRek" style="color: red;">This field is required.</span>
        </div>
        <div class="form-input-mbx">
          <label for="nomorRek">Internet Banking</label>
          <div
            class="list-cp-bank"
            v-for="(internet, idx) in dataExecutor.BankDetails.internet"
            v-bind:key="idx"
          >
            <div class="left">
              <span>{{ idx+1 }}.</span>
              <input
                type="text"
                class="form-control"
                name="nomorRek"
                placeholder="Internet Banking"
                v-model="dataExecutor.BankDetails.internet[idx]"
              />
            </div>
            <div class="right">
              <div class="icon-plus-minus">
                <span
                  class="minus-jadwal"
                  @click="removeCP('internet', idx)"
                  v-if="dataExecutor.BankDetails.internet.length !== idx + 1"
                >
                  <i class="fa fa-trash"></i>
                </span>
                <span
                  class="plus-jadwal"
                  @click="addCP('internet', idx)"
                  v-if="dataExecutor.BankDetails.internet.length == idx + 1"
                >
                  <i class="fa fa-plus"></i>
                </span>
              </div>
            </div>
          </div>
          <span v-if="!cannotNull.nomorRek" style="color: red;">This field is required.</span>
        </div>
        <div class="form-input-mbx">
          <label for="nomorRek">Mobile Banking</label>
          <div
            class="list-cp-bank"
            v-for="(mobile, idx) in dataExecutor.BankDetails.mobile"
            v-bind:key="idx"
          >
            <div class="left">
              <span>{{ idx+1 }}.</span>
              <input
                type="text"
                class="form-control"
                name="nomorRek"
                placeholder="Mobile Banking"
                v-model="dataExecutor.BankDetails.mobile[idx]"
              />
            </div>
            <div class="right">
              <div class="icon-plus-minus">
                <span
                  class="minus-jadwal"
                  @click="removeCP('mobile', idx)"
                  v-if="dataExecutor.BankDetails.mobile.length !== idx + 1"
                >
                  <i class="fa fa-trash"></i>
                </span>
                <span
                  class="plus-jadwal"
                  @click="addCP('mobile', idx)"
                  v-if="dataExecutor.BankDetails.mobile.length == idx + 1"
                >
                  <i class="fa fa-plus"></i>
                </span>
              </div>
            </div>
          </div>
          <span v-if="!cannotNull.nomorRek" style="color: red;">This field is required.</span>
        </div>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelBank()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addBank()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editBank()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ExecutorBank",
  components: {},
  data() {
    return {
      section: "",
      id: "",
      dataExecutor: {
        kodeBank: "",
        namaBank: "",
        logoBank: "",
        pemilikRek: "",
        nomorRek: "",
        status: "",
        statusList: ["Publish", "Reject", "Draft"],
        BankDetails: {
          atm: [""],
          internet: [""],
          mobile: [""]
        }
      },
      formLogoBank: "",
      logoBankText: "",
      listStatusKelas: [
        {
          text: "Publish",
          value: "Publish"
        },
        {
          text: "Draft",
          value: "Draft"
        }
      ],
      cannotNull: {
        kodeBank: true,
        namaBank: true,
        logoBank: true,
        pemilikRek: true,
        nomorRek: true,
        status: true
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      // kodeBank
      // namaBank
      // logoBank
      // pemilikRek
      // nomorRek
      // status

      let params = this.$props.data;
      this.section = params.section;
      if (params.section == "edit") {
        this.id = params.id;
        this.getDetailBank();
      }
      console.log("this params ", params);
    },
    getDetailBank() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listBankAdmin() + `/${this.id}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;

          a.namaBank = result.bankName;
          a.kodeBank = result.code;
          a.logoBank = result.bankLogo;
          this.logoBankText = result.bankLogo;
          a.pemilikRek = result.accountName;
          a.nomorRek = result.rekNumber;
          a.status = result.status;
          a.BankDetails.atm = result.BankDetails.atm;
          a.BankDetails.internet = result.BankDetails.internet;
          a.BankDetails.mobile = result.BankDetails.mobile;

          a.BankDetails.atm.length == 0 ? a.BankDetails.atm.push('') : '';
          a.BankDetails.internet.length == 0 ? a.BankDetails.internet.push('') : '';
          a.BankDetails.mobile.length == 0 ? a.BankDetails.mobile.push('') : '';

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    uploadLogoBank() {
      let body = this.formLogoBank;
      let AuthSth = this.$root.authorization();
      this.$root.loadingData().show();
      let vm = this;
      axios
        .post(vm.$root.filesUploadSingle(), body, {
          headers: {
            Authorization: AuthSth,
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let a = this.dataExecutor;
          a.logoBank = data.files;
          let body = this.converPayloadBody();
          if (this.section == "add") {
            this.postBank(body);
          } else {
            this.putBank(body);
          }
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostProfile().failed(`${resErr.data.message}`);
        });
    },
    converPayloadBody() {
      let a = this.dataExecutor;
      let body = {
        kodeBank: a.kodeBank,
        namaBank: a.namaBank,
        logoBank: a.logoBank,
        pemilikRek: a.pemilikRek,
        nomorRek: a.nomorRek,
        status: a.status,
        BankDetails: a.BankDetails
      };
      return body;
    },
    addBank() {
      let a = this.dataExecutor;
      console.log("data executor ", a);
      this.uploadLogoBank();
    },
    editBank() {
      let a = this.dataExecutor;
      let formLogoBank = this.formLogoBank;
      console.log("data executor ", a, formLogoBank);
      if (formLogoBank !== "") {
        this.uploadLogoBank();
      } else {
        let body = this.converPayloadBody();
        this.putBank(body);
      }
    },
    postBank(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listBankAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostBank().success(
            `Bank ${body.namaBank} Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostBank().failed(`${resErr.data.message}`);
        });
    },
    putBank(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .put(vm.$root.listBankAdmin() + `/${this.id}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostBank().success(
            `Bank ${body.namaBank} Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostBank().failed(`${resErr.data.message}`);
        });
    },
    afterPostBank() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$emit('init');
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$emit('init');
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    statusBank() {
      let a = this.dataExecutor;
      return {
        show() {
          $("#statusBank").slideToggle();
        },
        choose(val) {
          a.status = val;
        }
      };
    },
    closeModal() {
      this.$modal.hide("executor-bank");
    },
    cancelBank() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit Bank ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    chooseFile(id) {
      $("#" + id).click();
    },
    handleFile(evt) {
      let target = evt.target;
      let files = target.files;
      let sizeFiles = target.files[0].size;
      if (sizeFiles > 1000000) {
        this.$root.notification("warning", "Warning", "Max Size file is 1MB.");
      } else {
        let nameFile = target.files[0].name;
        this.logoBankText = nameFile;
        let bodyFormData = new FormData();
        bodyFormData.append("files", files[0]);
        this.formLogoBank = bodyFormData;
        console.log('this.formLogoBank ', this.formLogoBank);
        this.renderImage(target.files[0]);
      }
      console.log("evt ", evt);
    },
    renderImage(files) {
      console.log('masuk render image ', files)
      var reader = new FileReader();
      reader.onload = function(e) {
        $("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    addCP(f, idx) {
      let a = this.dataExecutor.BankDetails[f];
      idx;
      a.push("");
    },
    removeCP(f, idx) {
      let a = this.dataExecutor.BankDetails[f];
      a.splice(idx, 1);
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-bank {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-eb {
    .kode-name-bank {
      display: flex;
      > .left {
        width: 50%;
        padding: 0 10px 0 0;
      }
      > .right {
        width: 50%;
        padding: 0 0 0 10px;
      }
    }
    .pemilik-norek-bank {
      display: flex;
      > .left {
        width: 50%;
        padding: 0 10px 0 0;
      }
      > .right {
        width: 50%;
        padding: 0 0 0 10px;
      }
    }
    .content-cara-pembayaran {
      > div {
        border-bottom: 1px solid #ccc;
        padding: 10px 0;
      }
      .list-cp-bank {
        margin: 3px 0;
        display: flex;
        > .left {
          width: 90%;
          display: flex;
          align-items: center;
          > span {
            display: inline-block;
            width: 40px;
            text-align: center;
          }
        }
        > .right {
          width: 10%;
          display: flex;
          align-items: center;
          .icon-plus-minus {
            display: flex;
            flex-direction: column;
            margin: auto;
            > span {
              padding: 5px 15px;
              border-radius: 10px;
              cursor: pointer;
              &.minus-jadwal {
                background: #f00707;
                color: #ffffff;
                border: 1px solid #f00707;
                margin: 0 0 5px 0;
                &:hover {
                  background: #ffffff;
                  color: #f00707;
                }
              }
              &.plus-jadwal {
                background: #003461;
                color: #ffffff;
                border: 1px solid #003461;
                &:hover {
                  background: #ffffff;
                  color: #003461;
                }
              }
            }
          }
        }
      }
    }
    .show-logo-bank {
      display: flex;
      align-items: center;
      > .left {
        width: 50%;
        padding: 0 10px 0 0;
      }
      > .right {
        width: 50%;
        padding: 0 0 0 10px;
        .show-image {
          > img {
            width: 200px;
            height: 100px;
            border-radius: 10px;
          }
        }
      }
    }
  }
}
</style>