<template>
  <div class="form-login">
    <h1>Login Ke Akun MentorBox Anda</h1>
    <div class="input-form">
      <label for="email-login">Email</label>
      <input
        type="email"
        name="email-login"
        id="idEmailLogin"
        class="form-input"
        placeholder="Email"
        v-model="formRegister.email"
        @keyup="checkEF()"
        @keyup.enter="login()"
      />
      <span v-if="!cannotNull.email" style="color: red;">This field is required.</span>
      <span v-if="!formatEmail" style="color: red;">Invalid email format.</span>
    </div>
    <div class="input-form">
      <label for="password-login">Password</label>
      <input
        type="password"
        name="password-login"
        id="idPasswordLogin"
        class="form-input"
        placeholder="Password"
        v-model="formRegister.password"
        @keyup.enter="login()"
      />
      <span
        class="sh-password"
        id="shPassLogin"
        v-on:click="$root.showPassword('shPassLogin', 'idPasswordLogin')"
      >
        <i class="fa fa-eye"></i>
      </span>
      <span v-if="!cannotNull.password" style="color: red;">This field is required.</span>
    </div>
    <router-link v-bind:to="'#'" class="forgot-passwd">Lupa Password?</router-link>
    <button class="btn btn-login" v-on:click="login()">Masuk</button>
    <p class="or-login">atau masuk dengan</p>
    <div class="login-sosmed">
      <div class="sosmed-fb">
        <img src="/icon/sosmed-fb.svg" alt="Facebook" />
      </div>
      <div class="sosmed-google">
        <img src="/icon/sosmed-google.svg" alt="Google" />
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "LoginMentorBox",
  components: {},
  data() {
    return {
      formRegister: {
        email: "",
        password: ""
      },
      cannotNull: {
        email: true,
        password: true
      },
      formatEmail: true
    };
  },
  methods: {
    checkEF() {
      let email = this.formRegister.email;
      var re = this.$root.checkValidateEmail(email);
      re == false ? (this.formatEmail = false) : (this.formatEmail = true);
    },
    validateLogin() {
      let data = this.formRegister;
      let required = this.cannotNull;

      data.email == "" ? (required.email = false) : (required.email = true);
      data.password == ""
        ? (required.password = false)
        : (required.password = true);

      if (data.email !== "" && data.password !== "") {
        return true;
      } else {
        return false;
      }
    },
    login() {
      $("");
      let data = this.formRegister;
      if (this.formatEmail) {
        if (this.validateLogin()) {
          // console.log("data sudah full", data);
          let body = {
            email: data.email,
            password: data.password
          };
          this.postLogin(body);
        }
      } else {
        this.$root.notification("warning", "Warning", "Invalid Email Format.");
      }
    },
    postLogin(body) {
      this.$root.loadingData().show();
      let vm = this;

      axios
        .post(this.$root.signIn(), body)
        .then(res => {
          // const response = res;
          // console.log("response ", response);
          window.$cookies.set('logUser', body);

          const data = res.data;
          let dataUser = data.result;
          vm.$root.notification(
            "success",
            "Success",
            `${dataUser.email} berhasil login. <br> name : ${
              dataUser.name
            } <br> phone number : ${
              dataUser.phone
            } <br> roles : ${dataUser.roles.join(" , ")}`
          );

          vm.$store.state.loadingData = data;
          window.$cookies.set("loginData", data);
          window.$cookies.set("USP", dataUser.photo);
          vm.$router.push("/profile/" + dataUser.uuid);
          // if ( dataUser.roles[0] == "MENTOR" ) {
          // } else if ( dataUser.roles[0] == "ADMIN" ) {
          //   vm.$router.push("/dashboard-admin/" + dataUser.uuid + "/welcome");
          // } else if ( dataUser.roles[0] == "CREATOR" ) {
          //   vm.$router.push("/dashboard-creator/" + dataUser.uuid);
          // } else if ( dataUser.roles[0] == "OWNER" ) {
          //   vm.$router.push("/dashboard-owner/" + dataUser.uuid);
          // } else if ( dataUser.roles[0] == "USER" ) {
          //   vm.$router.push("/dashboard-siswa/" + dataUser.uuid);
          // }
          vm.$root.loadingMentorbox().show();
          vm.$root.loadingData().hide();
        })
        .catch(error => {
          const err = error.response.data;
          console.log("error ", err);
          vm.$root.notification("danger", "Error", `${err.errors}`);

          vm.$root.loadingData().hide();
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.form-login {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  > h1 {
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
  }
  > .input-form {
    display: flex;
    flex-direction: column;
    > label {
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 24px;
      margin: 5px 0 0 0;
    }
    > input {
      padding: 5px 5px 5px 10px;
      margin: 5px 0 0 0;
      border-radius: 8px;
      border: 1px solid #cccccc;
      &[type="password"] {
        padding: 5px 35px 5px 10px;
      }
    }
    > .sh-password {
      cursor: pointer;
      display: flex;
      margin: -27px 10px 15px auto;
      font-size: 18px;
    }
  }
  > .forgot-passwd {
    text-align: right;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #000000;
    display: flex;
    margin: 5px 0 25px auto;
    &:hover {
      color: #1306c9;
    }
  }
  > .btn-login {
    cursor: pointer;
    background: #004c94;
    color: #ffffff;
    font-weight: bold;
    margin: 10px 0;
  }
  .or-login {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    display: flex;
    margin: 10px auto 25px auto;
  }
  .login-sosmed {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    div {
      margin: 0 5px;
      cursor: pointer;
      &.sosmed-google {
        img {
          background: #004c94;
          padding: 7px;
          border-radius: 50%;
        }
      }
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .form-login {
    > h1 {
      font-size: 20px;
    }
    > .input-form {
      > label {
        font-size: 18px;
      }
    }
    .forgot-passwd {
      font-size: 16px;
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .form-login {
    > h1 {
      font-size: 19px;
    }
    > .input-form {
      > label {
        font-size: 17px;
      }
    }
    .forgot-passwd {
      font-size: 15px;
    }
  }
}
@media (max-width: 481px) {
  .form-login {
    > h1 {
      font-size: 19px;
    }
    > .input-form {
      > label {
        font-size: 17px;
      }
    }
    .forgot-passwd {
      font-size: 15px;
    }
  }
}
</style>