<template>
  <div class="content-list-kategori">
    <h3 class="title-lk">List Kategori</h3>
    <div class="top-table-mbx">
      <div class="left">
        <!-- <div class="input-search-tbl-mbx">
          <input type="text" class placeholder="Search here..." />
          <button class="btn btn-primary">
            <i class="fa fa-search"></i>
          </button>
        </div> -->
      </div>
      <div class="right">
        <button
          @click="addKategori()"
          class="btn btn-primary btn-action-top-tbl-mbx"
        >Add Kategori</button>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Title
                  <i @click="tableMbx().search('title')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.title" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.title"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <!-- <th>
                <div class="th-tbl-mbx">
                  Slug
                  <span
                    @click="tableMbx().sort('list-slug')"
                    id="list-slug"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th> -->
              <th>
                <div class="th-tbl-mbx">
                  Status
                  <i @click="tableMbx().search('status')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.status" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.status"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jumlah Kelas
                  <i @click="tableMbx().search('jmlKelas')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.jmlKelas" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataTable.colSearchValue.jmlKelas"
                    @keyup.enter="tableMbx().filterSearch()"
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">Sub Kategori</div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr 
              v-for="(list, index) in dataTable.list" 
              v-bind:key="index"
              v-bind:hidden="!(index >= dataTable.minIndex && index <= dataTable.maxIndex)"
            >
              <td style="max-width: 80px;width: 100px;">
                <div class="btn-action-tbl-mbx">
                  <!-- <button class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>-->
                  <button @click="editKategori(list)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.title }}</td>
              <!-- <td>{{ list.slug }}</td> -->
              <td>{{ list.status }}</td>
              <td>{{ list.jmlKelas }}</td>
              <td>
                <ul>
                  <li
                    v-for="(child, index) in list.child"
                    v-bind:key="index"
                  >{{ child.title }}</li>
                </ul>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Kategori Kosong, Silahkan Tambah Kategori.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-list-kategori" :width="'70%'" :height="'70%'" :clickToClose="false">
      <ExecutorListKategori :data="executorKategoriData" @init="init()" />
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import ExecutorListKategori from "./ExecutorListKategori.vue";

export default {
  name: "ListKategori",
  components: {
    ExecutorListKategori
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        jenis: "",
        status: "",
        mentor: ""
      },
      dataTable: {
        list: [],
        listTemp: [],
        colSearch: {
          title: false,
          slug: false,
          status: false,
          jmlKelas: false
        },
        colSearchValue: {
          title: '',
          slug: '',
          status: '',
          jmlKelas: ''
        },
        colSort: {
          title: false,
          slug: false,
          status: false,
          jmlKelas: false
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
        perPage: 10,
        totalData: 0,
        maxIndex: 0,
        minIndex: 0,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Active',
              value: 'active'
            },
          ]
        }
      },
      executorKategoriData: {
        section: "",
        data: ""
      }
    };
  },
  methods: {
    init() {
      // id: 1
      // jmlKelas: 0
      // slug: "advertising"
      // status: "Publish"
      // title: "Advertising"
      // child: (3) [{…}, {…}, {…}]

      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&status=${a.status}&mentor=${a.mentor}`;
      axios
        .get(this.$root.listKategoryAdmin(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;
          this.dataTable.list = [];
          this.dataTable.list = result.map((e) => {
            return e;
          });
          this.dataTable.listTemp = result.map((e) => {
            return e;
          });

          this.dataTable.totalData = data.count;
          this.tableMbx().counterPageTable(data.count);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    perPage() {
      $(".list-pp").slideToggle();
    },
    addKategori() {
      let a = this.executorKategoriData;
      a.section = "add";

      this.$modal.show("executor-list-kategori");
    },
    editKategori(data) {
      let a = this.executorKategoriData;
      a.section = "edit";
      a.data = data;

      console.log("masuk edit ", a);

      this.$modal.show("executor-list-kategori");
    },
    tableMbx() {
			let a = this.dataTable;
			let b = this.dataBody;
      let vm = this;
      return {
        search: (field) => {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
            this.dataTable.colSearchValue[field] = "";
            let result = this.tableMbx().filterCache();
            this.dataTable.list = result;
            this.tableMbx().counterPageTable(result.length);
          }
        },
        filterSearch: () => {
          let result = this.tableMbx().filterCache();
          console.log('result ', result);
          this.dataTable.list = result;
          this.tableMbx().counterPageTable(result.length);
        },
        filterCache: () => {
          let result = this.dataTable.listTemp;
          let col = [
            'title',
            'status',
            'jmlKelas',
          ];
          for (let j = 0; j < col.length; j++) {
            if (this.dataTable.colSearchValue[col[j]] != '') {
              result = result.filter(search => {
                var name = String(search[col[j]]).toUpperCase();
                var findme = new RegExp(
                  String(this.dataTable.colSearchValue[col[j]]).toUpperCase(),
                  'g',
                );
                return name.match(findme);
              });
            }
          }
          return result;
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++;
          vm.tableMbx().changeShowPage();
        },
        backTable() {
					a.page.now--;
          b.page--;
          vm.tableMbx().changeShowPage();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
						a.page.now = val;
						b.page = val;
						vm.init();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
          a.perPage = page;
          vm.tableMbx().counterPageTable(vm.dataTable.list.length);
          vm.tableMbx().perPage();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            }
          }
        },
        counterPageTable: (length) => {
          let m = Math.ceil(length / a.perPage);
          if (m <= 1) {
            m = 1;
          }
          let lengthData = Array(m)
              .fill(0, 0)
              .map((x, i) => i).length
            a.page.total = lengthData;
            console.log('lengthData ', lengthData, m);
          vm.tableMbx().changeShowPage();
        },
        changeShowPage: () => {
          let pageNow = vm.dataTable.page.now;
          let perPage = vm.dataTable.perPage;
          this.dataTable.minIndex = ((pageNow*perPage)-perPage);
          this.dataTable.maxIndex = ((pageNow*perPage)-1);
        }
      };
    },
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-kategori {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    // width: 110%;
  }
}
</style>