<template>
  <div class="content-affiliate-user">
    <div class="content-eu" v-if="!referral">
      <h1 class="title-ja">Jadi Affiliate</h1>
      <div class="content-ja">
        <p
          class="p-ja"
        >Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
      </div>
      <a class="btn-jadi-mentor" @click="jadiAffiliate()">
        <button class="btn">Selengkapnya</button>
      </a>

      <modal name="daftar-affiliate" :width="'50%'" :height="'70%'" :clickToClose="false">
        <DaftarAffiliate @init="init()" />
      </modal>
    </div>
    <div class="content-eu" v-if="referral">
      <div class="filter-affiliate">
        <div class="schedule-kelas">
          <h5 class="t-schedule">Filter Affiliate</h5>
          <div class>
            <div id="dateAffiliate" class="date-kelas" v-on:click="openFilterAffiliate()">
              <div class="choose-date">
                <!-- <img src="/icon/calendar.svg" /> -->
                <p class="text-date">{{ chooseFilterAffiliate }}</p>
              </div>
              <i class="lnr lnr-chevron-down" id="activeFilterAffiliate"></i>
            </div>
            <ul class="list-filter-affiliate">
              <li
                class="date-kl"
                v-for="(list, idx) in dateListAffiliate"
                v-bind:key="idx"
                v-bind:readonly="!list.available"
                v-on:click="chooseDK(list)"
              >
                <input
                  class="radio-kl radio-mbx"
                  type="radio"
                  name="date-list"
                  v-bind:id="'radio-kl-' + idx"
                  v-bind:disabled="!list.available"
                  v-bind:checked="list.title == chooseFilterAffiliate"
                />
                <label v-bind:for="'radio-kl-' + idx" class="text-date">{{ list.title }}</label>
                <!-- <p class="status-kl-available" v-if="list.available">Available</p>
                <p class="status-kl-unavailable" v-else>Unvailable</p>-->
              </li>
            </ul>
          </div>
        </div>
      </div>

      <template v-if="sectionFilterAffiliate == 'list-member'">
        <ListMember />
      </template>
      <template v-if="sectionFilterAffiliate == 'list-komisi'">
        <ListKomisi />
      </template>
      <template v-if="sectionFilterAffiliate == 'list-withdrawal'">
        <ListWithdrawal />
      </template>
      <template v-if="sectionFilterAffiliate == 'marketing-kit'">
        <MarketingKit />
      </template>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

import DaftarAffiliate from "./DaftarAffiliate";
import ListMember from "./ListMember";
import ListKomisi from "./ListKomisi";
import ListWithdrawal from "./ListWithdrawal";
import MarketingKit from "./MarketingKit";

export default {
  name: "ContentAffiliate",
  components: {
    DaftarAffiliate,
    ListMember,
    ListKomisi,
    ListWithdrawal,
    MarketingKit
  },
  data() {
    return {
      referral: "",
      chooseFilterAffiliate: "List Member",
      sectionFilterAffiliate: "list-member",
      dateListAffiliate: [
        {
          title: "List Member",
          slug: "list-member",
          available: true
        },
        {
          title: "List Komisi",
          slug: "list-komisi",
          available: true
        },
        {
          title: "List Withdrawal",
          slug: "list-withdrawal",
          available: true
        },
        {
          title: "Marketing Kit",
          slug: "marketing-kit",
          available: true
        }
      ]
    };
  },
  methods: {
    init() {
      let getLoginData = window.$cookies.get("loginData");
      let kodeReferral = getLoginData.result.referral;
      this.referral = kodeReferral == null ? false : true;
    },
    openFilterAffiliate() {
      $("#dateAffiliate").toggleClass("active");
      $("#activeFilterAffiliate").toggleClass(
        "lnr-chevron-down lnr-chevron-up"
      );
      $(".list-filter-affiliate").slideToggle();
    },
    chooseDK(data) {
      this.chooseFilterAffiliate = data.title;
      this.sectionFilterAffiliate = data.slug;
    },
    jadiAffiliate() {
      this.$modal.show("daftar-affiliate");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-affiliate-user {
  padding: 0 0 30px 0;
  font-family: "Montserrat";
  .content-eu {
    > .title-ja {
      font-style: normal;
      font-weight: normal;
      font-size: 36px;
      line-height: 44px;
      color: #000000;
    }
    > .content-ja {
      background: #cbcdce;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 8px;
      padding: 25px;
      text-align: left;
      > .p-ja {
        color: #000000;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
      }
    }
    > .btn-jadi-mentor {
      margin: 15px auto 0 auto;
      display: flex;
      &:hover {
        text-decoration: none;
      }
      > button {
        background: #003461;
        color: #ffffff;
        border: 1px solid #003461;
        padding: 5px 35px;
        &:hover {
          border: 1px solid #003461;
          background: #ffffff;
          color: #003461;
        }
      }
    }
  }
}
.schedule-kelas {
  display: flex;
  > .t-schedule {
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 34px;
    color: #000000;
    margin: 0%;
  }
  .date-kelas {
    display: flex;
    margin: 0 20px;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 7px;
    cursor: pointer;
    width: 240px;
    &.active {
      border-radius: 7px 7px 0 0;
    }
    > .choose-date {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      > p {
        margin: 3px 10px 0 20px;
      }
    }
    > i {
      margin: auto 0 auto auto;
      padding: 0 0 0 10px;
    }
  }
  .list-filter-affiliate {
    padding: 5px 10px;
    background: #ccc;
    color: #ffffff;
    margin: 0 20px;
    border-radius: 0 0 7px 7px;
    display: none;
    position: absolute;
    width: 240px;
    > .date-kl {
      list-style: none;
      display: flex;
      align-items: center;
      padding: 3px 0;
      &[readonly="readonly"] {
        opacity: 0.5;
        cursor: not-allowed;
        > label {
          cursor: not-allowed;
        }
      }
      > .text-date {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 15px;
        width: 165px;
        color: #ffffff;
        margin: 7px 0;
        padding: 0px 0 0 39px;
      }
      > p {
        color: #ffffff;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        width: 95px;
        padding: 3px;
        text-align: center;
        margin: 0px 0 0 auto;
        border-radius: 20px;
        &.status-kl-available {
          background: #219653;
        }
        &.status-kl-unavailable {
          background: #e40000;
        }
      }
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .schedule-kelas {
    > .t-schedule {
      font-size: 23px;
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .schedule-kelas {
    > .t-schedule {
      font-size: 20px;
    }
  }
}
@media (max-width: 481px) {
  .schedule-kelas {
    flex-direction: column;
    text-align: left;
    > .t-schedule {
      font-size: 20px;
    }
  }
}
</style>
