<template>
  <section class="content-detail-kelas">
    <TitleKelas :data="dataDetailKelas" />
    <DeskripsiKelas :data="dataDetailKelas" />
    <TentangMentor :data="dataDetailKelas" />
    <KurikulumKelas :data="dataDetailKelas" />
    <CaraBelajar :data="dataDetailKelas" />
    <UlasanKelas :data="dataDetailKelas" />
    <BayarSekarang :data="dataDetailKelas" />
  </section>
</template>

<script>
import axios from "axios";

import TitleKelas from "./TitleKelas";
import DeskripsiKelas from "./DeskripsiKelas";
import TentangMentor from "./TentangMentor";
import CaraBelajar from "./CaraBelajar";
import UlasanKelas from "./UlasanKelas";
import KurikulumKelas from "./KurikulumKelas";
import BayarSekarang from "./BayarSekarang";

export default {
  name: "ContentDetailKelas",
  components: {
    TitleKelas,
    DeskripsiKelas,
    TentangMentor,
    CaraBelajar,
    UlasanKelas,
    KurikulumKelas,
    BayarSekarang
  },
  data() {
    return {
      slug: this.$route.params.slug,
      dataDetailKelas: {},
    };
  },
  methods: {
    init() {
      let data = {
        result: {
          // for title kelas
          name: "Kelas Coding Seru",
          banner: "5_6337001753057165546-2.pdf",
          about: "mari kita terus semangat belajar",
          note: "Kuy gaskeun",
          reviews: { count: 0, rating: null },
          price: 70000000,
          promo: 90000000,
          discount: -20000000,
          jenis: "Kelas WhatsApp",
          schedules: [
            {
              id: 8,
              name: "yeah",
              slot: 90,
              startDate: "2020-06-17 00:00:00",
              endDate: "2020-06-18 00:00:00"
            }
          ],

          mentor: { name: "Change Profile", about: "Yakin Sukses" },

          curriculums: [
            {
              id: 5,
              topics: "gaskeun",
              materi: "hahaha",
              description: "hayu wae lah",
              files: "5_6337001753057165546-2.pdf"
            }
          ],


          uuid: "79f59346-ebd2-4043-8669-998dd3e8a4ff",
          slug: "kelas-coding-seru",
        }
      };
      data;

      this.getDetailKelas();
    },
    getDetailKelas() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.detailKelasPage() + `/${this.slug}`)
        .then(res => {
          let data = res.data;
          let result = data.result;
          this.dataDetailKelas = result;
          this.$store.state.dataDetailKelas = result;

          console.log("res ", result);
          setTimeout(() => {
            this.$root.loadingData().hide();
          }, 2000)
        })
        .catch(err => {
          console.log("err ", err);
          this.$root.loadingData().hide();
        });
    }
  },
  mounted() {
    this.init();
  }
};
</script>