<template>
  <div class="content-detail-kelas modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Detail Kelas</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr class="line-modal" />

    <div class="content-dk">
      <div class="show-image">
        <img 
          id="showImage" 
          v-bind:src="$root.getPhotoUser() + `/${dataDetailKelas.banner}`"
          alt 
        />
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Name Kelas :</label>
        <span class="span-inp">{{ dataDetailKelas.name }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">About :</label>
        <span class="span-inp">{{ dataDetailKelas.about }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Note :</label>
        <span class="span-inp">{{ dataDetailKelas.note }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Jenis :</label>
        <span class="span-inp">{{ dataDetailKelas.jenis }}</span>
      </div>
      <!-- <div class="form-input-mbx inline">
        <label class="title">Mentor Kelas :</label>
        <span class="span-inp">
          {{ dataDetailKelas.mentor.name }} |
          {{ dataDetailKelas.mentor.about == null ? "-" : dataDetailKelas.mentor.about }}
        </span>
      </div> -->
      <div class="form-input-mbx inline">
        <label class="title">Price :</label>
        <span class="span-inp">Rp.{{ dataDetailKelas.price }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Promo :</label>
        <span class="span-inp">Rp.{{ dataDetailKelas.promo }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Kategori :</label>
        <span class="span-inp">{{ categories }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Sub Kategori :</label>
        <span class="span-inp">{{ subCategories }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label class="title">Status :</label>
        <span class="span-inp">{{ dataDetailKelas.status }}</span>
      </div>
      <!-- <div class="form-input-mbx inline">
        <label class="title">Reviews :</label>
        <span class="span-inp">
          {{ dataDetailKelas.reviews.count }} |
          {{ dataDetailKelas.reviews.rating == null ? "-" : dataDetailKelas.reviews.rating }}
        </span>
      </div> -->
      <div class="form-input-mbx inline">
        <label class="title">Jadwal :</label>
        <span class="span-inp">
          <ol class="list-jadwal-kelas">
            <li v-for="(j, idx) in dataDetailKelas.Courses_Schedules" v-bind:key="idx">
              <p>
                Name :
                <span>{{ j.name }}</span>
              </p>
							<p>
                Slot :
                <span>{{ j.slot }}</span>
              </p>
              <p>
                Start date :
                <span>{{ j.startDate }}</span>
              </p>
              <p>
                End date :
                <span>{{ j.endDate }}</span>
              </p>
            </li>
          </ol>
        </span>
      </div>
			<div class="form-input-mbx inline">
        <label class="title">Kurikulum :</label>
        <span class="span-inp">
          <ol class="list-jadwal-kelas">
            <li v-for="(j, idx) in dataDetailKelas.Courses_Curriculums" v-bind:key="idx">
              <p>
                Topik :
                <span>{{ j.topics }}</span>
              </p>
							<p>
                Materi :
                <span>{{ j.materi }}</span>
              </p>
              <p>
                Deskripsi :
                <span>{{ j.description }}</span>
              </p>
              <p>
                Files :
                <span>{{ j.files }}</span>
              </p>
            </li>
          </ol>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "DetailKelas",
  components: {},
  data() {
    return {
      uuidKelas: "",
      dataDetailKelas: {
        about: '' ,
        banner: '' ,
        categories: '' ,
        curriculums: '' ,
        discount: '' ,
        id: '' ,
        jenis: '' ,
        mentor: '' ,
        name: '' ,
        note: '' ,
        price: '' ,
        promo: '' ,
        reviews: '' ,
        schedules: '' ,
        slug: '' ,
        status: '' ,
        subCategories: '' ,
      },
      categories: "",
      subCategories: ""
    };
  },
  methods: {
    init() {

      this.getDetailKelas();
    },
    getDetailKelas() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listKelasMentor() + `/${this.uuidKelas}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;
          this.dataDetailKelas = result;
          this.categories = result.Categories == null ? "-" : result.Categories.title;
          this.subCategories = result.Sub_Categories == null ? "-" : result.Sub_Categories.title;
          console.log("data detail kelas ", this.dataDetailKelas);
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.$root.notification("danger", "Error", "Check Error");
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    closeModal() {
      this.$modal.hide("detail-kelas");
    }
  },
  props: ["data"],
  mounted() {
    let dataParams = this.$props.data;
    this.uuidKelas = dataParams.uuid;
    console.log("data params ", dataParams);
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-detail-kelas {
  padding: 15px;
  font-family: "Montserrat";
  height: 100%;
	overflow-y: auto;
  > .content-dk {
    .show-image {
      display: flex;
      justify-content: center;
      margin: 15px 0;
      > img {
        width: 700px;
        height: 580px;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      }
    }
  }
}
.list-jadwal-kelas {
  margin: 0%;
  padding: 0 0 0 30px;
  > li {
    margin: 3px 0;
    text-align: left;
    > p {
      margin: 0%;
      font-weight: 700;
      > span {
        font-weight: 500;
      }
    }
  }
}
</style>