<template>
  <div class="content-list-withdraw">
    <h3 class="title-lk">List Withdraw</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="init()"
          />
          <button 
            class="btn btn-primary"
            @click="init()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button @click="submitWithdrawal()" class="btn btn-primary btn-action-top-tbl-mbx">Add Withdraw</button>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <!-- <th>
                <div class="th-tbl-mbx">Action</div>
              </th> -->
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Tanggal
                  <span
                    @click="tableMbx().sort('list-createdAt')"
                    id="list-createdAt"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  User
                  <span
                    @click="tableMbx().sort('list-user')"
                    id="list-user"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jumlah
                  <span
                    @click="tableMbx().sort('list-jumlah')"
                    id="list-jumlah"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Bank
                  <span
                    @click="tableMbx().sort('list-bank')"
                    id="list-bank"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  No Rekening
                  <span
                    @click="tableMbx().sort('list-rekening')"
                    id="list-rekening"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Note
                  <span
                    @click="tableMbx().sort('list-note')"
                    id="list-note"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Status
                  <span
                    @click="tableMbx().sort('list-status')"
                    id="list-status"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <!-- <td style="max-width: 100px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailWithdraw(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button @click="editDataWithdraw(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>
                </div>
              </td> -->
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.createdAt }}</td>
              <td>{{ list.User == null ? "-" : list.User.name }}</td>
              <td>Rp.{{ list.jumlah == null ? "-" : list.jumlah }}</td>
              <td>{{ list.bank == null ? "-" : list.bank }}</td>
              <td>{{ list.rekening == null ? "-" : list.rekening }}</td>
              <td>{{ list.note == null ? "-" : list.note }}</td>
              <td>
                <span 
                  style="font-weight:700;"
                  class="text-danger"
                  v-if="list.status == 'Reject'"
                >{{ list.status }}</span>
                <span 
                  style="font-weight:700;"
                  class="text-warning"
                  v-if="list.status == 'Unpaid'"
                >{{ list.status }}</span>
                <span 
                  style="font-weight:700;"
                  class="text-success"
                  v-if="list.status == 'Paid'"
                >{{ list.status }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Withdraw Kosong, Silahkan Tambah Withdraw.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>

    <modal name="executor-withdrawal" :width="'70%'" :height="'70%'" :clickToClose="false">
      <ExecutorWithdrawal @init="init()" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import ExecutorWithdrawal from './ExecutorWithdrawal';

export default {
  name: "ListWithdraw",
  components: {
    ExecutorWithdrawal
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        search: "",
      },
      editWithdraw: {
        section: "detail",
        uuidDetailWithdraw: 0
      },
      dataTable: {
        list: [],
        colSearch: {
          createdAt: false,
          jumlah: false,
          bank: false,
          rekening: false,
          note: false,
          status: false,
        },
        colSort: {
          createdAt: false,
          jumlah: false,
          bank: false,
          rekening: false,
          note: false,
          status: false,
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: "Active",
              value: "active"
            },
            {
              text: "Suspend",
              value: "suspend"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
			// Tanggal, 
			// Jumlah, 
			// Bank, 
			// NoRek, 
			// Note, 
			// Status
			
			// "id": 1,
			// "bank": "BNI",
			// "rekening": "7031234567",
			// "pemilik": "Nasrullah",
			// "jumlah": 26800,
			// "note": "Bismillah",
			// "status": "Unpaid",
			// "createdAt": "2020-06-06 11:15:52",
			// "User": {
			// 		"name": "Nasrullah"
			// }

      this.getListWithdraw();
    },
    getListWithdraw() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&search=${a.search}`;
      axios
        .get(this.$root.listWithdrawalAffiliate() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: val => {
              b.status = val;
              this.init();
            }
          };
        }
      };
    },
    submitWithdrawal() {
      this.$modal.show("executor-withdrawal");
    },
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-withdraw {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 100%;
  }
}
</style>