<template>
  <div class="executor-sales modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Sales</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Sales</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-executor-lk">
      <div class="form-input-mbx">
        <label for="name-sales">Nama Sales</label>
        <input
          type="text"
          class="form-control"
          name="name-sales"
          placeholder="Nama Sales"
          v-model="dataExecutor.name"
        />
        <span v-if="!cannotNull.name" style="color: red;">This field is required.</span>
      </div>
			<div class="form-input-mbx">
        <label for="email-register" class="title">Email</label>
        <input
          type="email"
          name="email-register"
          id="idEmailRegister"
          class="form-control"
          placeholder="yourname@domain.com"
          v-model="dataExecutor.email"
          @keyup="checkEF()"
          
        />
        <span v-if="!cannotNull.email" style="color: red;">This field is required.</span>
        <span v-if="!formatEmail" style="color: red;">Invalid email format.</span>
      </div>
      <div class="form-input-mbx">
        <label for="password-login" class="title">Password</label>
        <input
          type="password"
          name="password-register"
          id="idPasswordSales"
          class="form-control"
          placeholder="Password"
          v-model="dataExecutor.password"
        />
          <!-- @keyup="checkPass()" -->
        <span
          class="sh-password"
          id="shPassSales"
          v-on:click="$root.showPassword('shPassSales', 'idPasswordSales')"
        >
          <i class="fa fa-eye"></i>
        </span>
        <!-- <span v-if="!cannotNull.password" style="color: red;">This field is required.</span>
        <span v-if="!minLength.password" style="color: red;">Password min length 6 characters.</span> -->
      </div>
      <div class="form-input-mbx">
        <label for="phone-number" class="title">Phone Number</label>
        <input
          type="text"
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          name="phone-number"
          id="idNomorWA"
          class="form-control"
          placeholder="08xxxxxxxxxxx"
          v-model="dataExecutor.phone"
          maxlength="16"
        />
        <span v-if="!cannotNull.phone" style="color: red;">This field is required.</span>
      </div>
			<div class="form-input-mbx">
        <label for="fee" class="title">Fee (%)</label>
        <input
          type="text"
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          name="fee"
          class="form-control"
          placeholder="Fee"
          v-model="dataExecutor.fee"
        />
        <span v-if="!cannotNull.phone" style="color: red;">This field is required.</span>
      </div>

    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelSales()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addSales()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editSales()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ExecutorListSales",
  components: {},
  data() {
    return {
			section: "",
			uuid: '',
      dataExecutor: {
        name: "",
        email: "",
        password: "",
        phone: "",
        fee: "",
      },
      cannotNull: {
        name: true,
        email: true,
        password: true,
        phone: true,
        fee: true,
			},
			formatEmail: true,
    };
  },
  props: ["data"],
  methods: {
    init() {
      let params = this.$props.data;
      this.section = params.section;
      if (params.section == "edit") {
        this.uuid = params.uuid;
				this.getDetailSales();
      }
			console.log("this params ", params);
			
		},
		getDetailSales() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&roles=${a.roles}&search=${a.search}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listSalesAffiliate() + `/${this.uuid}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
					console.log("res ", res);
					let data = res.data;
					let result = data.result;

					let a = this.dataExecutor;
					a.name = result.name;
					a.email = result.email;
					a.phone = result.phone;
					a.fee = result.SalesCommission.persen;

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
		},
		checkEF() {
      let email = this.dataExecutor.email;
      var re = this.$root.checkValidateEmail(email);
      re == false ? (this.formatEmail = false) : (this.formatEmail = true);
    },
    validatePostSales() {
      let a = this.dataExecutor;
      let b = this.cannotNull;

      a.title == "" ? (b.title = false) : (b.title = true);
      a.status == "" ? (b.status = false) : (b.status = true);
      a.child.length == 0 ? (b.child = false) : (b.child = true);

      if (a.title !== "" && a.status !== "" && a.child.length !== 0) {
        return true;
      } else {
        return false;
      }
    },
    addSales() {
      let a = this.dataExecutor;
			let body = a;
			this.postSales(body);
    },
    postSales(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listSalesAffiliate(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostSales().success(
            `Sales ${body.name} Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostSales().failed(`${resErr.data.message}`);
        });
    },
    afterPostSales() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelSales() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit sales ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    editSales() {
      let a = this.dataExecutor;
			let body = a;
			this.putSales(body);
    },
    putSales(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;

      axios
        .put(vm.$root.listSalesAffiliate() + `/${this.uuid}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostSales().success(
            `Sales ${body.name} Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostSales().failed(`${resErr.data.message}`);
        });
    },
    closeModal() {
      this.$modal.hide('executor-sales');
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-sales {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  > .title-lk {
    text-align: left;
    font-size: 20px;
    font-weight: 600;
  }
  .list-sub-sales {
    margin: 0 0 5px 0;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    > span {
      background: #cccccc;
      padding: 5px;
      border-radius: 5px;
      margin: 3px;
      > i {
        color: #000000;
        cursor: pointer;
        &:hover {
          color: #ee0f0f;
        }
      }
    }
  }
  .list-status-sales {
    // position: absolute;
    background: #ffffff;
    padding: 0%;
    display: none;
    width: 50%;
    left: 0;
    margin: 3px 0 0 0;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
    border-radius: 0 0 10px 10px;
    > li {
      color: #000000;
      list-style: none;
      padding: 5px 10px;
      cursor: pointer;
      &:nth-child(even) {
        background: #cccccc;
      }
      &:hover {
        background: #999898;
      }
    }
  }
  .btn-add-cancel {
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    bottom: 0;

    background: #ffffff;
    width: 100%;
    padding: 10px;
    > button {
      width: 49%;
    }
  }
}
</style>