<template>
  <div class="content-executor-transaksi modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'accept'" class="title-lk">Accept Transaksi</h3>
      <h3 v-if="section == 'detail'" class="title-lk">Detail Transaksi</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr class="line-modal" />

    <div class="content-et">
      <div class="data-detail-user">
        <label for>
          Invoice
          <span>:</span>
        </label>
        <span class="bold">{{ dataTransaksi.invoice == null ? '-' : dataTransaksi.invoice }}</span>
      </div>
			<div class="data-detail-user">
        <label for>
          Tanggal
          <span>:</span>
        </label>
        <span>{{ dataTransaksi.createdAt == null ? '-' : dataTransaksi.createdAt }}</span>
      </div>
			<div class="data-detail-user">
        <label for>
          Member
          <span>:</span>
        </label>
        <span class="detail-data-et">
					<p style="margin: 0;">Nama : <span style="font-weight: 700;">{{ userData.name == null ? '-' : userData.name }}</span></p>
					<p style="margin: 0;">Email : <span style="font-weight: 700;">{{ userData.email == null ? '-' : userData.email }}</span></p>
					<p style="margin: 0;">Phone : <span style="font-weight: 700;">{{ userData.phone == null ? '-' : userData.phone }}</span></p>
				</span>
      </div>
			<div class="data-detail-user">
        <label for>
          Produk
          <span>:</span>
        </label>
        <span>
          <ol class="list-kl">
						<li v-for="(k, idx) in dataTransaksi.Transaction_Details" v-bind:key="idx">
							<p>Name : <span>{{ k.Course.name }}</span></p>
							<p>Price : <span>Rp.{{ k.price }}</span></p>
							<p>Promo : <span>Rp.{{ k.promo }}</span></p>
							<p>Diskon : <span>{{ k.diskon }}</span></p>
							<p>Total : <span>Rp.{{ k.total }}</span></p>
						</li>
					</ol>
        </span>
      </div>
			<div class="data-detail-user">
        <label for>
          Harga
          <span>:</span>
        </label>
				<span class="detail-data-et">
					<p>Voucher : <span>{{ dataTransaksi.voucher == (null || "") ? '-' : dataTransaksi.voucher }}</span></p>
					<p>SubTotal : <span>Rp.{{ dataTransaksi.subtotal == null || "" ? '-' : dataTransaksi.subtotal }}</span></p>
					<p>PPN : <span>{{ dataTransaksi.ppn == null || "" ? '-' : dataTransaksi.ppn }}</span></p>
					<p>Kode Unik : <span>{{ dataTransaksi.kodeUnik == null || "" ? '-' : dataTransaksi.kodeUnik }}</span></p>
					<p>Total : <span>Rp.{{ dataTransaksi.total == null || "" ? '-' : dataTransaksi.total }}</span></p>
				</span>
      </div>
			<div class="data-detail-user">
        <label for>
          Payment
          <span>:</span>
        </label>
				<span class="detail-data-et">
					<p>Type : <span>{{ dataTransaksi.payment == null || "" ? '-' : dataTransaksi.payment }}</span></p>
					<p>Rek Number : <span>{{ dataTransaksi.rekNumber == null || "" ? '-' : dataTransaksi.rekNumber }}</span></p>
				</span>
      </div>
			<div class="data-detail-user">
        <label for>
          Status
          <span>:</span>
        </label>
        <span class="bold">
					{{ dataTransaksi.status == null ? '-' : dataTransaksi.status }}
				</span>
      </div>


    </div>
    <div 
      class="content-et"
      v-if="dataTransaksi.Confirm_Transaction !== null"
    >
      <h5 class="text-center">Bukti Transfer</h5>
      <div class="data-detail-user">
        <label for>
          Tanggal Transfer
          <span>:</span>
        </label>
        <span class="bold">{{ konfirmasiTransaksi.tglTransfer }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Jumlah Transfer
          <span>:</span>
        </label>
        <span class="bold">Rp.{{ konfirmasiTransaksi.jmlTransfer }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Rekening Tujuan
          <span>:</span>
        </label>
        <span class="bold">{{ konfirmasiTransaksi.rekTujuan }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Nama Pengirim
          <span>:</span>
        </label>
        <span class="bold">{{ konfirmasiTransaksi.namaPengirim }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Rekening Pengirim
          <span>:</span>
        </label>
        <span class="bold">{{ konfirmasiTransaksi.rekPengirim }}</span>
      </div>
      <div class="data-detail-user">
        <label for>
          Bukti Transfer
          <span>:</span>
        </label>
        <span class="img-bukti-transfer">
          <img 
            v-bind:src="$root.filesImg() + '/' + konfirmasiTransaksi.buktiTransfer" alt=""
          >
        </span>
      </div>
    </div>

		<div class="btn-add-cancel">
      <button v-if="section == 'accept'" @click="rejectTransaksi()" class="btn btn-danger">Reject</button>
      <button v-if="section == 'accept'" @click="acceptTransaksi()" class="btn btn-success">Accept</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import * as moment from 'moment';

export default {
  name: "ExecutorTransaksi",
  components: {},
  data() {
    return {
			section: '',
      invoice: "",
			dataTransaksi: {},
      userData: {},
      konfirmasiTransaksi: {
        buktiTransfer: "",
        jmlTransfer: "",
        namaPengirim: "",
        rekPengirim: "",
        rekTujuan: "",
        status: "",
        tglTransfer: ""
      }
    };
  },
  props: ["data", "user"],
  methods: {
    init() {
			this.getDetailTransaksi();
			
			// ini untuk detail
			// =======================
			// Invoice, 
			// Tanggal, 
			// Member (Nama, Email, PhoneNumber), 
			// Produk (Title, HargaCoret, HargaJual), 
			// Harga (Kode Voucher, SubTotal, PPN, KodeUnik, Diskon & Total) 
			// Payment (Type & RekNumber), 
			// Status
    },
    getDetailTransaksi() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listTransaksiAdmin() + `/${this.invoice}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          this.dataTransaksi = data;
          this.dataTransaksi.createdAt = moment(data.createdAt).format('DD - MMM - YYYY HH:mm:ss');
          if ( data.Confirm_Transaction !== null ) {
            this.konfirmasiTransaksi = data.Confirm_Transaction;
            this.konfirmasiTransaksi.tglTransfer = moment(data.Confirm_Transaction.tglTransfer).format('DD - MMM - YYYY HH:mm:ss');
          }
          this.$emit('init');
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.afterPostTransaksi().failed(`${resErr.data.message}`);
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    afterPostTransaksi() {
      return {
        success: msg => {
          this.$modal.show("dialog", {
            title: "Success",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  this.closeModal();
                  this.$emit('init');
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed: msg => {
          this.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  this.closeModal();
                  this.$emit('init');
                  this.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    closeModal() {
      this.$modal.hide("executor-transaksi");
		},
		postTransaksi(body) {
			this.$root.loadingData().show();
			let vm = this;
			let AuthSth = this.$root.authorization();

      axios
        .post(this.$root.listTransaksiAdmin(), body, {
					headers: { Authorization: AuthSth }
				})
        .then(res => {
          console.log("res ", res);
					vm.afterPostTransaksi().success(`Status ${body.invoice} berhasil di perbarui.`);
          vm.$root.loadingData().hide();
        })
        .catch(err => {
					let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
						this.$root.sessionTimeUp();
          } else if ( resErr.status == 400 ) {
						vm.afterPostTransaksi().failed(resErr.data.message);
					} else {
						vm.$root.notification("danger", "Error", `${resErr.data.errors}`);
					}

          console.log("error ", resErr);
					this.$root.loadingData().hide();
        });
		},
		rejectTransaksi() {
			let body = {
				invoice: this.invoice,
				status: 'Reject'
			};
			this.postTransaksi(body);
		},
		acceptTransaksi() {
			let body = {
				invoice: this.invoice,
				status: 'Accept'
			};
			this.postTransaksi(body);
		}
  },
  mounted() {
		let dataParams = this.$props.data;
		let userParams = this.$props.user;
		this.section = dataParams.section;
		this.invoice = dataParams.invoice;
		this.userData = userParams;
    console.log("data params ", this.$props);
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-executor-transaksi {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  padding: 15px 15px 70px 15px;
  overflow-y: auto;
  height: 100%;
  .content-et {
    .data-detail-user {
      font-size: 1em;
      text-align: left;
			margin: 3px 0;
      display: flex;
      > label {
        font-weight: 600;
        width: 220px;
        max-width: 250px;
        > span {
          float: right;
        }
      }
      > span {
        display: inline-block;
        margin: 0 0 0 15px;
				&.bold {
					font-weight: 700;
				}
        > input,
        > textarea {
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid #797878;
          padding: 3px 7px;
          width: 150%;
        }
        &.img-bukti-transfer {
          display: flex;
          width: 80%;
          > img {
            width: 70%;
            max-width: 70%;
            height: 100%;
            max-height: 100%;
          }
        }
      }
    }
		.detail-data-et {
      display: flex;
      width: 40%;
			> p {
				margin: 0%;
				display: flex;
        justify-content: space-between;
				> span {
					font-weight: 700;
					margin: 0 0 0 15px;
				}
			}
		}
    .list-kl {
			padding: 0%;
			margin: 0%;
			> li {
				list-style: none;
				margin: 3px;
				border-bottom: 1px solid #575555;
				padding: 5px;
				> p {
					margin: 0%;
					> span {
						font-weight: 500;
					}
				}
			}
		}
  }
}
</style>