<template>
  <div class="tabs-siswa">
    <div class="content-tabs-siswa">
      <ul class="tabs">
        <li
          v-for="tab in dataTabSiswa"
          v-bind:key="tab.id"
          v-bind:class="{[ tab.sub_id ] : tab.sub_id}"
          v-bind:active="sectionTab == tab.sub_id"
          v-on:click="changeTab($event, tab.sub_id)"
        >
          <a>{{ tab.field }}</a>
        </li>
        <hr />
      </ul>
    </div>
    <div class="result-tabs-siswa">
      <KelasSaya v-if="sectionTab == 'kelas-saya'" />
      <Pembelian v-if="sectionTab == 'history'" />
      <ContentAffiliate v-if="sectionTab == 'affiliate'" />

      <!-- <ContentKelasSaya v-if="sectionTab == 'kelas-saya'" /> -->
			<!-- <ContentPembelian v-if="sectionTab == 'history'" /> -->
			<JadiMentor v-if="sectionTab == 'jadi-mentor'" />
    </div>
  </div>
</template>

<script>
// import ContentKelasSaya from "./KelasSaya/ContentKelasSaya";
// import ContentPembelian from "./Pembelian/ContentPembelian";
import JadiMentor from "./JadiMentor";

import KelasSaya from './KelasSaya/KelasSaya';
import Pembelian from './Pembelian/Pembelian';
import ContentAffiliate from './Affiliate/ContentAffiliate';

export default {
  name: "TabsSiswa",
  components: {
		// ContentKelasSaya,
		// ContentPembelian,
    JadiMentor,
    KelasSaya,
    Pembelian,
    ContentAffiliate,
  },
  data() {
    return {
      sectionTab: this.$store.state.dashboardSiswa.sectionTab,
      dataTabSiswa: [
        {
          id: 1,
          field: "Kelas Saya",
          sub_id: "kelas-saya",
          show: true
        },
        {
          id: 2,
          field: "History",
          sub_id: "history",
          show: false
        },
        {
          id: 3,
          field: "Affiliate",
          sub_id: "affiliate",
          show: false
        },
        {
          id: 4,
          field: "Jadi Mentor",
          sub_id: "jadi-mentor",
          show: false
        }
      ]
    };
  },
  methods: {
    init() {
      
    },
    changeTab(evt, id) {
			evt;
      this.sectionTab = id;
      this.$store.state.dashboardSiswa.sectionTab = id;
    },
  },
  mounted() {
    this.init();  
  },
	computed: {
    getSectionTab() {
      let tab = this.$store.state.dashboardSiswa.sectionTab;
      return tab;
    }
  },
  watch: {
    getSectionTab(newTab, oldTab) {
      this.sectionTab = newTab;
      console.log('tab section ', newTab, oldTab);
    }
  }
};
</script>

<style lang="scss" scoped>
.tabs-siswa {
	padding: 0 0 30px 0;
	font-family: "Montserrat";
  .content-tabs-siswa {
    width: 100%;
    ul.tabs {
      padding: 0%;
      width: 100%;
      margin: 10px auto;
      li {
        display: inline;
        text-align: center;
        cursor: pointer;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 59px;
        color: #003461;
        opacity: 0.56;

        a {
          display: inline-block;
          width: 25%;
          padding: 0;
          margin: 0;
          text-decoration: none;
          line-height: 23px;
        }

        &[active="true"] {
					opacity: 1;
          &.history ~ hr {
            margin-left: 25%;
          }
          &.affiliate ~ hr {
            margin-left: 50%;
          }
          &.jadi-mentor ~ hr {
            margin-left: 75%;
          }
        }
      }
      hr {
        height: 0.25rem;
        width: 25%;
        margin: 0;
        background: #c4c4c4;
        border: none;
        transition: 0.5s ease-in-out;
      }
    }
  }
}
@media (max-width: 767px) {
  .tabs-siswa {
		.content-tabs-siswa {
			display: none;
		}
	}
}
</style>