<template>
  <section class="sign-in">
    <HeaderTop />

    <ContentSignIn />

    <Footer />
  </section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";
import ContentSignIn from "@/components/SignIn/ContentSignIn.vue";

export default {
  name: "SignIn",
  components: {
    HeaderTop,
    Footer,
    ContentSignIn
  },
  mounted() {
    this.$root.goToTop();
    this.$root.loadingMentorbox().show();
    setTimeout(() => {
      this.$root.loadingMentorbox().hide();
    }, 2000);
  }
};
</script>