<template>
  <div class="content-edit-pp modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Edit Photo Profile</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-e-pp">
      <div class="show-image">
        <img id="showImage" src alt />
      </div>
      <div class="form-input-mbx">
        <label for="uploadNewPP">Upload New File</label>
        <input
          type="file"
          class="form-control"
          name="uploadNewPP"
          accept="image/png, image/jpeg"
          id="uploadNewPP"
          placeholder="Banner"
          v-on:change="handleFile($event)"
        />
        <div class="file-upload-div d-flex" v-on:click="chooseFile('uploadNewPP')">
          <span>{{ uploadNewPPText }}</span>
          <span class="btn btn-up">Unggah Dokumen</span>
        </div>
        <span>Note : 1MB Max Size file dokumen.</span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelBtn()" class="btn btn-danger">Cancel</button>

      <button 
				@click="updateProfile()" 
				class="btn btn-success"
				v-bind:disabled="uploadNewPPText == 'Unggah Photo'"
			>Update Photo Profile</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from  'axios';

export default {
  name: "EditPP",
  components: {},
  data() {
    return {
			uploadNewPPText: "Unggah Photo",
			dataBody: new FormData(),
    };
  },
  props: ["data"],
  methods: {
    init() {},
    closeModal() {
      this.$modal.hide("edit-pp");
    },
    handleFile(evt) {
      let target = evt.target;
      let files = target.files;
      let sizeFiles = target.files[0].size;
      if (sizeFiles > 1000000) {
        this.$root.notification("warning", "Warning", "Max Size file is 1MB.");
      } else {
        let nameFile = target.files[0].name;
        this.uploadNewPPText = nameFile;
				this.dataBody.append("files", files[0]);
        var reader = new FileReader();
        reader.onload = function(e) {
					$("#showImage").attr("src", e.target.result);
        };
        reader.readAsDataURL(target.files[0]);
      }
      console.log("evt ", evt);
    },
    chooseFile(id) {
      $("#" + id).click();
		},
		updateProfile() {
			let body = this.dataBody;
			let AuthSth = this.$root.authorization();
			this.$root.loadingData().show();
      let vm = this;
      axios
        .post(vm.$root.filesUploadSingle(), body, {
          headers: { 
						Authorization: AuthSth,
						"Content-Type": "multipart/form-data" 
					}
        })
        .then(res => {
          console.log("res ", res);
					let data = res.data;
					let body = {
						photo: data.files
          };
          window.$cookies.set("USP", data.files);
          this.$store.state.USP = data.files;
					this.postProfile(body);
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
					vm.afterPostProfile().failed(`${resErr.data.message}`);
        });
		},
		postProfile(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.getMyProfile(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
					vm.$emit('init');
					vm.afterPostProfile().success(
						`Photo Profile Berhasil di Perbarui.`
					);

        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
					vm.afterPostProfile().failed(`${resErr.data.message}`);
        });
    },
    afterPostProfile() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelBtn() {
      let msg = "Apakah Anda yakin <br> tidak ingin merubah photo profile ?";

      this.$modal.show("dialog", {
        title: "Warning",
        text: msg,
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-edit-pp {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
.show-image {
	> img {
		width: 200px;
		height: 200px;
		border-radius: 50%;
	}
}
</style>