<template>
  <div class="kelas-saya">
    <div class="filter-table-mbx">
      <h5>Filter :</h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Kelas :</span>
            </div>
            <div class="right d-flex">
              <template v-for="(kelas, index) in dataFilter.filter.kelas">
                <input
                  v-bind:key="index+5"
                  type="radio"
                  name="kelas"
                  v-bind:id="'f-kelas-'+ kelas.value"
                  class="radio-mbx"
                  v-bind:value="kelas.value"
                  v-bind:checked="dataBody.filter == kelas.value"
                />
                <label
                  v-bind:key="index"
                  v-bind:for="'f-kelas-'+kelas.value"
                  style="margin: 3px 5px;"
                  @click="filterHistoriKelas(kelas.value)"
                >{{ kelas.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <h3 
      v-if="dataBody.filter == 'Semua Pembelian'" 
      class="title-pbn"
    >Semua Kelas</h3>
    <h3 
      v-if="dataBody.filter == 'Menunggu Pembayaran'" 
      class="title-pbn"
    >Menunggu Pembayaran</h3>
    <h3 
      v-if="dataBody.filter == 'Lunas'" 
      class="title-pbn"
    >Lunas</h3>
    <h3 
      v-if="dataBody.filter == 'Dibatalkan'" 
      class="title-pbn"
    >Dibatalkan</h3>
    <div v-if="(dataListHistori).length > 0" class="content-pbn">
      <ul>
        <li class="list-pbn d-flex" v-for="(data, index) in dataListHistori" v-bind:key="index">
          <div class="left-pbn">
            <p 
							v-if="data.status == 'Accept'"
							class="status-pbn tech-success"
						>
              <i class="fa fa-check"></i>
              {{ data.status }}
            </p>
						<p 
							v-if="data.status == 'Pending'"
							class="status-pbn tech-warning"
						>
              <i class="fa fa-clock-o"></i>
              {{ data.status }}
            </p>
            <p 
							v-if="data.status == 'New'"
							class="status-pbn tech-primary"
						>
              <i class="fa fa-clock-o"></i>
              {{ data.status }}
            </p>
            <p 
							v-if="data.status == 'Reject'"
							class="status-pbn tech-danger"
						>
              <i class="fa fa-times"></i>
              {{ data.status }}
            </p>
            <div class="date-and-invoice">
              <p class="date-pbn">{{ data.tanggal }}</p>
              <p class="invoice-pbn">{{ data.invoice }}</p>
            </div>
            <p class="total-pbn">
              <span>Total Pembayaran :</span>
              Rp.{{ data.total }}
            </p>
            <p 
							v-if="data.status == 'New'"
							class="status-pbn tech-primary konfirmasi-pembayaran"
              @click="acceptPayment(data)"
						>
              <i class="fa fa-check"></i>
              Konfirmasi Pembayaran
            </p>
          </div>
          <div class="right-pbn">
            <p class="tech-primary text-center" >
							<i class="fa fa-bars"></i>
							Produk
						</p>
            <ol class="list-data-pbn">
              <li v-for="(p, idx) in data.produk" v-bind:key="idx">
                <div 
									class="cntn-list-p"
									@click="showPbn(data.invoice, idx)"
								>
                  <p>{{ p.kelas }}</p>
                  <i class="fa fa-plus"></i>
                </div>
                <div 
									v-bind:id="'detailPCntn' + data.invoice + idx" 
									class="detail-cntn"
								>
                  <p>
                    <span>Kelas <span>:</span></span>
                    <span>{{ p.kelas }}</span>
                  </p>
                  <p>
                    <span>Jadwal <span>:</span></span>
                    <span>{{ p.jadwal }}</span>
                  </p>
                  <p>
                    <span>Harga <span>:</span></span>
                    <span style="font-weight: 700;">Rp.{{ p.harga }}</span>
                  </p>
                </div>
              </li>
            </ol>
          </div>
        </li>
      </ul>
      <div class="btn-etc-pbn">
        <!-- <button class="btn">Lainnya</button> -->
      </div>
    </div>
    <div v-else class="empty-kelas">
      <img src="/icon/empty-folder.svg" alt />
      <p>Oops, Kamu Belum Memulai Kelas.</p>
      <router-link v-bind:to="'/mentoring'" v-on:click.native="goToPage('mentoring')">
        <button class="btn btn-choose-kelas">Pilih Kelas</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import axios from "axios";

export default {
  name: "Pembelian",
  components: {},
  data() {
    return {
      dataListHistori: [],
      dataBody: {
        page: 1,
        limit: 10,
        filter: "Semua Pembelian",
      },
      dataFilter: {
        filter: {
          kelas: [
            {
              text: "Semua Pembelian",
              value: "Semua Pembelian"
            },
            {
              text: "Menunggu Pembayaran",
              value: "Menunggu Pembayaran"
            },
            {
              text: "Lunas",
              value: "Lunas"
            },
            {
              text: "Dibatalkan",
              value: "Dibatalkan"
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      this.getListKelas();
    },
    getListKelas() {
      let a = this.dataBody;
      let sQuery = `?filter=${a.filter}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listHistoriUser() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res kelas user ", res);
          let data = res.data;
          let result = data.result;
          this.dataListHistori = result.map(e => {
            let obj = {
              tanggal: e.tanggal,
              invoice: e.invoice,
              total: e.total,
              status: e.status,
              produk:
                e.produk == undefined
                  ? "-"
                  : e.produk.map(j => {
                      let objC = {
                        harga: j.harga,
                        kelas: j.kelas,
                        jadwal: j.jadwal
                      };
                      return objC;
                    })
            };
            return obj;
          });

          console.log("list data histori ", this.dataListHistori);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    filterHistoriKelas(val) {
      this.dataBody.filter = val;
      console.log('data body ', this.dataBody);
      this.init();
    },
    goToPage(id) {
      this.$store.state.headerTop.activeMenu = id;
		},
		showPbn(inv, idx) {
			$('#detailPCntn' + inv + idx).slideToggle();
    },
    acceptPayment(list) {
      let konfirmasiPembayaran = {
        invoice: list.invoice,
        total: list.total
      }
      console.log('list ', list, konfirmasiPembayaran);

      window.$cookies.set('konfirmasiPembayaran', konfirmasiPembayaran);
      this.$router.push("/konfirmasi/" + list.invoice);
    }
  },
  mounted() {
    this.init();
    // console.log('yeah terpanggil ')
  }
};
</script>

