<template>
  <div class="tab-detail-kelas">
    <div class="content-tdk">
      <h3 class="title-tdk">Detail Kelas</h3>
      <div class="form-input-tdk">
        <label for="nama-kelas">Nama Kelas</label>
        <input
          type="text"
          class="form-control"
          name="nama-kelas"
          id="namaKelas"
          placeholder="Nama Kelas"
          v-model="dataProductMentor.name"
        />
      </div>
      <div class="form-input-tdk">
        <label for="tentang-kelas">Tentang Kelas</label>
        <textarea
          type="text"
          class="form-control"
          name="tentang-kelas"
          id="tentangKelas"
          placeholder="Tentang Kelas"
          v-model="dataProductMentor.about"
        ></textarea>
      </div>
      <div class="form-input-tdk">
        <label for="catatan-penting">Catatan Penting</label>
        <textarea
          type="text"
          class="form-control"
          name="catatan-penting"
          id="catatanPenting"
          placeholder="Catatan Penting"
          v-model="dataProductMentor.note"
        ></textarea>
      </div>
      <div class="form-input-tdk">
        <label for="jenis">Jenis</label>
        <div class="d-flex">
          <template v-for="(jkl, index) in jenisKelasList">
            <input
              v-bind:key="index+5"
              type="radio"
              name="jkl"
              v-bind:id="'f-jkl-'+ jkl.value"
              class="radio-mbx"
              v-bind:value="jkl.value"
              v-bind:checked="dataProductMentor.jenis == jkl.value"
            />
            <label
              v-bind:key="index"
              v-bind:for="'f-jkl-'+jkl.value"
              style="margin: 3px 5px;"
              @click="jenisKelas().choose(jkl.value)"
            >{{ jkl.text }}</label>
          </template>
        </div>
      </div>
      <div class="form-input-tdk" v-if="roleAdmin">
        <label for="status">Status</label>
        <div class="d-flex">
          <template v-for="(status, index) in listStatusKelas">
            <input
              v-bind:key="index+5"
              type="radio"
              name="status"
              v-bind:id="'f-status-'+ status.value"
              class="radio-mbx"
              v-bind:value="status.value"
              v-bind:checked="dataProductMentor.status == status.value"
            />
            <label
              v-bind:key="index"
              v-bind:for="'f-status-'+status.value"
              style="margin: 3px 5px;"
              @click="statusKelas().choose(status.value)"
            >{{ status.text }}</label>
          </template>
        </div>
      </div>
      <div class="form-input-tdk">
        <label for="harga-coret">Harga Coret (price)</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="harga-coret"
          id="hargaCoret"
          placeholder="Harga Coret"
          v-model="dataProductMentor.price"
        />
      </div>
      <div class="form-input-tdk">
        <label for="harga-jual">Harga Jual (promo)</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          class="form-control"
          name="harga-jual"
          id="hargaJual"
          placeholder="Harga Jual"
          v-model="dataProductMentor.promo"
          @keyup="checkPromo()"
        />
        <span 
          style="color:red;"
          v-if="!statusPromo"
        >Harga Jual (price) tidak boleh lebih dari Harga Coret (promo).</span>
      </div>
      <div class="form-input-tdk">
        <label for="kategori-kelas">Kategori Kelas</label>
        <input
          type="text"
          class="form-control"
          name="kategori-kelas"
          placeholder="Kategori Kelas"
          v-model="dataProductMentor.categoryText"
          @keyup.enter="kategoriKelas().search($event)"
          @focus="kategoriKelas().show()"
          @focusout="kategoriKelas().show()"
        />
        <ul id="kategoriKelas" class="list-data-field">
          <li
            v-for="(kategori, index) in listKategoriData"
            v-bind:key="index"
            v-bind:class="{'active': kategori.title == dataProductMentor.categoryText}"
            @click="kategoriKelas().choose(index)"
          >{{ kategori.title }}</li>
        </ul>
      </div>
      <div class="form-input-tdk">
        <label for="sub-kategori-kelas">Sub Kategori Kelas</label>
        <input
          type="text"
          class="form-control"
          name="sub-kategori-kelas"
          placeholder="Sub Kategori Kelas"
          v-model="dataProductMentor.subCategoryText"
          @focus="subKategoriKelas().show()"
          @focusout="subKategoriKelas().show()"
          v-bind:disabled="listSubKategoriData.length == 0"
        />
        <ul id="subKategoriKelas" class="list-data-field">
          <li
            v-for="(subKategori, index) in listSubKategoriData"
            v-bind:key="index"
            @click="subKategoriKelas().choose(index)"
          >{{ subKategori.title }}</li>
        </ul>
      </div>
      <div class="form-input-tdk">
        <label for="banner">Banner</label>
        <input
          type="file"
          class="form-control"
          name="banner"
          accept="image/png, image/jpeg"
          id="banner"
          placeholder="Banner"
          v-on:change="handleFile($event)"
        />
        <div class="file-upload-div d-flex" v-on:click="chooseFile('banner')">
          <span>{{ dataProductMentor.banner }}</span>
          <span class="btn btn-up">Unggah Banner</span>
        </div>
        <span>Note : 1MB Maximal Size file image.</span>
        <div class="show-image">
          <img 
            id="showImage" 
            v-bind:src="$root.getPhotoUser() + `/${dataProductMentor.banner}`"
            alt 
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "TabDetailKelas",
  components: {},
  data() {
    return {
      dataProductMentor: this.$store.state.dataProductMentor,
      jenisKelasList: [
        {
          text: "Kelas WhatsApp",
          value: "WhatsApp"
        },
        {
          text: "Kelas Webinar",
          value: "Webinar"
        }
      ],
      listStatusKelas: [
        {
          text: "Publish",
          value: "Publish"
        },
        {
          text: "Draft",
          value: "Draft"
        },
        {
          text: "Reject",
          value: "Reject"
        },
      ],
      listKategoriData: this.$store.state.dataProductMentor.listKategoriData,
      listSubKategoriData: [],
      roleAdmin: this.$root.checkRole("ADMIN"),
      statusPromo: true,
      formBannerPM: new FormData()
    };
  },
  props: ["data"],
  methods: {
    init() {
      // let propslistKategori = this.$props.listKategori;
      // this.listKategoriData = this.$root.sortBy(
      //   propslistKategori,
      //   "title",
      //   true
      // );
      // console.log("props ", this.listKategoriData);

      let targetFilesBanner = this.$store.state.targetFilesBanner;
      console.log('targetFilesBanner ', targetFilesBanner);
      if ( targetFilesBanner !== "" ) {
        this.renderImage(targetFilesBanner);
      }
    },
    handleData(newData) {
      console.log("handle new data ", newData);
      this.listKategoriData = newData;
    },
    chooseFile(id) {
      $("#" + id).click();
    },
    handleFile(evt) {
      let target = evt.target;
      let files = target.files;
      let sizeFiles = target.files[0].size;
      if (sizeFiles > 1000000) {
        this.$root.notification("warning", "Warning", "Max Size file is 1MB.");
      } else {
        let nameFile = target.files[0].name;
        this.dataProductMentor.banner = nameFile;
        let dataBanner = new FormData();
        dataBanner.append("files", files[0])
        this.$store.state.formBannerPM = dataBanner;
        window.$cookies.set('formBannerPM', dataBanner);
        this.$store.state.targetFilesBanner = target.files[0];
        this.renderImage(target.files[0])
      }
      console.log("evt ", evt);
    },
    renderImage(files) {
      var reader = new FileReader();
      reader.onload = function(e) {
        $("#showImage").attr("src", e.target.result);
      };
      reader.readAsDataURL(files);
    },
    jenisKelas() {
      let a = this.dataProductMentor;
      return {
        show() {
          $("#jenisKelas").slideToggle();
        },
        choose(val) {
          a.jenis = val;
        }
      };
    },
    kategoriKelas() {
      let a = this.dataProductMentor;
      let b = this.listKategoriData;
      return {
        show() {
          $("#kategoriKelas").slideToggle();
        },
        choose: idx => {
          let data = b[idx];
          a.categoryText = data.title;
          a.category = b[idx].id;
          a.subCategoryText = "";

          this.listSubKategoriData = [];
          this.listSubKategoriData = b[idx].child;
        },
        search: event => {
          let evt = event;
          let val = evt.target.value;

          console.log("evt ", val);
        }
      };
    },
    subKategoriKelas() {
      let a = this.dataProductMentor;
      let b = this.listSubKategoriData;
      return {
        show() {
          $("#subKategoriKelas").slideToggle();
        },
        choose(idx) {
          let val = b[idx];

          a.subCategory = val.id;
          a.subCategoryText = val.title;
        }
      };
    },
    statusKelas() {
      let a = this.dataProductMentor;
      return {
        show() {
          $("#statusKelas").slideToggle();
        },
        choose(val) {
          a.status = val;
        }
      };
    },
    checkPromo() {
      let price = this.dataProductMentor.price;
      let promo = this.dataProductMentor.promo;
      if ( parseInt(promo) > parseInt(price) ) {
        this.statusPromo = false;
        return true;
      } else {
        this.statusPromo = true;
        return false;
      }
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top props ", newData, oldData);
    }
  }
};
</script>

<style lang="scss" scoped>
.tab-detail-kelas {
  .content-tdk {
    > .title-tdk {
      font-style: normal;
      font-weight: 500;
      font-size: 33px;
      line-height: 44px;
    }
    > .form-input-tdk {
      text-align: left;
      margin: 5px 0;
      > label {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 44px;
        color: #000000;
        margin: 0%;
      }
      > input, > textarea {
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 1px solid #000000;
        border-radius: 0%;
        &[type="file"] {
          display: none;
        }
      }
      > .file-upload-div {
        justify-content: space-between;
        border-bottom: 1px solid #000000;
        padding: 6px 12px;
        align-items: center;
        cursor: pointer;
        > .btn-up {
          background: #003461;
          border-radius: 10px;
          color: #ffffff;
        }
      }
      > .date-form {
        justify-content: space-between;
        > input {
          width: 49%;
          border-top: none;
          border-right: none;
          border-left: none;
          border-bottom: 1px solid #000000;
          border-radius: 0%;
        }
      }
    }
    .show-image {
      display: flex;
      justify-content: center;
      margin: 15px 0;
      > img {
        width: 700px;
        height: 580px;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      }
    }
  }
}
#statusKelas,
#jenisKelas,
#kategoriKelas,
#subKategoriKelas {
  display: none;
}
.list-data-field {
  position: absolute;
  transform: translate(120px, 0px);
  background: #ffffff;
  padding: 0%;
  width: 50%;
  left: 0;
  margin: 3px 0 0 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  border-radius: 0 0 10px 10px;
  max-height: 200px;
  overflow-y: auto;
  > li {
    color: #000000;
    list-style: none;
    padding: 5px 10px;
    cursor: pointer;
    &:nth-child(even) {
      background: #cccccc;
    }
    &:hover {
      background: #999898;
    }
    &.active {
      background: #003461;
      color: #ffffff;
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .tab-detail-kelas {
    .content-tdk {
      > .title-tdk {
        font-size: 25px;
      }
      > .form-input-tdk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .tab-detail-kelas {
    .content-tdk {
      > .title-tdk {
        font-size: 25px;
      }
      > .form-input-tdk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>