<template>
  <section class="sign-in">
    <HeaderTop />

    <ContentSignInMentor />

    <Footer />
  </section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";
import ContentSignInMentor from "@/components/SignInMentor/ContentSignInMentor.vue";

export default {
  name: "SignInMentor",
  components: {
    HeaderTop,
    Footer,
    ContentSignInMentor
  },
  mounted() {
    this.$root.goToTop();
    this.$root.loadingMentorbox().show();
    setTimeout(() => {
      this.$root.loadingMentorbox().hide();
    }, 2000);
  }
};
</script>