<template>
  <div class="form-register">
    <h1>Isilah Form dibawah ini untuk data anda</h1>
    <div class="input-form">
      <label for="nama-lengkap-register">Nama Lengkap</label>
      <input
        type="text"
        name="nama-lengkap-register"
        id="idFullName"
        class="form-input"
        placeholder="Nama Lengkap"
        v-model="formRegister.fullname"
        @keyup.enter="register()"
      />
      <span v-if="!cannotNull.fullname" style="color: red;">This field is required.</span>
    </div>
    <div class="input-form">
      <label for="email-register">Email</label>
      <input
        type="email"
        name="email-register"
        id="idEmailRegister"
        class="form-input"
        placeholder="yourname@gmail.com"
        v-model="formRegister.email"
        @keyup="checkEF()"
        @keyup.enter="register()"
      />
      <span v-if="!cannotNull.email" style="color: red;">This field is required.</span>
      <span v-if="!formatEmail" style="color: red;">Invalid email format.</span>
    </div>
    <div class="input-form">
      <label for="password-login">Password</label>
      <input
        type="password"
        name="password-register"
        id="idPasswordRegister"
        class="form-input"
        placeholder="Password"
        v-model="formRegister.password"
        @keyup="checkPass()"
        @keyup.enter="register()"
      />
      <span
        class="sh-password"
        id="shPassRegister"
        v-on:click="$root.showPassword('shPassRegister', 'idPasswordRegister')"
      >
        <i class="fa fa-eye"></i>
      </span>
      <span v-if="!cannotNull.password" style="color: red;">This field is required.</span>
      <span v-if="!minLength.password" style="color: red;">Password min length 6 characters.</span>
    </div>
    <div class="input-form">
      <label for="nomor-wa">Nomor WhatsApp</label>
      <input
        type="text"
        onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
        name="nomor-wa"
        id="idNomorWA"
        class="form-input"
        placeholder="08xxxxxxxxxxx"
        v-model="formRegister.nowa"
        maxlength="16"
        @keyup.enter="register()"
      />
      <span v-if="!cannotNull.nowa" style="color: red;">This field is required.</span>
    </div>
    <button class="btn btn-register" v-on:click="register()">Daftar</button>
    <p class="or-register">atau Daftar dengan</p>
    <div class="register-sosmed">
      <div class="sosmed-fb">
        <img src="/icon/sosmed-fb.svg" alt="Facebook" />
      </div>
      <div class="sosmed-google">
        <img src="/icon/sosmed-google.svg" alt="Google" />
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
  name: "Register",
  components: {},
  data() {
    return {
      formRegister: {
        fullname: "",
        email: "",
        password: "",
        nowa: ""
      },
      cannotNull: {
        fullname: true,
        email: true,
        password: true,
        nowa: true
      },
      minLength: {
        password: true,
      },
      formatEmail: true
    };
  },
  methods: {
    checkEF() {
      let email = this.formRegister.email;
      var re = this.$root.checkValidateEmail(email);
      re == false ? (this.formatEmail = false) : (this.formatEmail = true);
    },
    checkPass() {
      let password = this.formRegister.password;
      let minLength = this.minLength;
      password.length >= 6 ? minLength.password = true : minLength.password = false;
    },
    validateRegister() {
      let data = this.formRegister;
      let required = this.cannotNull;

      data.fullname == ""
        ? (required.fullname = false)
        : (required.fullname = true);
      data.email == "" ? (required.email = false) : (required.email = true);
      data.password == ""
        ? (required.password = false)
        : (required.password = true);
      data.nowa == "" ? (required.nowa = false) : (required.nowa = true);

      if (
        data.fullname !== "" &&
        data.email !== "" &&
        data.password !== "" &&
        data.nowa !== ""
      ) {
        return true;
      } else {
        this.$root.notification(
          "warning",
          "Warning",
          "Tidak boleh ada field yang kosong."
        );
        return false;
      }
    },
    register() {
      $("");
      if (this.formatEmail && this.minLength.password) {
        if (this.validateRegister()) {
          console.log("form sudah full ", this.formRegister);
          let data = this.formRegister;
          let body = {
            name: data.fullname,
            email: data.email,
            password: data.password,
            phone: data.nowa,
            roles: ["USER"]
          };
          this.postRegister(body);
        } else {
          console.log("data belum full");
        }
      } else if ( !this.formatEmail ) {
        this.$root.notification("warning", "Warning", "Invalid Email Format.");
      } else if ( !this.minLength.password ) {
        this.$root.notification("warning", "Warning", "Password min length is 6 characters.");
      }
    },
    postRegister(body) {
      this.$root.loadingData().show();
      let vm = this;

      axios.post( this.$root.signUp(), body )
      .then((response) => {
        console.log('res ', response)
        vm.$root.notification("success", "Success", `${body.name} berhasil daftar, silahkan login.`);
        vm.$root.loadingData().hide();
        vm.$parent.changeTab('', 'login');
        vm.emptyRegister();
      })
      .catch((error) => {
        const err = error.response;
        console.log('error ', err)
        vm.$root.notification("danger", "Error", `${err.data.errors}`);
        vm.$root.loadingData().hide();
      })
    },
    emptyRegister() {
      let data = this.formRegister;
      data.fullname = '';
      data.email = '';
      data.password = '';
      data.nowa = '';
    }
  }
};
</script>

<style lang="scss" scoped>
.form-register {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  > h1 {
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
  }
  > .input-form {
    display: flex;
    flex-direction: column;
    > label {
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 24px;
      margin: 5px 0 0 0;
    }
    > input {
      padding: 5px 5px 5px 10px;
      margin: 5px 0 0 0;
      border-radius: 8px;
      border: 1px solid #cccccc;
      &[type="password"] {
        padding: 5px 35px 5px 10px;
      }
      &.required {
        border: 1px solid #f70707;
      }
    }
    > .sh-password {
      cursor: pointer;
      display: flex;
      margin: -27px 10px 15px auto;
      font-size: 18px;
    }
  }
  > .btn-register {
    cursor: pointer;
    background: #004c94;
    color: #ffffff;
    font-weight: bold;
    margin: 20px 0;
  }
  .or-register {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    display: flex;
    margin: 10px auto 25px auto;
  }
  .register-sosmed {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    div {
      margin: 0 5px;
      cursor: pointer;
      &.sosmed-google {
        img {
          background: #004c94;
          padding: 7px;
          border-radius: 50%;
        }
      }
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .form-register {
    > h1 {
      font-size: 20px;
    }
    > .input-form {
      > label {
        font-size: 18px;
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .form-register {
    > h1 {
      font-size: 19px;
    }
    > .input-form {
      > label {
        font-size: 17px;
      }
    }
  }
}
@media (max-width: 481px) {
  .form-register {
    > h1 {
      font-size: 19px;
    }
    > .input-form {
      > label {
        font-size: 17px;
      }
    }
    .forgot-passwd {
      font-size: 15px;
    }
  }
}
</style>