<template>
  <div class="content-follow-up-member modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Detail Follow Up {{ invoiceMember }} | Jumlah : {{ count }}</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-fum">
      <ol class="parent-list-fum">
				<li 
					v-for="(list, idx) in dataTable.list"
					v-bind:key="idx"
				>
					<div class="list-fum">
						<div class="left">
							<p>
								Title : 
								<span>
									{{ list.title }}
								</span>
							</p>
							<p>
								Status : 
								<span>
									{{ list.status }}
								</span>
							</p>
							<p>
								Created At : 
								<span>
									{{ list.createdAt }}
								</span>
							</p>
						</div>
						<div class="right">
							<p>
								Message : 
								<span>
									{{ list.message }}
								</span>
							</p>
						</div>
					</div>
				</li>
			</ol>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import * as moment from "moment";

export default {
  name: "FollowUpMember",
  components: {},
  data() {
    return {
      invoiceMember: "",
      count: 0,
      dataTable: {
        list: []
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      this.invoiceMember = this.$props.data;
      console.log("invoiceMember ", this.invoiceMember);

      // "title": "Order Detail",
      // "message": "Halo kak *User*\nSilahkan menyelesaikan pembayaran untuk mentoring *Membuka Usaha Ghost Kitchen* dengan nomor invoice *INV2006196KXNP*.\n\nBiaya Mentoring: *Rp. 249000*\nPotongan Kode Unik : *Rp. 608*\n\n*Total Transfer : Rp. 248392*\n\nSilahkan melakukan transfer pembayaran segera ke nomor rekening\n\n*Bank BNI Syariah*\n*1117575889*\na.n *IIN KUSUMA WARDANI*",
      // "status": "sent",
      // "createdAt": "2020-06-19 14:51:51"
      this.getListFollowUp();
    },
    getListFollowUp() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();

      axios
        .get(this.$root.listFollowUpUser() + `/${this.invoiceMember}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          let data = res.data;
          let result = data.data;

          this.count = data.count;
          this.dataTable.list = [];
          this.dataTable.list = result.map(e => {
            let obj = {
              title: e.title,
              message: e.message,
              status: e.status,
              createdAt: moment(e.createdAt).format("DD - MMM - YYYY HH:mm:ss")
            };
            return obj;
          });

          console.log("res list follow up ", res);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err;

          console.log("err ", resErr.response);
          this.$root.loadingData().hide();
        });
    },
    closeModal() {
      this.$modal.hide("follow-up-member");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-follow-up-member {
  padding: 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
	.content-fum {
		> .parent-list-fum {
			padding: 0 0 0 20px;
			> li {
				border-bottom: 1px solid #cccccc;
				padding: 10px 0 10px 15px;
				> .list-fum {
					display: flex;
					text-align: left;
					.left {
						width: 40%;
						> p {
							font-weight: 700;
							margin: 0 0 5px 0;
							> span {
								font-weight: normal;
							}
						}
					}
					.right {
						width: 60%;
						> p {
							font-weight: 700;
							margin: 0 0 5px 0;
							> span {
								font-weight: normal;
							}
						}
					}
				}
			}
		}
	}
}
</style>