<template>
  <div class="content-voucher-sale modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Voucher Sale</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Voucher Sale</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr class="line-modal" />
    <div class="content-evs">
      <div class="form-input-mbx">
        <label for="produk-voucher" class="title">Produk</label>
        <p v-if="dataExecutor.listCourses.length > 0" class="list-sub-kategori">
          <span v-for="(c, index) in dataExecutor.listCourses" v-bind:key="index">
            {{ c.name }}
            <i @click="produkForVoucher().remove(index)" class="fa fa-times"></i>
          </span>
        </p>
        <input
          type="text"
          name="produk-voucher"
          class="form-control"
          placeholder="Produk Voucher"
          v-model="dataExecutor.produkText"
          @keyup.enter="searchKelas()"
        />
        <ul
          id="listKelasSearch"
          class="list-status-kategori show"
          v-if="dataExecutor.listSearchKelas.length !== 0"
        >
          <li v-if="dataExecutor.listSearchKelas.length == 0">Data Not Found or Data Selected.</li>
          <li
            v-for="(kelas, index) in dataExecutor.listSearchKelas"
            v-bind:key="index"
            @click="produkForVoucher().choose(kelas, index)"
          >{{ kelas.name }}</li>
        </ul>
        <span>Note : klik by enter untuk mencari produk, minimal 3 characters.</span>
        <span v-if="!cannotNull.produk" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="kode-voucher" class="title">Kode Voucher</label>
        <input
          type="text"
          name="kode-voucher"
          class="form-control text-uppercase"
          placeholder="Kode Voucher"
          v-bind:disabled="section == 'edit'"
          v-model="dataExecutor.kode"
        />
        <span v-if="!cannotNull.kode" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="deskripsi-voucher" class="title">Deskripsi</label>
        <input
          type="text"
          name="deskripsi-voucher"
          class="form-control"
          placeholder="Deskripsi"
          v-model="dataExecutor.deskripsi"
        />
        <span v-if="!cannotNull.deskripsi" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="jenis-voucher" class="title">Jenis</label>
        <input
          type="text"
          name="jenis-voucher"
          class="form-control"
          placeholder="Jenis"
          v-model="dataExecutor.jenis"
          @focus="jenisVoucher().show()"
          @focusout="jenisVoucher().show()"
        />
        <span v-if="!cannotNull.jenis" style="color: red;">This field is required.</span>
        <ul id="jenisVoucher" class="list-status-kategori">
          <li
            v-for="(jenis, index) in dataExecutor.listJenisVoucher"
            v-bind:key="index"
            @click="jenisVoucher().choose(jenis)"
          >{{ jenis }}</li>
        </ul>
      </div>
      <div class="form-input-mbx">
        <label for="diskon-voucher" class="title">Diskon</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          type="text"
          name="diskon-voucher"
          class="form-control"
          placeholder="Diskon Voucher"
          v-model="dataExecutor.diskon"
        />
        <span v-if="!cannotNull.diskon" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="startAt-voucher" class="title">Start At</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.startAt"
          id="startAt-voucher"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.startAt" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="expiredAt-voucher" class="title">Expired At</label>
        <vc-date-picker
          :value="'2020-06-01 07:59:59'"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.expiredAt"
          id="expiredAt-voucher"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.expiredAt" style="color: red;">This field is required.</span>
      </div>
    </div>
    <div class="btn-add-cancel">
      <button @click="cancelUser()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="saveVoucherSale()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="saveVoucherSale()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ExecutorVoucherSale",
  components: {},
  props: ["data"],
  data() {
    return {
      kode: "",
      section: "",
      dataExecutor: {
        kode: "",
        deskripsi: "",
        jenis: "",
        diskon: "",
        startAt: "",
        expiredAt: "",
        produk: [
          "238cab7b-4366-47c3-a2db-8b2a5c8ab913",
          "bf77dcfc-3925-4d2e-bc56-7351468417ea"
        ],
        produkText: "",
        listCourses: [],
        listSearchKelas: [],
        listJenisVoucher: ["Persen", "Price", "Promo"]
      },
      cannotNull: {
        kode: true,
        deskripsi: true,
        jenis: true,
        diskon: true,
        startAt: true,
        expiredAt: true,
        produk: true
      }
    };
  },
  methods: {
    init() {
      // Kode Voucher,
      // Pilih Produk,
      // JenisDiskon,
      // JumlahDiskon,
      // StartDate,
      // EndDate

      let dataParams = this.$props.data;
      this.kode = dataParams.kode;
      this.section = dataParams.section;
      console.log("data params ", dataParams);

      if (this.section == "edit") {
        this.getDetailVoucherSale();
      }
      this.fixStyleInputVCalendar();
    },
    fixStyleInputVCalendar() {
      $(".vc-text-base").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%"
      });

      $(`#startAt-voucher > input`).attr("placeholder", "Start At");
      $(`#expiredAt-voucher > input`).attr("placeholder", "Expired At");
    },
    getDetailVoucherSale() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listVoucherSaleAdmin() + `/${this.kode}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          a.listCourses = result.Courses;
          a.kode = result.code;
          a.deskripsi = result.description;
          a.jenis = result.type;
          a.diskon = result.discount;
          a.startAt = new Date(result.startAt);
          a.expiredAt = new Date(result.expiredAt);

          console.log("dataExecutor ", a);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    closeModal() {
      this.$modal.hide("executor-voucher-sale");
    },
    cancelUser() {
      this.$modal.show("dialog", {
        title: "Warning",
        text:
          "Apakah Anda yakin <br> tidak ingin menambah/edit Data Voucher Sale ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    getSearchKelas(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let sQuery = `?search=${body}`;
      axios
        .get(this.$root.searchKelas() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          let listCourses = a.listCourses;
          let listSearchKelas = a.listSearchKelas;

          if (listCourses.length == 0) {
            this.dataExecutor.listSearchKelas = result;
          } else {
            for (let i = 0; i < result.length; i++) {
              for (let j = 0; j < listCourses.length; j++) {
                console.log("nge looping ", result[i], listCourses[j]);

                if (result[i].uuid !== listCourses[j].uuid) {
                  listSearchKelas.push(result[i]);
                }
              }
            }
          }

          console.log("dataExecutor ", result);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchKelas() {
      let val = this.dataExecutor.produkText;
      if (val.length < 3) {
        this.$root.notification(
          "warning",
          "Warning",
          "Search Kelas minimal 3 characters."
        );
      } else {
        this.getSearchKelas(val);
      }
    },
    jenisVoucher() {
      let a = this.dataExecutor;
      return {
        show() {
          $("#jenisVoucher").slideToggle();
        },
        choose(val) {
          a.jenis = val;
        }
      };
    },
    produkForVoucher() {
      let a = this.dataExecutor.listCourses;
      let b = this.dataExecutor.listSearchKelas;
      return {
        choose: (data, idx) => {
          a.push(data);
          b.splice(idx);
          console.log("data choose ", data);
        },
        remove: idx => {
          a.splice(idx, 1);
        }
      };
    },
    afterPostVoucher() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    postVoucherSale(body) {
      console.log('body ', body);
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listVoucherSaleAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostVoucher().success(
            `Voucher ${body.kode} Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostVoucher().failed(`${resErr.data.message}`);
        });
    },
    putVoucherSale(body) {
      console.log('body ', body);
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .put(vm.$root.listVoucherSaleAdmin() + `/${body.kode}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostVoucher().success(
            `Voucher ${body.kode} Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostVoucher().failed(`${resErr.data.message}`);
        });
    },
    convertPayloadBody() {
      let a = this.dataExecutor;
      let body = {
        kode: "",
        deskripsi: "",
        jenis: "",
        diskon: "",
        startAt: "",
        expiredAt: "",
        produk: []
      };

      body.kode = a.kode.toUpperCase();
      body.deskripsi = a.deskripsi;
      body.jenis = a.jenis;
      body.diskon = a.diskon;
      body.startAt = a.startAt;
      body.expiredAt = a.expiredAt;
      body.produk = a.listCourses.map(e => {
        return e.uuid;
      })

      if ( this.section == 'add' ) {
        this.postVoucherSale(body);
      } else if ( this.section == 'edit' ) {
        this.putVoucherSale(body);
      }
    },
    saveVoucherSale() {
      let a = this.dataExecutor;
      this.convertPayloadBody();
      console.log("data executoor ", a);
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-voucher-sale {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  padding: 15px 15px 70px 15px;
  overflow-y: auto;
  height: 100%;
  .content-evs {
    .list-sub-kategori {
      margin: 0 0 5px 0;
      height: auto;
      display: flex;
      flex-wrap: wrap;
      > span {
        background: #cccccc;
        padding: 5px;
        border-radius: 5px;
        margin: 3px;
        > i {
          color: #000000;
          cursor: pointer;
          &:hover {
            color: #ee0f0f;
          }
        }
      }
    }
    .list-status-kategori {
      // position: absolute;
      background: #ffffff;
      padding: 0%;
      display: none;
      width: 50%;
      left: 0;
      margin: 3px 0 0 0;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      border-radius: 0 0 10px 10px;
      &.show {
        display: block;
      }
      > li {
        color: #000000;
        list-style: none;
        padding: 5px 10px;
        cursor: pointer;
        &:nth-child(even) {
          background: #cccccc;
        }
        &:hover {
          background: #999898;
        }
      }
    }
  }
}
</style>