<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/>-->

    <HeaderTop />
    <JumbotronLanding />
    <LandingMenu />
    <FlashSaleMenu />
    <KelasMenu />
    <JoinMentor />
    <TipsArticleMenu />
    <Footer />
  </div>
</template>

<script>
// import $ from "jquery";
import axios from 'axios';

// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import HeaderTop from "@/components/HeaderTop.vue";
import JumbotronLanding from "@/components/LandingPage/JumbotronLanding.vue";
import LandingMenu from "@/components/LandingPage/LandingMenu.vue";
import FlashSaleMenu from "@/components/LandingPage/FlashSaleMenu.vue";
import KelasMenu from "@/components/LandingPage/KelasMenu.vue";
import JoinMentor from "@/components/LandingPage/JoinMentor.vue";
import TipsArticleMenu from "@/components/LandingPage/TipsArticleMenu.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "Home",
  title: "Ruang Meraih Cita.",
  components: {
    HeaderTop,
    JumbotronLanding,
    LandingMenu,
    FlashSaleMenu,
    KelasMenu,
    JoinMentor,
    TipsArticleMenu,
    Footer
  },
  data() {
    return {
      listKelas: [],
      dataBody: {
        page: 1,
        limit: 10,
        jenis: 'whatsapp',
        kategori: '',
        sub: ''
      }
    }
  },
  methods: {
    init() {
      this.$root.loadingMentorbox().show();
      setTimeout(() => {
        this.$root.loadingMentorbox().hide();
      }, 3000)
    },
    getListKelas() {
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}`;

      axios.get( this.$root.listKelasPage() + sQuery )
      .then((res) => {
        console.log('res ', res)
        this.$root.loadingMentorbox().hide();
      })
      .catch((err) => {
        console.log('err ', err)
        this.$root.loadingMentorbox().hide();
      })
    }
  },
  mounted() {
    this.init();
    this.$root.goToTop();
  }
};
</script>
