<template>
  <div class="executor-profile modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'edit-profile'" class="title-lk">Edit Profile</h3>
      <h3 v-if="section == 'change-password'" class="title-lk">Change Password</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-executor-p" v-if="section == 'edit-profile'">
      <div class="form-input-mbx">
        <label for="title">Nama Lengkap</label>
        <input
          type="text"
          class="form-control"
          name="nama-lengkap"
          placeholder="Nama Lengkap"
          v-model="dataExecutor.name"
          @keyup.enter="updateProfile()"
        />
        <span v-if="!cannotNull.name" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="title">Job Title</label>
        <input
          type="text"
          class="form-control"
          name="job-title"
          placeholder="Job Title"
          v-model="dataExecutor.jobTitle"
          @keyup.enter="updateProfile()"
        />
        <!-- <span v-if="!cannotNull.jobTitle" style="color: red;">This field is required.</span> -->
      </div>
      <div class="form-input-mbx">
        <label for="title">About</label>
        <input
          type="text"
          class="form-control"
          name="about"
          placeholder="About"
          v-model="dataExecutor.about"
          @keyup.enter="updateProfile()"
        />
        <!-- <span v-if="!cannotNull.about" style="color: red;">This field is required.</span> -->
      </div>
      <div class="form-input-mbx">
        <label for="title">Email</label>
        <input
          type="text"
          class="form-control"
          name="email"
          placeholder="Email"
          v-model="dataExecutor.email"
          @keyup="checkEF()"
          @keyup.enter="updateProfile()"
        />
        <span v-if="!cannotNull.email" style="color: red;">This field is required.</span>
        <span v-if="!formatEmail" style="color: red;">Invalid email format.</span>
      </div>
      <div class="form-input-mbx">
        <label for="title">Nomor WhatsApp</label>
        <input
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          maxlength="16"
          type="text"
          class="form-control"
          name="nomor-wa"
          placeholder="Nomor WhatsApp"
          v-model="dataExecutor.phone"
          @keyup.enter="updateProfile()"
        />
        <span v-if="!cannotNull.phone" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="title">Alamat</label>
        <input
          type="text"
          class="form-control"
          name="alamat"
          placeholder="Alamat"
          v-model="dataExecutor.alamat"
          @keyup.enter="updateProfile()"
        />
        <!-- <span v-if="!cannotNull.alamat" style="color: red;">This field is required.</span> -->
      </div>
    </div>

    <div class="content-executor-p" v-if="section == 'change-password'">
      <div class="form-input-mbx">
        <label for="title">Old Password</label>
        <input
          type="password"
          class="form-control"
          name="title"
          placeholder="Old Password"
          v-model="dataPassword.oldPassword"
        />
        <span v-if="!cannotNull.oldPassword" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="title">New Password</label>
        <input
          type="password"
          class="form-control"
          name="title"
          placeholder="New Password"
          @keyup="checkPass('new')"
          v-model="dataPassword.newPassword"
        />
        <span v-if="!cannotNull.newPassword" style="color: red;">This field is required.</span>
        <span
          v-if="!minLength.newPassword"
          style="color: red;"
        >This field is min length 6 characters.</span>
      </div>
      <div class="form-input-mbx">
        <label for="title">Re-Type New Password</label>
        <input
          type="password"
          class="form-control"
          name="title"
          placeholder="New Password"
          v-model="dataPassword.retypeNewPassword"
          @keyup="checkPass('renew'); checkMatchPassword();"
          @keyup.enter="updatePassword()"
        />
        <span v-if="!cannotNull.retypeNewPassword" style="color: red;">This field is required.</span>
        <span
          v-if="!minLength.retypeNewPassword"
          style="color: red;"
        >This field is min length 6 characters.</span>
        <span v-if="!matchPassword && dataPassword.retypeNewPassword.length >= 6" style="color: red;">
          <i class="fa fa-times" style="margin: 30x 10px"></i>
          Password not match.
        </span>
        <span
          v-if="matchPassword && dataPassword.retypeNewPassword.length >= 6"
          style="color: green;"
        >
          <i class="fa fa-check" style="margin: 30x 10px"></i>
          Password match.
        </span>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button
        v-if="section == 'edit-profile'"
        @click="cancelBtn('edit-profile')"
        class="btn btn-danger"
      >Cancel</button>
      <button
        v-if="section == 'change-password'"
        @click="cancelBtn('change-password')"
        class="btn btn-danger"
      >Cancel</button>

      <button
        v-if="section == 'edit-profile'"
        @click="updateProfile()"
        class="btn btn-success"
      >Update Profile</button>
      <button
        v-if="section == 'change-password'"
        @click="updatePassword()"
        class="btn btn-success"
      >Update Password</button>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
import * as axios from "axios";

export default {
  name: "ExecutorProfile",
  components: {},
  data() {
    return {
      section: "",
      dataExecutor: {},
      dataPassword: {
        oldPassword: "",
        newPassword: "",
        retypeNewPassword: ""
      },
      cannotNull: {
        name: true,
        email: true,
        phone: true,
        oldPassword: true,
        newPassword: true,
        retypeNewPassword: true
      },
      minLength: {
        newPassword: true,
        retypeNewPassword: true
      },
      matchPassword: true,
      formatEmail: true
    };
  },
  props: ["data"],
  methods: {
    init() {
      // name: "admin"
      // jobTitle: null
      // about: "admin pokonya bebas"
      // email: "admin@admin.com"
      // phone: "08928378123"
      // address: null

      let params = this.$props.data;
      this.section = params.section;
      if (params.section == "edit-profile") {
        this.getDetailProfile();
      }
      console.log("this params ", params);
    },
    getDetailProfile() {
      // let a = this.dataBody;
      // let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&status=${a.status}&mentor=${a.mentor}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.getMyProfile(), {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          this.dataExecutor = result;

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    postProfile(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.getMyProfile(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.$modal.hide("executor-profile");
          vm.$emit("init");


          if (this.section == 'edit-profile') {
            vm.afterPostProfile().success(
              `Profile ${body.name} Berhasil di Perbarui.`
            );
          } else if ( this.section == 'change-password' ) {
            vm.afterPostProfile().success(
              `Password Berhasil di Perbarui.`
            );
          }

        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          if ( resErr.status == 400 )  {
            vm.afterPostProfile().failed("Invalid Old Password!");
          } else {
            vm.afterPostProfile().failed(`${resErr.data.message}`);
          }

        });
    },
    afterPostProfile() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("executor-list-kategori");
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("executor-list-kategori");
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelBtn(s) {
      let msg = "";
      if (s == "edit-profile") {
        msg = "Apakah Anda yakin <br> tidak ingin merubah profile ?";
      } else if (s == "change-password") {
        msg = "Apakah Anda yakin <br> tidak ingin merubah password ?";
      }

      this.$modal.show("dialog", {
        title: "Warning",
        text: msg,
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.$modal.hide("executor-profile");
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    validatePostProfile() {
      let a = this.dataExecutor;
      let b = this.cannotNull;

      a.name == "" ? (b.name = false) : (b.name = true);
      a.email == "" ? (b.email = false) : (b.email = true);
      a.phone == "" ? (b.phone = false) : (b.phone = true);

      if (a.name !== "" && a.email !== "" && a.phone !== "") {
        return true;
      } else {
        this.$root.notification(
          "warning",
          "Warning",
          "Field Nama Lengkap, Email, Nomor WhatsApp is required."
        );
        return false;
      }
    },
    validatePostPassword() {
      let a = this.dataPassword;
      let b = this.cannotNull;

      a.oldPassword == "" ? (b.oldPassword = false) : (b.oldPassword = true);
      a.newPassword == "" ? (b.newPassword = false) : (b.newPassword = true);
      a.retypeNewPassword == ""
        ? (b.retypeNewPassword = false)
        : (b.retypeNewPassword = true);

      if (
        a.oldPassword !== "" &&
        a.newPassword !== "" &&
        a.retypeNewPassword !== ""
      ) {
        return true;
      } else {
        this.$root.notification(
          "warning",
          "Warning",
          "All field Password Cannot Null!"
        );
        return false;
      }
    },
    updateProfile() {
      let a = this.dataExecutor;
      let validate = this.validatePostProfile();
      if (validate) {
        let body = {
          name: a.name,
          jobTitle: a.jobTitle,
          about: a.about,
          email: a.email,
          phone: a.phone,
          alamat: a.alamat
        };
        this.postProfile(body);
      }
    },
    updatePassword() {
      let a = this.dataPassword;
      let validate = this.validatePostPassword();
      if (validate) {
        let body = {
          oldPassword: a.oldPassword,
          newPassword: a.retypeNewPassword
        };
        this.postProfile(body);
      }
    },
    closeModal() {
      this.$modal.hide("executor-profile");
    },
    checkEF() {
      let email = this.dataExecutor.email;
      var re = this.$root.checkValidateEmail(email);
      re == false ? (this.formatEmail = false) : (this.formatEmail = true);
    },
    checkPass(s) {
      let password = this.dataPassword;
      let minLength = this.minLength;

      if (s == "new") {
        password.newPassword.length >= 6
          ? (minLength.newPassword = true)
          : (minLength.newPassword = false);
      } else if (s == "renew") {
        password.retypeNewPassword.length >= 6
          ? (minLength.retypeNewPassword = true)
          : (minLength.retypeNewPassword = false);
      }
    },
    checkMatchPassword() {
      let password = this.dataPassword;
      password.retypeNewPassword == password.newPassword
        ? (this.matchPassword = true)
        : (this.matchPassword = false);
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-profile {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
</style>