<template>
  <section class="title-kelas">
    <div class="content-tk">
      <div class="img-kelas">
        <img
          v-bind:src="$root.getPhotoUser() + `/${dataTitleKelas.banner}`"
          v-bind:alt="dataTitleKelas.name"
        />
      </div>
      <div class="part-kelas">
        <div class="btn-back">
          <i class="fa fa-arrow-left"></i>
          <p>Kembali</p>
        </div>
        <div class="head-desc-kelas">
          <div class="tech-head-desc">
            <p
              class="tech-warning"
              v-if="dataTitleKelas.categories !== null"
            >{{ dataTitleKelas.categories.title }}</p>
            <p class="tech-success">{{ dataTitleKelas.jenis }}</p>
          </div>
          <p
            class="category-kelas"
            v-if="dataTitleKelas.subCategories !== null"
          >{{ dataTitleKelas.subCategories.title }}</p>
          <h1 class="title-hk">{{ dataTitleKelas.name }}</h1>
          <div class="rating-kelas">
            <star-rating :rating="rating" :read-only="true" :star-size="20"></star-rating>
            <p
              class="count_rating"
            >{{ dataTitleKelas.reviews.rating == null ? 0 : dataTitleKelas.reviews.rating }}</p>
            <p
              class="amount_rating"
            >{{ dataTitleKelas.reviews.count == null ? "0" : `(${ dataTitleKelas.reviews.count } Rating) ` }}</p>
          </div>
          <p class="p-head-desc">{{ dataTitleKelas.about.slice(0, 330) }}...</p>
          <button 
            class="btn btn-buy-class" 
            @click="checkout()"
            v-bind:disabled="dataTitleKelas.schedules.length == 0"
          >Beli Kelas</button>

          <div class="price-kelas">
            <p class="price-through">Rp.{{ dataTitleKelas.price }}</p>
            <div class="real-price">
              <p class="rp-price">Rp.{{ dataTitleKelas.promo }}</p>
              <p class="slots-kelas">Sisa {{ slot }} Slot</p>
            </div>
          </div>
          <div class="schedule-kelas">
            <h5 class="t-schedule">Jadwal Kelas</h5>
            <div class>
              <div id="dateKelas" class="date-kelas" v-on:click="openDateKelas()">
                <div class="choose-date">
                  <img src="/icon/calendar.svg" />
                  <p class="text-date">{{ chooseDateKelas }}</p>
                </div>
                <i class="lnr lnr-chevron-down" id="activeDateKelas"></i>
              </div>
              <ul class="list-date-kelas">
                <li
                  class="date-kl"
                  v-for="(list, idx) in dataJadwal"
                  v-bind:key="idx"
                  v-bind:readonly="!list.available"
                  v-on:click="chooseDK(list, list.available)"
                >
                  <input
                    class="radio-kl radio-mbx"
                    type="radio"
                    name="date-list"
                    v-bind:id="'radio-kl-' + idx"
                    v-bind:disabled="!list.available"
                    v-bind:checked="list.date_kelas == chooseDateKelas"
                  />
                  <label v-bind:for="'radio-kl-' + idx" class="text-date">{{ list.date_kelas }}</label>
                  <p class="status-kl-available" v-if="list.available">Available</p>
                  <p class="status-kl-unavailable" v-else>Unvailable</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from "jquery";
import StarRating from "vue-star-rating";
import * as moment from "moment";

export default {
  name: "TitleKelas",
  components: {
    StarRating
  },
  props: ["data"],
  data() {
    return {
      chooseDateKelas: "Pilih Jadwal",
      slot: 0,
      scheduleId: 0,
      rating: 0,
      dateListKelas: [
        {
          id: 1,
          date_kelas: "20 April 2020",
          available: true
        },
        {
          id: 2,
          date_kelas: "21 April 2020",
          available: true
        },
        {
          id: 3,
          date_kelas: "22 April 2020",
          available: true
        },
        {
          id: 4,
          date_kelas: "23 April 2020",
          available: true
        },
        {
          id: 5,
          date_kelas: "24 April 2020",
          available: false
        }
      ],
      dataTitleKelas: {
        name: "",
        banner: "",
        about: "",
        note: "",
        reviews: {
          count: 0,
          rating: null
        },
        price: 0,
        promo: 0,
        discount: 0,
        jenis: "",
        schedules: [],
        categories: {
          id: "",
          slug: "",
          title: ""
        },
        subCategories: {
          id: "",
          slug: "",
          title: ""
        }
      },
      dataJadwal: [],
      forCheckout: {
        uuid: "",
        name: "",
        price: "",
        promo: "",
        diskon: "",
        total: "",
        scheduleId: 0
      }
    };
  },
  methods: {
    init() {
      console.log(
        "dataTitleKelas from init ",
        this.dataTitleKelas,
        this.$props.data
      );
      window.scheduleId = 0;
      this.scheduleId = window.scheduleId;

      this.dataTitleKelas = this.$store.state.dataDetailKelas;
      setTimeout(() => {
        $(".vue-star-rating-rating-text").hide();
      }, 1500);
    },
    openDateKelas() {
      $("#dateKelas").toggleClass("active");
      $("#activeDateKelas").toggleClass("lnr-chevron-down lnr-chevron-up");
      $(".list-date-kelas").slideToggle();
    },
    chooseDK(data, status) {
      if (status) {
        window.scheduleId = data.id;
        this.scheduleId = data.id;
        this.chooseDateKelas = data.date_kelas;
        this.slot = data.slot;
      }
    },
    handleData(newData) {
      this.dataTitleKelas = newData;
      this.rating = newData.reviews.rating;
      // this.slot = newData.schedules[0].slot;
      // this.scheduleId = newData.schedules[0].id;
      // this.chooseDateKelas = moment(
      //   newData.schedules[0].startDate.split(" ")[0]
      // ).format("DD - MMM - YYYY");
      this.dataJadwal = newData.schedules.map(e => {
        let obj = {
          id: e.id,
          slot: e.slot,
          date_kelas: moment(e.startDate.split(" ")[0]).format(
            "DD - MMM - YYYY"
          ),
          available: e.slot !== 0 ? true : false
        };
        return obj;
      });

      let forCheckout = this.forCheckout;
      forCheckout.uuid = newData.uuid;
      forCheckout.name = newData.name;
      forCheckout.price = newData.price;
      forCheckout.promo = newData.promo;
      forCheckout.total = newData.promo;
      forCheckout.scheduleId = this.scheduleId;
    },
    checkout() {
      let url = "/detail-pembayaran/" + "checkout";
      url;
      let auth = this.$root.authentication();
      let roleUser = this.$root.checkRole("USER");
      let forCheckout = this.forCheckout;
      forCheckout.scheduleId = window.scheduleId;
      console.log("role user ", roleUser);
      if (!auth) {
        this.$router.push("/signin/user/login");
        this.$root.notification(
          "warning",
          "Warning",
          "Anda belum Login, silahkan Login terlebih dahulu."
        );
      } else if (!roleUser) {
        this.$root.notification(
          "warning",
          "Warning",
          "Anda bukan seorang User, silahkan Daftar sebagai User."
        );
      } else if ( this.scheduleId == 0 ) {
        this.$root.notification(
          "warning",
          "Warning",
          "Anda belum memilih Jadwal, Silahkan pilih Jadwal terlebih dahulu."
        );
      } else {
        let data = this.forCheckout;
        this.$root.executeCheckout(data);
      }
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    getFromProps() {
      let data = this.$props.data;
      return data;
    }
  },
  watch: {
    getFromProps(newData, oldData) {
      this.handleData(newData);
      oldData;
      // console.log("watch from top props ", newData, oldData);
    }
  }
};
</script>

