            border-radius: 0 0 7px 7px;
            display: none;
            position: absolute;
            width: 300px;
            > .date-kl {
              list-style: none;
              display: flex;
              align-items: center;
              padding: 3px 0;
              &[readonly="readonly"] {
                opacity: 0.5;
                cursor: not-allowed;
                > label {
                  cursor: not-allowed;
                }
              }
              > .text-date {
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 15px;
                width: 165px;
