                margin: 7px 0;
                padding: 0px 0 0 39px;
              }
              > p {
                color: #ffffff;
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                width: 95px;
                padding: 3px;
                text-align: center;
                margin: 0px 0 0 auto;
                border-radius: 20px;
                &.status-kl-available {
                  background: #219653;
                }
                &.status-kl-unavailable {
