<template>
  <div class="content-list-kelas">
    <h3 class="title-lk">List Kelas</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="searchList()"
          />
          <button 
            class="btn btn-primary"
            @click="searchList()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Kelas</button>
      </div>
    </div>
    <div class="filter-table-mbx">
      <h5>Filter : </h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Status : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(status, index) in dataTable.filter.status"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="status" 
                  v-bind:id="'f-status-'+ status.value" 
                  class="radio-mbx"
                  v-bind:value="status.value"
                  v-bind:checked="dataBody.status == status.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-status-'+status.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().status(status.value)"
                >{{ status.text }}</label>
              </template>
            </div>
          </li>
          <li>
            <div class="left">
              <span>Jenis : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(jenis, index) in dataTable.filter.jenis"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="jenis" 
                  v-bind:id="'f-jenis-'+ jenis.value" 
                  class="radio-mbx"
                  v-bind:value="jenis.value"
                  v-bind:checked="dataBody.jenis == jenis.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-jenis-'+jenis.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().jenis(jenis.value)"
                >{{ jenis.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">Banner</div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Name Kelas
                  <i @click="tableMbx().search('name')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.name" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.search"
                    @keyup.enter="init()" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Mentor
                  <i @click="tableMbx().search('mentor')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.mentor" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.mentor"
                    @keyup.enter="init()" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Price
                  <i @click="tableMbx().search('price')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.price" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.search"
                    @keyup.enter="init()" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Promo
                  <i @click="tableMbx().search('promo')" class="fa fa-search"></i>
                </div>
                <div v-if="dataTable.colSearch.promo" class="th-search-tbl-mbx">
                  <input 
                    type="text" 
                    name="search-tbl" 
                    placeholder="Search here..."
                    v-model="dataBody.search"
                    @keyup.enter="init()" 
                  />
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Status
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jenis
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jadwal
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 140px;width: 120px;">
                <div class="btn-action-tbl-mbx">
                  <button 
                    class="btn btn-success"
                    @click="detailKelas(list.uuid)"
                  >
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button 
                    class="btn btn-warning"
                    @click="editLK(list.uuid)"
                  >
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>
               {{  list.banner }}
                <div class="img-banner">
                  <img 
                    v-bind:src="$root.getPhotoUser() + `/${list.banner}`" 
                    alt=""
                  >
                </div>
              <td>{{ list.name }}</td>
              <td>{{ list.mentor }}</td>
              <td>
                <div class="list-price-kelas">
                  <p>Rp. 
                    <span> {{ list.price }}</span>
                  </p>
                </div>
              </td>
              <td>
                <div class="list-price-kelas">
                  <p>Rp. 
                    <span> {{ list.promo }}</span>
                  </p>
                </div>
              </td>
              <td>{{ list.status }}</td>
              <td>{{ list.jenis }}</td>
              <td>
                <ol class="list-jadwal-kelas">
                  <li v-for="(j, idx) in list.schedules" v-bind:key="idx">
                    <p>
                      Name :
                      <span>{{ j.name }}</span>
                    </p>
                    <p>
                      Start date :
                      <span>{{ j.startDate }}</span>
                    </p>
                    <p>
                      End date :
                      <span>{{ j.endDate }}</span>
                    </p>
                  </li>
                </ol>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Kelas Kosong, Silahkan Tambah Kelas.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li 
							v-for="(page, index) in dataTable.rowPerPage"
							v-bind:key="index"
							@click="tableMbx().rowPerPage(page)"
						>{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input 
						type="text" 
						v-model="dataTable.page.gotopage"
						@keyup.enter="tableMbx().gotopage()"
					/>
          <p
						@click="tableMbx().gotopage()"
					>Go</p>
        </div>
      </div>
    </div>

    <modal name="add-list-kelas" :width="'90%'">
      <p>kuy gaskeun add kelas</p>
    </modal>

    <modal name="detail-kelas" :clickToClose="false" :width="'70%'" :height="'80%'">
      <DetailKelas :data="dataDetailKelas" />
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import DetailKelas from "./DetailKelas";

export default {
  name: "ListKelas",
  components: {
    DetailKelas
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        jenis: "",
        status: "",
        mentor: "",
        search: "",
      },
      dataDetailKelas: {
        section: '',
        uuid: ''
      },
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
					total: 1,
					gotopage: ''
				},
				perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Draft',
              value: 'Draft'
            },
            {
              text: 'Publish',
              value: 'Publish'
            },
            {
              text: 'Reject',
              value: 'Reject'
            },
          ],
          jenis: [
            {
              text: 'WhatsApp',
              value: 'WhatsApp'
            },
            {
              text: 'Webinar',
              value: 'Webinar'
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Table:
      //   Action (Edit, Detail)
      //   Banner,
      //   Name,
      //   Mentor,
      //   Price (Harga Coret & Harga Jual),
      //   Jadwal (Nama Batch & Tanggal),
      //   Status,
      // Filter : Jenis, Status, Mentor
      // Search : Name, hargaCoret, hargaJual

      // id: 8,
      // uuid: "4706d298-fb28-40df-bb50-6def63203747",
      // slug: "kelas-vue-js",
      // banner: "5_6337001753057165546-2.pdf",
      // name: "Kelas Vue JS",
      // price: 70000000,
      // promo: 90000000,
      // status: "Draft",
      // mentor: "Mentor",
      // schedules: [
      //   {
      //     name: "yeah",
      //     startDate: "2020-06-24 00:00:00",
      //     endDate: "2020-06-25 00:00:00"
      //   },
      //   {
      //     name: "kuy",
      //     startDate: "2020-06-26 00:00:00",
      //     endDate: "2020-06-27 00:00:00"
      //   }
      // ]

      let a = this.dataBody;
      a.page = 1;
      a.limit = this.dataTable.perPage;
      this.getListKelas();
    },
    getListKelas() {
      this.$root.loadingData().show();
      let a = this.dataBody;
      let AuthSth = this.$root.authorization();
      let sQuery = `?page=${a.page}&limit=${a.limit}&jenis=${a.jenis}&status=${a.status}&mentor=${a.mentor}&search=${a.search}`;
      axios
        .get(this.$root.listKelasAdmin() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchList() {
      this.init();
    },
    detailKelas(uuid) {
      let a = this.dataDetailKelas;
      a.uuid = uuid;

      this.$modal.show("detail-kelas");
      console.log('uuid ', uuid);
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
            b['mentor'] = "";
            b['search'] = "";
            vm.init();
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
					a.page.now++;
					b.page++
          vm.init();
        },
        backTable() {
					a.page.now--;
					b.page--
          vm.init();
				},
				gotopage() {
					let val = a.page.gotopage;
					if ( val > a.page.total) {
						vm.$root.notification('warning', 'Warning', `Total Page is ${a.page.total}, cannot go to page ${val}!`)
					} else {
						a.page.now = val;
						b.page = val;
						vm.init();
					}
				},
				perPage() {
					$(".list-pp").slideToggle();
				},
				rowPerPage(page) {
					a.perPage = page;
					b.limit = page;
					vm.tableMbx().perPage();
					vm.getListKelas();
					console.log('rowPerPage ', page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            },
            jenis: (val) => {
              b.jenis = val;
              this.init();
            }
          }
        }
      };
    },
    addLK() {
      this.$router.push("/product-mentor");
    },
    editLK(uuid) {
      uuid;
      this.$router.push("/pm" + `/${uuid}`);
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-kelas {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 170%;
  }
}
.img-banner {
  > img {
    width: 300px;
    height: 200px;
  }
}
.list-price-kelas {
  text-align: left;
  > p {
    margin: 0%;
    font-weight: 700;
    > span {
      font-weight: 500;
    }
  }
}
.list-jadwal-kelas {
  margin: 0%;
  padding: 0 0 0 30px;
  > li {
    margin: 3px 0;
    text-align: left;
    > p {
      margin: 0%;
      font-weight: 700;
      > span {
        font-weight: 500;
      }
    }
  }
}
</style>