<template>
  <div class="content-list-kelas">
    <h3 class="title-lk">List Kelas</h3>
    <div class="top-table-mbx">
      <div class="left">
        <div class="input-search-tbl-mbx">
          <input 
            type="text" 
            class 
            placeholder="Search here..." 
            v-model="dataBody.search"
            @keyup.enter="init()"
          />
          <button 
            class="btn btn-primary"
            @click="init()"
          >
            <i class="fa fa-search"></i>
          </button>
        </div>
      </div>
      <div class="right">
        <button @click="addLK()" class="btn btn-primary btn-action-top-tbl-mbx">Add Kelas</button>
      </div>
    </div>
    <div class="filter-table-mbx">
      <h5>Filter : </h5>
      <div class="form-filter">
        <ul class="list-f-filter">
          <li>
            <div class="left">
              <span>Status : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(status, index) in dataTable.filter.status"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="status" 
                  v-bind:id="'f-status-'+ status.value" 
                  class="radio-mbx"
                  v-bind:value="status.value"
                  v-bind:checked="dataBody.status == status.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-status-'+status.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().status(status.value)"
                >{{ status.text }}</label>
              </template>
            </div>
          </li>
          <li>
            <div class="left">
              <span>Jenis : </span>
            </div>
            <div class="right d-flex">
              <template 
                v-for="(jenis, index) in dataTable.filter.jenis"
              >
                <input 
                  v-bind:key="index+5" type="radio" name="jenis" 
                  v-bind:id="'f-jenis-'+ jenis.value" 
                  class="radio-mbx"
                  v-bind:value="jenis.value"
                  v-bind:checked="dataBody.jenis == jenis.value"
                >
                <label 
                  v-bind:key="index" 
                  v-bind:for="'f-jenis-'+jenis.value" 
                  style="margin: 3px 5px;"
                  @click="tableMbx().filter().jenis(jenis.value)"
                >{{ jenis.text }}</label>
              </template>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="content-table-mbx">
      <div class="table-list-mbx">
        <table id="tableLK" class="table-mbx">
          <thead>
            <tr>
              <th>
                <div class="th-tbl-mbx">Action</div>
              </th>
              <!-- <th>ID</th> -->
              <th>
                <div class="th-tbl-mbx">
                  Name
                  <span
                    @click="tableMbx().sort('list-name')"
                    id="list-name"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Promo
                  <span
                    @click="tableMbx().sort('list-promo')"
                    id="list-promo"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Price
                  <span
                    @click="tableMbx().sort('list-price')"
                    id="list-price"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jadwal
                  <span
                    @click="tableMbx().sort('list-jadwal')"
                    id="list-jadwal"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Jenis
                  <span
                    @click="tableMbx().sort('list-jenis')"
                    id="list-jenis"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Status
                  <span
                    @click="tableMbx().sort('list-status')"
                    id="list-status"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Kategori
                  <span
                    @click="tableMbx().sort('list-kategori')"
                    id="list-kategori"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
              <th>
                <div class="th-tbl-mbx">
                  Sub Kategori
                  <span
                    @click="tableMbx().sort('list-subKategori')"
                    id="list-subKategori"
                    class="sort-tbl-list"
                  >
                    <i class="fa fa-sort-up"></i>
                    <i class="fa fa-sort-down"></i>
                  </span>
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(list, index) in dataTable.list" v-bind:key="index">
              <td style="max-width: 120px;width: 100px;">
                <div class="btn-action-tbl-mbx">
                  <button @click="detailKelas(list.uuid)" class="btn btn-success">
                    <i class="fa fa-external-link"></i>
                  </button>
                  <button @click="editLK(list.uuid)" class="btn btn-warning">
                    <i class="fa fa-pencil"></i>
                  </button>
                  <!-- <button class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                  </button>-->
                </div>
              </td>
              <!-- <td>{{ list.id }}</td> -->
              <td>{{ list.name }}</td>
              <td>Rp.{{ list.hargaJual }}</td>
              <td>Rp.{{ list.hargaCoret }}</td>
              <td>
                <ol class="list-jadwal-kelas">
                  <li v-for="(j, idx) in list.jadwal" v-bind:key="idx">
                    <p>Batch : <span>{{ j.batch }}</span></p>
                    <p>Slot : <span>{{ j.slot }}</span></p>
                    <p>Start date : <span>{{ j.startDate }}</span></p>
                    <p>End date : <span>{{ j.endDate }}</span></p>
                  </li>
                </ol>
              </td>
              <td>{{ list.jenis }}</td>
              <td>{{ list.status }}</td>
              <td>{{ list.kategori  == null ? "-" : list.kategori.title }}</td>
              <td>{{ list.subKategori == null ? "-" : list.subKategori.title }}</td>
              
              <!-- <td>
                <ul>
                  <li v-for="(role, index) in list.roles" v-bind:key="index">{{ role }}</li>
                </ul>
              </td> -->
            </tr>
          </tbody>
        </table>
      </div>
      <div
        class="data-list-kosong"
        v-if="dataTable.list.length == 0"
      >Data List Kelas Kosong, Silahkan Tambah Kelas.</div>
      <div class="pagination-tbl-mbx" v-else>
        <div class="per-page-tbl-mbx">
          <p>Row Per Page</p>
          <p @click="tableMbx().perPage()">
            {{ dataTable.perPage }}
            <i class="fa fa-sort-down"></i>
          </p>
          <ul class="list-pp">
            <li
              v-for="(page, index) in dataTable.rowPerPage"
              v-bind:key="index"
              @click="tableMbx().rowPerPage(page)"
            >{{ page }}</li>
          </ul>
        </div>
        <div class="total-data-tbl-mbx">
          <i
            @click="tableMbx().backTable()"
            v-if="dataTable.page.now !== 1"
            class="fa fa-chevron-left"
          ></i>
          <p>
            Page
            <span>{{ dataTable.page.now }}</span>
          </p>
          <p>
            Of
            <span>{{ dataTable.page.total }}</span>
          </p>
          <i
            v-if="dataTable.page.total > 1 && dataTable.page.now !== dataTable.page.total"
            @click="tableMbx().nextTable()"
            class="fa fa-chevron-right"
          ></i>
        </div>
        <div class="gotopage-tbl-mbx">
          <p>Go to Page</p>
          <input type="text" v-model="dataTable.page.gotopage" @keyup.enter="tableMbx().gotopage()" />
          <p @click="tableMbx().gotopage()">Go</p>
        </div>
      </div>
    </div>

    <modal name="detail-kelas" :width="'80%'" :height="'80%'">
      <DetailKelas :data="dataDetailKelas" />
    </modal>

  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import DetailKelas from './DetailKelas';

export default {
  name: "ListKelas",
  components: {
    DetailKelas,
  },
  data() {
    return {
      dataBody: {
        page: 1,
        limit: 10,
        status: "",
        jenis: "",
        search: ""
      },
      dataDetailKelas: {
        section: '',
        uuid: ''
      },
      dataTable: {
        list: [],
        colSearch: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        colSort: {
          name: false,
          mentor: false,
          banner: false,
          status: false,
          price: false,
          promo: false,
          slug: false
        },
        page: {
          now: 1,
          total: 1,
          gotopage: ""
        },
        perPage: 10,
        rowPerPage: [5, 10, 20],
        filter: {
          status: [
            {
              text: 'Draft',
              value: 'Draft'
            },
            {
              text: 'Publish',
              value: 'Publish'
            },
            {
              text: 'Reject',
              value: 'Reject'
            },
          ],
          jenis: [
            {
              text: 'WhatsApp',
              value: 'Whatsapp'
            },
            {
              text: 'Webinar',
              value: 'Webinar'
            }
          ]
        }
      }
    };
  },
  methods: {
    init() {
      // Action (Edit)
      // Name, 
      // Price (Harga Coret & Harga Jual), 
      // Jadwal (Nama Batch & Tanggal),
      // Jenis 
      // Status, 


      // name: "Kelas Vue JS"
      // hargaCoret: 70000000
      // hargaJual: 90000000
      // jadwal: Array(2)
      // 0: {batch: "yeah", slot: 10, startDate: "2020-06-24 00:00:00", endDate: "2020-06-25 00:00:00"}
      // 1: {batch: "kuy", slot: 20, startDate: "2020-06-26 00:00:00", endDate: "2020-06-27 00:00:00"}
      // about: "mari kita terus semangat belajar"
      // id: 8
      // jenis: "Kelas WhatsApp"
      // kategori: ["Advertising"]
      // kurikulum: Array(2)
      // 0: {topik: "gaskeun", materi: "kemana aja", description: "liburan"}
      // 1: {topik: "hayu", materi: "gaskeun", description: "kemana aja"}
      // note: "semangat belajar ngoding"
      // slug: "kelas-vue-js"
      // subKategori: ["FB Ads"]
      // uuid: "4706d298-fb28-40df-bb50-6def63203747"

      this.getListKelas();
    },
    getListKelas() {
      let a = this.dataBody;
      let sQuery = `?page=${a.page}&limit=${a.limit}&status=${a.status}&jenis=${a.jenis}&search=${a.search}`;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listKelasMentor() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let dataTable = this.dataTable;
          dataTable.page.total = data.pages == 0 ? 1 : data.pages;

          let result = res.data.result;
          this.dataTable.list = [];
          let list = this.dataTable.list;
          for (let i = 0; i < result.length; i++) {
            list.push(result[i]);
          }

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    tableMbx() {
      let a = this.dataTable;
      let b = this.dataBody;
      let vm = this;
      return {
        search(field) {
          if (!a.colSearch[field]) {
            a.colSearch[field] = true;
          } else {
            a.colSearch[field] = false;
          }
        },
        sort(cls) {
          let field = cls.split("-")[1];
          let cond = a.colSort[field];
          let up = $("#" + cls).find("i.fa-sort-up");
          let down = $("#" + cls).find("i.fa-sort-down");

          if (!cond) {
            a.colSort[field] = true;
            up.removeClass("active");
            down.addClass("active");
          } else {
            a.colSort[field] = false;
            up.addClass("active");
            down.removeClass("active");
          }
        },
        nextTable() {
          a.page.now++;
          b.page++;
          vm.init();
        },
        backTable() {
          a.page.now--;
          b.page--;
          vm.init();
        },
        gotopage() {
          let val = a.page.gotopage;
          if (val > a.page.total) {
            vm.$root.notification(
              "warning",
              "Warning",
              `Total Page is ${a.page.total}, cannot go to page ${val}!`
            );
          } else {
            a.page.now = val;
            b.page = val;
            vm.init();
          }
        },
        perPage() {
          $(".list-pp").slideToggle();
        },
        rowPerPage(page) {
          a.perPage = page;
          b.limit = page;
          vm.tableMbx().perPage();
          vm.init();
          console.log("rowPerPage ", page);
        },
        filter: () => {
          return {
            status: (val) => {
              b.status = val;
              this.init();
            },
            jenis: (val) => {
              b.jenis = val;
              this.init();
            }
          }
        }
      };
    },
    addLK() {
      this.$router.push("/product-mentor");
    },
    editLK(uuid) {
      uuid;
      this.$router.push("/pm" + `/${uuid}`);
    },
    detailKelas(uuid) {
      let a = this.dataDetailKelas;
      a.uuid = uuid;

      this.$modal.show("detail-kelas");
      console.log('uuid ', uuid);
    }
  },
  mounted() {
    $("#tableLK");
    this.init();
  }
};
</script>,

<style lang="scss" scoped>
.caret {
  display: none;
}
.form-inline {
  justify-content: flex-end;
}
.footable.table {
  width: 115%;
  max-width: 115%;
}
.content-list-kelas {
  font-family: "Montserrat";
  > .title-lk {
    color: #000000;
    font-size: 1.7em;
    text-align: left;
    margin: 10px 0;
  }
  .btn-action-top-tbl-mbx {
    margin: 10px 0;
  }
  #tableLK {
    width: 150%;
  }
}
.list-jadwal-kelas {
  margin: 0%;
  padding: 0 0 0 30px;
  > li {
    margin: 3px 0;
    text-align: left;
    > p {
      margin: 0%;
      font-weight: 700;
      > span {
        font-weight: 500;
      }
    }
  }
}
</style>