<template>
  <div class="review-kelas modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Review Kelas</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-rk">
      <div class="title-ks-rk">
        <h5>{{ dataProps.name }}</h5>
        <p>
          Oleh :
          <span>{{ dataProps.mentor }}</span>
        </p>
      </div>

      <div class="form-input-mbx">
        <label for="title" style="margin: 0 0 10px 0;">Rating</label>
        <star-rating :rating="dataBody.rating" :star-size="25"></star-rating>
      </div>
      <div class="form-input-mbx">
        <label for="title">Review</label>
        <textarea
          type="text"
          class="form-control"
          name="title"
          placeholder="Review"
          v-model="dataBody.review"
        ></textarea>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelReview()" class="btn btn-danger">Cancel</button>
      <button @click="submitReview()" class="btn btn-success">Submit Review</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import StarRating from "vue-star-rating";

export default {
  name: "ReviewKelas",
  components: {
    StarRating
  },
  data() {
    return {
      dataProps: {},
      dataBody: {
        coursesId: "",
        rating: 5,
        review: ""
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      this.handleData();
      setTimeout(() => {
        $(".vue-star-rating-rating-text").hide();
      }, 1500);
    },
    handleData() {
      let data = this.$props.data;
      this.dataProps = data;
      this.dataBody.coursesId = data.id;
    },
    submitReview() {
      let body = this.dataBody;
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.submitReviewKelasUser(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

					vm.$root.loadingData().hide();
					vm.closeModal();
          vm.$root.notification(
            "success",
            "Success",
            `Review untuk kelas ${this.dataProps.name} Berhasil di Submit. Terimakasih.`
          );

          vm.$emit("init");
        })
        .catch(err => {
					let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
					}
					
          console.log("err ", resErr);
          vm.$root.loadingData().hide();
        });
    },
    closeModal() {
      this.$modal.hide("review-kelas");
    },
    cancelReview() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin memberikan Review Kelas ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.$modal.hide("dialog");
              this.closeModal();
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.review-kelas {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
}
</style>