export default {
  name: "DetailKelas",
  components: {},
  data() {
    return {
      uuidKelas: "",
      dataDetailKelas: {
        about: '' ,
        banner: '' ,
        categories: '' ,
        curriculums: '' ,
        discount: '' ,
        id: '' ,
        jenis: '' ,
        mentor: '' ,
        name: '' ,
        note: '' ,
        price: '' ,
        promo: '' ,
        reviews: '' ,
        schedules: '' ,
        slug: '' ,
        status: '' ,
        subCategories: '' ,
      },
      categories: "",
      subCategories: ""
    };
  },
  methods: {
    init() {
      // banner: "5_6337001753057165546-2.pdf"
      // name: "Kelas Vue JS"
      // about: "mari kita terus semangat belajar"
      // note: "semangat belajar ngoding"
      // jenis: "Kelas WhatsApp"
      // mentor: Object
      // price: 70000000
      // promo: 90000000
      // categories: Array(1)
      // subCategories: Array(1)
