      // let data = {
      //   result: {
      //     id: 8,
      //     uuid: "4706d298-fb28-40df-bb50-6def63203747",
      //     slug: "kelas-vue-js",
      //     banner: "5_6337001753057165546-2.pdf",
      //     name: "Kelas Vue JS",
      //     about: "mari kita terus semangat belajar",
      //     note: "semangat belajar ngoding",
      //     price: 70000000,
      //     promo: 90000000,
      //     discount: -20000000,
      //     jenis: "Kelas WhatsApp",
      //     mentor: { name: "Mentor", about: null },
      //     curriculums: [
      //       {
      //         id: 6,
      //         topics: "gaskeun",
      //         materi: "kemana aja",
      //         description: "liburan",
      //         files: "Undangan BUKBER.pdf"
      //       },
      //       {
      //         id: 7,
      //         topics: "hayu",
      //         materi: "gaskeun",
      //         description: "kemana aja",
      //         files: "WFH & Corona Preventif.pdf"
      //       }
      //     ],
      //     schedules: [
      //       {
      //         id: 9,
      //         name: "yeah",
      //         slot: 10,
      //         startDate: "2020-06-24 00:00:00",
      //         endDate: "2020-06-25 00:00:00"
      //       },
      //       {
      //         id: 10,
      //         name: "kuy",
      //         slot: 20,
      //         startDate: "2020-06-26 00:00:00",
      //         endDate: "2020-06-27 00:00:00"
      //       }
      //     ],
      //     categories: ["Advertising"],
      //     subCategories: ["FB Ads"],
      //     reviews: { count: 0, rating: null }
      //   }
      // };

      this.getDetailKelas();
    },
    getDetailKelas() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listKelasAdmin() + `/${this.uuidKelas}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;
          this.dataDetailKelas = result;
          this.categories = result.Categories == null ? "-" : result.Categories.title;
          this.subCategories = result.Sub_Categories == null ? "-" : result.Sub_Categories.title;
          console.log("data detail kelas ", this.dataDetailKelas);
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.$root.notification("danger", "Error", "Check Error");
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    closeModal() {
      this.$modal.hide("detail-kelas");
    }
  },
  props: ["data"],
  mounted() {
    let dataParams = this.$props.data;
    this.uuidKelas = dataParams.uuid;
    console.log("data params ", dataParams);
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-detail-kelas {
  padding: 15px;
  font-family: "Montserrat";
  height: 100%;
	overflow-y: auto;
  > .content-dk {
    .show-image {
      display: flex;
      justify-content: center;
      margin: 15px 0;
      > img {
        width: 700px;
        height: 580px;
        border-radius: 10px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
      }
    }
  }
}
.list-jadwal-kelas {
  margin: 0%;
  padding: 0 0 0 30px;
  > li {
    margin: 3px 0;
    text-align: left;
    > p {
      margin: 0%;
      font-weight: 700;
      > span {
        font-weight: 500;
      }
    }
  }
}
</style>