<template>
  <section class="dashboard-mentor">
    <HeaderTop />

    <ContentDashboardMentor />

    <Footer />
  </section>
</template>

<script>
import HeaderTop from "@/components/HeaderTop.vue";
import Footer from "@/components/Footer.vue";

import ContentDashboardMentor from "@/components/DashboardMentor/ContentDashboardMentor.vue";

export default {
  name: "DashboardMentor",
  components: {
    HeaderTop,
    Footer,
    ContentDashboardMentor,
  },
  data() {
    return {
    }
  },
  methods: {
    init() {
      setTimeout(() => {
        this.$root.loadingMentorbox().hide();
      }, 2000);
    }
  },
  mounted() {
    this.init();
    this.$root.goToTop();
  },
  watch: {
    $route(to, from) {
      to;
      from;
    }
  }
};
</script>

