<template>
  <div class="tab-kurikulum-kelas">
    <div class="content-tkk">
      <h3 class="title-tkk">Kurikulum Kelas</h3>
      <template v-for="(kkl, index) in dataKurikulum">
        <div v-bind:key="index" class="content-form-tkk">
          <div class="left">
            <div class="form-input-tkk inline">
              <label for="topics">Topik</label>
              <input
                type="text"
                class="form-control"
                name="topics"
                v-bind:id="'topics' + index"
                placeholder="Topik"
                v-model="kkl.topics"
              />
            </div>
            <div class="form-input-tkk inline">
              <label for="description">Deskripsi</label>
              <input
                type="text"
                class="form-control"
                name="description"
                v-bind:id="'description' + index"
                placeholder="Deskripsi"
                v-model="kkl.description"
              />
            </div>
            <div class="form-input-tkk inline">
              <label for="materi">Materi</label>
              <input
                type="text"
                class="form-control"
                name="materi"
                v-bind:id="'materi' + index"
                placeholder="Materi"
                v-model="kkl.materi"
              />
            </div>
            <div class="form-input-tkk inline-file">
              <label for="unggah-dokumen">Unggah Dokumen</label>
              <input
                type="file"
                class="form-control"
                name="unggah-dokumen"
                v-bind:id="'unggahDokumen' + index"
                placeholder="Unggah Dokumen"
                v-on:change="handleFile($event, index)"
              />
              <div class="file-upload-div d-flex" v-on:click="chooseFile('unggahDokumen' + index)">
                <span>{{ kkl.files }}</span>
                <span class="btn btn-up">Unggah Dokumen</span>
              </div>
            </div>
          </div>
          <div class="right">
            <div class="icon-plus-minus-jadwal">
              <span
                class="minus-jadwal"
                @click="removeKurikulum(index)"
                v-if="dataKurikulum.length !== index + 1"
              >
                <i class="fa fa-trash"></i>
                Hapus
              </span>
              <span
                class="plus-jadwal"
                @click="addKurikulum()"
                v-if="dataKurikulum.length == index + 1"
              >
                <i class="fa fa-plus"></i>
                Tambah
              </span>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "TabKurikulumKelas",
  components: {},
  data() {
    return {
      formKurikulumKelas: {
        kurikulum: "Kurikulum"
      },
      dataKurikulum: this.$store.state.dataProductMentor.curriculum
    };
  },
  methods: {
    init() {},
    chooseFile(id) {
      $("#" + id).click();
    },
    handleFile(evt, idx) {
      let nameFile = evt.target.files[0].name;

      console.log("files ", nameFile, idx);
      this.dataKurikulum[idx].files = nameFile;
    },
    addKurikulum() {
      let a = this.dataKurikulum;
      let list = {
        topics: "",
        description: "",
        materi: "",
        files: ""
        // unicId: this.$root.makeid(7)
      };
      a.push(list);
    },
    removeKurikulum(idx) {
      let a = this.dataKurikulum;
      this.$modal.show("dialog", {
        title: "Warning",
        text: `Apakah kamu yakin ingin menghapus kurikulum "${this.dataKurikulum[idx].topics}" ?`,
        buttons: [
          {
            title: "Cancel"
          },
          {
            title: "Ya",
            handler: () => {
              a.splice(idx, 1);
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
		this.init();
	}
};
</script>

<style lang="scss" scoped>
.tab-kurikulum-kelas {
  .content-tkk {
    > .title-tkk {
      font-style: normal;
      font-weight: 500;
      font-size: 33px;
      line-height: 44px;
    }
    .content-form-tkk {
      display: flex;
      padding: 0 0 10px 0;
      border-bottom: 1px solid #000000;
      > .left {
        width: 80%;
      }
      > .right {
        width: 20%;
        display: flex;
        align-items: center;
        > .icon-plus-minus-jadwal {
          display: flex;
          flex-direction: column;
          margin: auto;
          > span {
            padding: 10px 20px;
            border-radius: 10px;
            cursor: pointer;
            &.minus-jadwal {
              background: #f00707;
              color: #ffffff;
              border: 1px solid #f00707;
              margin: 0 0 5px 0;
              &:hover {
                background: #ffffff;
                color: #f00707;
              }
            }
            &.plus-jadwal {
              background: #003461;
              color: #ffffff;
              border: 1px solid #003461;
              margin: 5px 0 0 0;
              &:hover {
                background: #ffffff;
                color: #003461;
              }
            }
          }
        }
      }
    }
    .form-input-tkk {
      text-align: left;
      margin: 5px 0;
      > label {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 44px;
        color: #000000;
        margin: 0%;
      }
      > input {
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 1px solid #000000;
        border-radius: 0%;
        &[type="file"] {
          display: none;
        }
      }
      > .file-upload-div {
        justify-content: space-between;
        border-bottom: 1px solid #000000;
        padding: 6px 12px;
        align-items: center;
        cursor: pointer;
        > .btn-up {
          background: #003461;
          border-radius: 10px;
          color: #ffffff;
        }
      }
      > .date-form {
        justify-content: space-between;
        > input {
          width: 49%;
          border-top: none;
          border-right: none;
          border-left: none;
          border-bottom: 1px solid #000000;
          border-radius: 0%;
        }
      }

      &.inline {
        display: flex;
        > label {
          width: 25%;
          font-size: 1.5em;
        }
        > input {
          width: 75%;
        }
      }
      &.inline-file {
        display: flex;
        > label {
          width: 25%;
          font-size: 1.3em;
        }
        > .file-upload-div {
          width: 75%;
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .tab-kurikulum-kelas {
    .content-tkk {
      > .title-tkk {
        font-size: 25px;
      }
      > .form-input-tkk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .tab-kurikulum-kelas {
    .content-tkk {
      > .title-tkk {
        font-size: 25px;
      }
      > .form-input-tkk {
        > label {
          font-size: 20px;
        }
        > input {
          font-size: 18px;
        }
        > .file-upload-div {
          > .btn-up {
            font-size: 15px;
          }
        }
        > .date-form {
          > input {
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>