<template>
  <div class="form-register modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add User</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit User</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr class="line-modal" />
    <div class="content-fr">
      <div class="form-input-mbx">
        <label for="nama-lengkap-register" class="title">Nama Lengkap</label>
        <input
          type="text"
          name="nama-lengkap-register"
          id="idFullName"
          class="form-control"
          placeholder="Nama Lengkap"
          v-model="formRegister.fullname"
          @keyup.enter="register()"
        />
        <span v-if="!cannotNull.fullname" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="email-register" class="title">Email</label>
        <input
          type="email"
          name="email-register"
          id="idEmailRegister"
          class="form-control"
          placeholder="yourname@gmail.com"
          v-model="formRegister.email"
          @keyup="checkEF()"
          @keyup.enter="register()"
        />
        <span v-if="!cannotNull.email" style="color: red;">This field is required.</span>
        <span v-if="!formatEmail" style="color: red;">Invalid email format.</span>
      </div>
      <div class="form-input-mbx">
        <label for="password-login" class="title">Password</label>
        <input
          type="password"
          name="password-register"
          id="idPasswordRegister"
          class="form-control"
          placeholder="Password"
          v-model="formRegister.password"
          @keyup.enter="register()"
        />
          <!-- @keyup="checkPass()" -->
        <span
          class="sh-password"
          id="shPassRegister"
          v-on:click="$root.showPassword('shPassRegister', 'idPasswordRegister')"
        >
          <i class="fa fa-eye"></i>
        </span>
        <!-- <span v-if="!cannotNull.password" style="color: red;">This field is required.</span>
        <span v-if="!minLength.password" style="color: red;">Password min length 6 characters.</span> -->
      </div>
      <div class="form-input-mbx">
        <label for="nomor-wa" class="title">Nomor WhatsApp</label>
        <input
          type="text"
          onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
          name="nomor-wa"
          id="idNomorWA"
          class="form-control"
          placeholder="08xxxxxxxxxxx"
          v-model="formRegister.nowa"
          maxlength="16"
          @keyup.enter="register()"
        />
        <span v-if="!cannotNull.nowa" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="status" class="title">Status</label>
        <input
          type="text"
          class="form-control"
          name="status"
          placeholder="Status"
          v-model="formRegister.status"
          @focus="statusUser().show()"
          @focusout="statusUser().show()"
        />
        <ul id="statusUser" class="list-status-user">
          <li
            v-for="(status, index) in formRegister.statusList"
            v-bind:key="index"
            @click="statusUser().choose(status)"
          >{{ status }}</li>
        </ul>
        <span v-if="!cannotNull.status" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="roles" class="title">Roles</label>
        <ul class="inp-roles">
          <li v-for="(role, index) in rolesRegister" v-bind:key="index">
            <input
              type="checkbox"
              name="check-roles"
              :id="'check'+ role.roles"
              class="checkbox-mbx"
              @click="rolesCheck($event, index)"
							v-bind:checked="role.checked"
            />
            <label :for="'check'+ role.roles" class="checklist">{{ role.label }}</label>
          </li>
        </ul>
        <span v-if="!cannotNull.roles" style="color: red;">This field is required.</span>
      </div>
    </div>
    <div class="btn-add-cancel">
      <button @click="cancelUser()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="register()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="register()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "AddUser",
	components: {},
  props: ['data'],
  data() {
    return {
			section: this.$props.data.section,
      uuidUser: 0,
      formRegister: {
        fullname: "",
        email: "",
        password: "",
        nowa: "",
        status: "",
        statusList: ["Active", "Suspend"],
        roles: []
      },
      cannotNull: {
        fullname: true,
        email: true,
        password: true,
        nowa: true,
				roles: true,
				status: true,
      },
      minLength: {
        password: true
      },
      formatEmail: true,
      rolesRegister: [
        {
          id: 1,
          label: "ADMIN",
          roles: "ADMIN",
          checked: false
        },
        {
          id: 2,
          label: "OWNER",
          roles: "OWNER",
          checked: false
        },
        {
          id: 3,
          label: "CREATOR",
          roles: "CREATOR",
          checked: false
        },
        {
          id: 4,
          label: "USER",
          roles: "USER",
          checked: false
        },
        {
          id: 5,
          label: "MENTOR",
          roles: "MENTOR",
          checked: false
        },
        {
          id: 6,
          label: "AFFILIATE",
          roles: "AFFILIATE",
          checked: false
        }
      ]
    };
  },
  methods: {
		init() {
			if ( this.section == 'edit' ) {
				this.getDetailUser();
			}
		},
		getDetailUser() {
			this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listUserAdmin() + `/${this.uuidUser}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
					let result = data.result;
          let roles = result.roles;
					let a = this.formRegister;
					a.fullname = result.name;
					a.email = result.email;
					a.nowa = result.phone;
					a.status = result.status;
          a.roles = roles;
          let rolesRegister = this.rolesRegister;
          for (let i = 0; i < roles.length; i++) {
            for (let j = 0; j < rolesRegister.length; j++) {
              if (roles[i] == rolesRegister[j].roles) {
                rolesRegister[j].checked = true;
              }
            }
					}
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.afterPostUser().failed(`${resErr.data.message}`);
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
		},
    checkEF() {
      let email = this.formRegister.email;
      var re = this.$root.checkValidateEmail(email);
      re == false ? (this.formatEmail = false) : (this.formatEmail = true);
    },
    checkPass() {
      let password = this.formRegister.password;
      let minLength = this.minLength;
      password.length >= 6
        ? (minLength.password = true)
        : (minLength.password = false);
    },
    rolesCheck(evt, idx) {
      let dataRoles = this.rolesRegister;
      let checked = evt.target.checked;
      checked == true
        ? (dataRoles[idx].checked = true)
        : (dataRoles[idx].checked = false);
      this.formRegister.roles = dataRoles
        .filter(e => {
          return e.checked == true;
        })
        .map(e => {
          return e.roles;
        });
    },
    validateRegister() {
      let data = this.formRegister;
      let required = this.cannotNull;

      data.fullname == ""
        ? (required.fullname = false)
        : (required.fullname = true);
      data.email == "" ? (required.email = false) : (required.email = true);
      // data.password == ""
      //   ? (required.password = false)
      //   : (required.password = true);
      data.nowa == "" ? (required.nowa = false) : (required.nowa = true);
      data.status == "" ? (required.status = false) : (required.status = true);
      data.roles.length == 0
        ? (required.roles = false)
        : (required.roles = true);

      if (
        data.fullname !== "" &&
        data.email !== "" &&
        // data.password !== "" &&
        data.nowa !== "" &&
        data.status !== "" &&
        data.roles.length !== 0
      ) {
        return true;
      } else {
        this.$root.notification(
          "warning",
          "Warning",
          "Tidak boleh ada field yang kosong."
        );
        return false;
      }
    },
    register() {
      $("");
      if (this.formatEmail) {
        if (this.validateRegister()) {
					let data = this.formRegister;
          let body = {
						name: data.fullname,
            email: data.email,
            password: data.password,
            phone: data.nowa,
						status: data.status,
						roles: data.roles,
          };
					console.log("form sudah full ", this.formRegister, body);
					if ( this.section == 'add' ) {
						this.postRegister(body);
					} else if ( this.section == 'edit' ) {
						this.putDataUser(body);
					}

        } else {
          console.log("data belum full", this.formRegister);
        }
      } else if (!this.formatEmail) {
        this.$root.notification("warning", "Warning", "Invalid Email Format.");
      } 
      // else if (!this.minLength.password) {
      //   this.$root.notification(
      //     "warning",
      //     "Warning",
      //     "Password min length is 6 characters."
      //   );
      // }
    },
    postRegister(body) {
      this.$root.loadingData().show();
			let vm = this;
			let AuthSth = this.$root.authorization();

      axios
        .post(this.$root.addUserByAdmin(), body, {
					headers: { Authorization: AuthSth }
				})
        .then(res => {
          console.log("res ", res);
          vm.$root.notification(
            "success",
            "Success",
            `${body.name} berhasil daftar.`
          );
          vm.$root.loadingData().hide();
					vm.emptyRegister();
					vm.closeModal();
        })
        .catch(err => {
					let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          vm.$root.notification("danger", "Error", `${resErr.data.errors}`);
          console.log("error ", resErr);
					this.$root.loadingData().hide();
					vm.closeModal();
        });
		},
		putDataUser(body) {
      this.$root.loadingData().show();
			let AuthSth = this.$root.authorization();
			let vm = this;
			
      axios.put(this.$root.addUserByAdmin() + `/${this.uuidUser}`, body, {
        headers: { Authorization: AuthSth }
      })
      .then((res) => {
        console.log("res ", res);
				vm.$root.notification(
					"success",
					"Success",
					`${body.name} berhasil Update.`
				);
				vm.$root.loadingData().hide();
				vm.emptyRegister();
				vm.closeModal();
				vm.$emit('init');
      })
      .catch((err) => {
        let resErr = err.response;
        let auth = resErr.data.auth;
        if ( resErr.status == 500 && !auth) {
					this.$root.sessionTimeUp();
				}

				vm.$root.notification("danger", "Error", `${resErr.data.errors}`);
				console.log("error ", resErr);
				this.$root.loadingData().hide();
				vm.closeModal();
      })
    },
    emptyRegister() {
      let data = this.formRegister;
      data.fullname = "";
      data.email = "";
      data.password = "";
      data.nowa = "";
    },
    closeModal() {
      this.$modal.hide("add-user");
    },
    cancelUser() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin menambah Data User ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.$modal.hide("add-user");
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    statusUser() {
      let a = this.formRegister;
      return {
        show() {
          $("#statusUser").slideToggle();
        },
        choose(val) {
          a.status = val;
        }
      };
    }
	},
	mounted() {
		let dataParams = this.$props.data;
    this.uuidUser = dataParams.uuidDetailUser;
    this.section = dataParams.section;
    console.log("data params ", dataParams);
    this.init();
	},
};
</script>

<style lang="scss" scoped>
.form-register {
  font-family: "Montserrat";
  text-align: left;
  display: flex;
  flex-direction: column;
  padding: 15px 15px 70px 15px;
  overflow-y: auto;
  height: 100%;
  > .content-fr {
    > .input-form {
      display: flex;
      flex-direction: column;
      > label {
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 24px;
        margin: 5px 0 0 0;
      }
      > input {
        padding: 5px 5px 5px 10px;
        margin: 5px 0 0 0;
        border-radius: 8px;
        border: 1px solid #cccccc;
        &[type="password"] {
          padding: 5px 35px 5px 10px;
        }
        &.required {
          border: 1px solid #f70707;
        }
      }
      > .sh-password {
        cursor: pointer;
        display: flex;
        margin: -27px 10px 15px auto;
        font-size: 18px;
      }
    }
    .inp-roles {
      display: flex;
      padding: 0%;
      margin: 0 auto;
      max-width: 100%;
      flex-wrap: wrap;
      justify-content: center;

      > li {
        list-style: none;
        margin: 5px 15px;
      }
    }
  }
}

.list-status-user {
  // position: absolute;
  background: #ffffff;
  padding: 0%;
  display: none;
  width: 50%;
  left: 0;
  margin: 3px 0 0 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
  border-radius: 0 0 10px 10px;
  > li {
    color: #000000;
    list-style: none;
		padding: 5px 10px;
		cursor: pointer;
    &:nth-child(even) {
      background: #cccccc;
    }
    &:hover {
      background: #999898;
    }
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .form-register {
    > h1 {
      font-size: 20px;
    }
    > .content-fr {
      > .input-form {
        > label {
          font-size: 18px;
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .form-register {
    > h1 {
      font-size: 19px;
    }
    > .content-fr {
      > .input-form {
        > label {
          font-size: 17px;
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .form-register {
    > h1 {
      font-size: 19px;
    }
    > .content-fr {
      > .input-form {
        > label {
          font-size: 17px;
        }
      }
    }
    .forgot-passwd {
      font-size: 15px;
    }
  }
}
</style>