<template>
  <div class="executor-flash-sale modal-mbx">
    <div class="top-title-modal">
      <h3 v-if="section == 'add'" class="title-lk">Add Flash Sale</h3>
      <h3 v-if="section == 'edit'" class="title-lk">Edit Flash Sale</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />

    <div class="content-executor-lk">
      <div class="form-input-mbx" v-if="section == 'edit'">
        <label for="nameProduk" class="title">Nama Produk</label>
        <input
          type="text"
          name="nameProduk"
          class="form-control"
          placeholder="Nama Produk"
          disabled
          v-model="dataExecutor.nameProduk"
        />
        <span v-if="!cannotNull.nameProduk" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx" v-if="section == 'edit'">
        <label for="priceFlash" class="title">Price Flash</label>
        <input
          type="text"
          name="priceFlash"
          class="form-control"
          placeholder="Nama Produk"
          v-model="dataExecutor.priceFlash"
        />
        <span v-if="!cannotNull.priceFlash" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="startAt">Start At</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.startAt"
          id="startAt-fs"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.startAt" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx">
        <label for="expiredAt">Expired At</label>
        <vc-date-picker
          :value="null"
          color="red"
          is-dark
          :locale="{
            id: 'id',
            data: ['YYYY-MM-DD']
          }"
          :data="'YYYY-MM-DD'"
          v-model="dataExecutor.expiredAt"
          id="expiredAt-fs"
          class="field-inp-date"
        />
        <span v-if="!cannotNull.expiredAt" style="color: red;">This field is required.</span>
      </div>
      <div class="form-input-mbx" v-if="section == 'add'">
        <label for="produk-voucher" class="title">Produk</label>
        <input
          type="text"
          name="produk-voucher"
          class="form-control"
          placeholder="Produk Voucher"
          v-model="dataExecutor.produkText"
          @keyup.enter="searchKelas()"
        />
        <ul
          id="listKelasSearch"
          class="list-status-kategori show"
          v-if="dataExecutor.listSearchKelas.length !== 0"
        >
          <li v-if="dataExecutor.listSearchKelas.length == 0">Data Not Found or Data Selected.</li>
          <li
            v-for="(kelas, index) in dataExecutor.listSearchKelas"
            v-bind:key="index"
            @click="produkForVoucher().choose(kelas, index)"
          >{{ kelas.name }}</li>
        </ul>
        <span>Note : klik by enter untuk mencari produk, minimal 3 characters.</span>
        <span v-if="!cannotNull.produkText" style="color: red;">This field is required.</span>
      </div>

      <template v-if="dataExecutor.produk.length > 0">
        <div class="list-produk-fs" v-for="(c, index) in dataExecutor.produk" v-bind:key="index">
          <div class="left">
            <div class="form-input-mbx inline">
              <label for="nama-produk" class="title">Nama Produk</label>
              <input
                type="text"
                name="nama-produk"
                class="form-control"
                placeholder="Nama Produk"
                v-model="c.name"
                disabled
              />
            </div>
            <div class="form-input-mbx inline">
              <label for="price-fs" class="title">Price Flash Sale</label>
              <input
                onkeypress="if ( isNaN( String.fromCharCode(event.keyCode) )) return false;"
                type="text"
                name="price-fs"
                class="form-control"
                placeholder="Price Flash Sale"
                v-model="c.priceFlash"
              />
            </div>
          </div>
          <div class="right">
            <div class="for-rm-list-fs">
              <button class="btn btn-danger" @click="produkForVoucher().remove(index)">
                <i class="fa fa-trash"></i>
                Hapus
              </button>
            </div>
          </div>
        </div>
      </template>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelFS()" class="btn btn-danger">Cancel</button>
      <button v-if="section == 'add'" @click="addFS()" class="btn btn-success">Save</button>
      <button v-if="section == 'edit'" @click="editFS()" class="btn btn-success">Update</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "ExecutorFlashSale",
  components: {},
  data() {
    return {
      section: "",
      id: "",
      dataExecutor: {
        nameProduk: "",
        priceFlash: "",
        startAt: "",
        expiredAt: "",
        produk: [],
        produkText: "",
        listSearchKelas: []
      },
      cannotNull: {
        startAt: true,
        expiredAt: true,
        produkText: true,
        nameProduk: true,
        priceFlash: true,
      }
    };
  },
  props: ["data"],
  methods: {
    init() {
      // "startAt": "2020-05-01 00:00:00",
      // "expiredAt": "2020-05-31 23:59:59",
      // "produk": [
      // 		{
      // 				"id": "1",
      // 				"priceFlash": 79000
      // 		}
      // ]

      let params = this.$props.data;
      this.section = params.section;
      // let a = this.dataExecutor;
      if (params.section == "edit") {
        this.id = params.id;
        this.getDetailFS();
      }
      console.log("this params ", params);
      this.fixStyleInputVCalendar();
    },
    fixStyleInputVCalendar() {
      $(".vc-text-base").css({
        "border-top": "none",
        "border-right": "none",
        "border-left": "none",
        "border-bottom": "1px solid #000000",
        "border-radius": "0%"
      });

      $(`#startAt-fs > input`).attr("placeholder", "Start At");
      $(`#expiredAt-fs > input`).attr("placeholder", "Expired At");
    },
    getDetailFS() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      axios
        .get(this.$root.listFlashSaleAdmin() + `/${this.id}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          a.nameProduk = result.produkName;
          a.priceFlash = result.hargaFlash;
          a.startAt = new Date(result.startAt);
          a.expiredAt = new Date(result.expiredAt);

          console.log("dataExecutor ", a);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getSearchKelas(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let sQuery = `?search=${body}`;
      axios
        .get(this.$root.searchKelas() + sQuery, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let result = res.data.result;
          let a = this.dataExecutor;
          let produk = a.produk;
          let listSearchKelas = a.listSearchKelas;

          if (produk.length == 0) {
            console.log("listSearchKelas ", this.dataExecutor.listSearchKelas);
            this.dataExecutor.listSearchKelas = result.map(e => {
              let obj = {
                id: e.id,
                name: e.name,
                uuid: e.uuid,
                priceFlash: ""
              };
              return obj;
            });
            console.log("listSearchKelas ", this.dataExecutor.listSearchKelas);
          } else {
            for (let i = 0; i < result.length; i++) {
              for (let j = 0; j < produk.length; j++) {
                console.log("nge looping ", result[i], produk[j]);

                if (result[i].uuid !== produk[j].uuid) {
                  let obj = {
                    id: result[i].id,
                    name: result[i].name,
                    uuid: result[i].uuid,
                    priceFlash: ""
                  };
                  listSearchKelas.push(obj);
                }
              }
            }
          }

          console.log("dataExecutor ", result);

          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    searchKelas() {
      let val = this.dataExecutor.produkText;
      if (val.length < 3) {
        this.$root.notification(
          "warning",
          "Warning",
          "Search Kelas minimal 3 characters."
        );
      } else {
        this.getSearchKelas(val);
      }
    },
    produkForVoucher() {
      let a = this.dataExecutor.produk;
      let b = this.dataExecutor.listSearchKelas;
      return {
        choose: (data, idx) => {
          a.push(data);
          b.splice(idx);
          console.log("data choose ", data);
        },
        remove: idx => {
          a.splice(idx, 1);
        }
      };
    },
    validatePostKategori() {
      let a = this.dataExecutor;
      let b = this.cannotNull;

      a.title == "" ? (b.title = false) : (b.title = true);
      a.status == "" ? (b.status = false) : (b.status = true);
      a.child.length == 0 ? (b.child = false) : (b.child = true);

      if (a.title !== "" && a.status !== "" && a.child.length !== 0) {
        return true;
      } else {
        return false;
      }
    },
    convertPayloadAdd() {
      let a = this.dataExecutor;
      let body = {
        startAt: a.startAt,
        expiredAt: a.expiredAt,
        produk: a.produk.map((e) => {
          let obj = {
            id: e.id,
            priceFlash: e.priceFlash
          }
          return obj;
        })
      };
      return body;
    },
    addFS() {
      let body = this.convertPayloadAdd();
      console.log('body ', body);
      this.postFS(body);
    },
    postFS(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listFlashSaleAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostFS().success(
            `Flash Sale Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let msg = resErr.data.message;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostFS().failed(msg);
        });
    },
    afterPostFS() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelFS() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin tambah/edit Flash Sale ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    },
    convertPayloadEdit() {
      let a = this.dataExecutor;
      let body = {
        priceSale: a.priceFlash,
        startAt: a.startAt,
        expiredAt: a.expiredAt,
        coursesId: this.id
      }
      return body;
    },
    editFS() {
      let body = this.convertPayloadEdit();
      console.log('body ', body);
      this.putFS(body);
    },
    putFS(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;

      axios
        .put(vm.$root.listFlashSaleAdmin() + `/${this.id}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostFS().success(
            `Flash Sale Berhasil di Perbarui`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostFS().failed(`${resErr.data.message}`);
        });
    },
    closeModal() {
      this.$modal.hide("executor-fs");
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-flash-sale {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  > .title-lk {
    text-align: left;
    font-size: 20px;
    font-weight: 600;
  }
  .list-sub-kategori {
    margin: 0 0 5px 0;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    > span {
      background: #cccccc;
      padding: 5px;
      border-radius: 5px;
      margin: 3px;
      > i {
        color: #000000;
        cursor: pointer;
        &:hover {
          color: #ee0f0f;
        }
      }
    }
  }
  .list-status-kategori {
    // position: absolute;
    background: #ffffff;
    padding: 0%;
    display: none;
    width: 50%;
    left: 0;
    margin: 3px 0 0 0;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
    border-radius: 0 0 10px 10px;
    &.show {
      display: block;
    }
    > li {
      color: #000000;
      list-style: none;
      padding: 5px 10px;
      cursor: pointer;
      &:nth-child(even) {
        background: #cccccc;
      }
      &:hover {
        background: #999898;
      }
    }
  }
  .btn-add-cancel {
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    bottom: 0;

    background: #ffffff;
    width: 100%;
    padding: 10px;
    > button {
      width: 49%;
    }
  }

  .list-produk-fs {
    display: flex;
    width: 100%;
    border-bottom: 1px solid #000;
    .left {
      width: 85%;
      > div {
        > label {
          font-size: 1em;
        }
      }
    }
    .right {
      width: 15%;
      > .for-rm-list-fs {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
      }
    }
  }
}
</style>