<template>
  <div class="metode-pembayaran">
    <div class="container">
      <div class="content-metode-pembayaran">
        <h1 class="title-mp">Metode Pembayaran</h1>
        <div class="transfer-va">
          <div class="transfer-va-top">
            <div class="tva-kelas">
              <input
                type="checkbox"
                name="tranferVA"
                id="tranferVA"
                class="checkbox-mbx"
                checked
                disabled="disabled"
              />
              <label for="tranferVA" class="checklist">Transfer Bank</label>
            </div>
            <div class="btn-plus" id="btnPlusTVA" v-on:click="showTVA()">
              <i class="fa fa-plus"></i>
            </div>
          </div>
          <div id="divPlusVA">
            <div class="transfer-va-bottom">
              <ul class="list-va-bank">
                <li
                  class="va-bank"
                  v-for="(bank, idx) in dataListBank"
                  v-bind:key="idx"
                  v-on:click="changeBank(bank)"
                >
                  <input
                    class="radio-tva radio-mbx"
                    type="radio"
                    name="date-list"
                    v-bind:id="'radio-mp-' + bank.unic_id"
                    v-bind:checked="bank.default"
                    v-bind:value="bank.slug"
                  />
                  <label
                    v-bind:for="'radio-mp-' + bank.unic_id"
                    v-bind:class="'label-mp-bank ' + 'label-mp' + bank.unic_id"
                  >
                    <p>
                      <span hidden>{{ bank.bankLogo }}</span>
                      <img
                        v-bind:src="bank.urlIcon"
                        v-bind:alt="bank.bankName"
                        class="img-logo-bank"
                      />
                      <span>{{ bank.bankName }} </span> -
                      <span>{{ bank.accountName }} </span> -
                      <span>{{ bank.code }} </span> -
                      <span>{{ bank.rekNumber }}</span>
                    </p>
                  </label>
                  <!-- <img v-bind:src="bank.icon" v-bind:alt="bank.bankName" /> -->
                </li>
              </ul>
            </div>
          </div>
        </div>

        <div class="total-pembelian">
          <div class="left-tp">
            <input
              type="checkbox"
              name="totalP"
              id="totalP"
              class="checkbox-mbx"
              checked
              disabled="disabled"
            />
            <label for="totalP" class="checklist">Total Pembelian</label>
          </div>
          <div class="right-tp">
            <div class="title-tp">
              <p>Hemat</p>
              <p>Diskon</p>
              <p>PPN</p>
              <p>Kode Unik</p>
            </div>
            <div class="price-tp">
              <p style="font-weight: 700;">Rp.{{ calculating.hemat }}</p>
              <p style="font-weight: 700;">Rp.{{ calculating.diskon }}</p>
              <p style="font-weight: 700;">{{ calculating.ppn }}%</p>
              <p style="font-weight: 700;">{{ kodeUnik }}</p>
            </div>
            <div class="checkout-tp">
              <p>
                Sub Total
                <span>Rp.{{ calculating.subtotal }}</span>
              </p>
              <p>
                Total
                <span>Rp.{{ calculating.total }}</span>
              </p>
              <p class="jumlah-harga">
                Jumlah
                <span>Rp.{{ calculating.jumlah }}</span>
              </p>
              <!-- checkOutSuccess -->
              <!-- <button class="btn btn-checkout-tp" v-on:click="checkOutSuccess()">CheckOut</button> -->
              <button class="btn btn-checkout-tp" v-on:click="checkOutKelas()">CheckOut</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "MetodePembayaran",
  components: {},
  data() {
    return {
      paramsBank: {},
      dataBank: [
        {
          id: 1,
          bankName: "BNI",
          slug: "bni",
          icon: "/icon/BNI_logo.svg",
          default: true,
          unic_id: this.$root.makeid(7)
        },
        {
          id: 2,
          bankName: "Mandiri",
          slug: "mandiri",
          icon: "/icon/Bank_Mandiri_logo.svg",
          default: false,
          unic_id: this.$root.makeid(7)
        }
      ],
      dataListBank: [],
      listKelas: [],
      kodeUnik: this.$root.makeidNumber(3),
      dataCheckoutUser: this.$store.state.dataCheckoutUser,
      dataList: [],
      dataForCheckout: this.$store.state.dataForCheckout,
      calculating: {
        hemat: 0,
        subtotal: 0,
        total: 0,
        diskon: 0,
        ppn: 0,
        jumlah: 0
      },
      dataListVoucher: []
    };
  },
  methods: {
    init() {
      this.handleData();
      this.getListBank();

      // for data when user click checkout;
      this.dataForCheckout.kodeUnik = this.kodeUnik;
    },
    getListBank() {
      this.$root.loadingData().show();

      axios
        .get(this.$root.listBankPage())
        .then(res => {
          let data = res.data;
          let result = data.result;
          this.dataListBank = result.map(e => {
            let obj = {
              accountName: e.accountName,
              bankLogo: e.bankLogo,
              bankName: e.bankName,
              code: e.code,
              rekNumber: e.rekNumber,
              urlIcon: this.$root.getPhotoUser() + `/${e.bankLogo}`,
              default: false,
              unic_id: this.$root.makeid(7),
              BankDetails: e.BankDetails,
            };
            return obj;
          });
          this.dataListBank[0].default = true;
          this.paramsBank = data.result[0];
          window.$cookies.set('chooseBankUser', data.result[0]);

          console.log("res list bank ", this.dataListBank);
          this.getPPN();

          // for data when user click checkout;
          this.dataForCheckout.payment = "Transfer Bank";
          this.dataForCheckout.rekNumber = this.dataListBank[0].rekNumber;
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    getPPN() {
      axios
        .get(this.$root.ppnPage())
        .then(res => {
          let data = res.data.data;
          this.calculating.ppn = data;
          console.log("res PPN ", this.calculating);
          this.$root.loadingData().hide();

          // for data when user click checkout;
          this.dataForCheckout.ppn = data;
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", resErr);
          this.$root.loadingData().hide();
        });
    },
    handleData() {
      let data = this.dataCheckoutUser;
      console.log("dataCheckoutUser  ", data);
      if (data.length !== 0) {
        let listKelas = data[0].kelas.map(e => {
          let detailData = window.$cookies.get(e);
          return detailData;
        });
        this.listKelas = listKelas;
        this.countTotal(listKelas);
      }
      console.log("data checkout user ", data);
      console.log("data for checkout ", this.dataForCheckout);
    },
    hitungJumlah() {
      let calculating = this.calculating;
      calculating.jumlah =
        calculating.total + (calculating.total / 100) * calculating.ppn;
    },
    countTotal(data) {
      let dataForCheckout = this.dataForCheckout;
      dataForCheckout.subtotal = 0;
      dataForCheckout.total = 0;

      let calculating = this.calculating;
      calculating.hemat = 0;
      calculating.subtotal = 0;
      calculating.total = 0;

      for (let i = 0; i < data.length; i++) {
        calculating.subtotal = calculating.subtotal + data[i].price;
        calculating.total = calculating.total + data[i].total;

        // for counting jumlah harga;
        if (i + 1 == data.length) {
          this.hitungJumlah();
        }
      }

      calculating.jumlah = calculating.jumlah - parseInt(this.kodeUnik);
      calculating.hemat = calculating.subtotal - calculating.total;
    },
    calculatingDiskon(data) {
      console.log("get data Voucher nih ", data);
      let calculating = this.calculating;
      calculating.hemat = 0;
      calculating.subtotal = 0;
      calculating.total = 0;
      calculating.diskon = 0;

      for (let i = 0; i < data.length; i++) {
        calculating.subtotal = calculating.subtotal + data[i].price;
        calculating.total = calculating.total + data[i].subtotal;
        calculating.diskon =
          calculating.diskon + (data[i].promo - data[i].subtotal);

        // for counting jumlah harga;
        if (i + 1 == data.length) {
          this.hitungJumlah();
        }
      }

      calculating.jumlah = calculating.jumlah - parseInt(this.kodeUnik);
      calculating.hemat = calculating.subtotal - calculating.total;

      // diskon: "10%"
      // name: "Angular From Scratch"
      // price: 25000
      // promo: 20000
      // subtotal: 18000
      // uuid: "e11850f2-9a65-4cc1-a2de-7ff9b05489ad"

      // diskon: 0
      // name: "Kelas yang sangat bagus"
      // price: 20000
      // promo: 15000
      // subtotal: 15000
      // uuid: "c107fa70-2b17-4f4d-b74c-9b777cdd137c"
    },
    showTVA() {
      $("#btnPlusTVA i").toggleClass("fa-plus fa-minus");
      $("#divPlusVA").slideToggle();
    },
    checkOutSuccess() {
      this.$root.loadingMentorbox().show();
      this.$root.executeSuccessCheckout();

      // let obj = {
      //   "invoice": "INV200609LVRW6",
      //   "voucher": null,
      //   "subtotal": 7000,
      //   "ppn": 0,
      //   "kodeUnik": "915",
      //   "total": 7915,
      //   "item": [{
      //     "uuid": "4706d298-fb28-40df-bb50-6def63203747",
      //     "name": "Kelas Vue JS",
      //     "price": 10000,
      //     "promo": 7000,
      //     "diskon": "",
      //     "total": 7000
      //   }],
      //   "user": {
      //     "name": "Wiseman",
      //     "email": "wise@wise.id",
      //     "phone": "083120384293",
      //     "alamat": null
      //   },
      //   "createdAt": "2020-06-09 15:30:56",
      //   "expiredAt": "2020-06-10 15:30:56"
      // };

      // this.$store.state.successCheckoutData = obj;


      let urlCheckout = "/detail-pembayaran";
      let params = {
        section: "/cara-pembayaran",
        bank: "/" + this.paramsBank.bankName.toLowerCase()
      };
      this.$router.push({ path: urlCheckout + params.section + params.bank });
    },
    checkOutKelas() {
      let a = this.dataForCheckout;
      a.subtotal = this.calculating.total;
      a.total = this.calculating.jumlah;
      a.kodeReferral = window.$cookies.get("kodeReferral");

      let dataBody = {
        voucher: a.voucher,
        subtotal: a.subtotal,
        ppn: a.ppn,
        kodeUnik: a.kodeUnik,
        total: a.total,
        payment: a.payment,
        rekNumber: a.rekNumber,
        kodeReferral: a.kodeReferral == "false" ? "" : a.kodeReferral,
        item: [
        ]
      };
      let dataCheckoutUser = this.dataCheckoutUser;
      let dataBank = this.paramsBank;
      let dataListVoucher = this.dataListVoucher;
      let listKelas = this.listKelas;
      dataBody.item = [];
      if (dataListVoucher.length !== 0) {
        for (let i = 0; i < listKelas.length; i++) {
          let obj = {
            uuid: listKelas[i].uuid,
            price: listKelas[i].price,
            promo: listKelas[i].promo,
            diskon: listKelas[i].diskon,
            total: listKelas[i].total,
            scheduleId: listKelas[i].scheduleId
          }

          for (let j = 0; j < dataListVoucher.length; j++) {
            if ( listKelas[i].uuid == dataListVoucher[j].uuid ) {
              obj.diskon = dataListVoucher[j].diskon;
              obj.total = dataListVoucher[j].subtotal;
              dataBody.item.push( obj );
            }
          }
        }
      } else {
        for (let i = 0; i < listKelas.length; i++) {
          let obj = {
            uuid: listKelas[i].uuid,
            price: listKelas[i].price,
            promo: listKelas[i].promo,
            diskon: listKelas[i].diskon,
            total: listKelas[i].total,
            scheduleId: listKelas[i].scheduleId
          }
          dataBody.item.push( obj );
        }
      }

      console.log("kuy checkout ", dataCheckoutUser, dataBank);
      console.log("kuy checkout 2 ", dataListVoucher, listKelas);
      console.log('kuy last data ', dataBody, a);

      this.postCheckoutProduk(dataBody);
    },
    postCheckoutProduk(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.checkoutPage(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;

          this.$store.state.successCheckoutData = data;
          window.$cookies.set('successCheckoutData', data);

          vm.$root.loadingData().hide();
          vm.afterPostCheckout().success(
            `Pembelian Kelas Berhasil!`
          );
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if ( resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          } else {
            vm.afterPostCheckout().failed(resErr.data.message);
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
        });
    },
    afterPostCheckout() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                  vm.checkOutSuccess();
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    changeBank(data) {
      let paramsBank = this.paramsBank;
      paramsBank.bankName = data.bankName;
      paramsBank.accountName = data.accountName;
      paramsBank.code = data.code;
      paramsBank.rekNumber = data.rekNumber;

      this.dataForCheckout.rekNumber = data.rekNumber;
      $("#radio-mp-" + data.unic_id).click();

      window.$cookies.set('chooseBankUser', data);
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    getDataKelasFromCookies() {
      let getDataKelas = this.$store.state.dataCheckoutUser;
      return getDataKelas;
    },
    getDataVoucher() {
      let data = this.$store.state.dataVoucherForCheckout;
      return data;
    }
  },
  watch: {
    getDataKelasFromCookies(newData, oldData) {
      this.dataCheckoutUser = newData;
      console.log("new Data for calculating nih bro ", newData);
      this.handleData();
      oldData;
    },
    getDataVoucher(newData, oldData) {
      this.dataListVoucher = newData;
      this.calculatingDiskon(newData);
      oldData;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/style-global.scss";

.metode-pembayaran {
  font-family: "Montserrat";
  padding: 20px 0;
  .container {
    .content-metode-pembayaran {
      color: #ffffff;
      > .title-mp {
        font-style: normal;
        font-weight: normal;
        font-size: 36px;
        line-height: 44px;
        color: #000000;
        text-align: left;
      }
      .transfer-va {
        > .transfer-va-top {
          background: #003461;
          box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
          padding: 2% 3%;
          border-radius: 10px;
          margin: 10px 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .tva-kelas {
            > label {
              margin: 0;
            }
          }
          .btn-plus {
            cursor: pointer;
          }
        }
        > #divPlusVA {
          width: 100%;
          // display: none;
          > .transfer-va-bottom {
            padding: 30px 20px 20px 20px;
            border: 1px solid #003461;
            margin: -20px 0 0 0;
            display: flex;
            flex-direction: column;
            border-radius: 0 0 10px 10px;
            > .list-va-bank {
              text-align: left;
              padding: 0 0 0 15px;
              > .va-bank {
                padding: 5px 0;
                cursor: pointer;
              }
            }
          }
        }
      }
      > .total-pembelian {
        background: #003461;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.56);
        padding: 2% 3%;
        border-radius: 10px;
        margin: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left-tp {
          > label {
            margin: 0;
          }
        }
        .right-tp {
          display: flex;
          text-align: left;
          > .title-tp {
            margin: 0 10px 0 0;
            > p {
              font-style: normal;
              font-weight: normal;
              font-size: 20px;
              line-height: 24px;
            }
          }
          > .price-tp {
            margin: 0 10px 0 0;
            > p {
              font-style: normal;
              font-weight: normal;
              font-size: 20px;
              line-height: 24px;
            }
          }
          > .checkout-tp {
            > p {
              font-style: normal;
              font-weight: normal;
              font-size: 20px;
              line-height: 24px;
              display: flex;
              justify-content: space-between;
              > span {
                margin: 0 0 0 10px;
                font-weight: 700;
              }

              &.jumlah-harga {
                margin: 0 0 10px 0;
                padding: 10px;
                background: #0ae24b;
                border-radius: 5px;
              }
            }
            > .btn-checkout-tp {
              background: #f2994a;
              color: #ffffff;
              border: 1px solid #f2994a;
              width: 100%;
              &:hover {
                background: #003461;
                border: 1px solid #ffffff;
              }
            }
          }
        }
      }
    }
  }
}
.label-mp-bank {
  display: flex;
  align-items: center;
  .img-logo-bank {
    width: 200px;
    height: 100px;
    padding: 5px 10px;
  }
}
@media (min-width: 577px) and (max-width: 767px) {
  .metode-pembayaran {
    .container {
      .content-metode-pembayaran {
        > .total-pembelian {
          flex-direction: column;
          .left-tp {
            > label {
              margin: 0;
            }
          }
          .right-tp {
            > .title-tp,
            .price-tp,
            .checkout-tp {
              > p {
                font-size: 19px;
              }
            }
          }
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .metode-pembayaran {
    .container {
      .content-metode-pembayaran {
        > .title-mp {
          font-size: 30px;
