            > .title-tp,
            .price-tp,
            .checkout-tp {
              > p {
                font-size: 19px;
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .metode-pembayaran {
    .container {
      .content-metode-pembayaran {
        > .title-mp {
          font-size: 25px;
        }
        > .total-pembelian {
          flex-direction: column;
          .left-tp {
            > label {
              margin: 0;
            }
          }
          .right-tp {
            > .title-tp,
            .price-tp,
            .checkout-tp {
              > p {
                font-size: 18px;
              }
            }
          }
        }
      }
    }
  }
}
</style>