<template>
  <div class="executor-withdrawal modal-mbx">
    <div class="top-title-modal">
      <h3 class="title-lk">Submit Withdrawal</h3>
      <div>
        <i class="fa fa-times" @click="closeModal()"></i>
      </div>
    </div>
    <hr />
    <div class="content-exe-with">
      <div class="form-input-mbx inline">
        <label for="bank">Bank</label>
        <span>: {{ detailData.bank }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="jumlah">Jumlah</label>
        <span>: {{ detailData.jumlah }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="pemilik">Pemilik</label>
        <span>: {{ detailData.pemilik }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="rekening">Rekening</label>
        <span>: {{ detailData.rekening }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="note">Note</label>
        <span>: {{ detailData.note }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="createdAt">Tanggal</label>
        <span>: {{ detailData.createdAt }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="User">User</label>
        <span>: {{ detailData.User.name }}</span>
      </div>
      <div class="form-input-mbx inline">
        <label for="status">Status</label>
        : <div class="d-flex">
          <template v-for="(status, index) in statusFilter">
            <input
              v-bind:key="index+5"
              type="radio"
              name="status"
              v-bind:id="'f-status-'+ status.value"
              class="radio-mbx"
              v-bind:value="status.value"
            />
            <label
              v-bind:key="index"
              v-bind:for="'f-status-'+status.value"
              style="margin: 3px 5px;"
              @click="statusCheck(status.value)"
            >{{ status.text }}</label>
          </template>
        </div>
      </div>
    </div>

    <div class="btn-add-cancel d-flex">
      <button @click="cancelWithdrawal()" class="btn btn-danger">Cancel</button>
      <button @click="submitWithdrawal()" class="btn btn-success">Submit</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ExecutorWithdrawal",
  components: {},
  data() {
    return {
      dataExecutor: {
        bank: "",
        rekening: "",
        pemilik: "",
        jumlah: "",
        note: ""
      },
      cannotNull: {
        bank: true,
        rekening: true,
        pemilik: true,
        jumlah: true,
        note: true
      },
      statusFilter: [
        {
          text: "Paid",
          value: "Paid"
        },
        {
          text: "Reject",
          value: "Reject"
        }
      ],
      statusValue: '',
      detailData: {
        id: '',
        bank: '',
        jumlah: '',
        pemilik: '',
        rekening: '',
        note: '',
        createdAt: '',
        User: {
          name: ''
        }
      }
    };
  },
  props: ['data'],
  methods: {
    init() {
      // Jumlah,
      // Bank,
      // NoRek,
      // Note

      // User: Object
      //   name: "Nasrullah"
      // bank: "BNI"
      // createdAt: "2020-06-10 20:33:29"
      // id: 3
      // jumlah: 900
      // note: "Kuy Gaskeun bro"
      // pemilik: "Nasrullah"
      // rekening: "7031234567"

      let dataParams = this.$props.data;
      this.detailData = dataParams;
      console.log('dataParams ', dataParams)
    },
    closeModal() {
      this.$modal.hide("executor-withdrawal");
    },
    statusCheck(val) {
      this.statusValue = val;
    },
    submitWithdrawal() {
      let dataBody = {
        id: this.detailData.id,
        status: this.statusValue
      }

      if ( this.statusValue == '' ) {
        this.$root.notification("warning", "Warning", "Field Status tidak boleh kosong.!")
      } else {
        this.postWithdrawal(dataBody);
      }
    },
    postWithdrawal(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listWithdrawalAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostWithdrawal().success(`Submit Withdrawal Berhasil.`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostWithdrawal().failed(resErr.data.message);
        });
    },
    afterPostWithdrawal() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.closeModal();
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    cancelWithdrawal() {
      this.$modal.show("dialog", {
        title: "Warning",
        text: "Apakah Anda yakin <br> tidak ingin Submit Withdrawal ?",
        buttons: [
          {
            title: "No"
          },
          {
            title: `<span style="color: red;">Yes</span>`,
            handler: () => {
              this.closeModal();
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.executor-withdrawal {
  padding: 15px 15px 70px 15px;
  font-family: "Montserrat";
  overflow-y: auto;
  height: 100%;
  .content-exe-with {
  }
}
</style>