<template>
  <div class="content-product-mentor">
    <div class="container">
      <div class="content-pm">
        <h3 class="title-pm">Detail Produk Mentor</h3>
        <div class="parent-tab-pm">
          <ul class="tab-pm d-flex">
            <li
              v-for="tab in dataTab"
              v-bind:key="tab.id"
              v-bind:active="tab.active"
              v-on:click="changeTab(tab.id)"
            >
              <!-- v-bind:disabled="!tab.active" -->
              <span>{{ tab.name }}</span>
            </li>
          </ul>
        </div>

        <div class="form-cpm" v-if="sectionTab == 1">
          <TabDetailKelas :data="listKategoriData" ref="form" style="margin-bottom: 20px;" />
          <button class="btn btn-blue-mbx" v-on:click="changeTab(2)">Berikutnya</button>
        </div>
        <div class="form-cpm" v-if="sectionTab == 2">
          <TabJadwalKelas style="margin-bottom: 20px;" ref="form" />
          <button class="btn btn-blue-mbx" v-on:click="changeTab(3)">Berikutnya</button>
        </div>
        <div class="form-cpm" v-if="sectionTab == 3">
          <TabKurikulumKelas style="margin-bottom: 20px;" ref="form" />
          <button class="btn btn-blue-mbx" @click="savePM()">Simpan</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import * as moment from 'moment';

import TabDetailKelas from "./TabDetailKelas";
import TabJadwalKelas from "./TabJadwalKelas";
import TabKurikulumKelas from "./TabKurikulumKelas";

export default {
  name: "ContentProductMentor",
  components: {
    TabDetailKelas,
    TabJadwalKelas,
    TabKurikulumKelas
  },
  data() {
    return {
      sectionTab: 1,
      dataTab: [
        {
          id: 1,
          name: 1,
          active: true
        },
        {
          id: 2,
          name: 2,
          active: false
        },
        {
          id: 3,
          name: 3,
          active: false
        }
      ],
      listKategoriData: [],
      dataForProps: {},
      uuidKelas: this.$route.params.uuid,
      section: "",
      roleNow: "",
      dataBody: ""
    };
  },
  methods: {
    init() {
      this.listKategori();

      let params = this.$route;
      console.log("params ", params, this.uuidKelas);

      if (this.uuidKelas == undefined) {
        console.log("this is add");
        this.section = "add";
      } else {
        console.log("this is edit bro");
        this.getDetailKelas();
        this.section = "edit";
      }
    },
    getDetailKelas() {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let url = "";

      if (this.$root.checkRole("ADMIN")) {
        url = this.$root.listKelasAdmin();
      } else {
        url = this.$root.listKelasMentor();
      }

      axios
        .get(url + `/${this.uuidKelas}`, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;
          let result = data.result;

          let tempKurikulum = [
            {
              id: "",
              description: "",
              files: "",
              materi: "",
              topics: ""
            }
          ];

          let a = this.$store.state.dataProductMentor;
          a.name = result.name;
          a.about = result.about;
          a.note = result.note;
          a.jenis =
            result.jenis.split(" ").length == 2
              ? result.jenis.split(" ")[1]
              : result.jenis;
          a.price = result.price;
          a.promo = result.promo;
          a.banner = result.banner;
          a.status = result.status;
          (a.category = result.Categories == null ? "-" : result.Categories.id),
            (a.subCategory =
              result.Sub_Categories == null ? "-" : result.Sub_Categories.id),
            (a.categoryText =
              result.Categories == null ? "-" : result.Categories.title);
          a.subCategoryText =
            result.Sub_Categories == null ? "-" : result.Sub_Categories.title;
          a.schedule = result.Courses_Schedules.map(e => {
            let obj = {
              id: e.id,
              name: e.name,
              slot: e.slot,
              // startDate: new Date(e.startDate),
              // endDate: new Date(e.endDate),
              startDate: moment(e.startDate).format(),
              endDate: moment(e.endDate).format(),
            };
            return obj;
          });
          a.curriculum =
            result.Courses_Curriculums.length == 0
              ? tempKurikulum
              : result.Courses_Curriculums;

          // about: "Pernah punya masalah membuat copywriting yang bisa membuat orang ingin membeli produk kita ? Di kelas ini anda akan mendapatkan mentoring langsung dari expert dan praktisi dibidang copy writing yang telah bertahun tahun melakoni bisnis copywriting. Anda akan diajarkan cara membuat copywriting yang dahsyat dan membuat omset penjualan produk anda melesat seperti roket hanya dengan copywriting yang dahsyat."
          // banner: "20200512_111701-600x600.jpg"
          // categories: null
          // curriculums: (2) [{…}, {…}]
          // discount: -251000
          // id: 9
          // jenis: "Kelas WhatsApp"
          // mentor: {name: "Nasrullah", about: "www.nasrullah.id"}
          // name: "Copywriting Dahsyat"
          // note: "Kelas via whatsapp group"
          // price: 249000
          // promo: 500000
          // reviews: {count: 0, rating: null}
          // schedules: (2) [{…}, {…}]
          // slug: "copywriting-dahsyat"
          // status: "Publish"
          // subCategories: null
          // uuid: "3f61dde0-f166-43d5-a4d0-967e5841bbfe"

          console.log('data jadwal = ', a, a.schedule)

          this.dataDetailKelas = result;
          this.categories =
            result.categories == null ? "-" : result.categories.title;
          this.subCategories =
            result.subCategories == null ? "-" : result.subCategories.title;
          // console.log("data detail kelas ", this.dataDetailKelas);
          setTimeout(() => {
            this.$root.loadingData().hide();
          }, 2000);
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          this.$root.notification("danger", "Error", "Check Error");
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    },
    changeTab(id) {
      let dataTab = this.dataTab;
      for (let i = 0; i < dataTab.length; i++) {
        if (id == dataTab[i].id) {
          dataTab[i].active = true;
          this.sectionTab = id;
          this.$refs.form.init();
        } else {
          dataTab[i].active = false;
        }
      }
      this.goToTop();
    },
    goToTop() {
      this.$root.goToTop();
    },
    convertPayloadBody() {
      let a = this.$store.state.dataProductMentor;
      let body = {
        name: a.name,
        about: a.about,
        note: a.note,
        jenis: a.jenis,
        price: a.price,
        promo: a.promo,
        banner: a.banner,
        status: a.status,
        category: a.category,
        subCategory: a.subCategory,
        schedule: a.schedule,
        curriculum: a.curriculum
      };

      console.log("data PM ", body);

      if (this.$root.checkRole("ADMIN")) {
        return body;
      } else {
        delete body.status;
        return body;
      }
    },
    savePM() {
      let dataBody = this.convertPayloadBody();
      this.dataBody = dataBody;

      console.log('dataBody ', dataBody)

      let newBanner = this.$store.state.targetFilesBanner;

      if (this.$root.checkRole("ADMIN")) {
        if (this.section == "add") {
          this.uploadFileBanner();
        } else if (this.section == "edit") {
          if (newBanner !== "") {
            this.uploadFileBanner();
          } else {
            this.putPMAdmin(dataBody);
          }
        }
      } else {
        if (this.section == "add") {
          this.uploadFileBanner();
        } else if (this.section == "edit") {
          if (newBanner !== "") {
            this.uploadFileBanner();
          } else {
            this.putPMMentor(dataBody);
          }
        }
      }
    },
    uploadFileBanner() {
      let body = this.$store.state.formBannerPM;
      // let body = window.$cookies.get('formBannerPM');
      let AuthSth = this.$root.authorization();
      this.$root.loadingData().show();
      let vm = this;
      axios
        .post(vm.$root.filesUploadSingle(), body, {
          headers: {
            Authorization: AuthSth,
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log("res ", res);
          let data = res.data;

          this.dataBody.banner = data.files;

          if (this.$root.checkRole("ADMIN")) {
            if (this.section == "add") {
              this.postPMAdmin(this.dataBody);
            } else if (this.section == "edit") {
              this.putPMAdmin(this.dataBody);
            }
          } else {
            if (this.section == "add") {
              this.postPMMentor(this.dataBody);
            } else if (this.section == "edit") {
              this.putPMMentor(this.dataBody);
            }
          }
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }

          console.log("err ", resErr);
          vm.$root.loadingData().hide();
          vm.afterPostProfile().failed(`${resErr.data.message}`);
        });
    },
    postPMAdmin(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listKelasAdmin(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostPM().success(
            `Kelas "${body.name}" Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostPM().failed(`${resErr.data.message}`);
        });
    },
    putPMAdmin(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .put(vm.$root.listKelasAdmin() + `/${this.uuidKelas}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostPM().success(`Kelas "${body.name}" Berhasil di Perbarui`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostPM().failed(`${resErr.data.message}`);
        });
    },
    postPMMentor(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .post(vm.$root.listKelasMentor(), body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostPM().success(
            `Kelas "${body.name}" Berhasil di Tambahkan`
          );

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostPM().failed(`${resErr.data.message}`);
        });
    },
    putPMMentor(body) {
      this.$root.loadingData().show();
      let AuthSth = this.$root.authorization();
      let vm = this;
      axios
        .put(vm.$root.listKelasMentor() + `/${this.uuidKelas}`, body, {
          headers: { Authorization: AuthSth }
        })
        .then(res => {
          console.log("res ", res);

          vm.$root.loadingData().hide();
          vm.afterPostPM().success(`Kelas "${body.name}" Berhasil di Perbarui`);

          vm.$emit("init");
        })
        .catch(err => {
          let resErr = err.response;
          console.log("err ", err.response);
          vm.$root.loadingData().hide();
          vm.afterPostPM().failed(`${resErr.data.message}`);
        });
    },
    afterPostPM() {
      let vm = this;
      return {
        success(msg) {
          vm.$modal.show("dialog", {
            title: "Notice",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                  vm.$root.backPage();
                }
              }
            ]
          });
        },
        failed(msg) {
          vm.$modal.show("dialog", {
            title: "Error",
            text: msg,
            buttons: [
              {
                title: "Oke",
                default: true,
                handler: () => {
                  vm.$modal.hide("dialog");
                }
              }
            ]
          });
        }
      };
    },
    listKategori() {
      this.$root.loadingData().show();
      // let AuthSth = this.$root.authorization();
      // { headers: { Authorization: AuthSth } }
      axios
        .get(this.$root.listKategoriPage())
        .then(res => {
          console.log("res ", res);

          let result = res.data.result;
          this.$store.state.dataProductMentor.listKategoriData = result;
          this.listKategoriData = result;
          this.$root.loadingData().hide();
        })
        .catch(err => {
          let resErr = err.response;
          let auth = resErr.data.auth;
          if (resErr.status == 500 && !auth) {
            this.$root.sessionTimeUp();
          }
          console.log("error ", resErr);
          this.$root.loadingData().hide();
        });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
.content-product-mentor {
  font-family: "Montserrat";
  padding: 15px 0 30px 0;
  .container {
    .content-pm {
      > .title-pm {
        font-style: normal;
        font-weight: 600;
        font-size: 36px;
        line-height: 44px;
        color: #000000;
      }
      > .parent-tab-pm {
        margin: 15px 0;
        > .tab-pm {
          padding: 0%;
          justify-content: center;
          margin: 0%;
          > li {
            list-style: none;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: #cbcdce;
            color: #000000;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 15px;
            cursor: pointer;
            > span {
              font-weight: bold;
            }

            &[active="true"] {
              background: #003461;
              color: #ffffff;
            }
            &[disabled="disabled"] {
              opacity: 0.7;
              cursor: not-allowed;
            }
          }
        }
      }
      > .form-cpm {
        > .btn-blue-mbx {
          background: #003461;
          border-radius: 10px;
          padding: 5px 7%;
          color: #ffffff;
          border: 1px solid #003461;
          &:hover {
            background: #ffffff;
            color: #003461;
          }
        }
      }
    }
  }
}
@media (min-width: 481px) and (max-width: 576px) {
  .content-product-mentor {
    .container {
      .content-pm {
        > .title-pm {
          font-size: 27px;
        }
      }
    }
  }
}
@media (max-width: 481px) {
  .content-product-mentor {
    .container {
      .content-pm {
        > .title-pm {
          font-size: 25px;
        }
      }
    }
  }
}
</style>